import { Tab } from "./Tab"

// Instance
export const GetTest = () => {
  var qI = new Tab();
  qI.Items = [
    {
      Title: 'London',
      Content: 'A'
    },
    {
      Title: 'Paris',
      Content: 'B'
    }
  ]

  qI.IdRender = (v) => v.Title
  return qI;
}

// Property
export const GetDefault_CurrentItem = (q) => {
  return q.Items && q.Items.length !== 0 ? q.Items[0] : undefined
}

export const GetDefault_IdRender = (q) => {
  return (v) => v.id
}

export const GetDefault_TitleRender = (q) => {
  return (v) => v.Title
}

export const GetDefault_TitleView_Render = (q) => {
  return (v) => {
    var active = q.CurrentItem === v ? 'active' : ''
    return <div className={`tab__title ${active}`}
      onClick={() => q.SetCurrent(v)}
      key={q.IdRender(v)}>{q.TitleRender(v)}</div>
  }
}

export const GetDefault_ContentRender = (q) => {
  return (v) => v.Content
}

export const GetDefault_ContentWrapper_Render = (q) => {
  return (v) => {
    return <div key={q.IdRender(v)}>
    {q.ContentRender(v)}
  </div>
  }
}