import { LeftNavVM } from "./LeftNavVM"
import * as modelUtil from './LeftNavUtil'
import { PMDataVM } from "../../../single/form/PMDataVM";
import * as genUtil from '../../../../../util/generalUtil'

export const pmDataVM = () => PMDataVM.Instance;

export const get = (q) => {
  var qI = new LeftNavVM();

  qI.ModelItem = q;
  genUtil.Add(
    pmDataVM().OnSet_CurrentProject_Func_Dict,
    () => qI.CurrentProject = pmDataVM().CurrentProject,
    false);

  return qI;
}

export const GetTest = () => {
  return get(modelUtil.GetTest());
}

// Property
export const GetItems = (q) => {
  return q.ModelItem.Items;
}

export const GetPreTitle = (q) => {
  return q.CurrentProject ? 'DỰ ÁN'
    : 'CÁC DỰ ÁN';
}

export const GetTitle = (q) => {
  return q.CurrentProject ? q.CurrentProject.Name 
    : '';
}