import * as genUtil from '../../../../../../util/generalUtil'
import * as itemUtil from './MGI_ItemUtil'
import * as mgii_ct from './MGI_I_CreateType'
import * as util from './MassGroupItemUtil'

export default class MassGroupItem {
  Field
  SubComponents = []
  NotIncludeValues = []
  IsHaveValueOrder
  BindingRows
  SupComponent

  Items = []

  _id
  get id() {
    if (!this._id) {
      this._id = genUtil.newGUID();
    }
    return this._id;
  }
  set id(v) {
    this._id = v;
  }

  get Index() {
    return this.SupComponent.Items.indexOf(this);
  }

  get Rank() {
    return this.SupComponent.Rank
  }

  get OrderItems() {
    return this.Items;
  }

  get OrderValues() {
    return this.Items.map(x => x.Value);
  }

  get ApiData() {
    var items = this.Items.map(x => x.ApiData);

    return {
      _id: this.id,
      field: this.Field,
      subComponents: this.SubComponents.map(x => x.ApiData),
      valueOrders: this.ValueOrders,
      notIncludeValues: this.NotIncludeValues,
      items
    }
  }

  AddNotIncludeValue = (v, handleUndo) => {
    var list = this.NotIncludeValues;
    var index = list.indexOf(v);
    if (index === -1) {
      this.NotIncludeValues.push(v);

      if (handleUndo){
        index = list.length -1;
        handleUndo(() => {
          list.splice(index, 1);
        })
      }
    }
  }

  GetItem = (v, title, handleUndo) => {
    var index = this.Items.findIndex(x => x.Value === v);
    if (index === -1) {
      var item = itemUtil.create(this)
      item.Value = v;
      item.Title = title;
      this.Items.push(item);

      if (handleUndo){
        var length = this.Items.length
        handleUndo(() => {
          this.Items.splice(length -1, 1);
        })
      }

      return item;
    }
    return this.Items[index];
  }

  SupRows = []

  // Những Row được add bằng Phương thức HandleCreatedByUserRows bên dưới
  CreatedByUser_Rows = []

  On_AddSupRow_Func_Dict = []

  AddSupRow = (v) => {
    var list = this.SupRows;
    if (list.indexOf(v) !== -1) return;

    this.NewRow = v;
    list.push(v);
    this.HandleCreatedByUserRows(v);

    genUtil.Handle(this.On_AddSupRow_Func_Dict, (v) => this.On_AddSupRow_Func_Dict = v);
  }

  OnIsFirstSetSupRow_Funcs = []

  isFirstSetSupRow
  get IsFirstSetSupRow() {
    return this.isFirstSetSupRow;
  }
  set IsFirstSetSupRow(v) {
    this.isFirstSetSupRow = v;
    this.OnIsFirstSetSupRow_Funcs.forEach(x => x());
  }

  get HaveMassItemSubComponents() {
    return this.SubComponents.filter(x => x.IsHaveMassItem);
  }

  table
  get Table() {
    if (!this.table) {
      this.table = util.GetTable(this);
    }
    return this.table;
  }
  set Table(v) {
    this.table = v;
  }

  HandleCreatedByUserRows = (newSupRow) => {
    var userItems = this.Items
      .filter(x => x.CreateType === mgii_ct.byuser && !x.IsAdd_Done);

    // tiến hành Sort UserItems
    var sortedUserItems = []
    var isFirst_Handle = true;

    while (userItems.length !== 0) {
      let filter
      const validIndexs = []

      if (isFirst_Handle) {
        // Không có SupItem hoặc SupItem không tạo bởi User
        filter = (x) => x.SupItem === undefined || 
          x.SupItem.CreateType !== mgii_ct.byuser;
        isFirst_Handle = false;
      } else {
        filter = (x) =>
          sortedUserItems.findIndex(y => y.Value === x.SupValue) !== -1
      }

      userItems.forEach((x, i, a) => {
        if (this.IsDebug){
          debugger
        }

        if (filter(x)) {
          sortedUserItems.push(x);
          validIndexs.push(i);
        }
      })

      validIndexs.forEach((x, i) => {
        userItems.splice(x - i, 1);
      })
    }

    sortedUserItems.forEach(x => {
      var value = x.Value;
      var supRows = this.SupRows.concat(this.CreatedByUser_Rows);
      var supRow

      var isValid = true;
      if (!x.SupValue && supRows.length !== 0) {
        supRow = supRows[0]
      } else {
        var index = supRows.findIndex(y => y.Name === x.SupValue);
        if (index !== -1) {
          supRow = supRows[index];
        } else {
          isValid = false;
        }
      }

      if (!isValid) return false;

      // Gán nhãn IsAdd_Done
      x.IsAdd_Done = true;

      const addRow = (supRow) => {
        var row = this.Table.GetRow(value, supRow);
        row.FullValue = value;
        row.IsCreatedByUser = true;

        // Lưu vào danh sách Tạo bởi người dùng trong đối tượng Table
        this.Table.CreatedByUser_Rows.push(row);
        this.CreatedByUser_Rows.push(row);
        return row;
      }

      const handleRow = (row) => {
        row.MassGroupItem = this;
        handleNonField(row);
      }

      const handleNonField = (row) => {
        var subMgcIndex = this.SubComponents.findIndex(y =>
          y.Filter.Values.includes(x.Value))
        if (subMgcIndex !== -1) {
          var subMgi = this.SubComponents[subMgcIndex].Items[0];
          subMgi.AddSupRow(row);
        }
      }

      var replaceMgi = x.NewMGI;
      if (replaceMgi) {
        var supName = x.SupValue;

        replaceMgi.GetItem(value);
        this.Table.Handle_ReplaceMGI(replaceMgi, supName, addRow, handleRow);
      } else {
        handleRow(addRow(supRow));
      }
    })
  }
}