import * as util from './MassItemUtil'
import * as mif from './MassItemField'
import { message } from 'antd'
import * as api from '../../../../../util/apiUtil'
import { PMData } from '../../../single/model/PMData'
import * as genUtil from '../../../../../util/generalUtil'

export const pmData = () => PMData.Instance;

export default class MassItem {
  projectId

  project
  get Project() {
    if (!this.project) {
      this.project = util.GetProject(this);
    }
    return this.project;
  } set Project(v) {
    this.project = v;
  }

  workpackageId

  workpackage
  get Workpackage() {
    if (!this.workpackage) {
      this.workpackage = util.GetWorkpackage(this);
    }
    return this.workpackage;
  }

  phases
  get Phases() {
    if (!this.phases) {
      this.phases = util.GetPhases(this);
    }
    return this.phases;
  } set Phases(v){
    this.phases = v;
  }

  get ValidPhasesByInput() {
    return util.GetValidPhasesByInput(this);
  }

  IsGetElement = false

  OnGetElementDone_Funcs = []

  set GetElementDone(v) {
    this.OnGetElementDone_Funcs.forEach(x => x());
  }

  GetValue = (field, handle) => {
    if (!field) {
      return;
    }

    var isHandle
    if (field === mif.level) {
      var obj = this.LevelItem;

      // Chưa truy xuất Value
      if (!obj.IsGet_Value && handle) {
        isHandle = true;

        genUtil.Add_2(
          obj.OnSet_Value_Func_Dict,
          () => {
            handle(obj.Value)
          }
        );
      }

      // return obj.Value;
      return {
        Value: obj.Value,
        IsHandle: isHandle
      }
    }

    // return this.GetValue_Mas(field);
    return {
      Value: this.GetValue_Mas(field),
      IsHandle: isHandle
    }
  }

  GetValue_Mas = (field) => {
    if (!field) {
      return;
    }

    var data = mif.GetFullFields(this, field);
    var { fields, isFromElement } = data;

    var getValFunc = () => {
      var obj = this;
      var isValid = true;

      fields.forEach(x => {
        if (isValid) {
          var temp = obj[x];

          obj = temp;
          if (obj === undefined) {
            isValid = false;
          }
        }
      })

      if (field === mif.Value) {
        var roundingFactor = 2;

        if (this.Unit === 'each') {
          roundingFactor = 0;
        }

        obj = obj.toLocaleString('en-US', { maximumFractionDigits: roundingFactor });
      }

      return obj;
    }

    if (!isFromElement || this.Element) {
      var val = getValFunc();
      return val;
    } else {
      return undefined;
    }
  }

  get ApiData() {
    return {
      _id: this._id,
      projectId: this.projectId,
      elementId: this.elementId,
      workpackageId: this.workpackageId,
      exportSetId: this.exportSetId,
      exportSetVersionId: this.exportSetVersionId,
      value: this.Value,
      unit: this.Unit,
      massType: this.MassType,
      info: this.Info
    }
  }

  get ApiData_ForInput(){
    return {
      valueData : this.ValueDict.Data.map(x => x.ApiData),
      info : this.Info
    }
  }

  AddDatabase = (handle) => {
    var apiData = this.ApiData;
    var { _id } = apiData;
    var url = `https://api.bimdev.vn/api/hbm`;

    var command = _id ? 'edit' : 'add';
    const apiFunc = () => _id ?
      api.put(`${url}/${command}`, apiData) : api.post(`${url}/${command}`, apiData);

    apiFunc().then(response => response.data)
      .then(data => {
        if (command === 'add') {
          this._id = data._id;
        }
        if (handle) {
          handle(data);
        }
        message.success(`${command} mass successfully`);
      })
  }

  DeleteDatabase = (handle) => {
    var apiData = {
      _id: this._id
    };
    var url = `https://api.bimdev.vn/api/hbm`;
    const apiFunc = () => api.put(`${url}/delete`, apiData);

    apiFunc().then(response => response.data)
      .then(data => {
        if (handle) {
          handle(data);
        }
        message.success(`delete mass successfully`);
      })
  }

  // LevelGroup
  OnGet_TLS_LevelGroup_Func_Dict = []

  get_TLS_LevelGroup
  get Get_TLS_LevelGroup(){
    return this.get_TLS_LevelGroup
  } set Get_TLS_LevelGroup(v){
    this.get_TLS_LevelGroup = v;
  }

  tls_LevelGroup
  get TLS_LevelGroup() {
    if (!this.tls_LevelGroup) {
      util.Handle_TLS_LevelGroup(this);
    } return this.tls_LevelGroup;
  } set TLS_LevelGroup(v) {
    this.tls_LevelGroup = v;
    this.Get_TLS_LevelGroup = true

    this.OnGet_TLS_LevelGroup_Func_Dict.forEach(x => x.Value());
  }

  OnGet_FullValue_Func_Dict = []
  set Get_FullValue(v) {
    genUtil.Handle(this.OnGet_FullValue_Func_Dict,
      (v) => this.OnGet_FullValue_Func_Dict = v);

    this.get_FullValue = v;
  }

  // ValueDict
  valueDict
  get ValueDict(){
    if (!this.valueDict){
      this.valueDict = util.GetValueDict(this)
    } return this.valueDict
  }

  // Phase
  phaseItems
  get PhaseItems() {
    if (!this.phaseItems) {
      this.phaseItems = util.GetPhaseItems(this);
    }
    return this.phaseItems;
  }

  GetPhaseItem = (phaseName) => {
    var list = this.PhaseItems;
    var index = list.findIndex(x => x.Phase.Name === phaseName);
    return list[index];
  }

  lastest_PhaseItem
  get Lastest_PhaseItem() {
    if (!this.lastest_PhaseItem) {
      this.lastest_PhaseItem = util.Get_Lastest_PhaseItem(this);
    }
    return this.lastest_PhaseItem;
  }

  // Level
  levelItem
  get LevelItem() {
    if (!this.levelItem) {
      this.levelItem = util.GetLevelItem(this);
    }
    return this.levelItem;
  }

  // LevelItem của phiên bản mới nhất
  originLevelItem
  get OriginLevelItem() {
    if (!this.originLevelItem) {
      this.originLevelItem = util.GetOriginLevelItem(this);
    }
    return this.originLevelItem;
  }

  // Site
  site
  get Site(){
    return this.site
  } set Site(v){
    this.site = v;
  }
}