import { LS_ItemVM } from "./LS_ItemVM"
import * as genUtil from '../../../../../../util/generalUtil'

// Instance
export const get = (q, dict) => {
  var qI = new LS_ItemVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Proeprty
export const Handle_Value_StorageList = (q) => {
  if (q.IsGet_Value_StorageList) return undefined;
  q.IsGet_Value_StorageList = true;

  var project = q.Project.ModelItem

  var setObj = (levelDict) => {
    q.Value_StorageList = levelDict.Items.map(x => x.Name);
  }

  if (project.LevelDict){
    setObj(project.LevelDict)
  } else {
    genUtil.Add_2(
    project.OnSet_LevelDict_Func_Dict,
    () => {
      setObj(project.LevelDict)
    }, false);
  }
}