import * as dataVMUtil from './MassTypeSettingDataVMUtil'
import { MassTypeSettingVM } from './MassTypeSettingVM'
import * as modelUtil from './MassTypeSettingUtil'

export const get = (q) => {
  if (!q) return undefined;
  
  var qI = new MassTypeSettingVM();

  qI.ModelItem = q;

  q.OnNameChanged = (v) => {
    if (q.M2VM){
      qI.Name = v;
    }
  }

  q.OnDescriptionChanged = (v) => {
    if (q.M2VM){
      qI.Description = v;
    }
  }

  q.OnDataChanged = (v) => {
    if (q.M2VM){
      qI.Data = v;
      qI.DataVM = qI.GetDataVM();
    }
  }

  qI.OnGetDataVM = () => {
    var data = q.Data;
    return dataVMUtil.get(data, qI);
  }    

  return qI;
}

export const GetDefault = () => {
  return get(modelUtil.GetDefault())
}