import { TLS_Filter } from "./TLS_Filter";
import { TLS_FilterValue } from "./TLS_FilterValue";

export const get = (data, dict) => {
  var qI = new TLS_Filter();
  qI.Field = data.field;

  if (data.items){
    qI.Items = data.items.map(x => {
      var obj = new TLS_FilterValue();
      obj.Value = x.value;
      obj.InputType = x.inputType
      return obj;
    })
  } else {
    qI.Items = data.values.map(x => {
      var obj = new TLS_FilterValue();
      obj.Value = x;
      return obj;
    })
  }

  qI.Dict = dict;
  return qI;
}

export const GetDefault = (dict) => {
  var qI = new TLS_Filter();
  qI.Dict = dict;
  return qI;
}