import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Divider, Input, Select, Spin } from "antd";
import { DO_ElementVM } from "./DO_ElementVM";
import './comp/DO_Comp.css'

class DO_ElementView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DO_ElementVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <span className='doelement'>
          {data.Title}

          {/* Toggle Sub Button */}
          {data.IsHaveButton ?
            <Button className='doelement__button'
              type='primary' size='small'
              onClick={data.ToggleSub}>
                {data.ToggleSubContentVM.Content}
              </Button>
            : ''}

          {/* Sub Items */}
          {data.IsOpenSub ?
            <div className='doelement__dict'>
              {data.SubItems.map(x => {
                return <DO_ElementView key={x.Title}
                  store={x} />
              })}
            </div>
            : ''}
        </span>
      </>
    )
  }
}

export default observer(DO_ElementView)