import { makeObservable, observable, action } from 'mobx';

export default class EntHoverVM {
  constructor() {
    makeObservable(this, {
      IsElementVisible: observable,
    })
  }

  IsElementVisible = false;

  ShowElement = () => {
    this.IsElementVisible =  true;
  }

  HideElement = () => {
    this.IsElementVisible =  false;
  }
}