import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Row } from 'antd';
import { TypicalLevelSettingVM } from './TypicalLevelSettingVM';
import TypicalLevelSettingDataView from './TypicalLevelSettingDataView';
import NewInputView from '../../../../input/NewInputView';
import './TypicalLevelSetting.css'
import { SaveFilled } from '@ant-design/icons';
import LevelSettingDataView from '../level/LevelSettingDataView';

class TypicalLevelSettingView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new TypicalLevelSettingVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='projectcomp__level'>
          <div className='projectcomp__title'>
            <h3>THIẾT LẬP TẦNG</h3>
          </div>
          <LevelSettingDataView store={data.DataVM.Binding} />
        </div>

        <hr />

        <div className='projectcomp__typicallevel'>
          <div className='projectcomp__title'>
            <h3>THIẾT LẬP TẦNG ĐIỂN HÌNH</h3>
          </div>
          <TypicalLevelSettingDataView store={data.DataVM.Typical} />
        </div>
      </>
    );
  }
}

export default observer(TypicalLevelSettingView);
