import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './LS_ItemUtil'

export class LS_Item {
  _id

  tempId
  get TempId(){
    if (!this.tempId){
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  get Index (){
    return this.Dict.Items.indexOf(this);
  }

  Delete = () => {
    this.Dict.Items.splice(this.Index, 1);
  }

  // Data
  Name = ''

  Values = []

  get ApiData() {
    var obj = {
      name: this.Name,
      values: this.Values
    }
    return obj;
  }
}