import { message } from 'antd';
import { url } from './ExportSet_Url'
import * as genUtil from '../../../../../util/generalUtil'
import * as util from './ExportSetUtil'
import * as rf from './ExportSet_RetrieveFrom'

export class ExportSet {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Name

  Description

  massType
  get MassType(){
    if (!this.massType){
      this.massType = 'Concrete'
    } return this.massType
  } set MassType(v){
    this.massType = v;
  }

  // Project
  get Project(){
    return this.Dict.Project;
  }

  get ApiData() {
    var dict = this.Dict;

    var obj = {
      _id: this._id,
      projectId: dict.Project?._id,
      name: this.Name,
      description: this.Description,
      massType : this.MassType,
      retrieveFrom : rf.input,
    }

    return obj;
  }

  SaveDatabase = (showMessage = true, handle, isSaveData = true) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.DeleteDatabase(true)
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, url, () => {
      if (showMessage){
        message.success('delete successflly!');
      }
      if (handle){
        handle()
      }
    });
  }

  // Version
  NewVersion = (handle) => {
    return util.NewVersion(this, handle)
  }

  // Save MassItems
  SaveMassItems = () => {
    util.SaveMassItems(this)
  }
  
  // MassItem
  OnSet_MassItems_Func = []

  massItems
  get MassItems(){
    if (!this.massItems){
      util.Handle_MassItems(this)
    } return this.massItems
  } set MassItems(v){
    this.massItems = v;

    genUtil.Handle(this.OnSet_MassItems_Func,
      (v) => this.OnSet_MassItems_Func = v);
  }
}