import { makeObservable, observable, action } from 'mobx';

export class CheckboxVM {
  constructor() {
    makeObservable(this, {
      ischecked: observable,
      isDisable : observable,
      Cancel : observable
    })
  }

  name

  OnCheckChanging

  Cancel = false;

  ischecked = undefined
  set Ischecked(v) {
    this.ischecked = v;
    if (this.onCheckChange) {
      this.onCheckChange(v);
    }
  }

  isDisable = false
}