import { EntDelete } from "../../../../../../delete/EntDelete";
import { AddTemplateVM } from "./AddTemplateVM"
import * as d_tt from '../../../../../../delete/EntDelete_ToggleType'
import { Button, message } from "antd";
import InputView from "../../../../../../input/InputView";

// Instance
export const get = (q, dict) => {
  var qI = new AddTemplateVM(dict);

  qI.ModelItem = q;
  qI.Dict = dict;

  qI.Tool.AdditionElement = q.DeleteView

  return qI;
}

// Property
export const GetTool = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.mouseEvent

  qI.OnPre_ShowHover = () => {
    q.InputNameVM.Content = undefined
  }

  qI.Modal_InfoView = <InputView store={q.InputNameVM} />
  qI.Modal_ContentView = <div className='mgs__template__modalcontent'>
    {qI.Modal_InfoView}
    {qI.Modal_ButtonView}
  </div>

  qI.Retrieve_ShowHover_Button = () => <div>
    <Button type='primary' size='small'
      onClick={qI.Show_Hover}>
      Add Template
    </Button>
    {q.Dict ? q.Delete.View : ''}
  </div>

  qI.OnPreHide = () => {
    if (q.Delete.Is_Hover_Visible) {
      qI.IsRun_Cancel = true;
    }
  }

  qI.OnPreRun = () => {
    if (!q.InputName || q.InputName.length === 0) {
      qI.IsRun_Cancel = true
      message.warn('Vui lòng nhập tên template!');
    }
  }

  qI.OnRun = q.Run

  return qI;
}

// Method
export const SetDict = (q, v) => {
  var q1 = v;
  q1.DeleteVM.OnSet_Is_Hover_Visible = (v1) => {
    var tool = q.Tool
    if (!v1) {
      // tool.Is_Visible = false
    } else {
      if (tool.Is_Hover_Visible) {
        tool.Is_Hover_Visible = false
      }
      tool.Is_Visible = true
    }
  }
}