import Column from "antd/lib/table/Column";
import { PMData } from "../../../../single/model/PMData";
import MassCell from "./MassCell"
import * as mif from '../../item/MassItemField'
import * as filterUtil from '../../../filter/EntFilterUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import { MI_ValueItem } from "../../item/value/MI_ValueItem";
import * as m_ut from '../../unit/MassUnitType'
import { MC_Update } from "./update/MC_Update"

export const pmData = () => {
  return PMData.Instance;
}

export const get = (col, row, dict) => {
  var qI = genUtil.SingleFilter(row.Cells, (x) => x.Column === col)
  col.Cells.push(qI)

  return qI;
}

export const GetIsHaveVCS_Item = (q) => {
  return q.Dict.ColumnField === mif.Phase
    && pmData().MassResultComp.Phases.length === 2
    && q.ColumnIndex === 1;
}

export const GetValueColor = (q) => {
  if (q.IsHaveVCS_Item && q.VCS_Item) {
    return '#FFFFFF';
  } else {
    return '#1890ff';
  }
}

export const GetTDBgColor = (q) => {
  if (q.IsHaveVCS_Item && q.VCS_Item) {
    return q.VCS_Item.Color;
  } else {
    return '#FFFFFF';
  }
}

// Liên quan tới việc lấy TotalValue
export const Handle_VCS_Item = (q) => {
  var list = pmData().VersionCompareSetting.Items;

  var cells = q.Row.Cells;
  var compareIndex = cells.findIndex(x => x.ColumnIndex === 0);
  if (compareIndex === -1) {
    q.VCS_Item = undefined;
    return
  }
  var compareItem = cells[compareIndex];

  const getItem = () => {
    var ratio = q.TotalValue / compareItem.TotalValue;
    var index = list
      .findIndex(x => filterUtil.IsValid(x.Filter, ratio));
    return index !== -1 ? list[index] : undefined;
  }

  if (compareItem.TotalValue !== undefined) {
    q.VCS_Item = getItem();
  } else {
    genUtil.Add(compareItem.OnGet_TotalValue_Func_Dict, () =>
      q.VCS_Item = getItem())
  }
}

export const GetMassType = (q) => {
  var massType = undefined
  if (q.SubCells) {
    var isPending = true;
    q.SubCells.forEach(x => {
      if (isPending && x.MassType) {
        massType = x.MassType;
        isPending = false;
      }
    })
  } else {
    if (q.Items && q.Items.length > 0) {
      massType = q.Items[0].MassType;
    }
  }
  return massType;
}

export const Handle_TLS_LevelGroups = (q) => {
  if (q.IsGet_TLS_LevelGroups) return undefined;
  q.IsGet_TLS_LevelGroups = true;

  var list = [];
  var handledCount = 0;
  var count = q.SubCells ?
    q.SubCells.length : q.Items.length;

  const checkadd_Val = (val, list) => {
    if (list.findIndex(x => x.id === val.id) === -1) {
      list.push(val);
    }
  }

  const handle_Val = (val) => {
    handledCount++;
    if (val) {
      if (Array.isArray(val)) {
        val.forEach(x => checkadd_Val(x, list));
      } else {
        checkadd_Val(val, list)
      }
    }
    if (handledCount === count) {
      q.TLS_LevelGroups = list;
    }
  }

  if (q.SubCells) {
    // Tồn tại sub cells
    q.SubCells.forEach(x => {
      if (x.TLS_LevelGroups) {
        handle_Val(x.TLS_LevelGroups);
      } else {
        genUtil.Add(x.OnGet_TLS_LevelGroups_Func_Dict,
          () => handle_Val(x.TLS_LevelGroup));
      }
    })
  } else {
    // Không có sub cells
    q.Items.forEach(x => {
      var targetObj = x;

      // Kiểm tra value theo Phase hay theo Property khác (Project ...)
      var table = q.Dict;
      var colField = table.ColumnField;
      if (colField === mif.Phase) {
        // Khối lượng theo Phase
        targetObj = x.GetPhaseItem(q.Column.Name);
      } else {
        // Khối lượng theo các thuộc tính khác
      }

      if (targetObj.TLS_LevelGroup || targetObj.Get_TLS_LevelGroup) {
        handle_Val(targetObj.TLS_LevelGroup);
      } else {
        genUtil.Add(targetObj.OnGet_TLS_LevelGroup_Func_Dict,
          () => handle_Val(targetObj.TLS_LevelGroup));
      }
    })
  }
}

// Unit
// TotalValue
export const ReHandle_TotalValueData = (q) => {
  q.IsGet_TotalValueData = false;
  Handle_TotalValueData(q);
}

export const Handle_TotalValueData = (q) => {
  if (q.IsGet_TotalValueData) return undefined;
  q.IsGet_TotalValueData = true;

  var totalVal_Data = m_ut.GetList().map(x => {
    var obj = new MI_ValueItem();
    obj.Unit = x;
    return obj;
  })

  var handledCount = 0;
  var list = q.SubCells ? q.SubCells : q.Items
  var count = list.length

  const handle_Val = (valData, item) => {
    handledCount++;

    var deleteIds = []
    totalVal_Data.forEach((x, i) => {
      var unit = x.Unit;
      var item = genUtil.SingleFilter(valData,
        (a) => a.Unit === unit);

      if (item === undefined) {
        deleteIds.push(i);
      } else {
        x.Value += item.Value;
      }
    })

    deleteIds.forEach((x, i) => {
      totalVal_Data.splice(x - i, 1);
    })

    if (handledCount === count) {
      q.TotalValueData = totalVal_Data;
    }
  }

  if (q.SubCells) {
    // Tồn tại sub cells
    q.SubCells.forEach(x => {
      if (x.TotalValueData) {
        handle_Val(x.TotalValueData, x)
      } else {
        genUtil.Add_2(x.OnGet_TotalValueData_Func,
          () => {
            handle_Val(x.TotalValueData, x)
          });
      }
    })
  } else {
    // Không có sub cells
    q.Items.forEach(x => {
      var targetObj = x.ValueDict;

      // Kiểm tra value theo Phase hay theo Property khác (Project ...)
      var table = q.Dict;
      var colField = table.ColumnField;
      if (colField === mif.Phase) {
        // Khối lượng theo Phase
        targetObj = x.GetPhaseItem(q.Column.Name);
      } else {
        // Khối lượng theo các thuộc tính khác
      }

      if (targetObj.FullData !== undefined) {
        handle_Val(targetObj.FullData, x)
      } else {
        genUtil.Add_2(targetObj.OnGet_FullData_Func_Dict,
          () => {
            handle_Val(targetObj.FullData, x)
          });
      }
    })
  }
}

export const Set_TotalValueData = (q, v) => {
  q.Row.AddHandleTotalData_Cell(q);
}

export const Handle_CurrentTotalValueItem = (q) => {
  var unit = q.Unit;
  if (!unit) return;

  const setObj = () => {
    q.CurrentTotalValueItem =
      genUtil.SingleFilter(q.TotalValueData,
        (x) => x.Unit === unit)
  }

  if (q.TotalValueData) {
    setObj()
  } else {
    genUtil.Add_2(
      q.OnGet_TotalValueData_Func,
      setObj
    )
  }
}

// Warning
export const GetIsHaveWarning = (q) => {
  if (q.Row.Field !== mif.level) {
    return false
  } else {
    const getWarningFunc = (levelGroups) => {
      return levelGroups.length !== 0;
    }

    if (q.TLS_LevelGroups) {
      return getWarningFunc(q.TLS_LevelGroups);
    } else {
      genUtil.Add(q.OnGet_TLS_LevelGroups_Func_Dict,
        () => {
          q.IsHaveWarning = getWarningFunc(q.TLS_LevelGroups)
        });
    }
  }
}

export const Handle_TLS_LevelGroup_Descriptions = (q) => {
  if (q.IsGet_TLS_LevelGroup_Descriptions) return undefined;
  q.IsGet_TLS_LevelGroup_Descriptions = true;

  const handle_Val = (val, list, handledCount, count) => {
    handledCount++;
    if (list.indexOf(val) === -1) {
      list.push(val);
    }
    if (handledCount === count) {
      q.TLS_LevelGroup_Descriptions = list;
    }
  }

  const getDescFunc = (levelGroups) => {
    var list = [];
    var handledCount = 0;
    var count = levelGroups.length

    levelGroups.forEach(x => {
      if (x.FullDescription) {
        handle_Val(x.FullDescription, list, handledCount, count);
      } else {
        // Để false, xử lý nhiều Funcs hơn, nhưng thực thi thành công :v
        genUtil.Add(x.OnGetLevelStorageList_Func_Dict, () => {
          handle_Val(x.FullDescription, list, handledCount, count)
        }, false)
      }
    })
  }

  if (q.TLS_LevelGroups) {
    getDescFunc(q.TLS_LevelGroups)
  } else {
    q.OnGet_TLS_LevelGroups_Funcs.push(() => {
      getDescFunc(q.TLS_LevelGroups)
    })
  }
}

// Sup - Sub
export const Get_SupItem = (q) => {
  if (q.IsGet_SupItem) return undefined;
  q.IsGet_SupItem = true;

  var supRow = q.Row.SupItem;
  if (!supRow) return undefined;

  var list = supRow.Cells;
  var index = list.findIndex(x => x.Column === q.Column);
  return index !== -1 ? list[index] : undefined
}

export const Get_SubCells = (q) => {
  var subRows = q.Row.SubItems;
  if (subRows && subRows.length !== 0) {
    var col = q.Column;
    var list = subRows.map((x, i, a) => {
      var obj = x.GetCellByColumn(col)
      return obj;
    }).filter(x => x !== undefined);

    return list;
  }
}

// ValueString
export const Get_ValueString = (q) => {
  if (!q.CanHave_TotalValue) return '';

  var totalValue = q.TotalValue;
  if (!totalValue) return '';

  var roundingFactor = 2;
  var unit = q.Unit;
  switch (unit) {
    case 'each':
      unit = 'cái';
      roundingFactor = 0;
      break;
    default:
      roundingFactor = 2;
      break;
  }

  var formatValue = totalValue.toLocaleString('en-US', { maximumFractionDigits: roundingFactor });
  return `${formatValue} ${unit}`;
}

// Update
export const GetUpdate = (q) => {
  var qI = new MC_Update();
  qI.Host = q;

  return qI;
}