import { PMData } from "../../../single/model/PMData";
import MassItem_Dict from "../item/MassItem_Dict"

const pmData = () => PMData.Instance

//
export const GetMassItem_Dict = (q) => {
  var qI = new MassItem_Dict();
  qI.Dict = q;
  return qI;
}

// Method
export const GetApiData = (q) => {
  var skip;
  var take;

  // skip = 0;
  // take = 10;

  var obj = {
    projectIds :  pmData().CurrentProject ? 
      [ pmData().CurrentProject._id ] : q.Projects.map(x => x._id),
    phaseIds : q.IsHavePhase ? q.Phases.map(x => x._id) : undefined,
    skip : skip,
    take : take
  }
  
  return obj;
}