import MassTable from "../table/MassTable";

// Property
export const GetMassTable = (q) => {
  var qI = new MassTable();

  qI.OnIsUpdateDataDone_Funcs.push(() => {
    q.IsDataPending = false
  })
  qI.OnSetHandledRatio_Funcs.push((v) => {
    // q1.HandledRatio = v;
    q.HandledRatio = v;
  })

  qI.Dict = q;
  qI.Items = q.Items;

  return qI;
}