import { PMData } from '../../../single/model/PMData';
import { LevelSettingData } from './LevelSettingData';
import * as itemUtil from './item/LS_ItemUtil';

export const pmData = ()  =>{
  return PMData.Instance;
}

// Instance
export const GetFromDb = (data, dict) => {
  var qI = new LevelSettingData();
  
  qI.Items = data.items.map(x => itemUtil.GetFromDb(x, qI));

  return qI;
}

export const GetDefault = (dict) => {
  var qI = new LevelSettingData();

  qI.Items = []

  return qI;
}

// Property
export const GetLevelStorageList = (q) => {
  return q.Items.map(x => x.Name);
}