import { ExportSet_Dict } from "./ExportSet_Dict"
import * as iUtil from '../item/ExportSetUtil'
import * as genUtil from '../../../../../util/generalUtil'
import Project from "../../project/Project";
import * as rf from '../item/ExportSet_RetrieveFrom'
import MassItem from '../../mass/item/MassItem';
import { MI_ValueItem } from '../../mass/item/value/MI_ValueItem';
import { message } from "antd";
import { string } from "../../mass/table/export/MT_Cell_ValueType";

// Instance
export const get = (dict) => {
  var qI = new ExportSet_Dict();

  if (dict instanceof Project) {
    qI.Project = dict;
  }
  qI.Dict = dict;

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => iUtil.GetFromDb(x, q))
    q.IsGet_Items = false;
  }

  const getApi = () => {
    var apiData = {
      projectId: q.Project?._id,
      retrieveFrom : rf.input
    }

    iUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }

  getApi()
}


// Method
