import { makeObservable, observable, action } from 'mobx';
import * as mif from '../item/MassItemField'
import * as colUtil from './MassColumnUtil'
import * as rowUtil from './row/MassRowUtil'
import * as cellUtil from './cell/MassCellUtil'
import * as util from './MassTableUtil'
import { PMData } from '../../../single/model/PMData';
import * as filterUtil from '../../filter/EntFilterUtil'
import * as textUtil from '../../../../text/TextVMUtil'
import { message } from 'antd';
import { PMDataVM } from '../../../single/form/PMDataVM';
import dayjs from 'dayjs';
import * as eeUtil from '../../../../export/excel/ExcelExportUtil';
import * as wbUtil from './export/MT_WorkbookUtil';
import InputView from '../../../../input/InputView';
import * as selectUtil from '../../../../select/SelectVMUtil';
import SelectView from '../../../../select/SelectView';
import Modal from 'antd/lib/modal/Modal';
import * as genUtil from '../../../../../util/generalUtil'
import * as logUtil from '../../../../../util/loggingUtil'
import * as u_sn from './row/update/MRU_SectionName'
import * as uc_sn from './cell/update/MCU_SectionName'
import * as u_dt from '../../update/Update_DirectionType'
import { MassTBody } from './tbody/MassTBody'


export const pmData = () => PMData.Instance;
export const pmDataVM = () => PMDataVM.Instance;

export default class MassTable {
  constructor() {
    makeObservable(this, {
      IsValidTable: observable,
      Columns: observable,
      MainRows: observable,
      Cells: observable,
      columnField: observable,
      tbody : observable
    })
  }

  // id
  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  items
  get Items() {
    return this.items;
  }
  set Items(v) {
    this.items = v;

    if (v && v.length !== 0) {
      this.Init();
      this.Build();
      this.IsValidTable = true;
    }
    else {
      this.IsValidTable = false;
    }
  }

  IsValidTable = false

  columnField = mif.Project
  get ColumnField() {
    return this.columnField;
  }
  set ColumnField(v) {
    this.columnField = v;
  }

  Columns = undefined
  FullRows = undefined
  MainRows = undefined
  Cells = undefined

  Init = () => {
    this.Columns = [];
    this.MainRows = [];

    this.FullRows = [];
    this.Cells = [];

    // Reset các dòng tạo bởi người dùng
    this.CreatedByUser_Rows = []
    this.Handled_CreatedByUser_Rows = []
  }

  get AllRows() {
    return util.GetAllRows(this);
  }

  GetColumn = (name, data) => {
    var list = this.Columns;
    var item = undefined;
    var index = list.findIndex(x => x.Name === name);
    if (index === -1) {
      item = colUtil.get(name, this, data);
      list.push(item);
      return item;
    }
    else {
      return list[index];
    }
  }

  GetRow = (v, supItem) => {
    var list = undefined;
    if (supItem) {
      list = supItem.SubItems;
    } else {
      list = this.MainRows;
    }

    var item = undefined;
    var index = list.findIndex(x => x.Name === v);
    if (index === -1) {
      item = rowUtil.get(v, this);
      item.SupItem = supItem;

      list.push(item);
      this.FullRows.push(item);
      return item;
    }
    else {
      return list[index];
    }
  }

  GetCell = (col, row, canAdd = true) => {
    var list = this.Cells;
    var item = undefined;
    var index = list.findIndex(x => x.Column === col && x.Row === row);
    if (index === -1) {
      if (!canAdd) return undefined;

      item = cellUtil.get(col, row, this);
      list.push(item);
      return item;
    }
    else {
      return list[index];
    }
  }

  Build = () => {
    const setObj = () => {
      var colField = this.ColumnField;
      if (colField === mif.Phase) {
        pmData().MassResultComp.Phases.forEach(x => {
          var name = mif.GetValueString(x, colField);
          var data = x;
          var col = this.GetColumn(name, data);
        })
      }

      this.Items.forEach(x => {
        switch (colField) {
          case mif.Phase:
            x.ValidPhasesByInput.forEach(y => {
              this.HandleItem(x, y);
            })
            break;
          default:
            var val = x.GetValue(colField).Value;
            this.HandleItem(x, val);
            break;
        }
      })
    }

    if (this.MainMGI){
      setObj();
    } else {
      genUtil.Add_2(
        this.OnSet_MainMGI_Func,
        setObj
      )
    }
  }

  IsOnSaveMassGroup = false;

  HandledItems = []
  HandledItemPhases = []

  OnSetHandledRatio_Funcs = []

  handledRatio
  get HandledRatio() {
    return this.handledRatio;
  } set HandledRatio(v) {
    this.handledRatio = v;
    this.OnSetHandledRatio_Funcs.forEach(x => x(v));
  }

  AddHandledItems = (item) => {
    if (this.ColumnField !== mif.Phase) {
      this.HandledItems.push(item);
    } else {
      var list = this.HandledItemPhases;
      var index = list.findIndex(x => x.Item === item);
      var obj = undefined;
      if (index === -1) {
        obj = {
          Item: item,
          Count: 0
        };
        list.push(obj);
      } else {
        obj = list[index];
      }
      obj.Count++;

      if (obj.Count === item.ValidPhasesByInput.length) {
        this.HandledItems.push(item);
      }
    }

    this.HandledRatio = this.HandledItems.length / this.Items.length;

    if (this.HandledItems.length === this.Items.length) {
      this.UpdateData();
      this.HandledItems = [];
    }
  }

  UpdateData = () => {
    // this.MainRows.forEach(x => x.UpdateData());
    this.MainRows = rowUtil.Order(this.MainRows);

    this.MainRows.forEach(x => {
      x.Update.Run(
        {
          Section: u_sn.celldata,
          Data: { Sections: uc_sn.subcells }
        },
        u_dt.lower
      )
    })

    this.TBody.Rows = this.MainRows;
    this.IsUpdateDataDone = true;
  }

  OnIsUpdateDataDone_Funcs = []

  set IsUpdateDataDone(v) {
    this.OnIsUpdateDataDone_Funcs.forEach(x => x());
  }

  HandleItem = (item, colFieldValue, mgc, initialRow) => {
    var name = mif.GetValueString(colFieldValue, this.ColumnField);
    var data = colFieldValue;

    var col = this.GetColumn(name, data);

    var rows = initialRow ? [initialRow] : [];
    var mgc = mgc ? mgc : this.MainMGC;

    this.HandleRowFieldIndex(item, col, rows, mgc, 0);
  }

  // Xử lý tạo Row với giá trị tương ứng và Cell bên trong
  GetRow_Value_FieldIndex = (item, col, rows, val, mgc, rowFieldIndex) => {
    var mgis = mgc.Items;
    var itemCount = mgis.length;
    var mgi = mgis[rowFieldIndex];

    // Sup Row của dòng khối lượng đang xét
    var lastRow = rows.length !== 0 ? rows[rows.length - 1] : undefined
    // mgi.SupRow = lastRow;
    mgi.AddSupRow(lastRow);

    var rowField = mgi.Field;
    if (!rowField) {
      this.AddHandledItems(item);
      return;
    }

    var valueString = mif.GetValueString(val, rowField);
    var fullValue = lastRow ? `${lastRow.FullValue}__${valueString}` : valueString;

    if (mgi.NotIncludeValues.includes(fullValue)) {
      this.AddHandledItems(item);
      return;
    }

    var row = undefined;
    const addRow = (supRow) => {
      return this.Handle_AddRow(fullValue, supRow);
    }

    const handleRow = (row) => {
      // Gắn thông tin gốc vào Row
      row.RawValue = valueString;
      row.MassGroupItem = mgi;
      rows.push(row);

      var subMgcs = mgi.SubComponents;
      var subIndex = subMgcs.findIndex(x =>
        filterUtil.IsValid(x.Filter, fullValue));
      if (subIndex !== -1) {
        mgc = subMgcs[subIndex];
        mgc.IsHaveMassItem = true;

        rowFieldIndex = -1;
        itemCount = mgc.Items.length;

        row.SubMassGroupComponent = mgc;
      }

      var cell = this.GetCell(col, row);
      cell.Items.push(item);

      if (rowFieldIndex === itemCount - 1) {
        this.AddHandledItems(item);
      } else {
        this.HandleRowFieldIndex(item, col, rows, mgc, rowFieldIndex + 1);
      }
    }

    var mgiItem = mgi.GetItem(fullValue, valueString);
    var replaceMgi = mgiItem.NewMGI;
    if (replaceMgi) {
      // Lấy thông tin SupName
      var supName = mgiItem.SupValue;

      // Thêm Value-Title cho MassGroupItem
      replaceMgi.GetItem(fullValue, valueString);

      // Đính thông tin OriginSupRow cũ
      if (lastRow) {
        lastRow.Is_Original_SupRow = true;
      }

      this.Handle_ReplaceMGI(replaceMgi, supName, addRow, handleRow, lastRow, item);
    } else {
      row = addRow(lastRow);
      handleRow(row);
    }
  }

  Handle_AddRow = (fullValue, supRow) => {
    var r1 = this.GetRow(fullValue, supRow);
    r1.FullValue = fullValue;
    return r1;
  }

  Handle_ReplaceMGI = (replaceMgi, supName, addRow, handleRow, originalSupRow, massItem) => {
    var isGet_SupRow_Done

    // Lấy SupRow phù hợp có trong ReplaceMGI
    const getSupRow = () => {
      var list = replaceMgi.SupRows;
      var index = list.findIndex(x => x.Name === supName);
      if (index !== -1) {
        isGet_SupRow_Done = true;
        return list[index]
      }
      return undefined;
    }

    // Thêm Row vào SupRow
    const awaitGetSupRowForAdd = (supRow) => {
      var r2 = addRow(supRow);
      r2.ReplaceMGI = replaceMgi;

      return r2;
    }

    // Đính thông tin OriginSupRow
    const setOriginal_SupRow = (row) => {
      row.Original_SupRow = originalSupRow;
      row.IsReplaced = true;
    }

    // Tổn hợp các Phương thức ở trên
    const fullFunc = () => {
      var sr = getSupRow();
      if (sr) {
        var r = awaitGetSupRowForAdd(sr);
        setOriginal_SupRow(r);
        handleRow(r);
      }
    }

    fullFunc();
    genUtil.Add_2(
      replaceMgi.On_AddSupRow_Func_Dict,
      () => {
        if (!isGet_SupRow_Done && replaceMgi.NewRow?.Name === supName) {
          fullFunc();
        }
      },
      false);
  }

  HandleRowFieldIndex = (item, col, rows, mgc, rowFieldIndex) => {
    var rowField = mgc.Items[rowFieldIndex].Field;

    // NewVersion: trả về data và có xử lý sau đó ko (isHandle ?)
    // Đầu mục từ tạo không có field
    if (!rowField) {
      this.GetRow_Value_FieldIndex(item, col, rows, undefined, mgc, rowFieldIndex);
    } else {
      var data = item.GetValue(rowField, (val) =>
        this.GetRow_Value_FieldIndex(item, col, rows, val, mgc, rowFieldIndex, true));

      if (!data.IsHandle) {
        var val = data.Value;
        this.GetRow_Value_FieldIndex(item, col, rows, val, mgc, rowFieldIndex);
      }
    }
  }

  // MassGroupSetting
  get MassGroupSetting_DictVM() {
    return pmDataVM().MassGroupSetting_Dict
  }

  get Mgs_Dict_Dict() {
    return pmData().Mgs_Dict_Dict
  }

  get MainMGC() {
    return this.Mgs_Dict_Dict.Used_MassGroupComponent
  }

  OnSet_MainMGI_Func = []

  mainMGI
  get MainMGI() {
    if (!this.mainMGI){
      util.Handle_MainMGI(this)
    } return this.mainMGI
  } set MainMGI(v){
    this.mainMGI = v
    genUtil.Handle(this.OnSet_MainMGI_Func,
      (v) => this.OnSet_MainMGI_Func = v)
  }

  AddMainRow = () => {
    return rowUtil.Add(undefined, this);
  }

  // Excel Export
  get ExportTitle() {
    var text = pmData().CurrentProject?.Name;
    text = text ? text + '_' : '';

    if (this.ColumnField === mif.Phase) {
      text += 'phase_'
    }

    const formatedDate = dayjs(Date.now()).format("YYMMDD");
    text += `masstable_${formatedDate}`
    return text;
  }

  get ExcelWorkbook() {
    return wbUtil.get(this).ResultData;
  }

  excelExport
  get ExcelExport() {
    if (!this.excelExport) {
      this.excelExport = eeUtil.get(
        undefined, undefined,
        () => this.ExportTitle,
        () => this.ExcelWorkbook);
    }
    return this.excelExport;
  }

  Export = () => {
    this.ExcelExport.Export();
  }

  // Lưu trữ các Row tạo bởi người dùng
  // Trước mặt dùng để xử lý lỗi không update TotalValue
  // trong lần đầu tiên truy xuất bảng khối lượng
  CreatedByUser_Rows
  Handled_CreatedByUser_Rows

  Add_Handled_CreatedByUser_Row = (row) => {
    this.Handled_CreatedByUser_Rows.push(row);

    if (this.CreatedByUser_Rows.length === this.Handled_CreatedByUser_Rows.length) {
      // Xử lý TotalValue thành công với tất cả các CreatedByUser_Row
    }
  }

  // Body
  tbody = undefined
  get TBody(){
    if (!this.tbody){
      var obj = this.tbody = new MassTBody();
      obj.Dict = this
    } return this.tbody
  }

  // Backup
  commandDict
  get CommandDict(){
    if (!this.commandDict){
      this.commandDict = util.GetCommandDict(this)
    } return this.commandDict;
  }
}