import MassGroupComponent from "./MassGroupComponent"
import * as filterUtil from '../../../filter/EntFilterUtil'
import * as itemUtil from './MassGroupItemUtil'
import { PMData } from "../../../../single/model/PMData"
import * as mgiUtil from './MassGroupItemUtil'
import * as mif from '../../item/MassItemField'
import { Data } from "../../../setting/massGroup/data/Data"
import MassGroupItem from "./MassGroupItem"

export const pmData = () => {
  return PMData.Instance;
}

// Instance
export const get = (data, dict) => {
  var qI = new MassGroupComponent();

  qI.Filter = data.filter ? filterUtil.get(data.filter) : undefined;
  qI.Items = data.items.map(x => itemUtil.getFromDb(x, qI));

  if (dict instanceof Data) {
    qI.Dict = dict;
  } else if (dict instanceof MassGroupItem) {
    qI.SupItem = dict;
  }

  return qI;
}

export const Clone = (q, supItem, forTemplate = false) => {
  if (!q) return q;
  var qI = new MassGroupComponent();
  qI.Filter = filterUtil.Clone(q.Filter);

  qI.Items = q.Items.map(x => itemUtil.Clone(x, qI, forTemplate));
  qI.SupItem = supItem;

  return qI;
}

export const GetOriginComponent = (q) => {
  return q.SupItem ? q.SupItem.SupComponent.OriginComponent : q;
}

export const GetDefault = (q) => {
  var qI = new MassGroupComponent();

  var mgi1 = mgiUtil.get(mif.MassType);
  qI.Items = [mgi1];

  return qI;
}