import { makeObservable, observable, action } from 'mobx';
import * as textVMUtil from '../../../../../text/TextVMUtil'
import * as api from '../../../../../../util/apiUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import * as mif from '../../item/MassItemField'
import { PMData } from '../../../../single/model/PMData';
import * as util from './MassCellUtil'
import TextView from '../../../../../text/TextView';

export const pmData = () => {
  return PMData.Instance;
}

export default class MassCell {
  constructor() {
    makeObservable(this, {
      valueColor: observable,
      tdBgColor: observable,
      isHaveWarning: observable,
      tls_LevelGroup_Descriptions: observable,
      vcs_Item: observable,
      CurrentTotalValueItem: observable
    })
  }

  _id
  get id() {
    if (!this._id) {
      this._id = genUtil.randomId();
    }
    return this._id;
  }

  get Table() {
    return this.Dict
  }

  get ColumnIndex() {
    return this.Column.Index;
  }

  items = []
  get Items() {
    return this.items;
  }
  set Items(v) {
    this.items = v;
  }

  get Unit(){
    return this.Row.Unit
  }

  // TotalValue
  OnGet_TotalValueData_Func = []

  totalValueData = undefined
  get TotalValueData() {
    if (!this.totalValueData === undefined) {
      util.Handle_TotalValueData(this)
    } return this.totalValueData
  } set TotalValueData(v) {
    this.totalValueData = v;

    util.Set_TotalValueData(this, v)

    genUtil.Handle(this.OnGet_TotalValueData_Func,
      (v) => this.OnGet_TotalValueData_Func = v);
  }

  CurrentTotalValueItem = undefined

  contentVM
  get ContentVM() {
    if (!this.contentVM) {
      this.contentVM = textVMUtil.get(this.ValueString);
    }
    return this.contentVM;
  }

  get ValueString() {
    return util.Get_ValueString(this);
  }

  // Sup - Sub
  supItem
  get SupItem() {
    if (!this.supItem) {
      this.supItem = util.Get_SupItem(this);
    }
    return this.supItem;
  } set SupItem(v) {
    this.supItem = v;
  }

  subCells
  get SubCells() {
    if (!this.subCells) {
      this.subCells = util.Get_SubCells(this);
    }
    return this.subCells;
  }
  set SubCells(v) {
    this.subCells = v;
  }

  massType
  get MassType() {
    if (!this.massType) {
      this.massType = util.GetMassType(this);
    }
    return this.massType;
  }
  set MassType(v) {
    this.massType = v;
  }

  AsString = () => {
    console.log(`${this.Column.Name} - ${this.Row.Name} - ${this.TotalValue}`)
  }

  // Update
  update
  get Update(){
    if (!this.update){
      this.update = util.GetUpdate(this)
    } return this.update
  }

  ShowDetail = () => {
    var isHash = true;
    var url = `${process.env.PUBLIC_URL}${isHash ? '/#' : ''}/massdetail`;
    var getUrl = (v) => `${url}/${v}`;
    var openTabFunc = (v) =>
      window.open(getUrl(v), "_blank");

    if (!this.viewDetailId) {
      this.GetViewDetailId(data => {
        var _id = this.viewDetailId = data._id;
        openTabFunc(_id);
      })
    } else {
      openTabFunc(this.viewDetailId);
    }
  }

  viewDetailId

  get AllItems() {
    if (this.SubCells) {
      return this.SubCells.map(x => x.Items).reduce((a, b) => a.concat(b), []);
    }
    return this.Items;
  }

  isHaveVCS_Item
  get IsHaveVCS_Item() {
    if (!this.isHaveVCS_Item) {
      this.isHaveVCS_Item = util.GetIsHaveVCS_Item(this);
    }
    return this.isHaveVCS_Item;
  }
  set IsHaveVCS_Item(v) {
    this.isHaveVCS_Item = v;
  }

  vcs_Item = undefined
  get VCS_Item() {
    return this.vcs_Item;
  }
  set VCS_Item(v) {
    this.vcs_Item = v;

    this.ValueColor = util.GetValueColor(this);
    this.TDBgColor = util.GetTDBgColor(this)
  }

  valueColor = '#1890ff'
  get ValueColor() {
    return this.valueColor;
  }
  set ValueColor(v) {
    this.valueColor = v
  }

  tdBgColor = undefined
  get TDBgColor() {
    return this.tdBgColor;
  }
  set TDBgColor(v) {
    this.tdBgColor = v;
  }

  get AStyle() {
    return {
      color: this.ValueColor,
      fontWeight: 'bold'
    }
  }

  get TDStyle() {
    return {
      backgroundColor: this.TDBgColor
    }
  }

  ApiData = () => {
    var phaseId
    if (this.Table.ColumnField === mif.Phase) {
      phaseId = this.Column.Data._id;
    }

    var massIds = this.AllItems.map(x => x._id);
    return {
      massType: this.MassType,
      massIds,
      phaseId
    };
  }

  GetViewDetailId = (handle) => {
    var apiData = this.ApiData();
    var url = "https://api.bimdev.vn/api/hbdv/updatemass";
    api.put(url, apiData).then(response => response.data)
      .then(data => handle(data))
  }

  OnGet_TLS_LevelGroups_Func_Dict = []
  set Get_TLS_LevelGroups(v) {
    this.OnGet_TLS_LevelGroups_Func_Dict.forEach(x => x.Value());
  }

  tls_LevelGroups
  get TLS_LevelGroups() {
    if (!this.tls_LevelGroups) {
      util.Handle_TLS_LevelGroups(this);
    }
    return this.tls_LevelGroups;
  } set TLS_LevelGroups(v) {
    this.tls_LevelGroups = v;
    this.Get_TLS_LevelGroups = true;
  }

  isHaveWarning = undefined
  get IsHaveWarning() {
    if (this.isHaveWarning === undefined) {
      this.isHaveWarning = util.GetIsHaveWarning(this);
    }
    return this.isHaveWarning;
  } set IsHaveWarning(v) {
    this.isHaveWarning = v;
  }

  tls_LevelGroup_Descriptions = undefined
  get TLS_LevelGroup_Descriptions() {
    if (!this.tls_LevelGroup_Descriptions) {
      util.Handle_TLS_LevelGroup_Descriptions(this);
    }
    return this.tls_LevelGroup_Descriptions;
  } set TLS_LevelGroup_Descriptions(v) {
    this.tls_LevelGroup_Descriptions = v;
  }
}