import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Row, Tabs } from 'antd';
import { LevelSettingDataVM } from './LevelSettingDataVM';
import './LevelSetting.css'
import TableView from '../../../../table/TableView';
import { SaveFilled } from '@ant-design/icons';
import * as pct from '../../command/ProjectCommand'

class LevelSettingDataView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new LevelSettingDataVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='ls__wrapper'>
          {data.Commands.includes(pct.edit) ?
            <>
              {/* Update Level Data */}
              <Button type='primary' size='small' className='ls__save__button'
                onClick={data.Update_LevelData}>
                <SaveFilled className='tls__save' />
              </Button>

              <Button type='primary' size='small'
                className='ls_add'
                onClick={data.AddItem}>+</Button>
            </> :
            ''}

          {/* Table */}
          <TableView className='table table-bordered' store={data.Table} />
        </div>
      </>
    );
  }
}

export default observer(LevelSettingDataView);
