import { PMData } from "../../../../../single/model/PMData";
import * as genUtil from '../../../../../../../util/generalUtil'
import * as mgsd_ut from '../../dict/MGS_Dict_UseType'

export const pmData = () => PMData.Instance

export const Run = (q) => {
  var mgs = q.Dict;

  var mgsd = genUtil.SingleFilter(pmData().Mgs_Dict_Dict.Items, 
    (x) => x.UseType === mgsd_ut.template)
  var save = mgsd.Save;
  save.MassGroupSetting = mgs;
  save.Name = q.InputName

  save.Run();
}