import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Input, Row, Select } from "antd";
import TableView from "../../../../table/TableView";
import { WorkItemExportSetting_DictVM } from "./WorkItemExportSetting_DictVM";

const { Option } = Select;

class WorkItemExportSetting_DictView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new WorkItemExportSetting_DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <Divider orientation="left" plain>
          <b>Thiết lập xuất hạng mục</b>
        </Divider>
        
        <TableView store={data.TableVM}/>
      </>
    )
  }
}

export default observer(WorkItemExportSetting_DictView)