import { makeObservable, observable, action } from 'mobx';
import { Button, message } from 'antd';
import * as genUtil from '../../../../../../../util/generalUtil'
import * as util from './ColumnGroupUtil'
import './ColumnGroup.css'
import { PMDataVM } from '../../../../../single/form/PMDataVM';
import * as pct from '../../../../command/ProjectCommand'

const pmDataVM = () => PMDataVM.Instance

export class ColumnGroup {
  constructor() {
    makeObservable(this, {
      columns: observable
    })
  }

  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Group

  get Title() {
    return util.GetTitle(this)
  }

  Fields

  get DefaultField() {
    return util.GetDefaultField(this)
  }

  get Table() {
    return this.Dict;
  }

  columns = undefined
  get Columns() {
    if (!this.columns) {
      this.columns = util.GetColumns(this)
    } return this.columns
  }

  get ColumnsCount() {
    return this.Columns.length
  }

  AddColumn = () => {
    util.AddColumn(this)
    this.Table.Refresh_ColumnCount()
  }

  get View() {
    return <th key={this.id} colSpan={this.ColumnsCount}>
      <span>{this.Title}</span>

      {this.Commands.includes(pct.edit) ?
        <Button type='primary' size='small'
          className='es__mi__add btn-success'
          onClick={this.AddColumn}>+</Button>
        : ''}
    </th>
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }
}