import * as util from './MT_WorkbookUtil'

export class MT_Workbook {
  Table

  get All_WS(){
    return [ this.RawData_WS, this.Data_WS]
    //return [ this.Data_WS, this.RawData_WS]
    //return [ this.Data_WS ]
  }

  data_WS
  get Data_WS(){
    if (!this.data_WS){
      this.data_WS = util.GetData_WS(this);
    }
    return this.data_WS;
  }

  rawData_WS
  get RawData_WS(){
    if (!this.rawData_WS){
      this.rawData_WS = util.GetRawData_WS(this);
    }
    return this.rawData_WS;
  }

  get ResultData (){
    var obj = {
      Sheets : { },
      SheetNames : []
    }

    this.All_WS.forEach(x => {
      obj.Sheets[x.Title] = x.ResultData
      obj.SheetNames.push(x.Title);
    })

    return obj;
  }
}