import MassItem_Dict from '../item/MassItem_Dict';
import * as util from './MassResultCompUtil'
import * as massItemUtil from '../item/MassItemUtil'
import { PMData } from '../../../single/model/PMData';
import * as mif from '../item/MassItemField'

export const pmData = () => {
  return PMData.Instance;
}

export default class MassResultComp {
  Projects = []

  Phases = []

  get ApiData() {
    return util.GetApiData(this);
  }

  massItem_Dict
  get MassItem_Dict(){
    if (!this.massItem_Dict){
      this.massItem_Dict = util.GetMassItem_Dict(this);
    }
    return this.massItem_Dict;
  }

  OnGetItemsDone_Funcs = []

  set GetItemsDone (v){
    this.OnGetItemsDone_Funcs.forEach(x => x());
  }

  isHavePhase = false
  get IsHavePhase(){
    return this.isHavePhase;
  }
  set IsHavePhase(v){
    this.isHavePhase = v;
    if (v){
    this.MassItem_Dict.MassTable.ColumnField = mif.Phase
    }else{
      this.MassItem_Dict.MassTable.ColumnField = mif.Project
    }
  }

  GetMassData = () => {
    // Refresh MGC và MGI
    var q1 = pmData().Mgs_Dict_Dict
    q1.Used_MassGroupComponent = undefined
    q1.Used_MassGroupItem = undefined

    var dict = this.MassItem_Dict;
    dict.IsDataPending = true;

     // Gắn thông tin truy xuất lần đầu hay không
     if (pmData().Is_First_Get_MassTable === undefined){
      // undefined => truy xuất lần đầu
      pmData().Is_First_Get_MassTable = true;
    } else {
      // có giá trị => không phải lần đầu
      pmData().Is_First_Get_MassTable = false;
    }

    massItemUtil.getAll(this.ApiData, (items) => {
      var dict = this.MassItem_Dict;

      dict.MassGroupSetting = this.MassGroupSetting ? 
        this.MassGroupSetting : undefined;
      dict.Items = items;
      
      this.GetItemsDone = true;
    })
  }
}