import * as api from '../../../../../util/apiUtil'
import * as dataUtil from './LevelSettingDataUtil'
import { PMData } from '../../../single/model/PMData';
import { LevelSetting } from './LevelSetting';
import { message } from 'antd';

export const pmData = () => {
  return PMData.Instance;
}

// Instance
export const GetFromDb = (data, project, phase) => {
  var qI = new LevelSetting();

  qI._id = data._id;
  qI.Name = data.name;
  qI.Description = data.description;
  qI.Data = dataUtil.GetFromDb(data.data, qI);
  qI.Project = project;
  qI.Phase = phase;

  return qI;
}

export const GetDefault = (project) => {
  var qI = new LevelSetting();

  qI.Name = 'thiết lập level';
  qI.Description = 'Mô tả thiết lập';
  qI.Data = dataUtil.GetDefault(qI);
  qI.Project = project;

  return qI;
}