import { Admin } from "./Admin";
import * as userUtil from '../../../../user/item/UserUtil'
import * as projectUtil from '../../project/ProjectUtil'

import * as umDictUtil from '../usermanager/dict/DictUtil'
import * as pcDictUtil from '../project/dict/DictUtil'
import * as pgcDictUtil from '../projectgroup/dict/DictUtil'

// Instance
export const get = (dict) => {
  var qI = new Admin();

  qI.Dict = dict;

  return qI;
}

// Property
export const GetGetUser_Func = (q) => {
  return (x) => {
    var qI = x ? userUtil.GetFromDb(x) : userUtil.get()

    qI.GetUrl_Func = () => "https://api.bimdev.vn/api/hbu"
    qI.GetApiData_Func = () => {
      return {
        _id: qI._id,
        username : qI.Username,
        name : qI.Name,
        password: qI.Password
      }
    }
    qI.Dict = q.UserManager_Dict
    
    return qI;
  }
}

export const Handle_Users = (q) => {
  if (q.IsGet_Users) return undefined
  q.IsGet_Users = true;

  var url = "https://api.bimdev.vn/api/hbu"

  const setObj = (data) => {
    q.Users = data.map(x => {
      return q.GetUser_Func(x)
    });
    q.IsGet_Users = false;
  }

  var apiData = {}

  userUtil.GetData(
    url,
    apiData, (data) => setObj(data)
  )
}

export const Handle_Projects = (q) => {
  if (q.IsGet_Projects) return undefined
  q.IsGet_Projects = true;

  const setObj = (data) => {
    q.Projects = data.map(x => projectUtil.GetFromDb(x));
    q.IsGet_Projects = false;
  }

  var apiData = {}

  projectUtil.GetData(
    apiData, (data) => setObj(data)
  )
}

export const GetUserManager_Dict = (q) => {
  return umDictUtil.get(q);
}

export const GetProjectCommand_Dict = (q) => {
  return pcDictUtil.get(q)
}

export const GetProjectGroupCommand_Dict = (q) => {
  return pgcDictUtil.get(q)
}

// Method
