import React, { Component } from "react";
import ReactDOM from 'react-dom'
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import EntHoverVM from "./EntHoverVM";

class EntHoverView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new EntHoverVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;
    var className = this.props.className;
    if (!data.style) {
      var style = this.props.style ? this.props.style : {};
      if (!style.minWidth) {
        style.minWidth = 150;
      }
      data.style = style;
    }

    return (
      <>
        <div className={className} style={data.style}
          onMouseEnter={data.ShowElement}
          onMouseLeave={data.HideElement}>
          {data.IsElementVisible ? data.Element : ''}
        </div>
      </>
    )
  }
}

export default observer(EntHoverView)