import { makeObservable, observable, action } from 'mobx';
import * as util from './DatePickerUtil'
import * as dateUtil from '../../util/dateUtil'
import ReactDatePicker from "react-datepicker";
import { Button } from "antd";
import './DatePicker.css'

export class DatePicker {
  constructor() {
    makeObservable(this, {
      content: observable,
      placeholder: observable,
      dateOnPicker: observable,
      Is_Popover_Visible: observable
    })
  }

  GetContentFunc

  content = undefined
  get Content() {
    return this.content;
  }
  set Content(v) {
    this.content = v;
    this.Date = util.GetDate(this)
  }

  date = undefined
  get Date() {
    if (!this.date) {
      this.date = util.GetDate(this)
    } return this.date
  } set Date(v) {
    this.date = v;
    this.DateOnPicker = v;
  }

  dateOnPicker = undefined
  get DateOnPicker() {
    if (!this.dateOnPicker) {
      this.dateOnPicker = this.Date
    } return this.dateOnPicker
  } set DateOnPicker(v) {
    if (v) {
      this.dateOnPicker = v
    }
  }

  SetDate = (v) => {
    this.Date = v;
    this.content = dateUtil.ConvertToString(v, 'dd/MM/yyyy');
  }

  Set_DateOnPicker = (v) => {
    this.HidePopover();
    this.SetDate(v);
  }

  placeholder = "nội dung input"
  set Placeholder(v) {
    this.placeholder = v;
  }

  State = 'text'

  get DatePickerView() {
    return <div className='datepicker__popover__wrapper'>
      <ReactDatePicker selected={this.DateOnPicker} inline
        value={this.DateOnPicker}
        // onChange={this.Set_DateOnPicker}
        onSelect={this.Set_DateOnPicker}
      />
      <Button type='primary' size='small'
        className='btn-danger' onClick={this.HidePopover}>x</Button>
    </div>
  }

  Is_Popover_Visible = false

  ShowPopover = () => {
    this.Is_Popover_Visible = true
  }

  HidePopover = () => {
    this.Is_Popover_Visible = false
  }
}