import { AdminVM } from "./AdminVM";
import * as genUtil from '../../../../../util/generalUtil'

import * as umDictUtil from '../usermanager/dict/DictVMUtil'
import * as pcDictUtil from '../project/dict/DictVMUtil'
import * as pgcDictUtil from '../projectgroup/dict/DictVMUtil'

// Instance
export const get = (q, dict) => {
  var qI = new AdminVM();

  qI.ModelItem = q;
  qI.Dict = dict

  return qI;
}

// Property
export const Handle_Users = (q) => {
  if (q.IsGet_Users) return undefined
  q.IsGet_Users = true;

  const setObj = (data) => {
    q.Users = data
    q.IsGet_Users = false;
  }

  var host = q.ModelItem
  if (host.Users){
    setObj(host.Users)
  } else {
    genUtil.Add_2(
      host.OnSet_Users_Func,
      () => setObj(host.Users)
    )
  }
}

export const Handle_Projects = (q) => {
  if (q.IsGet_Projects) return undefined
  q.IsGet_Projects = true;

  const setObj = (data) => {
    q.Projects = data
    q.IsGet_Projects = false;
  }

  var host = q.ModelItem
  if (host.Projects){
    setObj(host.Projects)
  } else {
    genUtil.Add_2(
      host.OnSet_Projects_Func,
      () => setObj(host.Projects)
    )
  }
}

export const GetUserManager_Dict = (q) => {
  return umDictUtil.get(q.ModelItem.UserManager_Dict, q);
}

export const GetProjectCommand_Dict = (q) => {
  return pcDictUtil.get(q.ModelItem.ProjectCommand_Dict, q)
}

export const GetProjectGroupCommand_Dict = (q) => {
  return pgcDictUtil.get(q.ModelItem.ProjectGroupCommand_Dict, q)
}