import * as util from './MT_RawData_WorksheetUtil'

export class MT_RawData_Worksheet {
  Workbook

  get Table() {
    return this.Workbook.Table;
  }

  Title

  dataRows
  get DataRows(){
    if (!this.dataRows){
      this.dataRows = util.GetDataRows(this);
    }
    return this.dataRows;
  }

  tableRows
  get TableRows() {
    if (!this.tableRows) {
      this.tableRows = util.GetTableRows(this);
    }
    return this.tableRows;
  }

  records
  get Records() {
    if (!this.records) {
      this.records = util.GetRecords(this);
    }
    return this.records
  }

  InfoFields = []

  valueFields
  get ValueFields() {
    if (!this.valueFields) {
      this.valueFields = util.GetValueFields(this);
    }
    return this.valueFields;
  }

  valueCells
  get ValueCells() {
    if (!this.valueCells) {
      this.valueCells = util.GetValueCells(this);
    }
    return this.valueCells
  }

  titleCells
  get TitleCells(){
    if (!this.titleCells){
      this.titleCells = util.GetTitleCells(this);
    }
    return this.titleCells;
  }

  allCells
  get AllCells(){
    if (!this.allCells){
      this.allCells = this.TitleCells.concat(this.ValueCells);
    }
    return this.allCells;
  }

  get RangeName() {
    return util.GetRangeName(this);
  }

  First_TitleColumn = 0
  First_TitleRow = 0

  First_ValueColumn = 0;
  First_ValueRow = 1;

  get ResultData() {
    var obj = {
      '!ref': this.RangeName
    };

    this.AllCells.forEach(x => {
      obj[x.Address] = x.ResultData;
    })

    return obj;
  }
}