import TableRow from "./TableRow"
import * as cellUtil from './TableCellUtil'
import { PMData } from "../main/single/model/PMData";

export const pmData = () => PMData.Instance;

export const get = (item, table) => {
  var qI = new TableRow();
  qI.Item = item;
  qI.Table = table;
  return qI;
}

export const GetCells = (q) => {
  var columns = q.Table.Columns;
  return columns.map(x => cellUtil.get(q, x));
}