import { PMDataVM } from '../../../single/form/PMDataVM';
import { MassTypeSettingDataVM } from './MassTypeSettingDataVM';
import * as itemUtil from './MEP/MEP_ItemVMUtil'
import TableVM from '../../../../table/TableVM';
import TableColumn from '../../../../table/TableColumn';
import * as genUtil from '../../../../../util/generalUtil';
import EditableTextView from '../../../../text/EditableTextView';
import SelectView from '../../../../select/SelectView';

export const pmDataVM = () => {
    return PMDataVM.Instance;
  }

  // Instance
export const get = (q, dict) => {
    var qI = new MassTypeSettingDataVM();
    qI.ModelItem = q;
    qI.Dict = dict;
  
    return qI;
  }

  // Property
export const GetItems = (q) => {
    return q.ModelItem.Items.map(x => itemUtil.get(x, q));
}

export const GetTable = (q) => {
    var qI = new TableVM();
  
    // name
    var nameCol = new TableColumn();
    nameCol.Title = 'MassType';
    nameCol.DataIndex = [ 'name' ]
    nameCol.Key = genUtil.randomId();
    nameCol.Table = qI;
    nameCol.ClassName = 'ls__title-column'
  
    // name
    var ClassCol = new TableColumn();
    ClassCol.Title = 'System Classification';
    ClassCol.DataIndex = [ 'systemClassification' ]
    ClassCol.Key = genUtil.randomId();
    ClassCol.Table = qI;
    ClassCol.ClassName = 'ls__title-column'

    // name
    var valCol = new TableColumn();
    valCol.Title = 'Tương đương với System Type';
    valCol.DataIndex = [ 'value' ]
    valCol.Key = genUtil.randomId();
    valCol.Table = qI;
    valCol.ClassName = 'ls__value-column'
  
    qI.Columns = [ nameCol,ClassCol, valCol];
    qI.CanShow_MoveCommand = false;
    qI.CanHandleColumn = false
  
    // Cell Value
    qI.Override_Get_CellRender = (value, item, dataIndex, cell, row, col) => {
      switch (col.DataIndex[0]) {
        case 'name':
          return item.Name_Element
        case 'systemClassification':
            return item.SystemClassification_Element
        case 'value':
          return item.Value_Element
      }
    }
  
    qI.DataSource = [...q.ItemCategories];
  
    return qI;
  }