import * as genUtil from '../../../../../../util/generalUtil'
import { MassGroupSettingVM } from './MassGroupSettingVM'
import { EntDelete } from '../../../../../delete/EntDelete';
import * as d_tt from '../../../../../delete/EntDelete_ToggleType'
import { Button, message } from 'antd';
import * as addTemplateUtil from './addtemplate/AddTemplateVMUtil';
import { CheckCircleOutlined, CloseCircleOutlined, SaveFilled } from '@ant-design/icons';
import * as mgsd_ut from '../dict/MGS_Dict_UseType'
import * as mgsdd_ut from '../dict/dict/MGS_Dict_Dict_UseType'

// Instance
export const get = (q, dict) => {
  var qI = new MassGroupSettingVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetAddTemplate = (q) => {
  return addTemplateUtil.get(q.ModelItem.AddTemplate, q);
}

export const GetActiveTool = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.mouseEvent
  qI.Retrieve_WrapperView = () => !q.IsActive ?
    <Button type='primary' size='small'
      onClick={q.Set_Active}>
      <CheckCircleOutlined />
    </Button>
    :
    <Button type='primary' size='small'
      onClick={q.UnSet_Active}
      className='btn-warning'>
      <CloseCircleOutlined />
    </Button>

  return qI;
}

export const GetSaveTable = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.mouseEvent
  qI.Title = 'Xác nhận lưu đè?'
  qI.ShowHover_Button = <Button type='primary' size='small'
    className='btn-success'
    onClick={qI.Show_Hover}>
    <SaveFilled />
  </Button>

  qI.OnRun = () => {
    var save = q.Dict.Save;
    save.Name = q.Name;
    save.Run();
  }

  return qI;
}

export const GetDD_UseType = (q) => {
  return q.Dict.Dict.UseType
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = q.D_UseType === mgsd_ut.project ? d_tt.manual : d_tt.mouseEvent
  qI.Title = 'Xác nhận xóa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}