import { TLS_PageItemVM } from "./TLS_PageItemVM"
import * as levelGroupUtil from '../levelGroup/LevelGroupVMUtil'
import * as filterUtil from '../filter/TLS_FilterVMUtil'
import * as genUtil from '../../../../../../util/generalUtil'

export const get = (q, dict) => {
  var qI = new TLS_PageItemVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  genUtil.Add_2(
    dict.OnSet_LevelStorageList_Func_Dict,
    () => qI.LevelStorageList = GetLevelStorageList(qI),
    false
  );

  return qI;
}

// Property
export const GetLevelGroups = (q) => {
  return q.ModelItem.LevelGroups.map(x => levelGroupUtil.get(x, q));
}

export const GetFilters = (q) => {
  return q.ModelItem.Filters.map(x => filterUtil.get(x, q));
}

export const GetLevelStorageList = (q) => {
  return q.Dict.LevelStorageList;
}