import { Button, Popover } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import NewInputView from '../../../../../input/NewInputView';
import * as textUtil from '../../../../../text/TextVMUtil';
import * as st from './TLS_PageItemStateType'
import * as levelGroupUtil from '../levelGroup/LevelGroupVMUtil'
import * as filterUtil from '../filter/TLS_FilterVMUtil'
import * as util from './TLS_PageItemVMUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import { PMDataVM } from '../../../../single/form/PMDataVM';
import * as pct from '../../../command/ProjectCommand'

const pmDataVM = () => PMDataVM.Instance

export class TLS_PageItemVM {
  constructor() {
    makeObservable(this, {
      name: observable,
      State: observable,
      levelGroups: observable,
      filters: observable,
    })
  }

  get id() {
    return this.ModelItem?.id;
  }
  set id(v) {
    this._id = v;
  }

  name = undefined
  get Name() {
    if (!this.name) {
      this.name = this.ModelItem.Name;
    }
    return this.name;
  }
  set Name(v) {
    this.name = this.ModelItem.Name = v;
  }

  State = st.text

  ToggleState = () => {
    switch (this.State) {
      case st.text:
        this.State = st.input
        break;
      case st.input:
        this.NameVM.Content = this.Name;
        this.State = st.text
        break;
    }
  }

  get DeleteMarginCss() {
    return this.State === st.input ? 'tls_pageItem__deletemargin' : ''
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      this.nameVM = textUtil.get(this.Name);
    }
    return this.nameVM;
  }

  NameOK = () => {
    this.State = st.text;
    this.Name = this.NameVM.Content;
  }

  NameCancel = () => {
    this.State = st.text;
  }

  get InputToolElement() {
    return <div >
      <Button type='primary' size='small'
        onClick={this.NameOK}>ok</Button>
      <Button type='dashed' size='small'
        onClick={this.NameCancel}>cancel</Button>
    </div>
  }

  get InputElement() {
    var obj = <NewInputView className='tls_pageItem_input' store={this.NameVM} />
    return obj;
  }

  get TitleElement() {
    var delElem = <Button type='primary' size='small' onClick={this.Delete}
      className='tls_pageItem__delete'>x</Button>

    switch (this.State) {
      case st.text:
        return <>
          <span>{this.Name}</span>
          {this.Commands.includes(pct.edit) ?
            <>
              <Button type='primary' size='small' className='tls_pageItem__edit'
                onClick={this.ToggleState}>e</Button>
              {delElem}
            </>
            : ''}
        </>
      case st.input:
        return <>
          <Popover content={this.InputToolElement} trigger="focus">
            <span>
              {this.InputElement}
            </span>
          </Popover>
          {delElem}
        </>
    }
  }

  get Index() {
    return this.Dict.PageItems.indexOf(this);
  }

  get IsLast() {
    return this.Dict.PageItems.length - 1 === this.Index;
  }

  Delete = () => {
    this.ModelItem.Delete();

    var index = this.Index;
    var isLast = this.IsLast;

    var dict = this.Dict;
    var list = dict.PageItems;
    list.splice(index, 1);
    dict.AllTabs.splice(index, 1);

    if (list.length !== 0) {
      dict.IsModelChangePageKey = true;
      dict.CurrentPageKey = (isLast ? list[index - 1] : list[index]).id
    }
  }

  // Setting
  get SettingVM() {
    return this.Dict.SettingVM;
  }

  // Level StorageList
  OnSet_LevelStorageList_Func_Dict = []

  levelStorageList
  get LevelStorageList() {
    if (!this.levelStorageList) {
      this.levelStorageList = util.GetLevelStorageList(this);
    }
    return this.levelStorageList;
  } set LevelStorageList(v) {
    this.levelStorageList = v;

    genUtil.Handle(this.OnSet_LevelStorageList_Func_Dict,
      (v) => this.OnSet_LevelStorageList_Func_Dict = v)
  }

  levelGroups = undefined
  get LevelGroups() {
    if (!this.levelGroups) {
      this.levelGroups = util.GetLevelGroups(this);
    }
    return this.levelGroups;
  }

  AddLevelGroup = () => {
    var item = levelGroupUtil.get(this.ModelItem.AddLevelGroup(), this);
    this.LevelGroups.push(item);
    return item;
  }

  // Filter
  get FieldFilterStorageList() {
    return this.Dict.FieldFilterStorageList;
  }

  filters = undefined
  get Filters() {
    if (!this.filters) {
      this.filters = util.GetFilters(this);
    }
    return this.filters;
  }

  AddFilter = () => {
    var item = filterUtil.get(this.ModelItem.AddFilter(), this);
    this.Filters.push(item);
    return item;
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }
}