import { makeObservable, observable, action } from 'mobx';
import * as util from './TwoCol_SelectVMUtil'
import * as genUtil from '../../../util/generalUtil'

export class TwoCol_SelectVM {
  constructor() {
    makeObservable(this, {
      items: observable,
      availableItems: observable,
      selectedItems: observable,
      inputTitle: observable,
      outputTitle: observable
    })
  }

  items = undefined
  get Items() {
    return this.items;
  }
  set Items(v) {
    this.items = v;

    this.AvailableItems = [...this.Items];
    this.SelectedItems = [];
  }

  inputTitle = undefined
  get InputTitle(){
    if (!this.inputTitle){
      this.inputTitle = 'Dữ liệu input'
    }
    return this.inputTitle;
  } set InputTitle(v){
    this.inputTitle = v;
  }

  outputTitle = undefined
  get OutputTitle(){
    if (!this.outputTitle){
      this.outputTitle = 'Dữ liệu output'
    }
    return this.outputTitle;
  } set OutputTitle(v){
    this.outputTitle = v;
  }

  availableItems = undefined
  get AvailableItems() {
    return this.availableItems;
  } set AvailableItems(v) {
    this.availableItems = v;
  }

  selectedItems = []
  get SelectedItems() {
    return this.selectedItems;
  } set SelectedItems(v) {
    this.selectedItems = v;
  }

  GetKey = (item) => {
    if (typeof item === 'string') return item;
    var id = item.id ? item.id : genUtil.newGUID();
    return id;
  }

  Select = (item) => {
    var index1 = this.AvailableItems.indexOf(item);
    this.AvailableItems.splice(index1, 1);

    this.SelectedItems.push(item);

    this.IsChanged = true;
  }

  Unselect = (item) => {
    var index1 = this.SelectedItems.indexOf(item);
    this.SelectedItems.splice(index1, 1);

    var index = this.Items.indexOf(item);
    if (index < this.AvailableItems.length) {
      this.AvailableItems.splice(index, 0, item);
    } else {
      this.AvailableItems.push(item);
    }

    this.IsChanged = true;
  }

  set IsChanged(v) {
    if (this.OnSelectedItemChanged) {
      this.OnSelectedItemChanged(this.SelectedItems);
    }
  }

  render
  get Render() {
    if (!this.render) {
      this.render = util.GetRender(this);
    }
    return this.render;
  }
  set Render(v) {
    this.render = v;
  }
}