import { LS_Item } from "./LS_Item"

// Instance
export const GetFromDb = (data, dict) => {
  var qI = new LS_Item();

  qI.Name = data.name;
  qI.Values = data.values
  qI.Dict = dict;

  return qI;
}

export const GetDefault = (dict) => {
  var qI = new LS_Item();

  qI.Dict = dict;

  return qI;
}

// Property
