import * as util from './CommandDictUtil'
import { makeObservable, observable, action } from 'mobx';

export class CommandDict {
  constructor() {
    makeObservable(this, {
      Data: observable,
    })
  }

  Data = []

  MaxLength = 15

  Add = (data) => {
    var list = this.Data;
    if (list.length === this.MaxLength) {
      list.splice(0, 1);
    }
    list.push(data)
  }

  Undo = () => {
    var list = this.Data;
    if (list.length === 0) return;
    var length = list.length;
    var q = list.splice(length - 1, 1)[0];
    q.Undo();
  }

  get ButtonView(){
    return util.GetButtonView(this)
  }

  get Disabled(){
    return this.Data.length === 0
  }
}