import * as genUtil from '../../../../../../util/generalUtil'
import { TLS_FilterValue } from './TLS_FilterValue';

export class TLS_Filter {
  _id
  get id() {
    if (!this._id) {
      this._id = genUtil.newGUID();
    }
    return this._id;
  }

  get Index (){
    return this.Dict.Filters.indexOf(this);
  }

  Delete = () => {
    this.Dict.Filters.splice(this.Index, 1);
  }

  //Values = [ 'Cột' ]
  get Values(){
    return this.Items.filter(x => x.Value).map(x => x.Value);
  }

  Items = []

  AddItem = () => {
    var item = new TLS_FilterValue();
    this.Items.push(item);
    return item;
  }

  field
  get Field(){
    if (!this.field){
      this.field = this.FieldStorageList[0]
    }
    return this.field;
  } set Field(v){
    this.field = v;
  }

  get FieldStorageList(){
    return this.Dict.FieldFilterStorageList;
  }

  get ApiData() {
    var obj = {
      field : this.Field,
      items : this.Items.filter(x => x.Value).map(x => x.ApiData)
    }
    return obj;
  }

  get Description() {
    var obj =  this.Values.reduce((a,b,i,arr) => {
      if (arr.indexOf(b) !== 0){
        a += ', ';
      }
      a += b;
      return a;
    }, '')

    return `${this.Field}: ${obj}`;
  }
}