import { makeObservable, observable, action } from 'mobx';
import * as util from './MR_UnitUtil'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as rs from './MR_Unit_RenderState'
import SelectView from '../../../../../../select/SelectView';
import * as u_dt from '../../../../update/Update_DirectionType'

export class MR_Unit {
  constructor() {
    makeObservable(this, {
      units: observable,
      RenderState: observable
    })
  }

  // Update
  update_DirectionType
  get Update_DirectionType(){
    if (!this.update_DirectionType){
      this.update_DirectionType = 
        util.GetUpdate_DirectionType(this)
    } return this.update_DirectionType
  } set Update_DirectionType(v){
    this.update_DirectionType = v
  }

  // Unit
  OnSet_Unit_Func = []

  units = undefined
  get Units() {
    if (!this.units) {
      util.Handle_Units(this)
    } return this.units
  } set Units(v) {
    this.units = v;

    genUtil.Handle(this.OnSet_Unit_Func,
      (v) => this.OnSet_Unit_Func = v)
  }

  Reset_Units = () => {
    util.ReHandle_Units(this);
  }

  currentUnit
  get CurrentUnit() {
    return this.currentUnit
  } set CurrentUnit(v) {
    if (this.currentUnit === v) {
      this.Update_DirectionType = u_dt.none
      // return
    }

    this.currentUnit = v
    this.Unit_RenderVM.Content = v
    util.Set_CurrentUnit(this, v);
  }

  SetUnit_FromHost = (v) => {
    const setObj = () => {
      if (!v || this.Units.includes(v)) {
        this.Unit_SelectVM.InitialItem = v;
      }
    }

    if (this.Units) {
      setObj()
    } else {
      genUtil.Add_2(
        this.OnSet_Unit_Func,
        setObj
      )
    }
  }

  Is_Unit_Select_Created

  unit_SelectVM
  get Unit_SelectVM() {
    if (!this.unit_SelectVM) {
      this.unit_SelectVM = util.GetUnit_SelectVM(this)
    } return this.unit_SelectVM;
  }

  unit_RenderVM
  get Unit_RenderVM() {
    if (!this.unit_RenderVM) {
      this.unit_RenderVM = util.GetUnit_RenderVM(this)
    } return this.unit_RenderVM
  }

  RenderState = rs.text

  OnMouseEnter = () => {
    this.RenderState = rs.select
  }

  OnMouseLeave = () => {
    if (this.Unit_SelectVM.Open) {
      return
    }
    this.RenderState = rs.text
  }

  get MainView() {
    var obj = this.Unit_SelectVM;

    switch (this.RenderState) {
      case rs.select:
        return <SelectView className='massrow__unit__select'
          store={this.Unit_SelectVM} />
      case rs.text:
        return this.Unit_RenderVM.Content
    }
  }
}