import { MT_Data_Worksheet } from "./MT_Data_Worksheet";
import { MT_RawData_Worksheet } from "./MT_RawData_Worksheet";
import { MT_Workbook } from "./MT_Workbook"

// Instance
export const get = (table) => {
  var qI = new MT_Workbook();

  qI.Table = table;

  return qI;
}

// Property
export const GetData_WS = (q) =>{
  var qI = new MT_Data_Worksheet();

  qI.Workbook = q;
  qI.Title = 'data'

  return qI;
}

export const GetRawData_WS = (q) => {
  var qI = new MT_RawData_Worksheet();

  qI.Workbook = q;
  qI.Title = 'raw_data'

  return qI;
}