import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Col, Row } from 'antd';
import './PMData.css'
import Workpackage_DictView from '../../entity/workpackage/Workpackage_DictView';
import ElementFilterSetting_DictView from '../../entity/setting/elementFilter/ElementFilterSetting_DictView';
import MassExportSetting_DictView from '../../entity/setting/massExport/MassExportSetting_DictView';

import { PMDataVM } from './PMDataVM';
import MassResultCompView from '../../entity/mass/result/MassResultCompView';
import WorkItemExportSetting_DictView from '../../entity/setting/workitemexport/WorkItemExportSetting_DictView';
import { withRouter } from "react-router-dom";
//import { Route, BrowserRouter as Router, Switch } from 'react-router-dom';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';
import LeftNavView from '../../entity/nav/left/LeftNavView';
import TestView from '../../../view/TestView';
import MassDetailView from '../../entity/mass/detail/MassDetailView';
import ProjectCompView from '../../entity/project/comp/ProjectCompView';
import AdminView from '../../entity/admin/main/AdminView';

class PMDataView extends Component {
  render() {
    var data = PMDataVM.Instance;

    return (
      <>
        <div className='projectcomp'>
          <LeftNavView />
          <div className='projectcomp__main__wrapper'>
            {data.UserDict.View}

            <div className='projectcomp__main__dec'>
              <div className={data.Main_Classname}>

                <Router basename={process.env.PUBLIC_URL}>
                  <Switch>
                    <Route exact path="/" component={TestView} />

                    {/* Mass Detail */}
                    <Route exact path="/massdetail/:id" component={MassDetailView} />

                    <Route exact path="/project/:id" component={ProjectCompView} />

                    <Route exact path="/admin" component={AdminView} />
                  </Switch>
                </Router>

              </div>

              <div className='projectcomp__main__bottom' />
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default observer(PMDataView);
