import * as u_dt from '../../../../update/Update_DirectionType'
import * as ut from '../../../unit/MassUnitType'
import * as sn from './MRU_SectionName'
import * as uc_sn from '../../cell/update/MCU_SectionName'
import * as hUtil from '../MassRowUtil'
import { message } from 'antd'

export class MR_Update {
  get UnitData() {
    return this.Host.UnitData
  }

  get Cells() {
    return this.Host.Cells
  }

  get SupItem() {
    return this.Host.SupItem
  }

  get SubItems() {
    return this.Host.SubItems
  }

  Update_Units = (data, dirType) => {
    this.UnitData.Reset_Units();
  }

  // *Set vào phần UnitData
  Update_Unit = (data, dirType) => {
    var unit
    if (data) {
      unit = data.Unit ? data.Unit : ut.each
    } else {
      switch (dirType) {
        case u_dt.upper:
          unit = hUtil.Get_DefaultUnit(this.Host)
          break;
        case u_dt.lower:
          unit = this.Host.SupItem.Unit
          break;
      }
    }

    var ud = this.UnitData
    ud.Update_DirectionType = u_dt.none
    ud.SetUnit_FromHost(unit);
  }

  Update_CellData = (data, dirType) => {
    if (data && data.Unit_SetType) {
      this.Host.Unit_SetType = data.Unit_SetType
    }

    if (data && data.Sections) {
      this.Cells.forEach(x => x.Update.Run(data.Sections));
    } else {
      throw 'invalid'
    }
  }

  Update_Delete = (data, dirType, handleUndo) => {
    this.Host.DeleteData.Run(data, handleUndo);
    this.IsContinue = data.IsContinue;
  }

  Update_AddSub = (data, dirType, handleUndo) => {
    var q = this.Host;
    var list = q.SubItems;
    list.push(data.Item);

    if (handleUndo){
      handleUndo(() => {
        list.splice(list.length -1, 1);
      })
    }

    q.Update.Run(
      [{ Section: sn.celldata, Data: { Sections: uc_sn.subcells } },
       sn.unitlist, sn.unit
      ],
      u_dt.upper
    )

    this.IsContinue = false;
  }

  Run = (sections, dirType, isHostApplied = true, handleUndo) => {
    if (!dirType) {
      message.error("dirType is undefined!");
      return
    }

    // Undo
    var undoFuncs = []
    var undo = () => {
      undoFuncs.forEach(x => x());
    }

    if (!Array.isArray(sections)) {
      sections = [sections]
    }

    if (isHostApplied) {
      sections.forEach(x => {
        this.RunItem(x, dirType, (undo) => {
          undoFuncs.push(undo)
        })
      })
    }

    

    if (!this.IsContinue) {
      this.IsContinue = true
    }
    else {
      switch (dirType) {
        case u_dt.none:
          break;
        case u_dt.lower:
          this.SubItems.forEach(x => {
            x.Update.Run(sections, dirType)
          })
          break;
        case u_dt.upper:
          this.SupItem?.Update.Run(sections, dirType)
          break;
        case u_dt.both:
          // Update Sub trước sau đó đến Sub
          this.SubItems.forEach(x => {
            x.Update.Run(sections, u_dt.lower)
          })
          this.SupItem?.Update.Run(sections, u_dt.upper)
          break;
      }
    }

    if (handleUndo){
      handleUndo(undo)
    }
  }

  RunItem = (secData, dirType, handleUndo) => {
    var section = typeof secData === 'string' ?
      secData : secData.Section
    var data = secData.Data

    switch (section) {
      case sn.unitlist:
        this.Update_Units(data, dirType, handleUndo);
        break;
      case sn.unit:
        this.Update_Unit(data, dirType, handleUndo);
        break;
      case sn.celldata:
        this.Update_CellData(data, dirType, handleUndo);
        break;
      case sn._delete:
        this.Update_Delete(data, dirType, handleUndo);
        break;
      case sn.addsub:
        this.Update_AddSub(data, dirType, handleUndo);
        break;
    }
  }

  IsContinue = true
}