import { makeObservable, observable, action } from 'mobx';
import * as util from './GroupVMUtil'
import SelectView from '../../../../../select/SelectView';
import TextView from '../../../../../text/TextView';
import * as rs from '../../../../../text/TextRenderState';
import * as genUtil from '../../../../../../util/generalUtil'
import { Button, message } from 'antd';
import * as iVMUtil from '../item/ItemVMUtil'
import * as iUtil from '../item/ItemUtil'
import './Group.css'

export class GroupVM {
  constructor() {
    makeObservable(this, {
      Items: observable,
    })
  }

  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  renderState = rs.edit
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
  }

  get Key() {
    return this.Project;
  }

  // Project
  project
  get Project() {
    return this.project;
  } set Project(v) {
    this.project = v;
    this.Project_Render.Content = v?.Name;
  }

  project_Render
  get Project_Render() {
    if (!this.project_Render) {
      this.project_Render = util.GetProject_Render(this)
    } return this.project_Render
  }

  project_Select
  get Project_Select() {
    if (!this.project_Select) {
      this.project_Select = util.GetProject_Select(this)
    } return this.project_Select
  }

  get Project_View() {
    switch (this.RenderState) {
      case rs._default:
        return <TextView store={this.Project_Render} />
      case rs.edit:
        return <SelectView store={this.Project_Select} />
    }
  }

  // Item
  Items = []

  AddItem = () => {
    var itemDict = this.Dict.ModelItem;
    var i = iUtil.get(itemDict);
    i.Project = this.Project;
    itemDict.Items.push(i);

    var ivm = iVMUtil.get(i, this);
    this.Items.push(ivm);
  }

  // Index
  get CurrentList() {
    return this.Dict.Groups
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.Items.forEach(x => {
      x.Delete();
    })
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  // Row
  get RowView() {
    return <tr key={this.id}>
      <td className='admin__pc__group__project'>
        <div className='f-ac'>
          {this.Project_View}
          <Button type='primary' size='small'
            className='btn-danger'
            onClick={this.Delete} >x</Button>
        </div>
      </td>
      <td className='admin__pc__group__usergroup'>
        <div className='f'>
          <div className='admin__pc__group__add'>
            <Button type='primary' size='small'
              className='btn-success'
              onClick={this.AddItem}>+</Button>
          </div>
          <div className='f1'>
            <table className='admin__pc__group__authorize__table'>
              <tbody>
                {this.Items?.map(x => {
                  return <tr key={x.id}>
                    <td className='admin__pc__group__user'>{x.User_View}</td>
                    <td className='admin__pc__group__command'>{x.Command_View}</td>
                    <td>
                      <Button type='primary' size='small'
                        className='btn-danger'
                        onClick={x.Delete} >x</Button>
                    </td>
                  </tr>
                })}
              </tbody>
            </table>
          </div>
        </div>
      </td>
    </tr>
  }
}