import Setting from "../base/Setting";
import * as util from './LevelSettingDataUtil'
import { reaction } from "mobx";
import { PMData } from "../../../single/model/PMData";
import * as itemUtil from './item/LS_ItemUtil'
import * as genUtil from "../../../../../util/generalUtil";

export const pmData = () => PMData.Instance;

export class LevelSettingData {
  // Items
  Items = []

  AddItem = () => {
    var item = itemUtil.GetDefault(this);
    this.Items.push(item);
    return item;
  }

  Update_LevelData = () => {
    this.LevelStorageList = util.GetLevelStorageList(this);
  }

  OnSet_LevelStorageList_Func_Dict = []

  levelStorageList
  get LevelStorageList(){
    if (!this.levelStorageList){
      this.levelStorageList = util.GetLevelStorageList(this);
    }
    return this.levelStorageList;
  } set LevelStorageList(v){
    this.levelStorageList = v;

    genUtil.Handle(this.OnSet_LevelStorageList_Func_Dict,
      (v) => this.OnSet_LevelStorageList_Func_Dict = v)
  }

  get ApiData() {
    return {
      items : this.Items.map(x => x.ApiData)
    }
  }
}