import * as genUtil from '../../../../../../util/generalUtil'
import { DictVM } from "./DictVM";
import * as iUtil from '../item/ItemVMUtil'
import './Dict.css'
import * as gUtil from '../group/GroupVMUtil'

// Instance
export const get = (q, dict) => {
  var qI = new DictVM();

  qI.ModelItem = q;
  qI.Dict = dict

  return qI;
}

// Property
export const Handle_Groups = (q) => {
  if (q.IsGet_Groups) return undefined;
  q.IsGet_Groups = true;

  const setObj = (data) => {
    const mainFunc = () => {
      q.Groups = genUtil.GroupBy(data, 'Project',
        (key) => {
          var qI = gUtil.get(q);
          qI.Project = key;
          return qI;
        },
        (x, group) => iUtil.get(x, group));
      q.IsGet_Groups = false;
    }

    var count = data.length;
    if (count === 0) {
      mainFunc();
    } else {
      var i = -1;
      const validFunc = () => {
        i++;
        if (i === count - 1) {
          mainFunc();
        }
      }

      data.forEach(x => {
        if (x.Project !== undefined) {
          validFunc();
        } else {
          genUtil.Add_2(
            x.OnSet_Project_Func,
            validFunc
          )
        }
      })
    }
  }

  var host = q.ModelItem
  if (host.Items) {
    setObj(host.Items)
  }

  genUtil.Add_2(
    host.OnSet_Items_Func,
    () => setObj(host.Items),
    false
  )
}