import { message } from 'antd';
import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './SaveUtil'

export class Save {
  Name

  Run = () => {
    util.Run(this);
  }

  // MassGroupSettingForSave
  massGroupSetting
  get MassGroupSetting(){
    return this.massGroupSetting
  } set MassGroupSetting(v){
    this.massGroupSetting =v
  }

  // MassGroupComponent
  OnSet_MassGroupComponent_Func = []

  massGroupComponent = undefined
  get MassGroupComponent(){
    if (!this.massGroupComponent){
      util.Handle_MassGroupComponent(this)
    } return this.massGroupComponent
  } set MassGroupComponent(v){
    this.massGroupComponent = v;
    genUtil.Handle(this.OnSet_MassGroupComponent_Func,
      (v) => this.OnSet_MassGroupComponent_Func = v)
  }

  OnSet_Retrieve_MassGroupComponent_Func = []

  retrieve_MassGroupComponent
  get Retrieve_MassGroupComponent(){
    if (!this.retrieve_MassGroupComponent){
      util.Handle_Retrieve_MassGroupComponent(this)
    } return this.retrieve_MassGroupComponent;
  } set Retrieve_MassGroupComponent(v){
    this.retrieve_MassGroupComponent = v;
    genUtil.Handle(this.OnSet_Retrieve_MassGroupComponent_Func,
      (v) => this.OnSet_Retrieve_MassGroupComponent_Func = v)
  }

  // Refresh
  Refresh = () => {
    util.Refresh(this)
  }
}