import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Row, Tabs } from 'antd';
import MultiSelectView from '../../../../../select/MultiSelectView';
import { DeleteFilled } from '@ant-design/icons';
import './TLS_PageItem.css'
import { TLS_PageItemVM } from './TLS_PageItemVM';
import TLS_FilterView from '../filter/TLS_FilterView';
import LevelGroupView from '../levelGroup/LevelGroupView';
import * as pct from '../../../command/ProjectCommand'

const { TabPane } = Tabs;

class TLS_PageItemView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new TLS_PageItemVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='tls__pageitem__wrapper'>
          {/* Filters */}
          <div className='typicallevel__list'>
            <div className='typicallevel__list__1'>
              {/* Add Filter */}
              {data.Commands.includes(pct.edit) ?
                <Button type='primary' size='small' onClick={data.AddFilter}
                  className='typicallevel__addbutton'>+</Button>
                : ''}
              <span><b>Thiết lập bộ lọc</b></span>
            </div>

            {/* Filter List */}
            {data.Filters.map(x => {
              return <TLS_FilterView key={x.id} store={x} />
            })}
          </div>

          {/* LevelGroup */}
          <div className='typicallevel__list'>
            <div className='typicallevel__list__1'>
              {/* Add Level */}
              {data.Commands.includes(pct.edit) ?
              <Button type='primary' size='small' onClick={data.AddLevelGroup}
                className='typicallevel__addbutton'>+</Button>
                : ''}
              <span><b>Thiết lập level</b></span>
            </div>

            {/* Level List */}
            {data.LevelGroups.map(x => {
              return <LevelGroupView key={x.id} store={x} />
            })}
          </div>
        </div>
      </>
    );
  }
}

export default observer(TLS_PageItemView);
