import { makeObservable, observable, action } from 'mobx';
import NewInputView from '../input/NewInputView';
import TextRender_Dict from './TextRender_Dict';
import * as ts from './TextState'
import * as rs from './TextRenderState'
import * as genUtil from '../../util/generalUtil'

export class EditableTextVM {
  constructor() {
    makeObservable(this, {
      content: observable,
      placeholder: observable,
      State: observable,
      IsEditing: observable,
      renderState: observable,
      Is_Tool_Visible: observable,
      Is_Edit_Visible : observable,
      Is_OK_Visible: observable,
      CanChange_RenderState : observable
    })
  }

  content = undefined
  get Content() {
    return this.content;
  }
  set Content(v) {
    this.content = v;
    if (this.onSetContent) {
      this.onSetContent(v);
    }
  }

  placeholder = "nội dung input"
  set Placeholder(v) {
    this.placeholder = v;
  }

  State = ts.text

  IsEditing = false

  titleFunc
  get TitleFunc() {
    if (!this.titleFunc) {
      this.titleFunc = () => this.Content;
    }
    return this.titleFunc;
  } set TitleFunc(v) {
    this.titleFunc = v;
  }

  render_Dict
  get Render_Dict() {
    if (!this.render_Dict) {
      this.render_Dict = new TextRender_Dict();
    }
    return this.render_Dict;
  }
  set Render_Dict(v) {
    this.render_Dict = v;
  }

  OnSet_RenderState_Func_Dict = []

  renderState = rs._default
  get RenderState() {
    return this.renderState;
  } set RenderState(v) {
    if (!this.CanChange_RenderState) {
      return;
    }

    this.renderState = v;
    this.OnSet_RenderState_Func_Dict.forEach(x => x.Value());
  }

  CanChange_RenderState = true

  Is_Tool_Visible = true
  Is_Edit_Visible = true
  Is_OK_Visible = true;

  Edit = () => {
    this.RenderState = rs.edit
  }

  EditOK = () => {
    this.RenderState = rs._default
  }

  Render = () => {
    var render = this.Render_Dict.GetRender(this.RenderState);
    return render.Render(this.Content, this);
  }

  OnSet_Ref_Func_Dict = []

  ref
  get Ref(){
    return this.ref
  } set Ref(v){
    this.ref = v;

    genUtil.Handle(this.OnSet_Ref_Func_Dict,
      (v) => this.OnSet_Ref_Func_Dict = v)
  }

  ref_Element
  get Ref_Element(){
    if (!this.ref_Element){
      var obj = this.Ref.current
      if (obj && obj.getEl){
        obj = obj.getEl(); 
      }

      this.ref_Element = obj;
    }
    return this.ref_Element;
  }

  Focus = () => {
    const setFocus = () => {
      this.Ref_Element?.focus();
    }

    if (this.Ref){
      setFocus()
    } else {
      genUtil.Add_2(
        this.OnSet_Ref_Func_Dict,
        () => setFocus()
      )
    }
  }
}