import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Select } from "antd";
import TableVM from "./TableVM";
import * as rs from './RenderState'
import './Table.css'
import { LeftCircleOutlined, RightCircleOutlined } from "@ant-design/icons";

const { Option } = Select;

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new TableVM();
    } else {
      this.state = store;
    }
  }

  onValueChange = (v) => {
    var data = this.state;
    data.SelectedIndex = v;
  }

  render() {
    var data = this.state;
    var columns = data.Columns;
    var className = this.props.className
    var style = this.props.style

    var rows = data.Rows;
    var tableElem = '';
    if (columns && rows) {
      var theadElem = <thead className="thead-light">
        <tr>
          {columns.map(x => {
            return <th scope="col" key={x.Key}
              className={x.ClassName}>
              <div className='column__title__wrapper'
                onMouseEnter={x.Show_MoveCommand}
                onMouseLeave={x.Collapse_MoveCommand}>

                {x.CanShow_MoveCommand ?
                  <div className='column__title__left'>
                    {x.IsShow_MoveCommand ?
                      <Button type='primary' size='small'
                        className='column__title__move__button'
                        onClick={x.MoveLeft}>
                        <LeftCircleOutlined />
                      </Button>
                      : <div className='column__title__move__blank' />}
                  </div>
                  : ''}

                {x.TitleRender()}

                {x.CanShow_MoveCommand ?
                  <div className='column__title__right'>
                    {x.IsShow_MoveCommand ?
                      <Button type='primary' size='small'
                        className='column__title__move__button right'
                        onClick={x.MoveRight}>
                        <RightCircleOutlined />
                      </Button>
                      : <div className='column__title__move__blank' />}
                  </div>
                  : ''}
              </div>
            </th>
          })}
        </tr>
      </thead>

      var tbodyElem = <tbody>
        {rows.map(x => {
          return <tr key={x.Key}>
            {x.Cells.map(y => {
              return <td key={y.Key}>{y.Render()}</td>
            })}
          </tr>
        })}
      </tbody>

      tableElem = <table className={className} style={style}>
        {theadElem}{tbodyElem}
      </table>
    }

    return (
      <>
        {tableElem}
      </>
    )
  }
}

export default observer(TableView)