import { makeObservable, observable, action } from 'mobx';
import * as rs from './RenderState'
import * as textUtil from '../text/TextVMUtil'
import * as util from './TableCellUtil'

export default class TableCell {
  constructor() {
    makeObservable(this, {
      renderState: observable
    })
  }

  get Key() {
    return `${this.Column.Key}_${this.Row.Key}`;
  }

  get Item() {
    return this.Row.Item;
  }

  get ColumnIndex() {
    return this.Row.Cells.indexOf(this);
  }

  get RowIndex() {
    return this.Row.Index;
  }

  renderState = undefined
  get RenderState() {
    if (!this.renderState) {
      this.renderState = this.Column.RenderState;
    }
    return this.renderState;
  }
  set RenderState(v) {
    this.renderState = v;
  }

  valueVM
  get ValueVM(){
    if (!this.valueVM){
      var val = util.GetValue(this);
      this.valueVM = textUtil.get(val);
    }
    return this.valueVM;
  }

  render_Dict
  get Render_Dict() {
    if (!this.render_Dict) {
      this.render_Dict = this.Column.Render_Dict;
    }
    return this.render_Dict;
  }

  get DataIndex() {
    return this.Column.DataIndex;
  }

  Render = () => {
    var dataIndex = this.DataIndex;
    var item = this.Item;
    var value  = util.GetValue(this);

    if (this.Table.Override_Get_CellRender){
      return this.Table.Override_Get_CellRender(value, item, dataIndex, this, this.Row, this.Column);
    }

    var render = this.Render_Dict.GetRender(this.RenderState);
    return render.Render(value, item, dataIndex, this, this.Row, this.Column);
  }
}