export const AcceptanceNumberInternal = 'AcceptanceNumberInternal'
export const AcceptanceNumber = 'AcceptanceNumber'
export const AcceptanceDate = 'AcceptanceDate'
export const SupervisorContractor = 'SupervisorContractor'
export const SupervisorConsultant = 'SupervisorConsultant'
export const SupervisorClient = 'SupervisorClient'
export const SubContractor = 'SubContractor'
export const PaymentPeriodSubContractor = 'PaymentPeriodSubContractor'
export const PaymentPeriodClient = 'PaymentPeriodClient'

export const GetList = () => {
  return [
    AcceptanceNumberInternal,
    AcceptanceNumber,
    AcceptanceDate,
    SupervisorContractor,
    SupervisorConsultant,
    SupervisorClient,
    SubContractor,
    PaymentPeriodSubContractor,
    PaymentPeriodClient
  ]
}

export const GetKey = (v) => {
  switch (v) {
    case AcceptanceNumberInternal:
      return 'acceptanceNumberInternal'
    case AcceptanceNumber:
      return 'acceptanceNumber'
    case AcceptanceDate:
      return 'acceptanceDate'
    case SupervisorContractor:
      return 'supervisorContractor'
    case SupervisorConsultant:
      return 'supervisorConsultant'
    case SupervisorClient:
      return 'supervisorClient'
    case SubContractor:
      return 'subContractor'
    case PaymentPeriodSubContractor:
      return 'paymentPeriodSubContractor'
    case PaymentPeriodClient:
      return 'paymentPeriodClient'
  }
}

export const GetTitle = (v) => {
  switch (v) {
    case AcceptanceNumberInternal:
      return 'BBNT nội bộ'
    case AcceptanceNumber:
      return 'BBNT'
    case AcceptanceDate:
      return 'Ngày nghiệm thu'
    case SupervisorContractor:
      return 'Giám sát Nhà thầu'
    case SupervisorConsultant:
      return 'Giám sát Tư vấn'
    case SupervisorClient:
      return 'Giám sát CĐT'
    case SubContractor:
      return 'Thầu phụ'
    case PaymentPeriodSubContractor:
      return 'Kỳ thanh toán Thầu phụ'
    case PaymentPeriodClient:
      return 'Kỳ thanh toán Chủ đầu tư'
  }
}