import { DO_CompVM } from "./DO_CompVM"
import * as modelUtil from './DO_CompUtil'
import * as itemUtil from '../DO_ElementVMUtil'

// Instance
export const get = (q) => {
  var qI =new DO_CompVM();
  qI.ModelItem = q;
  return qI;
}

export const GetTest = () => {
  return get(modelUtil.GetTest());
}

// Property
export const GetItems = (q) => {
  var obj = q.ModelItem.Items.map(x => itemUtil.get(x));
  return obj;
} 