import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { DatePicker } from "./DatePicker";
import InputView from "../input/InputView";
import ReactDatePicker from "react-datepicker";
import { Popover } from "antd";

class DatePickerView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DatePicker();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state

    return (
      <>
        <Popover content={data.DatePickerView}
          visible={data.Is_Popover_Visible} >
          <InputView store={data} 
            onFocus={data.ShowPopover} />
        </Popover>
      </>
    )
  }
}

export default observer(DatePickerView)