import * as util from './RebarMass_DictUtil'

export default class RebarMass_Dict {
  project
  get Project(){
    return this.project;
  }
  set Project(v){
    this.project = v;
    this.projectId = v._id;
  }

  projectId

  items = []
  get Items(){
    return this.items;
  }
  set Items(v){
    this.items = v;
    this.TableVM.DataSource = v;
  }

  tableVM
  get TableVM() {
    if (!this.tableVM){
      this.tableVM = util.GetTableVM(this);
    }
    return this.tableVM;
  }
}