import * as sn from './MCU_SectionName'
import * as u_dt from '../../../../update/Update_DirectionType'
import { message } from 'antd'
import * as genUtil from '../../../../../../../util/generalUtil'
import * as hUtil from '../MassCellUtil'

export class MC_Update {
  Update_SubCells = (data, dirType) => {
    var q = this.Host;
    q.SubCells = hUtil.Get_SubCells(q);

    this.Update_TotalValueData(undefined, dirType)
  }

  Update_TotalValueData = (data, dirType) => {
    hUtil.ReHandle_TotalValueData(this.Host);
  }

  Update_Unit = (data, dirType) => {
    var host = this.Host;

    this.Update_ValueCurrent(undefined, dirType);
  }

  Update_ValueCurrent = (data, dirType) => {
    var host = this.Host
    var unit = host.Unit


    const setObj = () => {
      host.CurrentTotalValueItem =
        genUtil.SingleFilter(host.TotalValueData,
          (x) => x.Unit === unit)
    }

    if (host.TotalValueData) {
      setObj()
    } else {
      genUtil.Add_2(
        host.OnGet_TotalValueData_Func,
        setObj
      )
    }
  }

  Run = (sections, dirType = u_dt.none, isHostApplied = true) => {
    if (!Array.isArray(sections)) {
      sections = [sections]
    }

    if (isHostApplied) {
      sections.forEach(x => {
        this.RunItem(x, dirType)
      })
    }

    switch (dirType) {
      case u_dt.none:
        break;
      case u_dt.lower:
        this.SubItems.forEach(x => {
          x.Update.Run(sections, dirType)
        })
        break;
      case u_dt.upper:
        this.SupItem?.Update.Run(sections, dirType)
        break;
      case u_dt.both:
        // Update Sub trước sau đó đến Sub
        this.SubItems.forEach(x => {
          x.Update.Run(sections, u_dt.lower)
        })
        this.SupItem?.Update.Run(sections, u_dt.upper)
        break;
    }
  }

  RunItem = (secData, dirType) => {
    var section = typeof secData === 'string' ?
      secData : secData.Section
    var data = secData.Data

    switch (section) {
      case sn.subcells:
        this.Update_SubCells(data, dirType);
        break;
      case sn.valuelist:
        this.Update_TotalValueData(data, dirType);
        break;
      case sn.unit:
        this.Update_Unit(data, dirType);
        break;
      case sn.valuecurrent:
        this.Update_ValueCurrent(data, dirType);
        break;
    }
  }
}