import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { ExportSetVM } from '../item/ExportSetVM';

class TableItemView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new ExportSetVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.RowView}
      </>
    );
  }
}

export default observer(TableItemView);
