import { TwoCol_SelectVM } from "./TwoCol_SelectVM";

// Instance
export const get = (items, render, inputTitle, outputTitle) => {
  var qI = new TwoCol_SelectVM();

  qI.Items = items;
  qI.Render = render;
  qI.InputTitle = inputTitle;
  qI.OutputTitle = outputTitle;
  
  return qI;
}

export const GetTest = () => {
  return get(
    [
      'a', 'b', 'c',
      'd', 'e', 'f',
      'g', 'h', 'i',
      'j', 'k', 'q',
      'r', 's', 't',
    ]
  )
}

// Property
export const GetRender = (q) => {
  return (item) => item;
}