import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Row, Tabs } from 'antd';
import { MassTypeSettingDataVM } from './MassTypeSettingDataVM';
import './MassTypeSetting.css'
import TableView from '../../../../table/TableView';
import { SaveFilled } from '@ant-design/icons';

class MassTypeSettingDataView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassTypeSettingDataVM()
    } else {
      this.state = store;
    }
  }
  
  render() {
    var data = this.state;

    return (
      <>
        <div className='tls__wrapper'>
          {/* Update massType Data */}
          <Button type='primary' size='small' className='tls__save__button'
            onClick={data.Update_MasstypeData}>
            <SaveFilled className='tls__save' />
          </Button>

          <Button type='primary' size='small'
            className='tls__save__button'
            onClick={data.AddItem}>+</Button>
          <TableView store={data.Table} />
        </div>
        
        <hr />

        <div className='tls__wrapper'>
          {/* Update massType Data */}
          <Button type='primary' size='small' className='tls__save__button'
            onClick={data.Update_MasstypeData}>
            <SaveFilled className='tls__save' />
          </Button>

          <Button type='primary' size='small'
            className='tls__save__button'
            onClick={data.AddItemCategory}>+</Button>
          <TableView store={data.TableCategory} />
        </div>

        <hr />

        <div className='tls__wrapper'>
            {/* Update massType Data */}
            <Button type='primary' size='small' className='tls__save__button'
            onClick={data.Update_MasstypeData}>
            <SaveFilled className='tls__save' />
          </Button>

          <Button type='primary' size='small'
            className='tls__save__button'
            onClick={data.AddItemMEP}>+</Button>
          <TableView store={data.TableMEP} />
        </div>
      </>
    );
  }
}

export default observer(MassTypeSettingDataView);