import { LevelSettingVM } from './LevelSettingVM'
import * as dataUtil from './LevelSettingDataVMUtil'

export const get = (q, project) => {
  if (!q) return undefined;
  
  var qI = new LevelSettingVM();

  qI.ModelItem = q;
  qI.Project = project;

  qI.OnGetDataVM = () => {
    var data = q.Data;
    return dataUtil.get(data, qI);
  }  

  return qI;
}