import Setting from "../base/Setting";
import * as util from './MassTypeSettingDataUtil'
import { reaction } from "mobx";
import { PMData } from "../../../single/model/PMData";
import * as itemUtil from './Item/MT_ItemUtil'
import * as genUtil from "../../../../../util/generalUtil";
import * as itemUtilCategory from './Category/Category_ItemUtil'

export const pmData = () => PMData.Instance;

export class MassTypeSettingData {
  
  // Items
  Items = []

  AddItem = () => {
    var item = itemUtil.GetDefault(this);
    this.Items.push(item);
    return item;
  }

  Update_MasstypeData = () => {
    this.masstypeStorageList = util.GetMassTypeStorageList(this);
  }

  OnSet_MassTypeStorageList_Func_Dict = []

  masstypeStorageList
  get masstypeStorageList(){
    if (!this.masstypeStorageList){
      this.masstypeStorageList = util.GetMassTypeStorageList(this);
    }
    return this.masstypeStorageList;
  } set masstypeStorageList(v){
    this.masstypeStorageList = v;

    genUtil.Handle(this.OnSet_MassTypeStorageList_Func_Dict,
      (v) => this.OnSet_MassTypeStorageList_Func_Dict = v)
  }

  //
  AddItemCategory = () => {
    var item = itemUtilCategory.GetDefault(this);
    this.Items.push(item);
    return item;
  }


  get ApiData() {
    return {
      items : this.Items.map(x => x.ApiData)
    }
  }
}