import { MT_Cell } from "./MT_Cell";
import { MT_Data_Row } from "./MT_Data_Row";
import * as c_vt from './MT_Cell_ValueType'

// Instance
export const get = (row, ws, supItem) => {
  var qI = new MT_Data_Row();

  qI.Row = row;
  qI.Worksheet = ws;
  qI.SupItem = supItem;

  return qI;
}

// Property
export const GetSubItems = (q) => {
  return q.Row.SubItems.map(x => get(x, q.Worksheet, q));
}

export const GetTitle = (q) => {
  var row = q.Row;
  var title = '';

  for (var i = 0; i < row.Rank; i++) {
    title += '  ';
  }

  title += row.Title;

  return title;
}

export const GetCells = (q) => {
  var vc_dict = q.ValueCell_Dict = [];

  var firstCol = q.Worksheet.First_ValueColumn;
  var firstRow = q.Worksheet.First_ValueRow;

  var row = q.Row;
  var list = []
  var getRowFunc = () => q.FullIndex + firstRow;

  var titleCell = new MT_Cell();
  titleCell.Dict = q;
  titleCell.Type = c_vt.string;

  titleCell.Value = q.Title;
  titleCell.Column = 0 + firstCol;
  titleCell.Override_Get_Row = getRowFunc
  list.push(titleCell);

  var columns = row.Table.Columns
  row.Cells.forEach(x => {
    var valueCell = new MT_Cell()
    valueCell.Dict = q;
    valueCell.Type = c_vt.number;
    valueCell.Value = x.TotalValue
    valueCell.Format = '0.00'
    valueCell.Column = 
      columns.indexOf(x.Column) + 1 + firstCol
    valueCell.Override_Get_Row = getRowFunc

    valueCell.Override_Get_Formula = () => {
      var subItems = valueCell.SubItems;
      var length = subItems.length;
      if (length === 0) return undefined;

      var formula
      if (!valueCell.IsContinueSubRows) {
        formula = subItems.reduce((a, x, i) => {
          if (i !== 0) {
            a += ','
          };
          a += x.Address
          return a
        }, 'SUM(');

        formula += ')';
      } else {
        formula = `SUM(${subItems[0].Address}:${subItems[length-1].Address})`
      }

      return formula;
    }

    var key = x.Column.Name
    list.push(valueCell);
    vc_dict.push({
      Key: key,
      Value: valueCell
    })
  })

  return list;
}

export const GetFullItems = (q) => {
  var list = [q];

  q.SubItems.forEach(x => {
    list.push.apply(list, x.FullItems);
  });

  return list;
}