import { TLS_FilterVM } from "./TLS_FilterVM";
import * as valueUtil from './TLS_FilterValueVMUtil'

export const get = (q, dict) => {
  var qI = new TLS_FilterVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

export const GetItems = (q) => {
  return q.ModelItem.Items.map(x => valueUtil.get(x, q));
}