import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import * as checkboxVMUtil from './CheckboxVMUtil'
import './Checkbox.css'

class CheckboxView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = checkboxVMUtil.get(false);
    } else {
      this.state = store;
    }
  }

  onCheckedChange = (e) => {
    var data = this.state;
    var { checked } = e.target;

    if (data.OnCheckChanging) {
      data.OnCheckChanging(checked);
    }

    if (!data.Cancel) {
      data.Ischecked = checked;
    }
  }

  render() {
    var { ischecked, name, isDisable } = this.state;

    return (
      <>
        <label>
          <input type="checkbox"
            checked={ischecked}
            onChange={this.onCheckedChange} disabled={isDisable} />
          <span className='checkbox__title'>{name}</span>
        </label>
      </>
    )
  }
}

export default observer(CheckboxView)