import * as genUtil from '../../../../../../../util/generalUtil'
import * as comUtil from '../../../../../../../util/commandUtil'
import { EntDelete } from '../../../../../../delete/EntDelete';
import { Tab } from '../../../../tab/Tab';
import * as mgsdUtil from '../MassGroupSetting_DictVMUtil'
import * as mgsd_ut from '../MGS_Dict_UseType'
import { MGS_Dict_DictVM } from './MGS_Dict_DictVM';
import * as d_tt from '../../../../../../delete/EntDelete_ToggleType'
import { PlusCircleOutlined, SaveFilled } from '@ant-design/icons';
import { Button, message } from 'antd';
import { TextVM } from '../../../../../../text/TextVM';
import InputView from '../../../../../../input/InputView';
import * as ut from './MGS_Dict_Dict_UseType'
import Modal from 'antd/lib/modal/Modal';
import * as pct from '../../../../command/ProjectCommand'

// Instance
export const get = (q) => {
  var qI = new MGS_Dict_DictVM();
  qI.ModelItem = q;
  return qI;
}

// Property
export const GetItems = (q) => {
  return q.ModelItem.Items.map(x => mgsdUtil.get(x, q));
}

export const GetMainItem = (q) => {
  return genUtil.SingleFilter
    (q.Items, (x) => x.UseType === mgsd_ut.project)
}

export const GetQueryTab = (q) => {
  var qI = new Tab();

  qI.Items = q.Items;
  qI.TitleRender = (v) => v.CategoryName
  qI.ContentRender = (v) => v.TableWrapper_View;

  return qI;
}

export const GetSaveTab = (q) => {
  var qI = new Tab();

  qI.Items = [q.MainItem]
  qI.TitleRender = (v) => v.CategoryName
  qI.ContentRender = (v) => v.TableWrapper_View;

  return qI;
}

export const GetAdd_MGS = (q) => {
  var qI = new EntDelete();

  var nameVM = new TextVM();
  nameVM.Placeholder = 'tên thiết lập ...'

  qI.ToggleType = d_tt.manual
  qI.Title = 'Xác nhận thêm thiết lập?'

  qI.OnPre_ShowHover = () => {
    nameVM.Content = undefined
  }

  qI.Modal_InfoView = <InputView store={nameVM} />
  qI.Modal_ContentView = <div className='msg__dd__save__add__hoverwrapper'>
    {qI.Modal_InfoView}
    {qI.Modal_ButtonView}
  </div>

  qI.ShowHover_Button = <Button type='primary' size='small'
    className='btn-success'
    onClick={qI.Show_Hover}>
    <PlusCircleOutlined />
  </Button>

  qI.OnPreRun = () => {
    if (!nameVM.Content || nameVM.Content.length === 0) {
      qI.IsRun_Cancel = true
      message.warn('Vui lòng nhập tên thiết lập!');
    }
  }

  qI.OnRun = () => {
    var save = q.MainItem.Save;
    save.Name = nameVM.Content;
    save.Run();
  }

  return qI;
}

export const GetPick_MGS_View = (q) => {
  var content = q.Current_MGS ? q.Current_MGS.Name_ForPick : '(none)'
  var click = () => {
    q.Is_Modal_Visible = true
    q.UseType = ut.query
  }

  return <Button type='primary'
    onClick={click}>
    <div className='msg__dd__pick__wrapper'>
      <div>CHỌN THIẾT LẬP:</div>
      <div className='msg__dd__pick__current'>{content}</div>
    </div>
  </Button>
}

export const GetSave_MGS_View = (q) => {
  if (comUtil.Validate(pct.edit)){
  // if (q.Commands.includes(pct.edit)) {
    var click = () => {
      q.Is_Modal_Visible = true
      q.UseType = ut.save
    }

    return <Button type='primary' size='small'
      onClick={click}>
      <SaveFilled />
    </Button>
  }
  return ''
}

export const GetView = (q) => {
  switch (q.UseType) {
    case ut.query:
      return <>
        {q.QueryTab.View}
      </>
    case ut.save:
      return <>
        <div className='msg__dd__save__wrapper'>
          <div className='msg__dd__save__add'>
            {q.Commands.includes(pct.edit) ? q.Add_MGS.View : ''}
          </div>
          <div className='msg__dd__save__main'>
            {q.SaveTab.ContentView}
          </div>
        </div>
      </>
  }
}

export const GetModalView = (q) => {
  var title = q.UseType === ut.query ?
    'CHỌN THIẾT LẬP' : 'LƯU THIẾT LẬP'

  const toggleVisible = () => {
    q.Is_Modal_Visible = !q.Is_Modal_Visible
  }

  return <Modal title={title} visible={q.Is_Modal_Visible}
    width={1000}
    className='msg__modal'
    onCancel={toggleVisible}
    footer={null}>
    <div>
      {q.View}
    </div>
  </Modal>
}

// Method
export const SetUseType = (q, v) => {
  var host = q.MainItem;

  const setObj = () => {
    host.Items.forEach(x => {
      x.DD_UseType = v
    })
  }

  if (host.Items) {
    setObj();
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      setObj
    )
  }
}