import { makeObservable, observable, action } from 'mobx';
import * as util from './MassRow_ToolElementUtil'

export default class MassRow_ToolElement {
  constructor() {
    makeObservable(this, {
      editable_TitleVM_RenderState: observable
    })
  }

  get Editable_TitleVM() {
    return this.Dict.Editable_TitleVM;
  }

  editable_TitleVM_RenderState= undefined
  get Editable_TitleVM_RenderState(){
    if (this.editable_TitleVM_RenderState === undefined){
      this.editable_TitleVM_RenderState =
        util.GetEditable_TitleVM_RenderState(this);
    }
    return this.editable_TitleVM_RenderState;
  } set Editable_TitleVM_RenderState(v){
    this.editable_TitleVM_RenderState = v;

    this.Editable_TitleVM.RenderState= v;
  }

  Method = (name) => {
    return this.Dict[name];
  }
}