import { makeObservable, observable, action } from 'mobx';
import * as rs from './RenderState'
import * as renderUtil from './RenderItemUtil'
import Render_Dict from './Render_Dict';
import TitleRender_Dict from './TitleRender_Dict';
import * as it from './TableColumn_InputType'
import * as selectUtil from '../select/SelectVMUtil';
import * as textUtil from '../text/TextVMUtil';
import InputView from '../input/InputView';
import SelectView from '../select/SelectView';
import { PMData } from '../main/single/model/PMData';
import * as util from './TableColumnUtil'
import * as genUtil from '../../util/generalUtil'

export const pmData = () => PMData.Instance;

export default class TableColumn {
  constructor() {
    makeObservable(this, {
      title: observable,
      TitleRenderState: observable,
      inputType: observable,
      IsShow_MoveCommand: observable
    })
  }

  get Index() {
    return this.CurrentList.indexOf(this);
  }

  get CurrentList() {
    return this.Table.Columns;
  } set CurrentList(v) {
    this.Table.Columns = v;
  }

  TitleRenderState = rs._default
  RenderState = rs._default;

  title = undefined
  get Title() {
    // Bỏ việc khởi tạo sẵn đối tượng
    // if (!this.title) {
    //   this.title = util.GetTitle(this);
    // }
    return this.title;
  } set Title(v) {
    this.title = v;
  }

  dataIndex = undefined
  get DataIndex(){
    return this.dataIndex;
  } set DataIndex(v){
    this.dataIndex = v;
  }

  key
  get Key(){
    if (!this.key){
      this.key = genUtil.newGUID();
    }
    return this.key;
  } set Key(v){
    this.key = v;
  }

  OnSetTitleOK_Funcs = []

  titleRender_Dict
  get TitleRender_Dict() {
    if (!this.titleRender_Dict) {
      this.titleRender_Dict = new TitleRender_Dict();
    }
    return this.titleRender_Dict;
  }

  render_Dict
  get Render_Dict() {
    if (!this.render_Dict) {
      this.render_Dict = new Render_Dict();
    }
    return this.render_Dict;
  }
  set Render_Dict(v) {
    this.render_Dict = v;
  }

  Edit = () => {
    var table = this.Table;
    if (table.CanHandleColumn && table.CanEditColumn) {
      this.TitleRenderState = rs.edit;
    }
  }

  OK = () => {
    this.TitleRenderState = rs._default;
    this.OnSetTitleOK_Funcs.forEach(x => x(this));
  }

  Delete = () => {
    var table = this.Table;
    if (table.CanHandleColumn && table.CanDeleteColumn) {
      table.Columns.splice(this.Index, 1);
      table.Update();
    }
  }

  TitleRender = () => {
    var title = this.Title;
    var table = this.Table;

    var render = this.TitleRender_Dict.GetRender(this.TitleRenderState);
    return render.Render(title, this, table);
  }

  inputType = undefined
  get InputType() {
    if (!this.inputType) {
      this.inputType = this.Table.Column_InputType
    }
    return this.inputType;
  }

  // Thuộc tính sử dụng khi select đầu mục cột
  get AllNames() {
    return this.Table.AllColumnNames
  }

  inputVM
  get InputVM() {
    if (!this.inputVM) {
      var obj = this.inputVM = textUtil.get(this.Title);
      obj.onSetContent = (v) => this.Title = v;
    }
    return obj;
  }

  selectVM
  get SelectVM() {
    if (!this.selectVM) {
      var list = this.AllNames;
      var obj = this.selectVM = selectUtil.get(this.AllNames);
      obj.TitleRender = (x) => x.Name
      obj.OnSelectedItemChanged = (v) => {
        this.Title = v.Name;
        this.DataIndex = [ v.Value ];
      }

      // Nếu không có thông tin Title
      if (!this.Title) {
        var firstItem = list[0]
        this.Title = firstItem.Name;
        this.DataIndex = [firstItem.Value]

        obj.InitialItem = firstItem;
      } else {
        // Có Title mà chưa có InitialItem
        if (!obj.InitialItem) {
          var index = list.findIndex(x => x.Name === this.Title);
          obj.InitialItem = list[index];
        }
      }
    }
    return this.selectVM;
  }

  get InputElement() {
    switch (this.InputType) {
      case it.input:
        return <InputView store={this.InputVM} />
      case it.select:
        var width = this.Table.Column_Title_SelectWidth
        return <div style={{ width }}>
          <SelectView store={this.SelectVM} />
        </div>
    }
  }

  IsShow_MoveCommand = false

  get CanShow_MoveCommand() {
    return this.Table.CanShow_MoveCommand;
  }

  Show_MoveCommand = () => {
    this.IsShow_MoveCommand = true;
  }

  Collapse_MoveCommand = () => {
    this.IsShow_MoveCommand = false;
  }

  MoveLeft = () => {
    var index = this.Index;
    if (index === 0) return;

    var list = this.CurrentList;
    var l1 = list.filter(x => !x.IsActive);
    if (l1.length === 1) return;

    genUtil.MoveItem(list, index, index - 1);
    this.Table.Update();
  }

  MoveRight = () => {
    var index = this.Index;

    var list = this.CurrentList;
    var l1 = list.filter(x => !x.IsActive);

    if (l1.length === 1 || index === l1.length) {
      return
    }

    genUtil.MoveItem(list, index, index + 1);
    this.Table.Update();
  }
}