import { PMData } from '../../../single/model/PMData';
import { MassTypeSettingData } from './MassTypeSettingData';
import * as itemUtil from './Item/MT_ItemUtil';

export const pmData = ()  =>{
  return PMData.Instance;
}

// Instance
export const GetFromDb = (data, dict) => {
  var qI = new MassTypeSettingData();
  
  qI.Items = data.items.map(x => itemUtil.GetFromDb(x, qI));

  return qI;
}

export const GetDefault = (dict) => {
  var qI = new MassTypeSettingData();

  qI.Items = []

  return qI;
}

// Property
export const GetMassTypeStorageList = (q) => {
  return q.Items.map(x => x.Name);
}