import { ColumnGroup } from "./ColumnGroup"
import * as cgt from '../column/Column_GroupType'
import * as colUtil from '../column/ColumnUtil'
import * as mif from '../../../../mass/item/MassItemField'
import * as mut from '../../../../mass/unit/MassUnitType'

// Instance
export const get = (group, dict) => {
  var qI = new ColumnGroup();
  qI.Group = group;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetTitle = (q) => {
  switch (q.Group) {
    case cgt.info:
      return 'THÔNG TIN'
    case cgt.value:
      return 'GIÁ TRỊ'
  }
}

export const GetColumns = (q) => {
  return q.Fields.map(x => {
    return colUtil.get(x, q.Group, q)
  })
}

export const GetDefaultField = (q) => {
  switch (q.Group) {
    case cgt.info:
      return mif.workItem;
    case cgt.value:
      return mut.each;
  }
}

// Method
export const AddColumn = (q) => {
  var qI = colUtil.get(q.DefaultField, q.Group, q);
  q.Columns.push(qI)
  return q;
}