import * as FileSaver from 'file-saver';
import * as XLSX from 'xlsx';

export class ExcelExport {
  workbook
  get Workbook(){
    if (this.Override_Get_Workbook){
      return this.Override_Get_Workbook()
    }
    return this.workbook;
  } set Workbook(v){
    this.workbook = v;
  }

  fileName
  get FileName() {
    if (this.Override_Get_FileName) {
      return this.Override_Get_FileName();
    }
    return this.fileName;
  } set FileName(v) {
    this.fileName = v;
  }

  Export = () => {
    const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
    const fileExtension = '.xlsx';
    
    const excelBuffer = XLSX.write(this.Workbook, { bookType: 'xlsx', type: 'array' });
    const data = new Blob([excelBuffer], { type: fileType });
    FileSaver.saveAs(data, this.FileName + fileExtension);
  }
}