import { makeObservable, observable, action } from 'mobx';
import * as textUtil from '../../../text/TextVMUtil';
import * as util from './DO_ElementVMUtil'

export class DO_ElementVM {
  constructor() {
    makeObservable(this, {
      subItems: observable,
      isOpenSub : observable
    })
  }

  get Title(){
    return this.ModelItem.Title
  }

  get IsHaveButton(){
    return this.SubItems.length !== 0;
  }

  subItems = undefined
  get SubItems(){
    if (!this.subItems){
      this.subItems = util.GetSubItems(this);
    }
    return this.subItems
  }

  isOpenSub = false
  get IsOpenSub(){
    return this.isOpenSub
  } set IsOpenSub(v){
    this.isOpenSub = v;
  }

  ToggleSub = () => {
    this.IsOpenSub = !this.IsOpenSub;
    this.ToggleSubContentVM.Content = util.GetToggleSubContent(this)
  }

  toggleSubContentVM
  get ToggleSubContentVM(){
    if (!this.toggleSubContentVM){
      this.toggleSubContentVM = 
        textUtil.get(util.GetToggleSubContent(this))
    }
    return this.toggleSubContentVM;
  }
}