import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import './Table.css'
import { Table } from './Table';
import * as mif from '../../../mass/item/MassItemField'
import * as mut from '../../../mass/unit/MassUnitType'
import * as genUtil from '../../../../../../util/generalUtil'
import * as cgt from './column/Column_GroupType'

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new Table();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <table className='table table-bordered'>
          <thead className='es__thead'>
            {/* Column Group */}
            <tr>
              {data.ColumnGroups.map(x => x.View)}
            </tr>

            {/* Column */}
            <tr>
              {data.ColumnGroups.map(x =>
                x.Columns.map(x => x.View))}
            </tr>
          </thead>
          <tbody>
            {/* Items */}
            {data.Items ?
              data.Items.map(x =>
                <tr key={x._id}>
                  {data.ColumnGroups.map(y =>
                    y.Columns.map(z => z.MassItem_Render(x)))}
                </tr>)
              : <tr />}
          </tbody>
        </table>
      </>
    );
  }
}

export default observer(TableView);
