
import MassResultComp from '../../entity/mass/result/MassResultComp';
import VersionCompareSetting from '../../entity/setting/versionCompare/VersionCompareSetting';
import { Tracking } from '../../entity/tracking/Tracking';
import * as util from './PMDataUtil'
import * as genUtil from '../../../../util/generalUtil'

export class PMData {
  constructor() {
    
  }

  static instance
  static get Instance(){
    if (!this.instance){
      this.instance = new PMData();
    }
    return this.instance;
  }

  project_Dict
  get Project_Dict() {
    if (!this.project_Dict){
      this.project_Dict = util.Get_Project_Dict(this);
    }
    return this.project_Dict;
  }

  project_Dict_ForMassDetail
  get Project_Dict_ForMassDetail() {
    if (!this.project_Dict_ForMassDetail){
      this.project_Dict_ForMassDetail = 
        util.Get_Project_Dict_ForMassDetail(this);
    }
    return this.project_Dict_ForMassDetail;
  }

  currentProject
  get CurrentProject(){
    return this.currentProject
  } set CurrentProject(v){
    this.currentProject = v;

    this.Reset_Commands(this);
    this.MassGroupSetting_Dict.Project = v;
  }

  mgs_Dict_Dict
  get Mgs_Dict_Dict(){
    if (!this.mgs_Dict_Dict){
      this.mgs_Dict_Dict = util.GetMgs_Dict_Dict(this)
    } return this.mgs_Dict_Dict
  }

  massGroupSetting_Dict = undefined
  get MassGroupSetting_Dict(){
    if (!this.massGroupSetting_Dict){
      this.massGroupSetting_Dict = util.GetMassGroupSetting_Dict(this)
    }return this.massGroupSetting_Dict
  }

  phase_Dict_ForMassDetail
  get Phase_Dict_ForMassDetail() {
    if (!this.phase_Dict_ForMassDetail){
      this.phase_Dict_ForMassDetail = 
        util.Get_Phase_Dict_ForMassDetail(this);
    }
    return this.phase_Dict_ForMassDetail;
  }

  massResultComp
  get MassResultComp(){
    if (!this.massResultComp){
      this.massResultComp = new MassResultComp();
    }
    return this.massResultComp;
  }
  set MassResultComp(v){
    this.massResultComp = v;
  }

  massItemFields
  get MassItemFields(){
    if (!this.massItemFields){
      this.massItemFields = util.GetMassItemFields();
    }
    return this.massItemFields;
  }

  mifs_ForMassGroup
  get MIFs_ForMassGroup(){
    if (!this.mifs_ForMassGroup){
      this.mifs_ForMassGroup = util.GetMIFs_ForMassGroup();
    }
    return this.mifs_ForMassGroup;
  }

  // Tên và Value của MassGroupField
  mif_Dict_ForMassGroup
  get MIF_Dict_ForMassGroup(){
    if (!this.mif_Dict_ForMassGroup){
      this.mif_Dict_ForMassGroup = util.GetMIF_Dict_ForMassGroup();
    }
    return this.mif_Dict_ForMassGroup;
  }

  // Thêm Unit và Value
  mif_Dict_ForDetail
  get MIF_Dict_ForDetail(){
    if (!this.mif_Dict_ForDetail){
      this.mif_Dict_ForDetail = util.GetMIF_Dict_ForDetail();
    }
    return this.mif_Dict_ForDetail;
  }

  // List dành cho Input
  mif_Dict_ForInput
  get MIF_Dict_ForInput(){
    if (!this.mif_Dict_ForInput){
      this.mif_Dict_ForInput = util.GetMIF_Dict_ForInput();
    }
    return this.mif_Dict_ForInput;
  }

  // UnitType
  mut_Dict
  get MUT_Dict(){
    if (!this.mut_Dict){
      this.mut_Dict = util.GetMUT_Dict(this)
    } return this.mut_Dict
  }

  // massTypes;
  // get MassTypes(){
  //   if (!this.massTypes){
  //     this.massTypes = util.GetMassTypes();
  //   }
  //   return this.massTypes;
  // }

  versionCompareSetting
  get VersionCompareSetting(){
    if (!this.versionCompareSetting){
      this.versionCompareSetting = new VersionCompareSetting();
    }
    return this.versionCompareSetting;
  }

  massDetailSetting_Dict
  get MassDetailSetting_Dict(){
    if (!this.massDetailSetting_Dict){
      this.massDetailSetting_Dict = util.GetMassDetailSetting_Dict(this);
    }
    return this.massDetailSetting_Dict;
  }

  DebugPoints = []

  tracking
  get Tracking(){
    if (!this.tracking){
      this.tracking = new Tracking();
    }
    return this.tracking;
  }

  IsDebug = false

  OnSet_CurrentProject_Func_Dict = []

  ApiUrl = 'https://api.bimdev.vn/api'

  UpdatedDate = '26/06/2021'

  // Kiểm tra có phải lần đầu truy xuất bảng khối lượng
  // sau khi mở trang web hay ko?
  Is_First_Get_MassTable

  // Đối tượng noted trong debug
  LoggingItems = []

  // UserDict
  userDict
  get UserDict(){
    if (!this.userDict){
      this.userDict = util.GetUserDict(this)
    } return this.userDict;
  }

  // Admin
  admin
  get Admin(){
    if(!this.admin){
      this.admin = util.GetAdmin(this)
    } return this.admin
  }

  // Commands
  OnSet_Commands_Func = []

  Reset_Commands = () => {
    this.Commands = undefined;
    this.IsGet_Commands = false
    util.Handle_Commands(this)
  }

  commands
  get Commands(){
    if (!this.commands){
      util.Handle_Commands(this)
    } return this.commands
  } set Commands(v){
    this.commands = v;
    genUtil.Handle(this.OnSet_Commands_Func,
      (v) => this.OnSet_Commands_Func = v)
  }

  // Admin Command
  OnSet_AdminCommands_Func = []

  Reset_AdminCommands = () => {
    this.AdminCommands = undefined;
    this.IsGet_AdminCommands = false
    util.Handle_AdminCommands(this)
  }

  adminCommands
  get AdminCommands(){
    if (!this.adminCommands){
      util.Handle_AdminCommands(this)
    } return this.adminCommands
  } set AdminCommands(v){
    this.adminCommands = v;
    genUtil.Handle(this.OnSet_AdminCommands_Func,
      (v) => this.OnSet_AdminCommands_Func = v)
  }
}