import { PlusCircleOutlined } from '@ant-design/icons';
import { Button } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../../../util/generalUtil'
import * as util from './MGS_Dict_DictVMUtil'
import * as ut from './MGS_Dict_Dict_UseType'
import './MGS_Dict_Dict.css'
import { PMDataVM } from '../../../../../single/form/PMDataVM';

const pmDataVM = () => PMDataVM.Instance

export class MGS_Dict_DictVM {
  constructor() {
    makeObservable(this, {
      useType: observable,
      current_MGS : observable,
      Is_Modal_Visible : observable
    })
  }

  items
  get Items() {
    if (!this.items) {
      this.items = util.GetItems(this)
    } return this.items;
  }

  useType = ut.query
  get UseType(){
    return this.useType
  } set UseType(v){
    this.useType = v;

    util.SetUseType(this, v)
  }

  Toggle_UseType = () => {
    this.UseType = this.UseType === ut.query ? ut.save : ut.query
  }

  queryTab
  get QueryTab() {
    if (!this.queryTab) {
      this.queryTab = util.GetQueryTab(this)
    } return this.queryTab
  }

  saveTab
  get SaveTab() {
    if (!this.saveTab) {
      this.saveTab = util.GetSaveTab(this)
    } return this.saveTab
  }

  get View() {
    return util.GetView(this)
  }

  Is_Modal_Visible = false

  get ModalView() {
    return util.GetModalView(this)
  }

  mainItem
  get MainItem(){
    if (!this.mainItem){
      this.mainItem = util.GetMainItem(this)
    } return this.mainItem
  }

  add_MGS
  get Add_MGS(){
    if (!this.add_MGS){
      this.add_MGS = util.GetAdd_MGS(this)
    } return this.add_MGS
  }

  current_MGS = undefined
  get Current_MGS() {
    return this.current_MGS
  } set Current_MGS(v) {
    this.current_MGS = v;
    this.ModelItem.Current_MGS = v?.ModelItem
  }

  get Pick_MGS_View(){
    return util.GetPick_MGS_View(this)
  }

  get Save_MGS_View(){
    return util.GetSave_MGS_View(this)
  }

  get Commands(){
    return pmDataVM().Commands
  }
}