import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../util/generalUtil'
import * as util from './AdminVMUtil'

export class AdminVM {
  constructor() {
    makeObservable(this, {
      
    })
  }

  // Users
  OnSet_Users_Func = []

  users
  get Users(){
    if (!this.users){
      util.Handle_Users(this)
    } return this.users
  } set Users(v){
    this.users = v;
    genUtil.Handle(this.OnSet_Users_Func,
      (v) => this.OnSet_Users_Func = v)
  }

  // Projects
  OnSet_Projects_Func = []

  projects
  get Projects(){
    if (!this.projects){
      util.Handle_Projects(this)
    } return this.projects
  } set Projects(v){
    this.projects = v;
    genUtil.Handle(this.OnSet_Projects_Func,
      (v) => this.OnSet_Projects_Func = v)
  }

  // Dict
  userManager_Dict
  get UserManager_Dict(){
    if (!this.userManager_Dict){
      this.userManager_Dict = util.GetUserManager_Dict(this)
    } return this.userManager_Dict;
  }

  projectCommand_Dict
  get ProjectCommand_Dict(){
    if (!this.projectCommand_Dict){
      this.projectCommand_Dict = util.GetProjectCommand_Dict(this)
    } return this.projectCommand_Dict
  }

  projectGroupCommand_Dict
  get ProjectGroupCommand_Dict(){
    if (!this.projectGroupCommand_Dict){
      this.projectGroupCommand_Dict = 
        util.GetProjectGroupCommand_Dict(this)
    } return this.projectGroupCommand_Dict
  }
}