import * as textUtil from '../../../../../../text/TextVMUtil'
import * as util from './AddTemplateVMUtil'
import { makeObservable, observable, action } from 'mobx';

export class AddTemplateVM {
  constructor() {
    makeObservable(this, {
      dict: observable
    })
  }

  get InputName() {
    return this.ModelItem?.InputName
  } set InputName(v) {
    this.ModelItem.InputName = v
  }

  inputNameVM
  get InputNameVM() {
    if (!this.inputNameVM) {
      var obj = this.inputNameVM = textUtil.get(this.InputName, 'tên template');
      obj.onSetContent = (v) => this.InputName = v;
    } return this.inputNameVM
  }

  tool
  get Tool() {
    if (!this.tool) {
      this.tool = util.GetTool(this)
    } return this.tool
  }

  get View() {
    return this.Tool.View
  }

  Show = this.Tool.Show

  Hide = this.Tool.Hide

  run
  get Run() {
    if (!this.run) {
      this.run = () => this.ModelItem.Run();
    } return this.run
  }

  dict = undefined
  get Dict(){
    return this.dict;
  } set Dict(v){
    this.dict = v;
    util.SetDict(this, v)
  }

  get Delete(){
    return this.Dict.DeleteVM
  }
}