import * as genUtil from '../../../../../../util/generalUtil'
import { DictVM } from "./DictVM";
import * as iUtil from '../../../../../user/item/UserVMUtil'
import './Dict.css'
import { Button } from 'antd';
import * as editTextUtil from '../../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../../text/EditableTextView';
import * as rs from '../../../../../text/TextRenderState';

// Instance
export const get = (q, dict) => {
  var qI = new DictVM();

  qI.ModelItem = q;
  qI.Dict = dict

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => {
      return q.GetItem_Func(x);
    });
    q.IsGet_Items = false;
  }

  var host = q.ModelItem
  if (host.Items) {
    setObj(host.Items)
  }

  genUtil.Add_2(
    host.OnSet_Items_Func,
    () => setObj(host.Items),
    false
  )
}

export const GetDefault_ItemRender = (q) => {
  return (v) => <tr key={v.id} onDoubleClick={v.Edit}>
    <td>
      <div className='admin__um__username'>
        {v.UsernameView}
      </div>
    </td>
    <td>
      <div className='admin__um__password f-ac'>
        <div className='f1'>
          {v.PasswordView}
        </div>
        {v.ToolView_Render()}
      </div>
    </td>
  </tr>
}

export const GetGetItem_Func = (q) => {
  return (x) => {
    var qI = iUtil.get(x, q)

    var q1 = qI.PasswordVM = editTextUtil.get('', undefined);
    q1.Is_Tool_Visible = false
    qI.OnSet_RenderState = (v) => {
      q1.RenderState = v
    }

    genUtil.Add(
      q1.OnSet_RenderState_Func_Dict,
      () => {
        if (q1.RenderState === rs._default) {
          x.Password = q1.Content;
          q1.Content = ''
        }
      },
      false
    )
    qI.PasswordView = <EditableTextView store={q1} />

    const ok = () => {
      // qI.IsSaveDatabase = false
      qI.RenderState = rs._default
    }
    const cancel = () => {
      qI.UsernameVM.Content = qI.Username;
      qI.PasswordVM.Content = ''
      qI.IsSaveDatabase = false
      qI.RenderState = rs._default
    }

    qI.ToolView_Render = () => <div className='f'>
      {qI.RenderState === rs.edit ?
        <>
          <Button type='primary' size='small'
            onClick={ok}>ok</Button>
          <Button type='primary' size='small'
            onClick={cancel}>cancel</Button>
        </>
        : ''}
      <Button type='primary' size='small'
        className='btn-danger'
        onClick={qI.Delete}>x</Button>
    </div>

    return qI;
  }
}