import { makeObservable, observable, action } from 'mobx';
import * as util from './DO_CompVMUtil'

export class DO_CompVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }
  
  items = undefined;
  get Items(){
    if (!this.items){
      this.items = util.GetItems(this);
    }
    return this.items;
  }
}