import { makeObservable, observable, action } from 'mobx';
import * as util from './WorkItemExportSetting_DictVMUtil'
import * as itemUtil from './WorkItemExportSettingVMUtil';
import { WorkItemExportSetting } from './WorkItemExportSetting';

export class WorkItemExportSetting_DictVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }

  items = undefined
  get Items() {
    if (!this.items) {
      this.items = [
        itemUtil.get(new WorkItemExportSetting())
      ]
    }
    return this.items;
  }

  tableVM
  get TableVM() {
    if (!this.tableVM) {
      this.tableVM = util.GetTableVM(this);
    }
    return this.tableVM;
  }
}