import * as util from './TLS_Data_WrapperVMUtil'
import * as genUtil from "../../../../../../util/generalUtil";

export class TLS_Data_WrapperVM {
  binding
  get Binding(){
    if (!this.binding){
      this.binding = util.GetBinding(this);
    }
    return this.binding 
  }

  typical
  get Typical(){
    if (!this.typical){
      this.typical = util.GetTypical(this);
    }
    return this.typical;
  }

  // Old versoin
  // get LevelStorageList() {
  //   return this.Dict.LevelStorageList;
  // }

  // Level StorageList
  OnSet_LevelStorageList_Func_Dict = []

  levelStorageList
  get LevelStorageList(){
    if (!this.levelStorageList){
      this.levelStorageList = util.GetLevelStorageList(this);
    }
    return this.levelStorageList;
  } set LevelStorageList(v){
    this.levelStorageList = v;

    genUtil.Handle(this.OnSet_LevelStorageList_Func_Dict,
      (v) => this.OnSet_LevelStorageList_Func_Dict = v)
  }

  get FieldFilterStorageList() {
    return this.Dict.FieldFilterStorageList;
  }

  get SettingVM (){
    return this.Dict;
  }

  get Project(){
    return this.Dict.Project
  }
}