import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './MT_CellUtil'

export class MT_Cell {
  // Lấy thứ tự trong lập trình
  row = 0
  get Row(){
    if (this.Override_Get_Row){
      return this.Override_Get_Row();
    }
    return this.row;
  } set Row(v){
    this.row = v;
    this.Override_Get_Row = undefined;
  }

  // Lấy thứ tự trong lập trình
  column = 0
  get Column(){
    if (this.Override_Get_Column){
      return this.Override_Get_Column();
    }
    return this.column
  } set Column(v){
    this.column = v;
    this.Override_Get_Column = undefined;
  }

  Type 
  Value

  formula
  get Formula(){
    if (this.Override_Get_Formula){
      return this.Override_Get_Formula();
    }
    return this.formula;
  } set Formula(v){
    this.formula = v;
    this.Override_Get_Formula = undefined;
  }

  get Address(){
    var obj = genUtil.ExcelCellName(this.Row, this.Column);
    return obj;
  }

  subItems
  get SubItems(){
    if (!this.subItems){
      this.subItems = util.GetSubItems(this);
    }
    return this.subItems;
  }

  isContinueSubRows
  get IsContinueSubRows(){
    if (this.isContinueSubRows === undefined){
      this.isContinueSubRows = util.GetIsContinueSubRows(this);
    }
    return this.isContinueSubRows;
  }

  get ResultData(){
    var obj = {
      t : this.Type,
      v : this.Value,
      f : this.Formula,
      z : this.Format,
      s : this.Style
    }
    return obj;
  }
}