import * as genUtil from '../../../../../../util/generalUtil'
import * as levelGroupUtil from '../levelGroup/LevelGroupUtil'
import * as filterUtil from '../filter/TLS_FilterUtil'
import * as util from './TLS_PageItemUtil'

export class TLS_PageItem {
  _id
  get id(){
    if (!this._id){
      this._id = genUtil.newGUID();
    }
    return this._id;
  }

  Name = 'tên setting'

  get Index (){
    return this.Dict.PageItems.indexOf(this);
  }

  Delete = () => {
    this.Dict.PageItems.splice(this.Index, 1);
  }

  get FieldFilterStorageList() {
    return this.Dict.FieldFilterStorageList;
  }

  // Setting
  get Setting() {
    return this.Dict.Setting;
  }

  // Level Groups
  get LevelStorageList() {
    return this.Dict.LevelStorageList;
  }

  levelGroups
  get LevelGroups(){
    if (!this.levelGroups){
      this.levelGroups = util.GetLevelGroups(this);
    }
    return this.levelGroups;
  } set LevelGroups(v){
    this.levelGroups = v;
  }

  AddLevelGroup = () => {
    var item = levelGroupUtil.GetDefault(this);
    this.LevelGroups.push(item);
    return item;
  }

  // Filter
  filters
  get Filters(){
    if (!this.filters){
      this.filters = util.GetFilters(this);
    }
    return this.filters;
  } set Filters(v){
    this.filters = v;
  }

  AddFilter = () => {
    var item = filterUtil.GetDefault(this);
    this.Filters.push(item);
    return item;
  }

  get FilterDescription() {
    var obj = this.Filters.reduce((a,b,i,arr) => {
      if (arr.indexOf(b) !== 0){
        a += '; ';
      }
      a += b.Description;
      return a;
    }, '')
    return obj;
  }

  get ApiData() {
    var obj = {
      name: this.Name,
      levelGroups : this.LevelGroups.map(x => x.ApiData).filter(x => x.levels.length !== 0),
      filters : this.Filters.map(x => x.ApiData)
    }
    return obj;
  }
}