import * as genUtil from '../../../../../../util/generalUtil'
import * as api from '../../../../../../util/apiUtil'
import * as dataUtil from '../data/DataUtil'
import { url } from './MassGroupSetting_Url'
import { MassGroupSetting } from './MassGroupSetting'
import { AddTemplate } from './addtemplate/AddTemplate'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = new MassGroupSetting();

  qI._id = data._id
  qI.projectId = data.projectId
  qI.Name = data.name;
  qI.Description = data.description
  //qI.Data = dataUtil.GetFromDb(data.data[0], qI);
  qI.Dict = dict;

  return qI;
}

export const get = (dict) => {
  var qI = new MassGroupSetting();

  qI.Dict = dict;
  
  return qI;
}

// Property
export const Handle_Data = (q) => {
  if (q.IsGet_Data) return undefined;
  q.IsGet_Data = true;

  const setObj = (data) => {
    q.Data = dataUtil.GetFromDb(data[0], q)
  }

  const getApi = () => {
    var apiData = {
      dictId : q._id
    }

    dataUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }

  getApi()
}

export const GetAddTemplate = (q) => {
  var qI = new AddTemplate();
  qI.Dict = q;
  return qI;
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}