import { Save } from "./Save"
import * as genUtil from '../../../../../../util/generalUtil'
import * as mgsUtil from "../item/MassGroupSettingUtil";
import * as dataUtil from "../data/DataUtil";
import { PMData } from "../../../../single/model/PMData";
import * as mgsd_ut from '../dict/MGS_Dict_UseType'
import * as mgcUtil from
  '../../../mass/table/rowgroup/MassGroupComponentUtil'

export const pmData = () => PMData.Instance;

// Instance
export const get = (dict) => {
  var qI = new Save();
  qI.Dict = dict;
  return qI;
}

// Property
export const Handle_MassGroupComponent = (q) => {
  if (q.IsGet_MassGroupComponent) return undefined
  q.IsGet_MassGroupComponent = true

  const setObj = () => {
    var funcObj = q.Retrieve_MassGroupComponent
    const setObj2 = () => {
      q.MassGroupComponent = funcObj.Get()
      q.IsGet_MassGroupComponent = false
    }

    if (funcObj.Get()) {
      setObj2();
    } else {
      genUtil.Add_2(
        funcObj.Func,
        setObj2
      )
    }
  }

  if (q.Retrieve_MassGroupComponent) {
    setObj();
  } else {
    genUtil.Add_2(
      q.OnSet_Retrieve_MassGroupComponent_Func,
      setObj
    )
  }
}

export const Handle_Retrieve_MassGroupComponent = (q) => {
  if (q.IsGet_Retrieve_MassGroupComponent) return undefined;
  q.IsGet_Retrieve_MassGroupComponent = true

  var dict = q.Dict;
  var mgs = q.MassGroupSetting

  const setObj = () => {
    var Get
    var Func
    var q1

    switch (dict.UseType) {
      case mgsd_ut.project:
        q1 = pmData().Mgs_Dict_Dict;
        Get = () => q1.Used_MassGroupComponent
        Func = q1.OnSet_Used_MassGroupComponent_Func
        break;
      case mgsd_ut.template:
        q1 = mgs
        Get = () => mgcUtil.Clone(q1.Data?.MainMassGroupComponent, undefined, true)
        Func = q1.OnSet_Data_Func
        break;
    }

    q.Retrieve_MassGroupComponent = {
      Get, Func
    }
    q.IsGet_Retrieve_MassGroupComponent = false
  }

  setObj();
}

// Method
export const Run = (q) => {
  var host = q.Dict;

  var setObj = () => {
    var items = host.Items
    var name = q.Name

    var setObj2 = () => {
      var mgc = q.MassGroupComponent

      var item = genUtil.SingleFilter(items, (x) => x.Name === name);
      if (!item) {
        item = mgsUtil.get(host);
        item.Name = name;
        // items.push(item);
        host.AddItem(item);
      }

      var q1 = item.Data = dataUtil.get(item)
      q1.MainMassGroupComponent = mgc
      item.SaveDatabase(true);

      // Refresh Data
      Refresh(q);
    }

    // var mgc = host.Used_MassGroupComponent
    if (q.MassGroupComponent) {
      setObj2()
    } else {
      genUtil.Add_2(
        q.OnSet_MassGroupComponent_Func,
        setObj2
      )
    }
  }

  if (host.Items) {
    setObj()
  } else {
    genUtil.Add_2(
      host.OnSet_Items_Func,
      setObj
    )
  }
}

// Refresh
export const Refresh = (q) => {
  q.Retrieve_MassGroupComponent = undefined;
  q.MassGroupComponent = undefined
}