import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Input, Popover, Row, Select } from "antd";
import MassCell from "./MassCell";
import './MassCell.css'
import Icon, { InfoCircleOutlined, LinkOutlined } from '@ant-design/icons';
// import MassCell_ValueView from "./value/MassCell_ValueView";

const { Option } = Select;

class MassCellView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassCell();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {/* Đối tượng ẩn để cập nhập màu sắc Cell */}
        {/* <span className='d-none'>
          {data.ContentVM.Content}
        </span> */}

        <td style={data.TDStyle}>
          <div className='masscell__div'>
            <div className='masscell__value'>
              {data.IsHaveWarning ?
                <Popover trigger='hover'
                  content={<div>
                    {data.TLS_LevelGroup_Descriptions ?
                      data.TLS_LevelGroup_Descriptions.map(x => {
                        return <span key={x}>{x}</span>
                      })
                      : ''}
                  </div>}>
                  <InfoCircleOutlined className='masscell__warningicon' />
                </Popover>
                : ''}
              <div style={data.AStyle}>
                {data.CurrentTotalValueItem?.ValueString}
              </div>

              <a className='masscell__linkdetail' onClick={data.ShowDetail}>
                <LinkOutlined />
              </a>
            </div>
          </div>
        </td>
      </>
    )
  }
}

export default observer(MassCellView)