import { makeObservable, observable, action } from 'mobx';
import * as util from './TabUtil'
import './Tab.css'

export class Tab {
  constructor() {
    makeObservable(this, {
      Items: observable,
      currentItem: observable,
      IsHaveBorder: observable
    })
  }

  Items = []

  currentItem = undefined
  get CurrentItem() {
    if (!this.currentItem) {
      this.currentItem = util.GetDefault_CurrentItem(this)
    } return this.currentItem
  } set CurrentItem(v) {
    this.currentItem = v;
  }

  SetCurrent = (v) => {
    this.CurrentItem = v;
  }

  idRender
  get IdRender() {
    if (!this.idRender) {
      this.idRender = util.GetDefault_IdRender(this)
    } return this.idRender
  } set IdRender(v) {
    this.idRender = v
  }

  titleRender;
  get TitleRender() {
    if (!this.titleRender) {
      this.titleRender = util.GetDefault_TitleRender(this)
    } return this.titleRender
  } set TitleRender(v) {
    this.titleRender = v
  }

  titleView_Render
  get TitleView_Render() {
    if (!this.titleView_Render) {
      this.titleView_Render = util.GetDefault_TitleView_Render(this)
    } return this.titleView_Render
  } set TitleView_Render(v) {
    this.titleView_Render = v
  }

  contentRender;
  get ContentRender() {
    if (!this.contentRender) {
      this.contentRender = util.GetDefault_ContentRender(this)
    } return this.contentRender
  } set ContentRender(v) {
    this.contentRender = v
  }

  contentWrapper_Render;
  get ContentWrapper_Render() {
    if (!this.contentWrapper_Render) {
      this.contentWrapper_Render = util.GetDefault_ContentWrapper_Render(this)
    } return this.contentWrapper_Render
  } set ContentWrapper_Render(v) {
    this.contentWrapper_Render = v
  }

  IsHaveBorder = true

  get BorderStyle() {
    return this.IsHaveBorder ? 'border' : ''
  }

  get TitleView() {
    return <div className='tab__title__wrapper'>
      {this.Items?.map(this.TitleView_Render)}
    </div>
  }

  get ContentView(){
    return this.CurrentItem ? 
      this.ContentWrapper_Render(this.CurrentItem) : ''
  }

  get ContentWrapper_View() {
    return <div className={`tab__content__wrapper ${this.BorderStyle}`}>
      {this.ContentView}
    </div>
  }

  get View() {
    return <div className='tab__wrapper'>
      {this.TitleView}
      {this.ContentWrapper_View}
    </div>
  }
}