import { EditableTextVM } from "./EditableTextVM"

export const get = (content, canEdit = true, isToolVisible) => {
  var qI = new EditableTextVM();
  
  qI.Content = content;
  qI.CanChange_RenderState = canEdit;

  if (isToolVisible === undefined){
    isToolVisible = canEdit;
  }

  qI.Is_Tool_Visible = isToolVisible;

  return qI;
}