import * as genUtil from '../../../../../../util/generalUtil'
import { makeObservable, observable, action } from 'mobx';
import * as util from './DictVMUtil'
import { Button, message } from 'antd';
import TableView from '../table/TableView';
import './Dict.css'
import { SaveFilled } from '@ant-design/icons';

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Items
  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
  }

  AddItem = () => {
    var i = this.ModelItem.Dict.GetUser_Func()
    this.ModelItem.Items.push(i);

    var iVM = this.GetItem_Func(i);
    this.Items.push(iVM);
  }

  getItem_Func
  get GetItem_Func(){
    if(!this.getItem_Func){
      this.getItem_Func = util.GetGetItem_Func(this)
    } return this.getItem_Func
  }

  itemRender
  get ItemRender(){
    if (!this.itemRender){
      this.itemRender = util.GetDefault_ItemRender(this)
    } return this.itemRender;
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get MainView() {
    return <div>
      <div className='f-jc admin__um__title'>
        <span>QUẢN LÝ NGƯỜI DÙNG</span>
      </div>
      <div className='f'>
        <div className='admin__um__add'>
          <Button type='primary' size='small'
            className='btn-success'
            onClick={this.AddItem}>+</Button>
        </div>
        <div className='es__main f1'>
          <TableView store={this} />
        </div>
      </div>
    </div>
  }
}