import MassGroupItem from "./MassGroupItem"
import * as compUtil from './MassGroupComponentUtil'
import * as itemUtil from './MGI_ItemUtil'
import { PMData } from "../../../../single/model/PMData";

export const pmData = () => {
  return PMData.Instance;
}

export const get = (field, subComps, supCom) => {
  var qI = new MassGroupItem();
  qI.Field = field;
  qI.SupComponent = supCom;
  if (subComps) {
    qI.SubComponents = subComps;
  }
  return qI;
}

export const getFromDb = (data, supCom) => {
  var qI = new MassGroupItem();
  qI._id = data._id;
  qI.Field = data.field;
  qI.SupComponent = supCom;
  qI.SubComponents = data.subComponents ? data.subComponents.map(x => compUtil.get(x, qI))
    : [];

  if (data.items) {
    qI.Items = data.items.map(x => itemUtil.getFromDb(x, qI))
      .filter(x => x.Value);
    qI.IsHaveValueOrder = true;
  } else {
    qI.Items = []
    qI.IsHaveValueOrder = false;
  }

  qI.NotIncludeValues = data.notIncludeValues ? data.notIncludeValues : [];

  return qI;
}

export const Clone = (q, supCom, forTemplate = false) => {
  if (!q) return q;
  var qI = new MassGroupItem();
  qI.id = q.id;
  qI.Field = q.Field;
  
  qI.NotIncludeValues = [...q.NotIncludeValues];
  qI.IsHaveValueOrder = q.IsHaveValueOrder;
  qI.SupComponent = supCom;
  
  if (forTemplate && q.Rank > 0){

  }else {
    qI.Items = q.Items.map(x => itemUtil.Clone(x, qI));
    qI.SubComponents = q.SubComponents.map(x => compUtil.Clone(x, qI, forTemplate));
  }
  
  return qI;
}

export const GetTable = (q) => {
  return q.SupComponent.SupItem.Table;
}