import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import MassItem_Dict from "./MassItem_Dict";
import { Spin } from "antd";
import TextView from "../../../../text/TextView";
import MassTableView from "../table/MassTableView";

class MassItem_DictView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassItem_Dict();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.IsDataPending ?
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <div>
              <Spin />
              <TextView store={data.LoadingSpinTipVM} />
            </div>
          </div> :
          // <MassTableView
          //   key={data.MassTable?.id}
          //   store={data.MassTable} />
          data.MassTableView
        }
      </>
    )
  }
}

export default observer(MassItem_DictView)