import { makeObservable, observable, action } from 'mobx';
import * as util from './ProjectVMUtil'
import * as genUtil from '../../../../util/generalUtil'

export default class ProjectVM {
  constructor() {
    makeObservable(this, {
      typicalLevelSetting: observable,
      typicalLevelSetting_ForEdit: observable,
      commands : observable
    })
  }

  get _id() {
    return this.ModelItem._id;
  }

  name
  get Name() {
    if (!this.name) {
      this.name = this.ModelItem.Name;
    }
    return this.name;
  }
  set Name(v) {
    this.name = v;
    this.ModelItem.Name = v;
  }

  code
  get Code() {
    if (!this.code) {
      this.code = this.ModelItem.Code;
    }
    return this.code;
  }
  set Code(v) {
    this.code = v;
    this.ModelItem.Code = v;
  }

  phase_DictVM
  get Phase_DictVM() {
    if (!this.phase_DictVM) {
      this.phase_DictVM = util.GetPhase_DictVM(this);
    }
    return this.phase_DictVM;
  }

  exportSet_Dict
  get ExportSet_Dict(){
    if (!this.exportSet_Dict){
      this.exportSet_Dict = util.GetExportSet_Dict(this)
    } return this.exportSet_Dict;
  }

  get DisplayContent() {
    return `${this.Code} - ${this.Name}`;
  }

  // Old Version
  get Level_Dict() {
    return this.ModelItem.Level_Dict;
  }

  // LevelSetting
  OnSet_LevelSetting_Func_Dict = []

  levelSetting
  get LevelSetting() {
    if (!this.levelSetting) {
      util.Handle_LevelSetting(this)
    }
    return this.levelSetting;
  } set LevelSetting(v) {
    this.levelSetting = v;

    genUtil.Handle(
      this.OnSet_LevelSetting_Func_Dict,
      (v) => this.OnSet_LevelSetting_Func_Dict = v)
  }

  // TypicalLevelSetting
  typicalLevelSetting = undefined
  get TypicalLevelSetting() {
    if (!this.typicalLevelSetting) {
      this.typicalLevelSetting = util.GetTypicalLevelSetting(this);
    }
    return this.typicalLevelSetting;
  } set TypicalLevelSetting(v) {
    this.typicalLevelSetting = v;
  }

  typicalLevelSetting_ForEdit = undefined
  get TypicalLevelSetting_ForEdit() {
    if (!this.typicalLevelSetting_ForEdit) {
      this.typicalLevelSetting_ForEdit = util.GetTypicalLevelSetting_ForEdit(this);
    }
    return this.typicalLevelSetting_ForEdit;
  } set TypicalLevelSetting_ForEdit(v) {
    this.typicalLevelSetting_ForEdit = v;
  }

  get Url(){
    return this.ModelItem.Url;
  }

  // Commands
  commands = undefined
  get Commands(){
    if (!this.commands){
      util.Handle_Commands(this)
    } return this.commands;
  } set Commands(v){
    this.commands = v
  }
}