import { MassGroupSetting_DictVM } from "./MassGroupSetting_DictVM";
import * as genUtil from '../../../../../../util/generalUtil'
import * as selUtil from "../../../../../select/SelectVMUtil";
import * as saveUtil from "../save/SaveVMUtil";
import { PMData } from "../../../../single/model/PMData";
import * as iUtil from '../item/MassGroupSettingVMUtil'
import * as mgsd_ut from './MGS_Dict_UseType'

export const pmData = () => PMData.Instance

// Instance
export const get = (q, dict) => {
  var qI = new MassGroupSetting_DictVM();

  qI.ModelItem = q;
  qI.Dict = dict

  q.OnAddItem = (v) => qI.AddItem(v)

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    // Ưu tiên data có projectId
    var isValid = true;

    if (q.UseType === mgsd_ut.project) {
      var pId = pmData().CurrentProject?._id;
      if (pId) {
        if (data.length !== 0) {
          isValid = data[0].projectId === pId
        } else {
          var oldData = q.Items;
          if (oldData && oldData.length !== 0) {
            isValid = oldData[0].projectId !== pId
          }
        }
      }
    }

    if (isValid) {
      q.Items = data.map(x => iUtil.get(x, q));
    }
  }

  var host = q.ModelItem
  if (host.Items) {
    setObj(host.Items)
  }

  genUtil.Add_2(
    host.OnSet_Items_Func,
    () => setObj(host.Items),
    false
  )
}

export const GetSave = (q) => {
  return saveUtil.get(q.ModelItem.Save, q)
}