import { LevelGroupVM } from "./LevelGroupVM"
import * as genUtil from '../../../../../../util/generalUtil'

export const get = (q, dict) => {
  var qI = new LevelGroupVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  genUtil.Add_2(
    dict.OnSet_LevelStorageList_Func_Dict,
    () => qI.LevelStorageList = GetLevelStorageList(qI),
    false
  );

  return qI;
}

// Property
export const GetDefaultPrimary = (q) => {
  if (q.Levels && q.Levels.length !== 0) {
    return q.Levels[0];
  }
}

export const GetLevelStorageList = (q) => {
  return q.Dict.LevelStorageList;
}