import * as genUtil from '../../../../../../util/generalUtil'
import * as api from '../../../../../../util/apiUtil'
import { PMData } from '../../../../single/model/PMData'
import * as mif from '../../../mass/item/MassItemField'
import * as mut from '../../../mass/unit/MassUnitType'
import * as cgt from './column/Column_GroupType'
import * as colGroupUtil from './columngroup/ColumnGroupUtil'
import { Table } from './Table'
import { url } from './TableUrl'
import MassItem from '../../../mass/item/MassItem'
import { MI_ValueItem } from '../../../mass/item/value/MI_ValueItem'
import { MI_ValueDict } from '../../../mass/item/value/MI_ValueDict'

const pmData = () => PMData.Instance

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);
  qI._id = data._id;
  qI.Infos = data.infos;
  qI.Values = data.values;
  return qI;
}

export const get = (dict) => {
  var qI = new Table();
  qI.Dict = dict;
  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data
    q.IsGet_Items = false;
  }

  var host = q.Dict
  if (host.MassItems) {
    setObj(host.MassItems)
  }

  genUtil.Add_2(
    host.OnSet_MassItems_Func,
    () => setObj(host.MassItems),
    false
  )
}

export const GetDefaultInfos = (q) => {
  return [mif.workItem, mif.level]
}

export const GetInfos = (q) => {
  return q.GetColumnGroup(cgt.info)
    .Columns.map(x => x.Field);
}

export const GetDefaultValues = (q) => {
  return [mut.each]
}

export const GetValues = (q) => {
  return q.GetColumnGroup(cgt.value)
    .Columns.map(x => x.Field);
}

export const GetColumnGroups = (q) => {
  var qInfo = colGroupUtil.get(cgt.info, q)
  qInfo.Fields = q.Infos;

  var qValue = colGroupUtil.get(cgt.value, q)
  qValue.Fields = q.Values;

  return [
    qInfo, qValue
  ]
}

export const GetColumnCount = (q) => {
  return q.ColumnGroups.reduce((a, b) => a + b.Columns.length, 0);
}

// Method
export const GetItems_FromPaste = (q, rawData) => {
  q.Refresh_Info();

  var infos = q.Infos;
  var infoCount = infos.length
  var values = q.Values;
  var totalCount = infoCount + values.length;

  return rawData.map(x => {
    var qI = new MassItem();
    var infoDict = qI.Info = {}
    var valueDict = qI.ValueDict
    valueDict.Dict = qI;
    // Khởi xướng trước value each = 1 
    // cho đồng bộ các dữ liệu khác
    valueDict.GetItem(mut.each).Value = 1;

    for (let i = 0; i < x.length; i++) {
      if (i >= totalCount){
        continue
      }

      if (i < infoCount){
        infoDict[infos[i]] = x[i]
      } else {
        if (!isNaN(x[i])){
          valueDict.GetItem(values[i - infoCount])
            .Value = parseFloat(x[i]);
        }
      }
    }

    return qI;
  })
}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}