import { PMDataVM } from '../../../single/form/PMDataVM'
import MassResultComp from '../../mass/result/MassResultComp'
import * as massResutlCompVMUtil from '../../mass/result/MassResultCompVMUtil'
import * as genUtil from '../../../../../util/generalUtil'
import ProjectCompVM from './ProjectCompVM';

export const pmDataVM = () => PMDataVM.Instance;

// Instance
export const get = () => {
  var qI = new ProjectCompVM();

  genUtil.Add(
    pmDataVM().OnSet_ProjectViewType_Func_Dict,
    () => qI.ProjectViewType = pmDataVM().ProjectViewType,
    false);

  return qI;
}

// Property
export const GetMassResultCompVM = (q) => {
  var obj = massResutlCompVMUtil.get(new MassResultComp(), false);
  obj.IsShowProjectSelect = false;
  obj.CanHavePhase = true;
  return obj;
}