import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Input, Row, Select, Space } from "antd";
import SelectView from "../../../../../../select/SelectView";
import { MR_Unit } from "./MR_Unit";
import './MR_Unit.css'

const { Option } = Select;

class MR_UnitView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MR_Unit();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <td className='massrow__unit__td'
          onMouseEnter={data.OnMouseEnter}
          onMouseLeave={data.OnMouseLeave}>
          {data.MainView}
        </td>
      </>
    )
  }
}

export default observer(MR_UnitView)