import * as genUtil from '../../../../../util/generalUtil'
import { makeObservable, observable, action } from 'mobx';
import * as util from './ExportSet_DictVMUtil'
import TableView from '../table/TableView';
import * as iUtil from '../item/ExportSetVMUtil'
import { PMDataVM } from '../../../single/form/PMDataVM';
import * as pct from '../../command/ProjectCommand'

const pmDataVM = () => PMDataVM.Instance

export class ExportSet_DictVM {
  constructor() {
    makeObservable(this, {
      items: observable,
      Is_Modal_Visible: observable,
      currentItem: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Item
  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
  }

  AddItem = (v) => {
    var list = this.Items;
    if (list.length === this.ModelItem.Items.length) return

    var obj = iUtil.get(v, this);
    list.push(obj);
  }

  addVM
  get AddVM() {
    if (!this.addVM) {
      this.addVM = util.GetAddVM(this)
    } return this.addVM;
  }

  //
  currentItem = undefined
  get CurrentItem() {
    return this.currentItem;
  } set CurrentItem(v) {
    this.currentItem = v;
    this.ModelItem.CurrentItem = v?.ModelItem
  }

  TempCurrentItem

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get MainView() {
    return <div className='f'>
      {this.Commands.includes(pct.edit) ?
        <div className='es__add'>
          {this.AddVM.View}
        </div>
        : ''}
      <div className='es__main f1'>
        <TableView store={this} />
      </div>
    </div>
  }

  // View
  Is_Modal_Visible = false

  get ModalView() {
    return util.GetModalView(this)
  }

  get PickView() {
    return util.GetPickView(this)
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }
}