import { makeObservable, observable, action } from 'mobx';
import * as textVMUtil from '../../../../../text/TextVMUtil';
import * as editTextUtil from '../../../../../text/EditableTextVMUtil';
import * as trs from '../../../../../text/TextRenderState';
import * as util from './MassRowUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import { EntFilter } from '../../../filter/EntFilter';
import * as fvt from '../../../filter/FilterValueType'
import * as sft from '../../../filter/StringFilterType'
import * as mgiUtil from '../rowgroup/MassGroupItemUtil'
import MassGroupComponent from '../rowgroup/MassGroupComponent';
import Modal from 'antd/lib/modal/Modal';
import MultiSelectView from '../../../../../select/MultiSelectView';
import { Col, Row, Space, Button, Switch, message, Popover } from 'antd';
import EntHoverVM from '../../../../../hover/EntHoverVM';
import EntHoverView from '../../../../../hover/EntHoverView';
import { InfoCircleOutlined } from '@ant-design/icons';
import SelectView from '../../../../../select/SelectView';
import ReplaceRowLocationFactory from '../location/ReplaceRowLocationFactory';
import * as rrlf_lt from '../location/RRLF_LocationType';
import { PMData } from '../../../../single/model/PMData';
import InputView from '../../../../../input/InputView';
import * as mif from '../../item/MassItemField'
import * as selectUtil from '../../../../../select/SelectVMUtil';
import * as mr_f_it from './MassRow_Field_InputType'
import NewInputView from '../../../../../input/NewInputView';
import * as tc_selectUtil from '../../../../../select/twocol/TwoCol_SelectVMUtil';
import TwoCol_SelectView from "../../../../../select/twocol/TwoCol_SelectView";
import MassRow_ToolElementView from '../tool/MassRow_ToolElementView';
import * as u_st from './unit/MR_Unit_SetType'
import * as ut from '../../unit/MassUnitType'
import * as u_dt from '../../../update/Update_DirectionType'
import * as at from '../../../action/ActionType'
import * as u_sn from './update/MRU_SectionName'
import * as uc_sn from '../cell/update/MCU_SectionName'
import * as delType from './delete/MR_DeleteType'
import * as tct from '../command/CommandType'

export const pmData = () => {
  return PMData.Instance;
}


export default class MassRow {
  constructor() {
    makeObservable(this, {
      rank: observable,
      isShowSub: observable,
      isHaveSub: observable,
      IsToolVisible: observable,
      subItems: observable,
      IsModalVisible: observable,
      title: observable,
      FieldInputType: observable,
      IsHaveAdditionInformation: observable,
      AdditionInformation: observable,
      Is_SelectSubItems_ModalOpen: observable,
      Temp_SelectSubItems: observable,
      isShow_RawValue: observable,

    })
  }

  _id
  get id() {
    if (!this._id) {
      this._id = genUtil.randomId();
    }
    return this._id;
  }

  get Index() {
    return this.CurrentList.indexOf(this);
  }

  // SupItem
  // Nguyên bản là SupRow gốc của Row (đã bị thay đổi vị trí)
  Is_Original_SupRow

  supItem
  get SupItem() {
    return this.supItem;
  }
  set SupItem(v) {
    this.supItem = v;
    this.CurrentList = undefined;
    this.Rank = undefined;
  }

  currentList
  get CurrentList() {
    if (!this.currentList) {
      this.currentList = this.SupItem ? this.SupItem.SubItems : this.Table.MainRows;
    }
    return this.currentList;
  }
  set CurrentList(v) {
    this.currentList = v;
  }

  get Table() {
    return this.Dict;
  }

  // SubItems
  subItems = []
  get SubItems() {
    return this.subItems;
  } set SubItems(v) {
    this.subItems = v;
  }

  get AllItems() {
    var list = [];
    list.push(this);
    this.SubItems.forEach(x => {
      list.push.apply(list, x.AllItems)
    });
    return list;
  }

  // Cells
  cells
  get Cells() {
    if (!this.cells) {
      this.cells = util.Get_Cells(this)
    } return this.cells;
  }

  // Update
  update
  get Update() {
    if (!this.update) {
      this.update = util.GetUpdate(this)
    } return this.update
  }

  get Items() {
    var items = this.Cells.reduce((a, b) => a.concat(b.Items), []);
    items = items.filter((x, i, a) => a.indexOf(x) === i);
    return items;
  }

  // MassGroupItem
  massGroupItem
  get MassGroupItem() {
    return this.massGroupItem;
  }
  set MassGroupItem(v) {
    this.massGroupItem = v;
    if (v) {
      var title = v.GetItem(this.FullValue).Title;
      this.Title = title;
    }
  }

  replaceMGI
  get ReplaceMGI() {
    return this.replaceMGI
  } set ReplaceMGI(v) {
    this.replaceMGI = v;
  }

  original_SubMassGroupItem
  get Original_SubMassGroupItem() {
    if (!this.original_SubMassGroupItem) {
      this.original_SubMassGroupItem = util.GetOriginal_SubMassGroupItem(this);
    }
    return this.original_SubMassGroupItem
  }
  set Original_SubMassGroupItem(v) {
    this.original_SubMassGroupItem = v;
  }

  replace_SubMassGroupItem
  get Replace_SubMassGroupItem() {
    if (!this.replace_SubMassGroupItem) {
      this.replace_SubMassGroupItem = util.GetReplace_SubMassGroupItem(this);
    }
    return this.replace_SubMassGroupItem
  }
  set Replace_SubMassGroupItem(v) {
    this.replace_SubMassGroupItem = v;
  }

  get SubMassGroupItem() {
    return this.Replace_SubMassGroupItem ?
      this.Replace_SubMassGroupItem : this.Original_SubMassGroupItem
  } set SubMassGroupItem(v) {
    this.Replace_SubMassGroupItem = v;
  }

  field
  get Field() {
    if (!this.field) {
      this.field = this.MassGroupItem ? this.MassGroupItem.Field : undefined;
    }
    return this.field;
  } set Field(v) {
    this.field = v;
  }

  isReplaced
  get IsReplaced() {
    return this.isReplaced;
  } set IsReplaced(v) {
    this.isReplaced = v;
    var obj = this.IsHaveAdditionInformation
      = util.Get_IsHaveAdditionInformation(this);

    if (obj) {
      this.AdditionInformation = util.Get_AdditionInformation(this);
    }
  }

  original_SupRow
  get Original_SupRow() {
    if (!this.original_SupRow) {
      this.original_SupRow = util.Get_Original_SupRow(this);
    }
    return this.original_SupRow;
  } set Original_SupRow(v) {
    this.original_SupRow = v;
  }

  get Original_Full_SupRows() {
    return util.Get_Original_Full_SupRows(this);
  }

  get OrderMGI() {
    return this.ReplaceMGI ? this.ReplaceMGI : this.MassGroupItem;
  }

  subFields
  get SubFields() {
    if (!this.subFields) {
      this.subFields = util.GetSubFields(this);
    }
    return this.subFields;
  }
  set SubFields(v) {
    this.subFields = v;
  }

  TempSubFields

  isShowSub = true
  get IsShowSub() {
    return this.isShowSub;
  }
  set IsShowSub(v) {
    this.isShowSub = v;
    this.ToggleButtonContentVM.Content = util.GetToggleButtonContent(this);
  }

  toggleButtonContent
  get ToggleButtonContent() {
    if (!this.toggleButtonContent) {
      this.toggleButtonContent = util.GetToggleButtonContent(this);
    }
    return this.toggleButtonContent;
  }
  set ToggleButtonContent(v) {
    this.toggleButtonContent = v;
  }

  toggleButtonContentVM
  get ToggleButtonContentVM() {
    if (!this.toggleButtonContentVM) {
      var obj = this.toggleButtonContentVM = textVMUtil.get(this.ToggleButtonContent);
      obj.OnSetContent = (v) => this.ToggleButtonContent = v;
    }
    return this.toggleButtonContentVM;
  }

  rank = undefined
  get Rank() {
    if (this.rank === undefined) {
      this.rank = util.GetRank(this);
    }
    return this.rank;
  }
  set Rank(v) {
    this.rank = v;
    this.SubItems.forEach(x => x.Rank = undefined);
  }

  isHaveSub = undefined
  get IsHaveSub() {
    if (!this.isHaveSub) {
      this.isHaveSub = util.GetIsHaveSub(this);
    }
    return this.isHaveSub;
  }
  set IsHaveSub(v) {
    this.isHaveSub = v;
  }

  get Style() {
    return util.GetStyle(this);
  }

  IsToolVisible = false

  FieldInputType = mr_f_it.select

  Toggle_FieldInputType = () => {
    switch (this.FieldInputType) {
      case mr_f_it.select:
        this.TempField = this.FieldInputVM.Content;
        this.FieldInputType = mr_f_it.input;
        break;
      case mr_f_it.input:
        this.FieldSelectVM.InitialItem = this.TempField
        this.FieldInputType = mr_f_it.select;
        break;
    }
  }

  TempField

  fieldInputVM
  get FieldInputVM() {
    if (!this.fieldInputVM) {
      var obj = this.fieldInputVM = textVMUtil.get();
      obj.onSetContent = (v) => this.TempField = v;
    }
    return this.fieldInputVM;
  }

  fieldSelectVM
  get FieldSelectVM() {
    if (!this.fieldSelectVM) {
      this.fieldSelectVM = util.GetFieldSelectVM(this);
    }
    return this.fieldSelectVM;
  }

  get FieldInputElement() {
    return <>
      <div className='massrow__fieldinput_wrapper'>
        <div className='massrow__fieldinput'>
          {this.FieldInputType === mr_f_it.select ?
            <SelectView store={this.FieldSelectVM} /> :
            <NewInputView store={this.FieldInputVM} />
          }
        </div>
        {this.IsCreatedByUser ?
          <Switch unCheckedChildren='inp' checkedChildren='sel'
            checked={this.FieldInputType === mr_f_it.select}
            onChange={this.Toggle_FieldInputType} />
          : ''}
      </div>
    </>
  }

  subFieldSelectVM
  get SubFieldSelectVM() {
    if (!this.subFieldSelectVM) {
      this.subFieldSelectVM = util.GetSubFieldSelectVM(this);
    }
    return this.subFieldSelectVM;
  }

  // Title
  title = undefined
  get Title() {
    // if (!this.title) {
    //   util.Handle_Title(this);
    // }
    return this.title;
  }
  set Title(v) {
    this.title = v;
    this.TitleVM.Content = v;
    this.Editable_TitleVM.Content = v;
  }

  titleVM
  get TitleVM() {
    if (!this.titleVM) {
      this.titleVM = textVMUtil.get(this.Title, 'tiêu đề');
    }
    return this.titleVM;
  }

  editable_TitleVM
  get Editable_TitleVM() {
    if (!this.editable_TitleVM) {
      var obj = this.editable_TitleVM =
        editTextUtil.get(this.Title, true, true);

      obj.Is_Edit_Visible = false

      genUtil.Add(
        obj.OnSet_RenderState_Func_Dict,
        () => {
          if (obj.RenderState === trs._default) {
            // CommandDict for Undo
            var oldTitle = this.Title;
            this.Table.CommandDict.Add({
              Key: tct.renamerow,
              Undo: () => {
                this.Title = oldTitle
              }
            })

            // Lưu thông tin Title
            var title = this.title = obj.Content;
            this.IsShow_RawValue = util.GetIsShow_RawValue(this);
            this.MassGroupItem.GetItem(this.FullValue).Title = title;

            this.ToolElement.editable_TitleVM_RenderState = obj.RenderState
          }
        },
        false
      )
    }
    return this.editable_TitleVM;
  }

  get LocationStorageList() {
    return this.Table.AllRows;
  }

  locationSelectVM
  get LocationSelectVM() {
    if (!this.locationSelectVM) {
      this.locationSelectVM = util.GetLocationSelectVM(this);
    }
    return this.locationSelectVM;
  }

  // Value lồng giữa Row hiện tại và SupRow
  // Ví dụ: concrete__Tầng 6__Dầm
  FullValue

  GetCellByColumn = (col) => {
    var index = this.Cells.findIndex(x => x.Column === col);
    return index !== -1 ? this.Cells[index] : undefined;
  }

  ToggleSub = () => {
    this.IsShowSub = !this.IsShowSub;
  }

  SetSubFields = (handleUndo) => {
    var tempSubFields = this.TempSubFields ? this.TempSubFields : [];

    var isSame = true;
    if (this.SubFields.length !== tempSubFields.length) {
      isSame = false;
    } else {
      this.SubFields.forEach((x, i) => {
        if (isSame && x !== tempSubFields[i]) {
          isSame = false;
        }
      })
    }
    if (isSame) return;

    // Undo Func
    if (handleUndo) {
      var subItems = this.SubItems;
      handleUndo(() => {
        this.SubItems = subItems
      })
    }

    this.SubFields = tempSubFields;

    var filter = new EntFilter();
    filter.ValueType = fvt.bystring;
    filter.StringFilterType = sft.byequal;
    filter.Values = [this.FullValue];

    var supMgi = this.MassGroupItem;

    var mgc = new MassGroupComponent();
    mgc.SubItem = supMgi;
    mgc.Filter = filter;

    var subMgis = mgc.Items = tempSubFields.map(x => mgiUtil.get(x, null, mgc));

    var subComps = supMgi.SubComponents;
    if (this.SubMassGroupComponent) {
      var index = subComps.indexOf(this.SubMassGroupComponent);
      subComps[index] = mgc;
    } else {
      subComps.push(mgc);
    }

    this.SubMassGroupComponent = mgc;
    this.SubItems = [];
    this.Original_SubMassGroupItem = undefined;

    if (subMgis.length !== 0) {
      var table = this.Table;
      this.Items.forEach(x => {
        // Xử lý Item theo ColumnField: Project ... (single value)
        // hoặc theo Phase (multiple values)
        switch (table.ColumnField) {
          case mif.Phase:
            x.ValidPhasesByInput.forEach(y =>
              table.HandleItem(x, y, mgc, this));
            break;
          default:
            table.HandleItem(x, x.GetValue(table.ColumnField).Value, mgc, this);
            break;
        }
      })

      // Update
      // UpdateCell với Unit_SetType là Both cho các SubCell
      this.Update.Run(
        {
          Section: u_sn.celldata,
          Data: { Sections: uc_sn.subcells }
        },
        u_dt.lower, false
      )

      // Update Unit với SubCell
      this.Update.Run(
        {
          Section: u_sn.unit,
          Data: { Unit: this.Unit }
        },
        u_dt.lower
      )
    }
  }

  MoveUp = () => {
    var list = this.CurrentList;
    var index = this.Index;
    if (0 === index) return;

    var orderItems = this.OrderMGI.OrderItems;
    var orderValues = this.OrderMGI.OrderValues;
    var mgiIndex1 = orderValues.indexOf(list[index].FullValue);
    var mgiIndex2 = orderValues.indexOf(list[index - 1].FullValue);

    const func = () => {
      genUtil.MoveItem(list, index, index - 1);
      genUtil.MoveItem(orderItems, mgiIndex1, mgiIndex2);
    }

    func();

    this.Table.CommandDict.Add({
      Key: tct.moveuprow,
      Undo: func
    })
  }

  MoveDown = () => {
    var list = this.CurrentList;
    var index = this.Index;
    if (list.length - 1 === index) return;

    var orderItems = this.OrderMGI.OrderItems;
    var orderValues = this.OrderMGI.OrderValues;
    var mgiIndex1 = orderValues.indexOf(list[index].FullValue);
    var mgiIndex2 = orderValues.indexOf(list[index + 1].FullValue);

    const func = () => {
      genUtil.MoveItem(list, index, index + 1);
      genUtil.MoveItem(orderItems, mgiIndex1, mgiIndex2);
    }

    func();

    this.Table.CommandDict.Add({
      Key: tct.movedownrow,
      Undo: func
    })
  }

  AddSub = () => {
    util.Add(this, this.Table);
  }

  // Delete
  deleteData
  get DeleteData() {
    if (!this.deleteData) {
      this.deleteData = util.GetDeleteData(this)
    } return this.deleteData
  }

  Delete = () => {
    // this.DeleteData.Run(at.remove);
    this.Update.Run({
      Section: u_sn._delete,
      Data: { DeleteType: delType.removedata }
    }, u_dt.upper, true,
      (undo) => {
        this.Table.CommandDict.Add({
          Key: tct.deleterow,
          Undo: undo
        })
      })
  }

  replaceFactory
  get ReplaceFactory() {
    if (!this.replaceFactory) {
      this.replaceFactory = new ReplaceRowLocationFactory();
    }
    return this.replaceFactory;
  }

  ReplaceOK = (handleUndo) => {
    var factory = this.ReplaceFactory;
    var refRow = factory.ReferenceRow;

    if (!refRow) return;

    var loc = factory.Location;
    if ((refRow === this && loc === rrlf_lt.lower) ||
      (refRow === this.SupItem && loc === rrlf_lt.inside)) {
      return
    }

    this.Replace(refRow, loc, handleUndo);

    factory.Location = rrlf_lt.inside;
  }

  Replace_SubItems = (handleUndo) => {
    // Undo
    var undoFuncs
    if (handleUndo) {
      undoFuncs = []
      handleUndo(() => {
        undoFuncs.forEach(x => x());
      });
    }

    var subItems = this.Temp_SelectSubItems;
    subItems.forEach(x => {
      x.Replace(this, rrlf_lt.inside,
        (undo) => {
          if (undoFuncs) {
            undoFuncs.push(undo)
          }
        })
    })
  }

  Replace = (refRow, loc, handleUndo) => {
    var osr = this.Original_SupRow;

    // Save data
    var oldMgi = this.MassGroupItem;
    var supRow = undefined;

    switch (loc) {
      case rrlf_lt.inside:
        supRow = refRow;
        break;
      case rrlf_lt.lower:
        supRow = refRow.SupItem;
        break;
    }

    // Đối tượng đã nằm trong mục cần đặt
    if (this.SupItem === supRow) return;

    // Undo
    var undoFuncs = []
    var undo = () => {
      undoFuncs.forEach(x => x());
    }

    // Init OriginalSupRow && IsReplaced
    // Undo
    var old_IsReplaced = this.IsReplaced;
    undoFuncs.push(() => this.IsReplaced = old_IsReplaced)

    this.IsReplaced = true;

    //var newMgi = supRow ? supRow.SubMassGroupItem : this.Table.MainMGI;
    var newMgi = supRow ?
      (supRow.Replace_SubMassGroupItem ? supRow.Replace_SubMassGroupItem :
        util.CreateSubMassGroupItem(supRow))
      : this.Table.MainMGI;

    //pmData().IsDebug = true;
    var mgi_i = oldMgi.GetItem(this.FullValue, (undo) => {
      undoFuncs.push(undo)
    });

    var oldNewMgi = mgi_i.NewMGI;
    var oldSupValue = mgi_i.SupValue;
    undoFuncs.push(() => {
      mgi_i.NewMGI = oldNewMgi;
      mgi_i.SupValue = oldSupValue;
    })

    mgi_i.NewMGI = newMgi;
    mgi_i.SupValue = supRow?.Name;

    var oldReplaceMGI = this.ReplaceMGI;
    undoFuncs.push(() => {
      this.ReplaceMGI = oldReplaceMGI
    });

    this.ReplaceMGI = newMgi;

    // Delete Row trong tập hợp cũ
    this.Update.Run({
      Section: u_sn._delete,
      Data: { DeleteType: delType._default }
    }, u_dt.upper, true, (undo) => undoFuncs.push(undo))

    // Add Row trong tập hợp mới
    this.SupItem = supRow;
    supRow.Update.Run({
      Section: u_sn.addsub,
      Data: { Item: this }
    }, u_dt.none, true, (undo) => undoFuncs.push(undo))

    var oldIsShowSubFieldSelect = this.IsShowSubFieldSelect;
    undoFuncs.push(() => {
      this.IsShowSubFieldSelect = oldIsShowSubFieldSelect
    });

    this.IsShowSubFieldSelect = false;

    if (handleUndo) {
      handleUndo(undo)
    }
  }

  IsModalVisible = false

  OpenModal = () => {
    this.IsModalVisible = true
    var list

    // Set tiêu đề hiện tại
    this.TitleVM.Content = this.Title;

    // Set giá trị nhóm hiện tại
    this.FieldInputVM.Content = this.Field

    //this.FieldSelectVM.InitialItem = this.Field;
    var fieldSelectVM = this.FieldSelectVM
    list = fieldSelectVM.Items;
    var index = list.findIndex(x => x.Value === this.Field);
    fieldSelectVM.InitialItem = index !== -1 ? list[index] : undefined

    this.TempField = this.Field;

    // Set giá trị phân nhóm hiện tại
    var subFieldSelectVM = this.SubFieldSelectVM;
    //subFieldSelectVM.InitialItems = this.SubFields;
    list = subFieldSelectVM.Items;
    subFieldSelectVM.InitialItems =
      list.filter(x => this.SubFields.indexOf(x.Value) !== -1);

    // Reset lại giá trị Select SubItems
    this.Temp_SelectSubItems = undefined
    this.SelectSubItemsVM.Items = this.LocationStorageList;

    var locSelectVM = this.LocationSelectVM;
    locSelectVM.Items = this.LocationStorageList
    locSelectVM.InitialItem = undefined;
  }

  ModalOK = () => {
    this.IsModalVisible = false;

    // Thiết lập tiêu đề
    var title = this.TitleVM.Content;
    this.Title = title;
    this.MassGroupItem.GetItem(this.FullValue).Title = title;

    // Thiết lập nhóm hiện tại
    if (this.Field !== this.TempField) {
      this.Field = this.TempField;
    }

    var undoFuncs = []
    var undo = () => {
      undoFuncs.forEach(x => x());
    };
    this.Table.CommandDict.Add({
      Key : 'rowmodalok',
      Undo : undo
    })

    // Thiết lập phân nhóm hiện tại
    // chỉ áp dụng cho Row tự sinh
    if (!this.IsCreatedByUser) {
      this.SetSubFields((undo) => {
        undoFuncs.push(undo)
      });
    }

    var subItems = this.Temp_SelectSubItems;
    if (subItems) {
      // Dịch chuyển tập hợp con
      this.Replace_SubItems((undo) => {
        undoFuncs.push(undo)
      });
    } else {
      // Dịch chuyển vị trí
      this.ReplaceOK((undo) => {
        undoFuncs.push(undo)
      });
    }
  }

  ModalCancel = () => {
    this.IsModalVisible = false;
  }

  PMS_WorkPackages = [
    'THI CÔNG CHÍNH (HBC)',
    'CÔNG TÁC CHUẨN BỊ PHẦN NGẦM',
    'CÔNG TÁC CHUẨN BỊ PHẦN NGẦM (PHƯƠNG ...)',
    'Kết thúc thô - Bàn giao'
  ]

  pms_WorkPackages_SelectVM
  get PMS_WorkPackages_SelectVM() {
    if (!this.pms_WorkPackages_SelectVM) {
      this.pms_WorkPackages_SelectVM = selectUtil.get(this.PMS_WorkPackages);
    }
    return this.pms_WorkPackages_SelectVM;
  }

  get ModalElement() {
    var factory = this.ReplaceFactory;
    var titleWidth = '180px';

    return <Modal title={this.Title} visible={this.IsModalVisible}
      width={700}
      onOk={this.ModalOK} onCancel={this.ModalCancel}>
      {/* Tiêu đề */}
      <Row gutter={15} style={{ marginTop: 10 }}>
        <Col flex={titleWidth}>
          <b> Tiêu đề:</b>
        </Col>
        <Col flex="auto">
          <InputView store={this.TitleVM} />
        </Col>
      </Row>

      {/* Thiết lập nhóm hiện tại */}
      <Row gutter={15} style={{ marginTop: 10 }}>
        <Col flex={titleWidth}>
          <b> Nhóm hiện tại:</b>
        </Col>
        <Col flex="auto">
          {this.FieldInputElement}
        </Col>
      </Row>

      {/* Thiết lập phân nhóm */}
      {!this.IsCreatedByUser ? <Row gutter={15} style={{ marginTop: 10 }}>
        <Col flex={titleWidth}>
          <b> Thiết lập phân nhóm:</b>
        </Col>
        <Col flex="auto">
          <MultiSelectView store={this.SubFieldSelectVM} />
        </Col>
      </Row> : ''}

      {/* Đổi vị trí */}
      <Row gutter={15} style={{ marginTop: 10 }}>
        <Col flex={titleWidth} className='mass__modal__title__wrapper'>
          <b>Đổi vị trí /</b>
          <Button type='primary' size='small'
            className={this.Temp_SelectSubItems ?
              'mass__modal__title__selectsubitems__button' : ''}
            onClick={this.Open_SelectSubItems_Modal}>chọn tập con</Button>
          {this.SelectSubItems_ModalElement}
        </Col>
        <Col flex="auto">
          <Row>
            <Col flex='auto'>
              <SelectView store={this.LocationSelectVM} />
            </Col>
            <Col>
              <Button type='primary' size='small'
                style={factory.ButtonStyle} onClick={factory.ToggleLocation}
              >{factory.Location}</Button>
            </Col>
          </Row>
        </Col>
      </Row>

      <Row gutter={15} style={{ marginTop: 10 }}>
        <Col flex={titleWidth}>
          <b>Thông tin công trường:</b>
        </Col>
        <Col flex="auto">
          <Button type='primary' size='small'
            className='btn-warning'
            onClick={this.Site.OpenModal}>...</Button>
          {this.Site.ModalView}
        </Col>
      </Row>

      {/* Gói công việc PMS */}
      {/* <Row gutter={15} style={{ marginTop: 10 }}>
        <Col flex={titleWidth}>
          <b> Gói công việc PMS:</b>
        </Col>
        <Col flex="auto">
          <SelectView store={this.PMS_WorkPackages_SelectVM} />
        </Col>
      </Row> */}
    </Modal>
  }

  get HoverToolElement() {
    var obj = new EntHoverVM();
    obj.Element = <MassRow_ToolElementView store={this.ToolElement} />
    return <EntHoverView className='mass__row__hovertool' store={obj} />
  }

  toolElement
  get ToolElement() {
    if (!this.toolElement) {
      this.toolElement = util.Get_ToolElement(this);
    }
    return this.toolElement;
  }

  get AdditionInfomation_Element() {
    return <>
      {this.IsHaveAdditionInformation ?
        <Popover trigger='hover'
          content={
            <span className='info'>
              {this.AdditionInformation}
            </span>
          } >
          <InfoCircleOutlined className='info' />
        </Popover>
        : ''}
    </>
  }

  // Excel Export
  get ExportInfo() {
    var key = this.Field;
    var info = {
      Key: key,
      Value: this.Title
    }

    var list
    if (!this.SupItem) {
      list = [info]
    } else {
      list = [...this.SupItem.ExportInfo]
      list.push(info);
    }

    return list;
  }

  get ExportValue() {
    var list = []
    this.Cells.forEach(x => {
      list.push({
        Key: x.Column.Name,
        Value: x.TotalValue
      })
    })

    return list;
  }

  get ExportData() {
    return {
      Infos: this.ExportInfo,
      Values: this.ExportValue
    }
  }

  // HandleTotal_Cells
  On_HandleTotal_Cells_Done_Dict_Funcs = []

  set Is_HandleTotal_Cells_Done(v) {
    this.On_HandleTotal_Cells_Done_Dict_Funcs
      .forEach(x => x.Value())
  }

  HandleTotal_Cells

  AddHandleTotal_Cell = (cell) => {
    if (!this.HandleTotal_Cells) {
      this.HandleTotal_Cells = []
    };

    var list = this.HandleTotal_Cells

    list.push(cell);
    if (list.length === this.Cells.length) {
      this.HandleTotal_Cells = undefined
      this.Is_HandleTotal_Cells_Done = true;

      // nếu IsCreatedByUser 
      // => thêm thông tin vào danh sách Handled_CreatedByUser_Rows 
      // đối tượng Table
      if (this.IsCreatedByUser) {
        this.Table.Add_Handled_CreatedByUser_Row(this);
      }
    }
  }

  On_HandleTotalData_Cells_Func = []

  is_HandleTotalData_Cells
  get Is_HandleTotalData_Cells() {
    return this.is_HandleTotalData_Cells
  }
  set Is_HandleTotalData_Cells(v) {
    genUtil.Handle(this.On_HandleTotalData_Cells_Func,
      (v) => this.On_HandleTotalData_Cells_Func = v)

    this.is_HandleTotalData_Cells = v;
  }

  HandleTotalData_Cells = undefined

  AddHandleTotalData_Cell = (cell) => {
    if (!this.HandleTotalData_Cells) {
      this.HandleTotalData_Cells = []
    }

    var list = this.HandleTotalData_Cells

    list.push(cell);
    if (list.length === this.Cells.length) {
      this.HandleTotalData_Cells = undefined

      this.Is_HandleTotalData_Cells = true;

      // nếu IsCreatedByUser 
      // => thêm thông tin vào danh sách Handled_CreatedByUser_Rows 
      // đối tượng Table
      if (this.IsCreatedByUser) {
        this.Table.Add_Handled_CreatedByUser_Row(this);
      }
    }
  }

  // AdditionInformation
  IsHaveAdditionInformation = false

  get IsHave_Addition_Wrapper() {
    return this.IsHaveAdditionInformation || this.IsShow_RawValue
  }

  AdditionInformation = undefined

  // Tùy chọn SelectSubItems
  Is_SelectSubItems_ModalOpen = false

  get SelectSubItems_ModalElement() {
    return <Modal
      title='Chọn tập hợp đầu mục con'
      visible={this.Is_SelectSubItems_ModalOpen}
      onOk={this.SelectSubItems_ModalOK}
      onCancel={this.SelectSubItems_ModalCancel}>
      <div className='mass__subitemmodal__wrapper'>
        <TwoCol_SelectView store={this.SelectSubItemsVM} />
      </div>
    </Modal>
  }

  Open_SelectSubItems_Modal = () => {
    this.Is_SelectSubItems_ModalOpen = true;
  }

  SelectSubItems_ModalCancel = () => {
    this.Is_SelectSubItems_ModalOpen = false;
  }

  SelectSubItems_ModalOK = () => {
    this.Is_SelectSubItems_ModalOpen = false;
  }

  selectSubItemsVM
  get SelectSubItemsVM() {
    if (!this.selectSubItemsVM) {
      var obj = this.selectSubItemsVM = tc_selectUtil.get(
        [],
        (item) => {
          var marginLeft = item.Rank * 15;
          return <div
            style={{ marginLeft }}>
            {item.Title}
          </div>
        },
        'Tập hợp vị trí',
        'Vị trí được chọn'
      )
      obj.OnSelectedItemChanged = (v) => this.Temp_SelectSubItems = v;
    }
    return this.selectSubItemsVM;
  }

  Temp_SelectSubItems = undefined

  isShow_RawValue = undefined
  get IsShow_RawValue() {
    if (!this.isShow_RawValue) {
      this.isShow_RawValue = util.GetIsShow_RawValue(this);
    }
    return this.isShow_RawValue;
  } set IsShow_RawValue(v) {
    this.isShow_RawValue = v;
  }

  get RawValue_Element() {
    return <>
      {this.IsShow_RawValue ?
        <Popover trigger='hover'
          content={
            <span className='success'>
              {this.RawValue}
            </span>
          }
        >
          <InfoCircleOutlined className='success' />
        </Popover>
        : ''}
    </>
  }

  IsCreatedByUser

  // Unit
  unit_SetType
  get Unit_SetType() {
    if (!this.unit_SetType) {
      this.unit_SetType = this.SubItems.length === 0
        ? u_st.upper : u_st.both
    }
    return this.unit_SetType
  } set Unit_SetType(v) {
    this.unit_SetType = v;
  }

  get Unit() {
    return this.UnitData.CurrentUnit
  }

  unitData
  get UnitData() {
    if (!this.unitData) {
      this.unitData = util.GetUnitData(this)
    } return this.unitData
  }

  // Site
  site
  get Site() {
    if (!this.site) {
      this.site = util.GetSite(this)
    } return this.site;
  }
}