import { makeObservable, observable, action } from 'mobx';
import { message } from 'antd';
import * as genUtil from '../../../../../util/generalUtil'
import * as util from './ExportSetVMUtil'
import * as rs from '../../../../text/TextRenderState';
import * as inputUtil from '../../../../../util/inputUtil';
import * as editTextUtil from '../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../text/EditableTextView';
import './ExportSet.css'
import SelectView from '../../../../select/SelectView';
import TextView from '../../../../text/TextView';
import * as pct from '../../command/ProjectCommand'
import { PMDataVM } from '../../../single/form/PMDataVM';

const pmDataVM = () => PMDataVM.Instance

export class ExportSetVM {
  constructor() {
    makeObservable(this, {
      isActive: observable,
      massItems: observable,
      massItems_Table: observable,
      renderState: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  get projectId() {
    return this.ModelItem.projectId;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    this.NameVM.RenderState = v;
    this.DescriptionVM.RenderState = v;

    if (v === rs._default) {
      this.SaveDatabase(false);
    }
  }

  Edit = () => {
    if (this.Commands.includes(pct.edit)) {
      if (this.RenderState === rs._default) {
        this.RenderState = rs.edit
        this.Hide_Tool();
      }
    }
  }

  // Name
  get Name() {
    return this.ModelItem.Name;
  } set Name(v) {
    this.ModelItem.Name = v;
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      var obj = this.nameVM = editTextUtil.get(this.Name, this.CanEdit);

      obj.Is_Tool_Visible = false
      obj.OnKeyDown = (e) => {
        var { key, value } = inputUtil.Get_Shortcut(e);
        switch (key) {
          case inputUtil.enter:
            this.RenderState = rs._default
            break;
        }
      }

      genUtil.Add(
        obj.OnSet_RenderState_Func_Dict,
        () => {
          if (obj.RenderState === rs._default) {
            this.Name = obj.Content;
          }
        },
        false
      )

    }
    return this.nameVM;
  }

  get NameView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.NameVM} />
  }

  get NameWrapper_View() {
    return <div className='mgs__cell__wrapper'>
      {this.NameView}
      {this.ActiveTool.View}
    </div>
  }

  // MassType
  get MassType() {
    return this.ModelItem.MassType;
  } set MassType(v) {
    this.ModelItem.MassType = v;
    this.MassType_Render.Content = v;
  }

  massType_Render
  get MassType_Render() {
    if (!this.massType_Render) {
      this.massType_Render = util.GetMassType_Render(this)
    } return this.massType_Render
  }

  massType_Select
  get MassType_Select() {
    if (!this.massType_Select) {
      this.massType_Select = util.GetMassType_Select(this)
    } return this.massType_Select
  }

  get MassType_View() {
    switch (this.RenderState) {
      case rs._default:
        return <TextView store={this.MassType_Render} />
      case rs.edit:
        return <SelectView store={this.MassType_Select} />
    }
  }

  // Description
  get Description() {
    return this.ModelItem.Description;
  } set Description(v) {
    this.ModelItem.Description = v;
  }

  descriptionVM
  get DescriptionVM() {
    if (!this.descriptionVM) {
      var obj = this.descriptionVM = editTextUtil.get(this.Description, this.CanEdit);

      obj.Is_Tool_Visible = false
      obj.OnKeyDown = (e) => {
        var { key, value } = inputUtil.Get_Shortcut(e);
        switch (key) {
          case inputUtil.enter:
            this.RenderState = rs._default
            break;
        }
      }

      genUtil.Add(
        obj.OnSet_RenderState_Func_Dict,
        () => {
          if (obj.RenderState === rs._default) {
            this.Description = obj.Content;
          }
        },
        false
      )

    }
    return this.descriptionVM;
  }

  get DescriptionView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.DescriptionVM} />
  }

  get DescriptionWrapper_View() {
    return <div className='mgs__cell__wrapper'>
      {this.DescriptionView}
      {this.Commands.includes(pct.edit) ? this.DeleteVM.View : ''}
    </div>
  }

  // Project
  get Project() {
    return this.Dict.Project
  }

  // Active
  isActive = false
  get IsActive() {
    return this.isActive
  } set IsActive(v) {
    this.isActive = v;
  }

  get ActiveStyle() {
    return this.IsActive ? 'active' : ''
  }

  Set_Active = () => {
    var q1 = this.Dict
    if (q1.TempCurrentItem) {
      q1.TempCurrentItem.IsActive = false
    }

    q1.TempCurrentItem = this;
    this.IsActive = true;
  }

  UnSet_Active = () => {
    this.Dict.TempCurrentItem = undefined;
    this.IsActive = false;
  }

  activeTool
  get ActiveTool() {
    if (!this.activeTool) {
      this.activeTool = util.GetActiveTool(this)
    } return this.activeTool;
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    this.ActiveTool.Show();
    this.DeleteVM.Show();
  }

  Hide_Tool() {
    this.ActiveTool.Hide();
    this.DeleteVM.Hide();
  }

  // Row
  get RowView() {
    return <tr className={`es__row ${this.ActiveStyle}`}
      onDoubleClick={this.Edit}
      onMouseEnter={this.OnMouseEnter}
      onMouseLeave={this.OnMouseLeave}>
      <td>{this.NameWrapper_View}</td>
      <td>{this.MassType_View}</td>
      <td>{this.DescriptionWrapper_View}</td>
    </tr>
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle, false);
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  // MassItem
  OnSet_MassItems_Func = []

  massItems = undefined
  get MassItems() {
    if (!this.massItems) {
      util.Handle_MassItems(this)
    } return this.massItems
  } set MassItems(v) {
    this.massItems = v;

    genUtil.Handle(this.OnSet_MassItems_Func,
      (v) => this.OnSet_MassItems_Func = v)
  }

  // MassItems_Table
  massItems_Table = undefined
  get MassItems_Table() {
    if (!this.massItems_Table) {
      util.Handle_MassItems_Table(this)
    } return this.massItems_Table
  } set MassItems_Table(v) {
    this.massItems_Table = v;
  }

  // Save MassItems
  SaveMassItems = () => {
    this.ModelItem.SaveMassItems();
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }
}