import { DO_ElementVM } from "./DO_ElementVM"

// Instance
export const get = (q) => {
  var qI = new DO_ElementVM();

  qI.ModelItem = q;

  return qI;
}

// Property
export const GetSubItems = (q) => {
  return q.ModelItem.SubItems.map(x => get(x));
}

export const GetToggleSubContent = (q) => {
  return q.IsOpenSub ? '-' : '+';
}