import { message } from 'antd';
import { url } from './MassGroupSetting_Url'
import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './MassGroupSettingUtil'

export class MassGroupSetting {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Name

  Description

  // Data
  OnSet_Data_Func = []

  data
  get Data() {
    if (!this.data) {
      util.Handle_Data(this)
    } return this.data
  } set Data(v) {
    this.data = v;
    genUtil.Handle(this.OnSet_Data_Func,
      (v) => this.OnSet_Data_Func = v)
  }

  // AddTemplate
  addTemplate
  get AddTemplate(){
    if (!this.addTemplate){
      this.addTemplate = util.GetAddTemplate(this)
    } return this.addTemplate
  }

  get ApiData() {
    var dict = this.Dict;

    var obj = {
      _id: this._id,
      projectId: dict.Project?._id,
      useType : dict.UseType,
      name: this.Name,
      description: this.Description
    }

    return obj;
  }

  SaveDatabase = (showMessage = true, handle, isSaveData = true) => {
    if (isSaveData) {
      const saveData = () => this.Data.SaveDatabase(false);
      if (this._id) {
        saveData();
      } else {
        genUtil.Add_2(
          this.OnSet_id_Func_Dict,
          saveData
        )
      }
    }

    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.DeleteDatabase(true)
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, url, () => {
      if (showMessage){
        message.success('delete successflly!');
      }
      if (handle){
        handle()
      }
    });
  }
}