import { message } from 'antd';
import { url } from './Item_Url'
import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './ItemUtil'

export class Item {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // User
  userId

  OnSet_User_Func = []

  user
  get User() {
    if (this.user === undefined){
      util.Handle_User(this)
    }
    return this.user;
  } set User(v) {
    this.user = v;
    this.userId = v?._id

    genUtil.Handle(this.OnSet_User_Func,
      (v) => this.OnSet_User_Func = v)
  }

  //Project
  projectId

  OnSet_Project_Func = []

  project
  get Project() {
    if (this.project === undefined){
      util.Handle_Project(this)
    }
    return this.project;
  } set Project(v) {
    this.project = v;
    this.projectId = v?._id

    genUtil.Handle(this.OnSet_Project_Func,
      (v) => this.OnSet_Project_Func = v)
  }

  Commands = []

  get ApiData() {
    var obj = {
      _id: this._id,
      userId: this.userId,
      projectId: this.projectId,
      commands: this.Commands
    }

    return obj;
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)

    if (this._id) {
      this.DeleteDatabase(false)
    }
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, url, () => {
      if (showMessage) {
        message.success('delete successflly!');
      }
      if (handle) {
        handle()
      }
    });
  }
}