import * as genUtil from '../../../../../util/generalUtil'
import { ExportSet_DictVM } from "./ExportSet_DictVM";
import * as iUtil from '../item/ExportSetVMUtil'
import ProjectVM from "../../project/ProjectVM";
import { EntDelete } from '../../../../delete/EntDelete';
import * as d_tt from '../../../../delete/EntDelete_ToggleType'
import { TextVM } from '../../../../text/TextVM';
import InputView from '../../../../input/InputView';
import { Button, message } from 'antd';
import { PlusCircleOutlined, SaveFilled } from '@ant-design/icons';
import Modal from 'antd/lib/modal/Modal';
import './ExportSet_Dict.css'


// Instance
export const get = (q, dict) => {
  var qI = new ExportSet_DictVM();

  qI.ModelItem = q;
  qI.Dict = dict

  if (dict instanceof ProjectVM) {
    qI.Project = dict;
  }

  q.OnAddItem = (v) => qI.AddItem(v)

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => iUtil.get(x, q));
    q.IsGet_Items = false;
  }

  var host = q.ModelItem
  if (host.Items) {
    setObj(host.Items)
  }

  genUtil.Add_2(
    host.OnSet_Items_Func,
    () => setObj(host.Items),
    false
  )
}

export const GetAddVM = (q) => {
  var qI = new EntDelete();

  var nameVM = new TextVM();
  nameVM.Placeholder = 'tên bảng ...'

  qI.ToggleType = d_tt.manual
  qI.Title = 'Xác nhận thêm bảng?'

  qI.OnPre_ShowHover = () => {
    nameVM.Content = undefined
  }

  qI.Modal_InfoView = <InputView store={nameVM} />
  qI.Modal_ContentView = <div className='f-ac'>
    {qI.Modal_InfoView}
    {qI.Modal_ButtonView}
  </div>

  qI.ShowHover_Button = <Button type='primary' size='small'
    className='btn-success'
    onClick={qI.Show_Hover}>
    <PlusCircleOutlined />
  </Button>

  qI.OnPreRun = () => {
    if (!nameVM.Content || nameVM.Content.length === 0) {
      qI.IsRun_Cancel = true
      message.warn('Vui lòng nhập tên bảng!');
    }
  }

  qI.OnRun = () => {
    q.ModelItem.NewItem(nameVM.Content);
  }

  return qI;
}

export const GetPickView = (q) => {
  var content = q.CurrentItem ? q.CurrentItem.Name : '(none)'
  var click = () => {
    q.Is_Modal_Visible = true
    if (q.TempCurrentItem) {
      q.TempCurrentItem.UnSet_Active();
    }
  }

  return <Button type='primary'
    onClick={click}>
    <div className='f'>
      <div>CHỌN BẢNG:</div>
      <div className='es__pick__current'>{content}</div>
    </div>
  </Button>
}


export const GetModalView = (q) => {
  var title = 'CHỌN BẢNG THỐNG KÊ'

  const hideVisible = () => {
    q.Is_Modal_Visible = false
  }

  const modalOK = () => {
    q.Is_Modal_Visible = false
    q.CurrentItem = q.TempCurrentItem
  }

  return <Modal title={title} visible={q.Is_Modal_Visible}
    width={1000}
    className='es__modal'
    onCancel={hideVisible}
    footer={
      <>
        <Button type='primary' size='small'
          onClick={modalOK}>ok</Button>
        <Button type='text' size='small'
          onClick={hideVisible}>cancel</Button>
      </>
    }>
    <div>
      {q.MainView}
    </div>
  </Modal>
}

