import * as genUtil from '../../../../../../util/generalUtil'
import { ItemVM } from './ItemVM'
import { EntDelete } from '../../../../../delete/EntDelete';
import * as d_tt from '../../../../../delete/EntDelete_ToggleType'
import { Button, message } from 'antd';
import { CheckCircleOutlined, CloseCircleOutlined, SaveFilled } from '@ant-design/icons';
import { SelectVM } from '../../../../../select/SelectVM';
import { TextVM } from '../../../../../text/TextVM';
import * as pc from '../../../command/ProjectCommand'
import { MultiSelectVM } from '../../../../../select/MultiSelectVM';

// Instance
export const get = (q, dict) => {
  var qI = new ItemVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.manual
  qI.Title = 'Xác nhận xóa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

// User
export const GetUser_Render = (q) => {
  var qI = new TextVM();
  qI.Content = q.User?.Username;
  return qI;
}

export const GetUser_Select = (q) => {
  var qI = new SelectVM();
  qI.Placeholder = 'chọn user ...'
  qI.TitleRender = (v) => v.Username;
  qI.OnSelectedItemChanged = (v) => {
    q.User = v;
  }

  const setList = (data) => {
    qI.Items = data
  }

  const setSelect = (data) => {
    qI.InitialItem = data;
  }

  var host = q.Dict.Dict
  if (host.Users) {
    setList(host.Users)
  } else {
    genUtil.Add_2(
      host.OnSet_Users_Func,
      () => {
        setList(host.Users)
      }
    )
  }

  var host2 = q.ModelItem
  if (host2.User !== undefined) {
    setSelect(host2.User)
  } else {
    genUtil.Add_2(
      host2.OnSet_User_Func,
      () => setSelect(host2.User)
    )
  }

  return qI;
}

// Command
export const GetCommand_Select = (q) => {
  var qI = new MultiSelectVM();

  qI.Items = pc.GetList();
  qI.InitialItems = q.Commands;
  qI.Placeholder = 'commands ...'
  qI.OnSelectedItemChanged = (v) => {
    q.Commands = v;
  }

  return qI;
}