import { PMData } from '../../../../single/model/PMData';
import * as util from './MGI_ItemUtil'

export const pmData = () => PMData.Instance;

export default class MGI_Item {
  Value

  SupValue

  supItem
  get SupItem(){
    if (!this.supItem){
      this.supItem = util.GetSupItem(this);
    }
    return this.supItem;
  }

  get Index() {
    return this.MassGroupItem.Items.indexOf(this);
  }

  title
  get Title(){
    if (!this.title){
      this.title = this.Value;
    }
    return this.title;
  }
  set Title(v){
    this.title = v;
  }

  newMGI
  get NewMGI(){
    if (!this.newMGI){
      this.newMGI = util.GetNewMGI(this);

      if (!this.newMGI && this.newMGI_id){
        this.newMGI_id = undefined;
      }
    }
    return this.newMGI;
  }
  set NewMGI(v){
    this.newMGI = v;
    if (!v || v === this.MassGroupItem){
      this.newMGI_id = undefined;
    }
    else{
      this.newMGI_id = v.id;
    }
  }

  newMGI_id

  get ApiData(){
    return {
      value : this.Value,
      supValue: this.SupValue,
      title : this.Title,
      newMGI_id : this.newMGI_id,
      createType : this.CreateType
    }
  }

  Delete = (handleUndo) => {
    var index = this.Index;
    this.MassGroupItem.Items.splice(index, 1);

    if (handleUndo){
      handleUndo(() => {
        this.MassGroupItem.Items.splice(index, 0, this);
      })
    }
  }
}