import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import React, { Component } from 'react';
import SelectView from '../../../../../select/SelectView';
import { Button, Popover } from 'antd';
import * as SelectUtil from '../../../../../select/SelectVMUtil';
import * as editTextUtil from '../../../../../text/EditableTextVMUtil';
import * as rs from '../../../../../text/TextRenderState';
import * as genUtil from '../../../../../../util/generalUtil';

export class MEP_ItemVM {
  constructor() {
    makeObservable(this, {
      Is_ShowValue_Delete: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Data
  get Name() {
    return this.ModelItem.Name;
  }
  set Name(v) {
    this.ModelItem.Name = v;
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      var list =  [
          'HVAC',
          'Electrical',
          'Plumbing',
          'Firefighter'
      ]
      var obj = this.nameVM = SelectUtil.get(list, this.name);
      obj.Placeholder = 'Chọn masstype'
      obj.OnSelectedItemChanged = (v) => this.name = v;
    }
    return this.nameVM;
  }

  systemClassificationVM
  get SystemClassificationVM() {
    if (!this.systemClassificationVM) {
      var list =  [
        'Supply Air',
        'Exhause Air',
        'Return Air',
        'Hydronic Return',
        'Sanitary',
        'Power',
        'Telecom',
        'Lighting',
        'Lightning Protection',
        'Domestic Cold Water',
        'Domestic Hot Water',
        'Other',
        'Sanitary',
        'Vent',
        'Fire Protection Wet',
        'Fire Protection Dry',
        'Fire Protection Pre-Action',
        'Fire Protection Other',
        'Sanitary',
        'Telecom'
      ]
      var obj = this.systemClassificationVM = SelectUtil.get(list, this.systemClassification);
      obj.Placeholder = 'System Classification'
      obj.OnSelectedItemChanged = (v) => this.systemClassification = v;
    }
    return this.systemClassificationVM;
  }

  valueVM
  get ValueVM() {
    if (!this.valueVM) {
      var list =  [
        'M-Supply Air',
        'M-Fresh Air',
        'M-Pre Cooled Air',
        'M-Kitchen Fresh Air',
        'M-Lobby Pressurization Air',
        'M-Stair Pressurization Air',
        'M-Exhause Air',
        'M-Kitchen Exhaust Air',
        'M-Smoke Exhaust Air',
        'M-Smoke Exhaust Generator',
        'M-Toilet Exhaust Air',
        'M-Return Air',
        'M-Chilled Water Supply',
        'M-Condenser Water Supply',
        'M-Steam Supply Piping',
        'M-Brine Supply Pipe',
        'M-Oil Supply Pipe',
        'M-Refrigerant Pipe',
        'M-Humidification Water',
        'M-Chilled Water Return',
        'M-Condenser Water Return',
        'M-Steam Return',
        'M-Brine Return',
        'M-Oil Return',
        'M-Drain Steam',
        'M-Drain AC',
        'E-Busway',
        'E-Electrical Medium Voltage',
        'E-Electrical Low Voltage',
        'E-Electrical Extra Low Voltage',
        'E-Electrical Lighting',
        'E-Electrical Earthing and Lightning',
        'P-Clean Water',
        'P-General Service Water',
        'P-Well Water',
        'P-Industrial Water',
        'P-Filtration Supply',
        'P-Filtration Return',
        'P-Hot Water Supply',
        'P-Hot Water Return',
        'P-Expansion',
        'P-Gas',
        'P-Soil Water',
        'P-Waste Water',
        'P-Waste Kitchen Water',
        'P-Storm Water',
        'P-Pump Up',
        'P-Ventilation',
        'F-Drencher',
        'F-Sprinkler',
        'F-Low Pressure',
        'F-High Pressure',
        'F-Hosereel',
        'F-Indoor Fire Hydrant',
        'F-Dry Riser',
        'F-Pre Action',
        'F-Foam',
        'F-CO2 Fire Extinguishing',
        'F-Drain Pipe',
        'F-Electrical Fire Alarm'
      ]
      var obj = this.valueVM = SelectUtil.get(list, this.Values);
      obj.Placeholder = 'Chọn SystemType tương ứng'
      obj.OnSelectedItemChanged = (v) => this.Values = v;
    }
    return this.valueVM;
  }

  Is_ShowValue_Delete = undefined

  ShowValue_Delete = () => {
    this.Is_ShowValue_Delete = true;
  }

  HideValue_Delete = () => {
    this.Is_ShowValue_Delete = false;
  }

  get Value_Element() {
    return <>
      <div className='ls_item__value__wrapper'
        onMouseEnter={this.ShowValue_Delete}
        onMouseLeave={this.HideValue_Delete}>
        <SelectView store={this.ValueVM} />

        <div className='ls_item__value__delete'>
          {this.Is_ShowValue_Delete ?
            <Button type='primary' size='small'
            onClick={this.Delete}>x</Button>
            : <div/>}
        </div>
      </div>
    </>
  }

  get Name_Element() {
    return <>
      <div className='ls_item__value__wrapper'>
        <SelectView store={this.NameVM} />
      </div>
    </>
  }

  get SystemClassification_Element() {
    return <>
      <div className='ls_item__value__wrapper'>
        <SelectView store={this.SystemClassificationVM} />
      </div>
    </>
  }

  // Index
  get Index() {
    return this.Dict.itemMEPs.indexOf(this);
  }

  get IsLast() {
    return this.Dict.Items.length - 1 === this.Index;
  }

  Delete = () => {
    this.ModelItem.Delete();

    var dict = this.Dict;
    dict.itemMEPs.splice(this.Index, 1);
    dict.TableMEP.DeleteItem(this);
  }
}