import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Input, Row, Select, Space } from "antd";
import TextView from "../../../../../text/TextView";
import MassRow from "./MassRow";
import MassCellView from "../cell/MassCellView";
import EditableTextView from "../../../../../text/EditableTextView";
import './MassRow.css'
import MR_UnitView from "./unit/MR_UnitView";

const { Option } = Select;

class MassRowView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassRow();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    var toggleButton = data.IsHaveSub ?
      <Button size='small' style={{ marginRight: 5, minWidth: 25 }} type='primary'
        onClick={data.ToggleSub}>
        <TextView store={data.ToggleButtonContentVM} />
      </Button>
      : <div style={{ padding: '0 15.5px' }} />;

    return (
      <>
        <tr>
          <th scope="row" >
            <div className='massrow__title__wrapper'
              style={data.Style}>
              {toggleButton}
              <span>
                <EditableTextView store={data.Editable_TitleVM} />
              </span>

              {data.IsHave_Addition_Wrapper ?
                <div className='massrow__addition__wrapper'>
                  {data.AdditionInfomation_Element}
                  {data.RawValue_Element}
                </div>
                : ''}

              {data.HoverToolElement}
              {data.ModalElement}
            </div>
          </th>
          {data.Cells.map(x => {
            return <MassCellView key={x.id} store={x} />
          })}

          <MR_UnitView store={data.UnitData} />
        </tr>

        {data.IsShowSub ? data.SubItems.map((x, i) => {
          return <MassRowView store={x} key={x.id} />
        }) : ''}
      </>
    )
  }
}

export default observer(MassRowView)