import * as util from './MassGroupComponentUtil'

export default class MassGroupComponent {
  Filter
  Items
  SupItem

  originComponent
  get OriginComponent(){
    if (!this.originComponent){
      this.originComponent = util.GetOriginComponent(this);
    }
    return this.originComponent;
  }

  get ApiData() {
    return {
      filter : this.Filter ? this.Filter.ApiData : undefined,
      items: this.Items.map(x => x.ApiData)
    }
  }

  get AllItems(){
    return this.OriginComponent.AllSubItems;
  }

  get AllSubItems(){
    var list = [...this.Items];
    this.Items.forEach(x => 
      x.SubComponents.forEach(y => list.push.apply(list, y.AllSubItems)));
    return list;
  }

  get Rank(){
    return this.SupItem ? this.SupItem.SupComponent.Rank + 1 : 0
  }
}