import * as mif from './MassItemField'
import * as sft from '../site/Site_FieldType'

export const GetForMassGroup = () => {
  return mif.GetForMassGroup().map(x => {
    return {
      _id: x,
      Name: mif.GetName(x),
      Value: x
    }
  })
}

