import { message } from "antd";
import MassItem from "../item/MassItem";
import MassRow from "../table/row/MassRow";
import { Site } from "./Site"
import * as urlUtil from './SiteUrl'
import * as ft from './Site_FieldType'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);

  qI._id = data._id;
  ft.GetList().forEach(x => {
    qI[x] = data[ft.GetKey(x)]
  })

  return qI;
}

export const get = (dict) => {
  var qI = new Site();

  if (dict instanceof MassRow){
    qI.Url = urlUtil.siteexport_url;
    qI.Row = dict;
  } else if (dict instanceof MassItem){
    qI.Url = urlUtil.siteinfo_url;
    qI.MassItem = dict;
  }

  return qI;
}

export const Clone = (q, dict) => {
  var qI = get(dict);

  ft.GetList().forEach(x => {
    qI[x] = q[x]
  })

  return qI;
}

// Method
export const Run = (q) => {
  q.SaveDatabase(false);

  var list = q.Row.Items;
  var length = list.length
  var i = 0;
  const handleSave = () => {
    i++;
    if (i === length){
      message.success('save successfully!');
    }
  }

  list.forEach(x => {
    var _id = x.Site?._id;

    var qI = x.Site = Clone(q, x);
    qI._id = _id;

    qI.SaveDatabase(false, handleSave);
  })
}