import * as util from './User_DictUtil'
import * as genUtil from '../../../util/generalUtil'

const userToken = 'userToken'

export class User_Dict {
  Url

  token
  get Token(){
    if (!this.token){
      this.token = localStorage.getItem(userToken);
    } return this.token
  } set Token(v){
    this.token = v
    if (!v){
      localStorage.removeItem(userToken)
    } else {
      localStorage.setItem(userToken,v)
    }
  }
  
  // LoginUser
  OnSet_LoginUser_Func =[]

  loginUser
  get LoginUser(){
    if (!this.loginUser){
      util.Handle_LoginUser(this)
    } return this.loginUser;
  } set LoginUser(v){
    this.loginUser = v;

    if (!v){
      this.Token = v;
    }

    if (this.OnSet_LoginUser){
      this.OnSet_LoginUser(v);
    }

    genUtil.Handle(this.OnSet_LoginUser_Func,
      (v) => this.OnSet_LoginUser_Func = v)
  }

  get Config(){
    return {
      headers : {
        'Authorization': 'Bearer ' + this.Token
      }
    }
  }
}