import * as itemUtil from './ProjectUtil'

export class Project_Dict {
  items
  get Items(){
    if (!this.items){
      this.items = []
    }
    return this.items;
  } set Items(v){
    this.items = v;

    this.GetItemsDone = true;
  }

  set GetItemsDone (v){
    if (this.OnGetItemsDone){
      this.OnGetItemsDone(v);
    }
  }

  Get_Project = (data) => {
    var list = this.Items;
    var index = list.findIndex(x => x._id === data._id);
    if (index === -1){
      var item = itemUtil.get(data, this);
      list.push(item);
      return item;
    }
    return list[index];
  }
}