import * as itemVMUtil from './ProjectVMUtil'
import { Project_DictVM } from "./Project_DictVM";

export const get = (q) => {
  var qI = new Project_DictVM();

  qI.ModelItem = q;

  return qI;
}

export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (v) => {
    q.Items = v.map(x => itemVMUtil.get(x, q));
  }

  var qI = q.ModelItem;
  if (qI.Items && qI.Items.length !== 0){
    setObj(qI.Items);
  } else {
    qI.OnGetItemsDone = () => setObj(qI.Items);
  }
}