import * as api from '../../../../util/apiUtil'
import * as genUtil from '../../../../util/generalUtil'
import Project from './Project';
import * as level_DictUtil from '../level/Level_DictUtil'
import * as levelDictUtil from '../level/new/LevelDictUtil'
import * as levelTypicalUtil from '../setting/typicallevel/TypicalLevelSettingUtil'
import * as levelUtil from '../setting/level/LevelSettingUtil'
import * as exportSetDictUtil from '../exportset/dict/ExportSet_DictUtil'
import { url } from './Project_Url'
import { url as pc_url } from './ProjectCommand_Url'
import { PMData } from '../../single/model/PMData';

const pmData = () => PMData.Instance

// Instance
export const GetFromDb = (data, dict) => {
  if (!data) return undefined;

  var qI = new Project();

  qI._id = data._id;
  qI.Name = data.name;
  qI.Code = data.code;
  qI.Dict = dict;

  return qI;
}

export const get = (q, dict) => {
  if (!q) return undefined;

  var qI = new Project();

  qI._id = q._id;
  qI.Name = q.name;
  qI.Code = q.code;
  qI.Dict = dict;

  return qI;
}

export const getAll = (dict, handle) => {
  var url = "https://api.bimdev.vn/api/hbp/list";
  api.get(url).then(response => response.data)
    .then(data => {
      var items = data.map(x => get(x, dict))
      if (handle) {
        handle(items);
      }
    });
}

export const getById = (_id, handle) => {
  if (!_id) return undefined;
  var url = `https://api.bimdev.vn/api/hbp/get?id=${_id}`;
  api.get(url).then(response => response.data)
    .then(data => {
      var item = get(data);
      if (handle) {
        handle(item);
      }
    });
}

// Property
export const GetExportSet_Dict = (q) => {
  return exportSetDictUtil.get(q)
}

// Level
export const Handle_LevelDict = (q) => {
  if (q.IsGet_LevelDict) return undefined;
  q.IsGet_LevelDict = true;

  const setLevelDict = (data) =>
    q.LevelDict = levelDictUtil.GetFromData(data, q);

  levelDictUtil.GetApi(q._id, setLevelDict);
}

// LevelSetting
export const Handle_LevelSetting = (q) => {
  if (q.IsGet_LevelSetting) return undefined
  q.IsGet_LevelSetting = true;

  q.LevelSetting = levelUtil.GetDefault(q);
}

// TypicalLevelSetting
export const GetTypicalLevelSetting = (q) => {
  return levelTypicalUtil.GetTest(q);
}

// Commands
export const Handle_Commands = (q) => {
  if (q.IsGet_Commands) return undefined;
  q.IsGet_Commands = true;

  const setObj = (data) => {
    q.Commands = data && data.length !== 0 ? data[0].commands : []
    q.IsGet_Commands = false;
  }

  var host = pmData().UserDict.LoginUser;
  if (!host) {
    setObj()
  } else {
    var apiData = {
      userId: host._id,
      projectId: q._id
    }

    const apiFunc = () => api.post(`${pc_url}/list`, apiData);
    apiFunc().then(response => response.data)
      .then(data => {
        setObj(data)
      })
  }
}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}