import * as genUtil from '../../../../../../util/generalUtil'
import * as api from '../../../../../../util/apiUtil'
import * as mgcUtil from 
  '../../../mass/table/rowgroup/MassGroupComponentUtil'
import { url } from './Data_Url'
import { Data } from './Data';

// Instance
export const GetFromDb = (data, dict) => {
  var qI = new Data();

  qI.MainMassGroupComponent = 
    mgcUtil.get(data.mainMassGroupComponent, qI);
  qI.Dict = dict;

  return qI;
}

export const get = (dict) => {
  var qI = new Data();

  qI.Dict = dict

  return qI;
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}