import * as genUtil from '../../../../../../util/generalUtil'
import { makeObservable, observable, action } from 'mobx';
import * as util from './DictVMUtil'
import * as iUtil from '../item/ItemVMUtil'
import * as gUtil from '../group/GroupVMUtil'
import { Button, message } from 'antd';
import TableView from '../table/TableView';
import './Dict.css'
import { SaveFilled } from '@ant-design/icons';

export class DictVM {
  constructor() {
    makeObservable(this, {
      groups: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Group
  groups = undefined
  get Groups() {
    if (!this.groups) {
      util.Handle_Groups(this)
    } return this.groups
  } set Groups(v) {
    this.groups = v;
  }

  AddGroup = () => {
    this.Groups.push(gUtil.get(this));
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get MainView() {
    return <div>
      <div className='f-jc admin__pc__title'>
        <span>PHÂN QUYỀN DỰ ÁN</span>
      </div>
      <div className='f'>
        <div className='admin__pc__add'>
          <Button type='primary' size='small'
            className='btn-success'
            onClick={this.AddGroup}>+</Button>
          <Button type='primary' size='small'
            className='btn-success'
            onClick={this.Save}>
            <SaveFilled />
          </Button>
        </div>
        <div className='es__main f1'>
          <TableView store={this} />
        </div>
      </div>
    </div>
  }

  // Refine
  Refine = () => {
    // filter groups
    var removeItems = []
    this.Groups.forEach((x, i, a) => {
      if (!x.Project || x.Items.length === 0
        || a.findIndex(y => y.Project === x.Project) !== i) {
        removeItems.push(x);
      }
    })

    removeItems.forEach(x => {
      x.Delete();
    })

    // filter user
    this.Groups.forEach(x => {
      removeItems = []
      x.Items.forEach((y, i, a) => {
        if (!y.User || y.Commands.length === 0
          || a.findIndex(z => z.User === y.User) !== i) {
          removeItems.push(y);
        }
      })

      removeItems.forEach(y => {
        y.Delete();
      })
    })

    // sau khi refine user, refine group 1 lần nữa Items === 0
    removeItems = []
    this.Groups.forEach((x, i, a) => {
      if (x.Items.length === 0) {
        removeItems.push(x);
      }
    })

    removeItems.forEach(x => {
      x.Delete();
    })
  }

  // Save
  Save = () => {
    this.Refine();
    this.ModelItem.Save();
  }
}