import * as phaseDictUtil from '../phase/Phase_DictUtil'
import * as util from './ProjectUtil'
import * as typicalLevelUtil from '../setting/typicallevel/TypicalLevelSettingUtil'
import * as genUtil from '../../../../util/generalUtil'

export default class Project {
  phase_Dict
  get Phase_Dict() {
    if (!this.phase_Dict) {
      this.phase_Dict = phaseDictUtil.get(this);
    }
    return this.phase_Dict;
  }

  exportSet_Dict
  get ExportSet_Dict(){
    if (!this.exportSet_Dict){
      this.exportSet_Dict = util.GetExportSet_Dict(this)
    } return this.exportSet_Dict;
  }

  // Level
  // New Version
  OnSet_LevelDict_Func_Dict = []

  levelDict
  get LevelDict(){
    if (!this.levelDict){
      util.Handle_LevelDict(this);
    }
    return this.levelDict;
  } set LevelDict(v){
    this.levelDict = v;

    genUtil.Handle(
      this.OnSet_LevelDict_Func_Dict,
      (v) => this.OnSet_LevelDict_Func_Dict = v)
  }

  // LevelSetting
  OnSet_LevelSetting_Func_Dict = []

  levelSetting
  get LevelSetting() {
    if (!this.levelSetting) {
      util.Handle_LevelSetting(this)
    }
    return this.levelSetting
  } set LevelSetting(v) {
    this.levelSetting = v;

    genUtil.Handle(
      this.OnSet_LevelSetting_Func_Dict,
      (v) => this.OnSet_LevelSetting_Func_Dict = v)
  }

  // TypicalLevelSetting
  OnGetTypicalLevelSetting_Func_Dict = []

  getTypicalLevelSetting
  get GetTypicalLevelSetting(){
    return this.getTypicalLevelSetting
  }
  set GetTypicalLevelSetting(v) {
    this.getTypicalLevelSetting = v;
    genUtil.Handle(this.OnGetTypicalLevelSetting_Func_Dict,
      (v) => this.OnGetTypicalLevelSetting_Func_Dict = v)
  }

  IsOnGetDb_Async

  typicalLevelSetting
  get TypicalLevelSetting() {
    if (!this.typicalLevelSetting) {
      if (!this.IsOnGetDb_Async) {
        this.IsOnGetDb_Async = true;
        typicalLevelUtil.GetFromDb_Async(this, null,
          (resData, editData) => {
            this.TypicalLevelSetting = resData;
            this.TypicalLevelSetting_ForEdit = editData;
            this.GetTypicalLevelSetting = true
          })
      }
    }
    return this.typicalLevelSetting;
  }
  set TypicalLevelSetting(v) {
    this.typicalLevelSetting = v;
  }

  typicalLevelSetting_ForEdit
  get TypicalLevelSetting_ForEdit() {
    if (!this.typicalLevelSetting_ForEdit) {
      this.typicalLevelSetting_ForEdit = this.TypicalLevelSetting;
    }
    return this.typicalLevelSetting_ForEdit;
  } set TypicalLevelSetting_ForEdit(v) {
    this.typicalLevelSetting_ForEdit = v;
  }

  url
  get Url() {
    if (!this.url) {
      var isHash = true;
      var obj = `${process.env.PUBLIC_URL}${isHash ? '/#' : ''}/project`;
      obj = `${obj}/${this._id}`;

      this.url = obj;
    }
    return this.url;
  }

  // Commands
  OnSet_Commands_Func = []

  Reset_Commands = () => {
    this.Commands = undefined;
    util.Handle_Commands(this)
  }

  commands
  get Commands(){
    if (!this.commands){
      util.Handle_Commands(this)
    } return this.commands;
  } set Commands(v){
    this.commands = v

    genUtil.Handle(this.OnSet_Commands_Func,
      (v) => this.OnSet_Commands_Func = v)
  }
}