import { Category_ItemVM } from "./Category_ItemVM"
import * as genUtil from '../../../../../../util/generalUtil'

// Instance
export const get = (q, dict) => {
    var qI = new Category_ItemVM();
  
    qI.ModelItem = q;
    qI.Dict = dict;
  
    return qI;
  }

  // Property
export const Handle_Value_StorageList = (q) => {
    if (q.IsGet_Value_StorageList) return undefined;
    q.IsGet_Value_StorageList = true;
  
    var project = q.Project.ModelItem
  
    var setObj = (CategoryDict) => {
      q.Value_StorageList = CategoryDict.Items.map(x => x.Name);
    }
  
    if (project.CategoryDict){
      setObj(project.CategoryDict)
    } else {
      genUtil.Add_2(
      project.OnSet_CategoryDict_Func_Dict,
      () => {
        setObj(project.CategoryDict)
      }, false);
    }
}