import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Select } from "antd";
import { TwoCol_SelectVM } from "./TwoCol_SelectVM";
import * as util from './TwoCol_SelectVMUtil'
import './TwoCol_Select.css'

class TwoCol_SelectView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = util.GetTest();
    } else {
      this.state = store;
    }
  }

  onValueChange = (v) => {
    var data = this.state;
    data.SelectedIndexs = v;
  }

  onDropdownVisibleChange = (v) => {
    var data = this.state;
    data.Open = v;
  }

  render() {
    var data = this.state;
    var style = this.props.style;

    return (
      <>
        <table className='tc_table'>
          <thead>
            <tr>
              <th>
                <div className='tc_table__title'>
                  <span>
                    {data.InputTitle}
                  </span>
                </div>
              </th>
              <th>
                <div className='tc_table__title'>
                  {data.OutputTitle}
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>
                <span className='tc_select__partition__items'>
                  {data.AvailableItems.map(x => {
                    return <span key={data.GetKey(x)} onClick={() => data.Select(x)}>
                      {data.Render(x)}
                    </span>
                  })}
                </span>
              </td>
              <td>
                <span className='tc_select__partition__items'>
                  {data.SelectedItems.map(x => {
                    return <span key={data.GetKey(x)} onClick={() => data.Unselect(x)}>
                      {data.Render(x)}
                    </span>
                  })}
                </span>
              </td>
            </tr>
          </tbody>
        </table>
      </>
    )
  }
}

export default observer(TwoCol_SelectView)