import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Row } from 'antd';
import { MassDetailTable } from './MassDetailTable';
import TableView from '../../../../table/TableView';

class MassDetailTableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassDetailTable();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='massdetail__wrapper'>
          <Button type='primary' size='small'
            className='massdetail__addcolumn'
            onClick={data.AddColumn}>+</Button>
          <TableView className='table table-sm table-bordered' 
            store={data.Table} />
        </div>
      </>
    );
  }
}

export default observer(MassDetailTableView);
