import { makeObservable, observable, action } from 'mobx';
import * as util from './ReplaceRowLocationFactoryUtil'
import { PMData } from '../../../../single/model/PMData';
import * as lt from './RRLF_LocationType'

export const pmData = () => {
  return PMData.Instance
}

export default class ReplaceRowLocationFactory {
  constructor() {
    makeObservable(this, {
      Location: observable,
    })
  }

  ReferenceRow
  Location = lt.inside

  get Color(){
    return util.GetColor(this);
  }

  get ButtonStyle(){
    return {
      width: 50,
      backgroundColor: this.Color,
      border: 'none'
    };
  }

  ToggleLocation = () =>{
    switch (this.Location) {
      case lt.inside:
        this.Location = lt.lower;
        break;
      case lt.lower:
        this.Location = lt.inside;
        break;
    }
  }
}