import { TextVM } from "../../../../text/TextVM";
import { SiteVM } from "./SiteVM"
import * as ft from './Site_FieldType'
import * as genUtil from '../../../../../util/generalUtil'
import InputView from "../../../../input/InputView";
import { Button } from "antd";
import Modal from "antd/lib/modal/Modal";
import './Site.css'
import ReactDatePicker from "react-datepicker";
import 'react-datepicker/dist/react-datepicker.css'
import * as dateUtil from '../../../../../util/dateUtil'
import DatePickerView from "../../../../datepicker/DatePickerView";
import { DatePicker } from "../../../../datepicker/DatePicker";

// Instance
export const get = (q, dict) => {
  var qI = new SiteVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetInputVMs = (q) => {
  return ft.GetList().map(x => {
    var qI
    switch(x){
      case ft.AcceptanceDate:
        qI = new DatePicker();
        break;
      default:
        qI = new TextVM();
        break;
    }

    qI.Placeholder = ''
    qI.Field = x;

    return qI;
  })
}

// View
const GetInputItem_Render = (q, x) => {
  switch (x.Field) {
    case ft.AcceptanceDate:
      x.Initial = () => x.SetDate(q.GetValue(x.Field))
      x.GetValue = () => x.Date
      return <DatePickerView store={x} />
    default:
      x.Initial = () => x.Content = q.GetValue(x.Field)
      x.GetValue = () => x.Content
      return <InputView store={x}/>
  }
}

export const GetInputView = (q) => {
  var obj = <table className='siteinput__table'>
    <tbody>
      {q.InputVMs.map(x => {
        return <tr className='siteinput__tr' key={genUtil.randomId()}>
          <th className='siteinput__title'>{ft.GetTitle(x.Field) + ':'}</th>
          <td className='siteinput__input'>
            {/* <InputView store={x} /> */}
            {GetInputItem_Render(q, x)}
          </td>
        </tr>
      })}
    </tbody>
  </table>

  return obj;
}

export const GetModalView = (q) => {
  return <Modal title='Thông tin công trường' visible={q.Is_Modal_Visible}
    footer={
      <>
        <Button type='primary' size='small' onClick={q.ModalOK}>run</Button>
        <Button type='text' size='small' onClick={q.ModalCancel}>cancel</Button>
      </>
    }>
    {q.InputView}
  </Modal>
}

// Method
export const Initial = (q) => {
  q.InputVMs.forEach(x => {
    // x.Content = q.GetValue(x.Field)
    x.Initial();
  })
}

export const Run_SetValue = (q) => {
  var isValid = q.Is_OK_Valid = true;
  q.InputVMs.forEach(x => {
    if (isValid) {
      // var content = x.Content;
      var content = x.GetValue();
      if (content && (typeof content !== 'string' || content.length !== 0)) {
        q.SetValue(x.Field, content);
      } else {
        q.Is_OK_Valid = false
      }
    }
  })
}