import { makeObservable, observable, action } from 'mobx';
import * as util from './MassResultCompVMUtil'
import * as multiSelectVMUtil from '../../../../select/MultiSelectVMUtil'
import * as selectVMUtil from '../../../../select/SelectVMUtil'
import * as checkboxVMUtil from '../../../../checkbox/CheckboxVMUtil'
import * as textUtil from '../../../../text/TextVMUtil'
import { Link } from 'react-router-dom';
import { Popover } from 'antd';

export default class MassResultCompVM {
  constructor() {
    makeObservable(this, {
      IsShowProjectSelect: observable,
      CanHavePhase: observable,
      isHavePhase: observable,
      IsDataPending: observable,
      massGroupSetting_Dict : observable
    })
  }

  projects
  get Projects() {
    if (!this.projects) {
      this.projects = this.ModelItem.Projects;
    }
    return this.projects;
  }
  set Projects(v) {
    this.projects = v;
    this.ModelItem.Projects = v;
  }

  projectVMs
  get ProjectVMs() {
    if (!this.projectVMs) {
      this.projectVMs = util.GetProjectVMs(this);
    }
    return this.projectVMs;
  }
  set ProjectVMs(v) {
    this.projectVMs = v;
    this.Projects = v.map(x => x.ModelItem);
  }

  projectVMStorageList
  get ProjectVMStorageList() {
    if (!this.projectVMStorageList) {
      this.projectVMStorageList = util.GetProjectVMStorageList(this);
    }
    return this.projectVMStorageList;
  }
  set ProjectVMStorageList(v) {
    this.projectVMStorageList = v;

    this.projectVMs = util.GetProjectVMs(this);

    var selectVM = this.ProjectSelectVM;
    selectVM.Items = v;
    selectVM.InitialItems = this.projectVMs;
  }

  projectSelectOpen
  get ProjectSelectOpen(){
    if (this.projectSelectOpen === undefined){
      this.projectSelectOpen = this.ProjectSelectVM.Open;
    }
    return this.projectSelectOpen;
  }
  set ProjectSelectOpen(v){
    this.projectSelectOpen = v;
    this.ProjectPopoverState.Content = util.GetProjectPopoverStateValue(this);
  }

  projectPopoverState
  get ProjectPopoverState(){
    if (!this.projectPopoverState){
      this.projectPopoverState = textUtil.get(util.GetProjectPopoverStateValue(this));
    }
    return this.projectPopoverState;
  }

  projectSelectVM
  get ProjectSelectVM() {
    if (!this.projectSelectVM) {
      var list = this.ProjectVMStorageList ? this.ProjectVMStorageList : [];
      var obj = this.projectSelectVM = multiSelectVMUtil.get(list, this.ProjectVMs);
      obj.Placeholder = 'chọn dự án'
      obj.TitleRender = (item) => <Popover trigger={this.ProjectPopoverState.Content}
        placement="topLeft"
        content={
          <a target='_blank' rel="noreferrer"
            // href={`/#/project/${item._id}`} 
            href={item.Url}
            >View project</a>
        }
      >
        {item.DisplayContent}
      </Popover>

      obj.OnSelectedItemChanged = (v) => this.ProjectVMs = v;
      obj.OnSetOpen = (v) => this.ProjectSelectOpen = v;
    }
    return this.projectSelectVM;
  }

  IsShowProjectSelect = true

  phases
  get Phases() {
    if (!this.phases) {
      this.phases = this.ModelItem.Phases;
    }
    return this.phases;
  }
  set Phases(v) {
    this.phases = v;
    this.ModelItem.Phases = v;
  }

  phaseVMs
  get PhaseVMs() {
    if (!this.phaseVMs) {
      this.phaseVMs = util.GetPhaseVMs(this);
    }
    return this.phaseVMs;
  }
  set PhaseVMs(v) {
    this.phaseVMs = v;
    this.Phases = v.map(x => x.ModelItem);
  }

  massItem_Dict
  get MassItem_Dict() {
    if (!this.massItem_Dict) {
      this.massItem_Dict = this.ModelItem.MassItem_Dict;
    }
    return this.massItem_Dict;
  }

  phaseVMStorageList
  get PhaseVMStorageList() {
    return this.phaseVMStorageList;
  }
  set PhaseVMStorageList(v) {
    this.phaseVMStorageList = v;

    this.phaseVMs = util.GetPhaseVMs(this);

    var selectVM = this.PhaseSelectVM;
    selectVM.Items = v;
    selectVM.InitialItems = this.phaseVMs;
  }

  phaseSelectVM
  get PhaseSelectVM() {
    if (!this.phaseSelectVM) {
      var list = this.PhaseVMStorageList ? this.PhaseVMStorageList : [];
      var obj = this.phaseSelectVM = multiSelectVMUtil.get(list, this.PhaseVMs);
      obj.Placeholder = 'chọn version'
      obj.OnSelectedItemChanged = (v) => this.PhaseVMs = v;
    }
    return this.phaseSelectVM;
  }

  CanHavePhase = false

  isHavePhase = undefined
  get IsHavePhase() {
    if (!this.isHavePhase) {
      this.isHavePhase = this.ModelItem.IsHavePhase;
    }
    return this.isHavePhase;
  }
  set IsHavePhase(v) {
    this.isHavePhase = v;
    this.ModelItem.IsHavePhase = v;
  }

  isShowPhaseSelectVM
  get IsShowPhaseSelectVM() {
    if (!this.isShowPhaseSelectVM) {
      var obj = this.isShowPhaseSelectVM
        = checkboxVMUtil.get("So sánh các version", this.IsHavePhase);

      obj.onCheckChange = (v) => {
        this.IsHavePhase = v;
      };
    }
    return this.isShowPhaseSelectVM;
  }

  IsDataPending = false;

  get LoadingSpinTip() {
    return `loading data ... (${(this.HandledRatio * 100).toFixed(1)}%)`;
  }

  loadingSpinTipVM
  get LoadingSpinTipVM() {
    if (!this.loadingSpinTipVM) {
      this.loadingSpinTipVM = textUtil.get(this.LoadingSpinTip);
    }
    return this.loadingSpinTipVM;
  }

  handledRatio = 0
  get HandledRatio() {
    return this.handledRatio;
  }
  set HandledRatio(v) {
    this.handledRatio = v;
    this.LoadingSpinTipVM.Content = this.LoadingSpinTip;
  }

  // function
  GetMassData = () => {
    this.IsDataPending = true;
    this.HandledRatio = 0;
    this.ModelItem.GetMassData();
  }

  // MassGroupSetting
  massGroupSetting_Dict = undefined
  get MassGroupSetting_Dict(){
    if (!this.massGroupSetting_Dict){
      util.Handle_MassGroupSetting_Dict(this)
    }return this.massGroupSetting_Dict
  } set MassGroupSetting_Dict(v){
    this.massGroupSetting_Dict = v;

    console.log(v.Dict);
  }
}