import { PMData } from '../../../single/model/PMData';
import { MassDetailSettingData } from './MassDetailSettingData';
import * as mif from '../../mass/item/MassItemField'
import * as mt from '../../mass/MassType'

export const pmData = ()  =>{
  return PMData.Instance;
}

export const GetFromDb = (data) => {
  var qI = new MassDetailSettingData();
  
  qI.Columns = data.columns
  qI.MainMassType = data.mainMassType;

  return qI;
}

export const GetDefault = () => {
  var qI = new MassDetailSettingData();

  qI.Columns = [mif.Value, mif.Unit]
  qI.MainMassType = mt.Concrete

  return qI;
}

export const Clone = (q) => {
  var qI = new MassDetailSettingData();
  
  qI.Columns = q.Columns
  qI.MainMassType = q.MainMassType;

  return qI;
}