import { GroupVM } from "./GroupVM"
import { SelectVM } from '../../../../../select/SelectVM';
import { TextVM } from '../../../../../text/TextVM';
import * as genUtil from '../../../../../../util/generalUtil'
import { EntDelete } from "../../../../../delete/EntDelete";
import * as d_tt from '../../../../../delete/EntDelete_ToggleType'

// Instance
export const get = (dict) => {
  var qI = new GroupVM();
  qI.Dict = dict;
  return qI;
}

// Property
export const GetProject_Render = (q) => {
  var qI = new TextVM();
  qI.Content = q.Project?.Name;
  return qI;
}

export const GetProject_Select = (q) => {
  var qI = new SelectVM();
  qI.TitleRender = (v) => v.Name;
  qI.Placeholder = 'chọn dự án ...'
  qI.OnSelectedItemChanged = (v) => {
    q.Project = v;
  }

  const setObj = (data) => {
    qI.Items = data
    qI.InitialItem = q.Project;
  }

  var host = q.Dict.Dict
  if (host.Projects) {
    setObj(host.Projects)
  } else {
    genUtil.Add_2(
      host.OnSet_Projects_Func,
      () => setObj(host.Projects)
    )
  }

  return qI;
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.manual
  qI.Title = 'Xác nhận xóa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}