import { makeObservable, observable, action, makeAutoObservable, observe } from 'mobx';
import * as util from './MassTypeSettingDataVMUtil'
import * as utilCategory from './CategorySettingDataVMUtil'
import * as utilMEP from './MEPSettingDataVMUtil'
import { ControlOutlined } from '@ant-design/icons';
import { Button, message } from 'antd';
import * as itemUtil from './Item/MT_ItemVMUtil'
import * as rs from '../../../../text/TextRenderState';
import * as itemUtilCategory from './Category/Category_ItemVMUtil'
import * as itemUtilMEP from './MEP/MEP_ItemVMUtil'

export class MassTypeSettingDataVM {
    constructor() {
        makeAutoObservable (this,{
            items:observable,
            itemCategories:observable,
            itemMEPs:observable
        })
    }

    items = undefined
    get Items() {
        if (!this.items) {
            this.items = util.GetItems(this);
        }
        return this.items;
    } set Items(v) {
        this.items = v;
    }

    itemCategories = undefined
    get ItemCategories() {
        if (!this.itemCategories) {
            this.itemCategories = utilCategory.GetItems(this);
        }
        return this.itemCategories;
    } set ItemCategories(v) {
        this.itemCategories = v;
    }

    itemMEPs = undefined
    get ItemMEPs() {
        if (!this.itemMEPs) {
            this.itemMEPs = utilMEP.GetItems(this);
        }
        return this.itemMEPs;
    } set ItemMEPs(v) {
        this.itemMEPs = v;
    }

    AddItem = () => {
        var item = itemUtil.get(this.ModelItem.AddItem(), this);
    
        item.NameVM.RenderState = rs.edit
    
        this.Items.push(item);
        this.Table.AddItem(item);
        return item;
      }
    
      // Table
    table
      get Table(){
        if (!this.table){
          this.table = util.GetTable(this);
        }
        return this.table;
      }

      AddItemCategory = () => {
        var item = itemUtilCategory.get(this.ModelItem.AddItem(), this);
    
        //item.NameVM.RenderState = rs.edit
    
        this.ItemCategories.push(item);
        this.TableCategory.AddItem(item);
        return item;
      }

    tableCategory
      get TableCategory(){
        if (!this.tableCategory){
          this.tableCategory = utilCategory.GetTable(this);
        }
        return this.tableCategory;
      }

      AddItemMEP = () => {
        var item = itemUtilMEP.get(this.ModelItem.AddItem(), this);
    
        //item.NameVM.RenderState = rs.edit
    
        this.ItemMEPs.push(item);
        this.TableMEP.AddItem(item);
        return item;
      }

    tableMEP
      get TableMEP(){
        if (!this.tableMEP){
          this.tableMEP = utilMEP.GetTable(this);
        }
        return this.tableMEP;
      }
    
    Update_MasstypeData = () => {
        this.ModelItem.Update_MasstypeData();
        message.success('save successfully!');
      }
}