import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import './Table.css'
import TableItemView from './TableItemView';
import { DictVM } from '../dict/DictVM';

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <table className='admin__pgc__table'>
          <thead className='admin__pgc__thead'>
            <tr>
              <th className='admin__pgc__table__user'>Người dùng</th>
              <th className='admin__pgc__table__authorize'>Phân quyền</th>
            </tr>
          </thead>
          <tbody className='admin__pgc__tbody'>
            {data.Items?.map(x => {
              return <TableItemView key={x.id} store={x} />
            })}
          </tbody>
        </table>
      </>
    );
  }
}

export default observer(TableView);
