import * as genUtil from '../../../../../util/generalUtil'
import * as util from './ExportSet_DictUtil'
import * as iUtil from '../item/ExportSetUtil'

export class ExportSet_Dict {
  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Project
  Project

  UseType

  // Item
  OnSet_Items_Func = []

  items
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
    
    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = (v) => {
    this.Items.push(v);
    if (this.OnAddItem){
      this.OnAddItem(v)
    }
  }

  NewItem = (name) => {
    var item = iUtil.get(this);
    item.Name = name;

    this.AddItem(item);
    item.SaveDatabase();
  }

  CurrentItem
}