import * as util from './LeftNavUtil'

export class LeftNav {
  Title

  items
  get Items(){
    if (!this.items){
      this.items = util.GetItems(this);
    }
    return this.items;
  } set Items(v){
    this.items = v;
  }

  currentProject
  get CurrentProject(){
    return this.currentProject;
  } set CurrentProject(v){
    this.currentProject = v;
    
    this.Items = util.GetItems(this);
  }
}