import { MassTypeSetting } from "./MassTypeSetting"
import * as dataUtil from './MassTypeSettingDataUtil'
import { PMData } from '../../../single/model/PMData';
import { message } from 'antd';

// Instance
export const get = () => {
  var qI = new MassTypeSetting();

  return qI;
}

export const GetDefault = () => {
  var qI = new MassTypeSetting();

  qI.Name = 'Thiết lập công tác'
  qI.Description = 'Mô tả thiết lập'
  qI.Data = dataUtil.GetDefault(qI);
  return qI;
}