import { makeObservable, observable, action } from 'mobx';
import SelectView from '../../../../../select/SelectView';
import * as util from './MassGroupSetting_DictVMUtil'
import TableView from '../table/TableView';
import * as iUtil from '../item/MassGroupSettingVMUtil'
import * as genUtil from '../../../../../../util/generalUtil'

export class MassGroupSetting_DictVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = (v) => {
    var list = this.Items;
    if (list.length === this.ModelItem.Items.length) return

    var obj = iUtil.get(v, this);
    list.push(obj)
  }

  get UseType(){
    return this.ModelItem.UseType
  }

  get CategoryName(){
    return this.ModelItem.CategoryName
  }

  // Save
  save
  get Save() {
    if (!this.save) {
      this.save = util.GetSave(this)
    } return this.save;
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get TableWrapper_View(){
    return <div className='mgs__table__wrapper'>
      <TableView store={this} />
    </div>
  }
}