import { Dict, ExportSet_Dict } from "./Dict"
import * as genUtil from '../../../../../../util/generalUtil'

// Instance
export const get = (dict) => {
  var qI = new Dict();

  qI.Dict = dict;

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data
    q.IsGet_Items = false;
  }

  var host = q.Dict;
  if (host.Users){
    setObj(host.Users)
  } else {
    genUtil.Add_2(
      host.OnSet_Users_Func,
      () => setObj(host.Users)
    )
  }
}


// Method
