import * as genUtil from '../../../../../../util/generalUtil'
import { makeObservable, observable, action } from 'mobx';
import * as util from './DictVMUtil'
import * as iVMUtil from '../item/ItemVMUtil'
import * as iUtil from '../item/ItemUtil'
import { Button, message } from 'antd';
import TableView from '../table/TableView';
import './Dict.css'
import { SaveFilled } from '@ant-design/icons';
import { Item } from '../item/Item';

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Items
  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
  }

  AddItem = () => {
    var i = iUtil.get(this.ModelItem);
    this.ModelItem.Items.push(i);

    var iVM = iVMUtil.get(i, this)
    this.Items.push(iVM);
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get MainView() {
    return <div>
      <div className='f-jc admin__pgc__title'>
        <span>PHÂN QUYỀN NHÓM DỰ ÁN</span>
      </div>
      <div className='f'>
        <div className='admin__pgc__add'>
          <Button type='primary' size='small'
            className='btn-success'
            onClick={this.AddItem}>+</Button>
          <Button type='primary' size='small'
            className='btn-success'
            onClick={this.Save}>
            <SaveFilled />
          </Button>
        </div>
        <div className='es__main f1'>
          <TableView store={this} />
        </div>
      </div>
    </div>
  }

  // Refine
  Refine = () => {
    // filter groups
    var removeItems = []
    this.Items.forEach((x, i, a) => {
      if (!x.User || x.Commands.length === 0
        || a.findIndex(y => y.User === x.User) !== i) {
        removeItems.push(x);
      }
    })

    removeItems.forEach(x => {
      x.Delete();
    })
  }

  // Save
  Save = () => {
    this.Refine();
    this.ModelItem.Save();
  }
}