import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './MI_ValueDictUtil'
import * as gt from './MI_ValueDict_GroupType'
import { MI_ValueItem } from './MI_ValueItem'

export class MI_ValueDict {
  Data = []

  // NewVersion
  OnGet_FullData_Func_Dict = []

  fullData
  get FullData() {
    if (!this.fullData) {
      util.Handle_FullData(this)
    }
    return this.fullData;
  } set FullData(v) {
    this.fullData = v;

    genUtil.Handle(this.OnGet_FullData_Func_Dict,
      (v) => this.OnGet_FullData_Func_Dict = v);
  }

  GetItem = (field, group = gt.raw, handle) => {
    const getObj = () => {
      var list = group === gt.raw ? this.Data : this.FullData
      var qI = genUtil.SingleFilter(list, x => x.Unit === field);
      if (!qI && group === gt.raw) {
        qI = new MI_ValueItem();
        qI.Unit = field;
        list.push(qI);
      }
      return qI;
    }

    if (group === gt.raw || !this.FullData) {
      return getObj();
    } else {
      genUtil.Add_2(
        this.OnGet_FullData_Func_Dict,
        () => {
          var obj = getObj();
          if (handle) {
            handle(obj)
          }
        }
      )
    }
  }

  GetValue = (field, group = gt.raw, handle) => {
    const getObj = () => {
      var list = group === gt.raw ? this.Data : this.FullData
      var qI = genUtil.SingleFilter(list, x => x.Unit === field);
      var value = qI ? qI.Value : undefined;
      return value;
    }

    if (group === gt.raw || !this.FullData) {
      return getObj();
    } else {
      genUtil.Add_2(
        this.OnGet_FullData_Func_Dict,
        () => {
          var obj = getObj();
          if (handle) {
            handle(obj)
          }
        }
      )
    }
  }
}