import { message } from "antd";
import { PMDataVM } from "../../../single/form/PMDataVM";
import { PMData } from "../../../single/model/PMData";
import MassResultCompVM from "./MassResultCompVM"
import * as genUtil from '../../../../../util/generalUtil'

export const pmDataVM = () => {
  return PMDataVM.Instance;
}

export const pmData = () => {
  return PMData.Instance;
}

export const get = (q, isfetchProjects = true) => {
  var qI = new MassResultCompVM();
  qI.ModelItem = q;

  q.ViewItem = qI;

  if (isfetchProjects) {
    pmDataVM().Project_DictVM.OnGetItemsDone = (v) => {
      qI.ProjectVMStorageList = GetProjectVMStorageList(qI);
    }
  }

  q.OnGetItemsDone_Funcs.push(() => {
    message.success('get data successfully');
  })

  return qI;
}

export const GetProjectVMStorageList = (q) => {
  return pmDataVM().Project_DictVM.Items;
}

export const GetProjectVMs = (q) => {
  var storageList = q.ProjectVMStorageList;
  if (!storageList) return [];

  var list = [];

  var projects = q.Projects;
  projects.forEach(x => {
    var index = storageList.findIndex(y => y.ModelItem === x);
    if (index !== -1) {
      list.push(storageList[index]);
    }
  })

  return list;
}

export const GetPhaseVMs = (q) => {
  var storageList = q.PhaseVMStorageList;
  if (!storageList) return [];

  var list = [];

  var phases = q.Phases;
  phases.forEach(x => {
    var index = storageList.findIndex(y => y.ModelItem === x);
    if (index !== -1) {
      list.push(storageList[index]);
    }
  })

  return list;
}

export const GetProjectPopoverStateValue = (q) => {
  return q.ProjectSelectOpen ? 'none' : 'hover';
}

export const Handle_MassGroupSetting_Dict = (q) => {
  if (q.IsGet_MassGroupSetting_Dict) return undefined
  q.IsGet_MassGroupSetting_Dict = true;

  const setObj = (data) => {
    q.MassGroupSetting_Dict = data
  }

  var host = pmDataVM();
  if (host.MassGroupSetting_Dict){
    setObj(host.MassGroupSetting_Dict)
  }
  genUtil.Add_2(
    host.OnSet_MassGroupSetting_Dict_Func,
    () => setObj(host.MassGroupSetting_Dict),
    false
  )
}