import { url } from './MassDetailSetting_Url'
import * as dataUtil from './MassDetailSettingDataUtil'
import { MassDetailSetting } from './MassDetailSetting';
import * as api from '../../../../../util/apiUtil'
import { PMData } from '../../../single/model/PMData';

export const pmData = () => PMData.Instance;

// Instance
export const GetFromDb = (data, dict) => {
  if (!dict){
    dict = pmData().MassDetailSetting_Dict
  }

  var qI = new MassDetailSetting();

  qI._id = data._id;
  qI.Name = data.name;
  qI.Description = data.description;
  qI.SettingType = data.settingType;
  qI.Data = dataUtil.GetFromDb(data.data);
  qI.Dict = dict;

  return qI;
}

export const getAll = (dict, handle) => {
  api.get(`${url}/list`).then(response => response.data)
    .then(data => {
      if (!data) return undefined
      var items = data.map(x => GetFromDb(x, dict))
      if (handle) {
        handle(items);
      }
    });
}

export const GetDefault = () => {
  var qI = new MassDetailSetting();

  qI.Dict = pmData().MassDetailSetting_Dict
  qI.Data = dataUtil.GetDefault();

  return qI;
}

export const Clone = (q) => {
  var qI = new MassDetailSetting();

  qI.Name = q.Name;
  qI.Description = q.Description;
  qI.SettingType = q.SettingType;
  qI.Data = dataUtil.Clone(q.Data);
  qI.Dict = q.Dict;

  return qI;
}