import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { MassGroupSetting_DictVM } from '../dict/MassGroupSetting_DictVM';
import './Table.css'
import TableItemView from './TableItemView';
import TabView from '../../../tab/TabView';

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassGroupSetting_DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <table className='table table-bordered'>
          <thead className='mgs__thead'>
            <tr>
              <th className='mgs__table__name'>Name</th>
              <th className='mgs__table__description'>Description</th>
            </tr>
          </thead>
          <tbody>
            {data.Items?.map(x => {
              return <TableItemView key={x.id} store={x} />
            })}
          </tbody>
        </table>
      </>
    );
  }
}

export default observer(TableView);
