export const m = 'm'
export const m2 = 'm2'
export const m3 = 'm3'
export const kg = 'kg'
export const each = 'each'

export const GetList = () => {
  return [m, m2, m3, kg, each]
}

export const GetName = (v) => {
  var name

  switch (v) {
    case each:
      name = 'Số lượng (cái)'
      break;
    case m:
      name = 'Chiều dài (m)'
      break;
    case m2:
      name = 'Diện tích (m2)'
      break;
    case m3:
      name = 'Thể tích (m3)'
      break;
    case kg:
      name = 'Khối lượng (kg)'
      break;
  }

  return name;
}