import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import './Admin.css'
import { AdminVM } from './AdminVM';
import { PMDataVM } from '../../../single/form/PMDataVM';
import * as comUtil from '../../../../../util/commandUtil'
import * as ct from '../../command/Command'

const pmDataVM = () => PMDataVM.Instance

class AdminView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = pmDataVM().Admin
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='admin__wrapper'>
          {comUtil.Validate(ct.admin) ?
            <>
              <div>
                {data.UserManager_Dict.MainView}
              </div>
              <div className='notfirst'>
                {data.ProjectCommand_Dict.MainView}
              </div>
              <div className='notfirst'>
                {data.ProjectGroupCommand_Dict.MainView}
              </div>
            </>
            : pmDataVM().UnAuthorized_View}
        </div>
      </>
    );
  }
}

export default observer(AdminView);
