import { Component } from "react";
import { observer } from 'mobx-react';
import { CSVLink } from "react-csv";
import { CSVExport } from "./CSVExport";

class CSVExportView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new CSVExport();
    } else {
      this.state = store;
    }
  }

  Test = (e) => {
    debugger
  }

  render() {
    var data = this.state;

    return (
      <>
        <CSVLink data={data.Data} filename={data.FileName}
          headers={data.Headers}
          onClick={this.Test}>
          {data.Title}
        </CSVLink>
      </>
    )
  }
}

export default observer(CSVExportView)