import { MassGroupSetting_Dict } from "./MassGroupSetting_Dict"
import * as iUtil from '../item/MassGroupSettingUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import * as mgcUtil from '../../../mass/table/rowgroup/MassGroupComponentUtil'
import * as saveUtil from '../save/SaveUtil'
import { PMData } from "../../../../single/model/PMData";
import * as mgsd_ut from './MGS_Dict_UseType'
import * as mgsd_c from './MGS_Dict_Category'

export const pmData = () => PMData.Instance

// Instance
export const get = (useType, dict) => {
  var qI = new MassGroupSetting_Dict();

  qI.UseType = useType
  qI.Dict = dict

  return qI;
}

// Property
export const ReHandle_Items = (q) => {
  q.IsGet_Items = false;
  Handle_Items(q);
}

export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    // Ưu tiên data có projectId
    var isValid = true;

    if (q.UseType === mgsd_ut.project) {
      var pId = pmData().CurrentProject?._id;
      if (pId) {
        if (data.length !== 0) {
          isValid = data[0].projectId === pId
        } else {
          var oldData = q.Items;
          if (oldData && oldData.length !== 0) {
            isValid = oldData[0].projectId !== pId
          }
        }
      }
    }

    if (isValid) {
      q.Items = data.map(x => iUtil.GetFromDb(x, q))
      q.IsGet_Items = false;
    }
  }

  const getApi = () => {
    var apiData = {
      projectId: q.Project?._id,
      useType: q.UseType
    }

    iUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }

  getApi()
}

export const GetSave = (q) => {
  return saveUtil.get(q);
}

export const GetCategory = (q) => {
  switch (q.UseType) {
    case mgsd_ut.project:
      return mgsd_c.main;
    case mgsd_ut.template:
      return mgsd_c.template;
  }
}

export const GetCategoryName = (q) => {
  switch (q.Category) {
    case mgsd_c.main:
      return 'Main'
    case mgsd_c.template:
      return 'Template'
  }
}

