import * as genUtil from '../../../../util/generalUtil'
import { TrackingItem } from './TrackingItem';

export class Tracking{
  Items = []

  AddItem = (values) => {
    var list = this.Items;
    var item = undefined;
    var index = list.findIndex(x => genUtil.IsSameValues(x.Values, values));
    if (index === -1){
      item = new TrackingItem();
      item.Values = values;
      list.push(item);
    } else {
      item = list[index];
    }

    item.Count ++;
    return item;
  }
}