import { DO_Element } from './DO_Element';
import * as et from './DO_ElementType'

export const get = (type, title) => {
  var qI = new DO_Element();
  qI.Title = title ? title : type;

  switch (type) {
    case et.element:
      qI.SubItems = [
        get('guid'),
        get('projectId'),
        get('exportSetId'),
        get(et.elementinfo, 'info')
      ]
      break;
    case et.project:
      qI.SubItems = []
      break;
    case et.elementinfo:
      qI.SubItems = [
        get(et.elementinfo_identify, 'identify'),
        get(et.elementinfo_locate, 'locate'),
        get(et.elementinfo_geometry, 'geometry'),
        get(et.elementinfo_specification, 'specification'),
        get(et.elementinfo_material, 'material'),
      ]
      break;
    case et.elementinfo_identify:
      qI.SubItems = [
        get('category'),
        get('typename'),
        get('elementName'),
        get('typemark'),
        get('typecomment'),
        get('description'),
        get('model'),
        get('manufacturer'),
        get('keynote'),
        get('assemblycode'),
        get('systemtype'),
        get('systemclassification'),
        get('workItem'),
      ];
      break;
    case et.elementinfo_locate:
      qI.SubItems = [
        get('level'),
        get('baseLevel'),
        get('topLevel'),
        get('block'),
        get('zone'),
      ];
      break;
    default:
      qI.SubItems = [];
      break;
  }

  return qI;
}