import { MI_ValueItem } from "./MI_ValueItem"

// Instance
export const GetFromDb = (data, dict) => {
  var qI = new MI_ValueItem();

  qI.Value = data.value
  qI.Unit = data.unit
  qI.Dict = dict;

  return qI;
}

export const Clone = (q, dict) => {
  if (!dict){
    dict = q.Dict;
  }

  var qI = new MI_ValueItem();

  qI.Value = q.Value
  qI.Unit = q.Unit
  qI.Dict = dict;

  return qI;
}
