import { makeObservable, observable, action } from 'mobx';
import { message } from 'antd';
import * as genUtil from '../../../../../../util/generalUtil'
import * as comUtil from '../../../../../../util/commandUtil'
import * as util from './MassGroupSettingVMUtil'
import * as rs from '../../../../../text/TextRenderState';
import * as inputUtil from '../../../../../../util/inputUtil';
import * as editTextUtil from '../../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../../text/EditableTextView';
import './MassGroupSetting.css'
import { PMDataVM } from '../../../../single/form/PMDataVM';
import * as mgsd_ut from '../dict/MGS_Dict_UseType'
import * as mgsdd_ut from '../dict/dict/MGS_Dict_Dict_UseType'
import * as pct from '../../../command/ProjectCommand'

export const pmDataVM = () => PMDataVM.Instance

export class MassGroupSettingVM {
  constructor() {
    makeObservable(this, {
      isActive: observable,
      dd_UseType: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  get projectId() {
    return this.ModelItem.projectId;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    this.NameVM.RenderState = v;
    this.DescriptionVM.RenderState = v;

    if (v === rs._default) {
      this.SaveDatabase(false);
    }
  }

  Edit = () => {
    if (this.Commands.includes(pct.edit)) {
      if (this.RenderState === rs._default) {
        this.RenderState = rs.edit
        this.Hide_Tool();
      }
    }
  }

  // Name
  get Name() {
    return this.ModelItem.Name;
  } set Name(v) {
    this.ModelItem.Name = v;
  }

  get Name_ForPick() {
    return `${this.Name}${this.D_UseType === mgsd_ut.template ? ' (from template)' : ''}`
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      var obj = this.nameVM = editTextUtil.get(this.Name, this.CanEdit);

      obj.Is_Tool_Visible = false
      obj.OnKeyDown = (e) => {
        var { key, value } = inputUtil.Get_Shortcut(e);
        switch (key) {
          case inputUtil.enter:
            this.RenderState = rs._default
            break;
        }
      }

      genUtil.Add(
        obj.OnSet_RenderState_Func_Dict,
        () => {
          if (obj.RenderState === rs._default) {
            this.Name = obj.Content;
          }
        },
        false
      )

    }
    return this.nameVM;
  }

  get NameView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.NameVM} />
  }

  get NameWrapper_View() {
    return <div className='mgs__cell__wrapper'>
      {this.NameView}
      {this.DD_UseType === mgsdd_ut.query ?
        this.ActiveTool.View : 
        (this.Commands.includes(pct.edit) ? this.SaveTable.View : '')}
    </div>
  }

  // Description
  get Description() {
    return this.ModelItem.Description;
  } set Description(v) {
    this.ModelItem.Description = v;
  }

  descriptionVM
  get DescriptionVM() {
    if (!this.descriptionVM) {
      var obj = this.descriptionVM = editTextUtil.get(this.Description, this.CanEdit);

      obj.Is_Tool_Visible = false
      obj.OnKeyDown = (e) => {
        var { key, value } = inputUtil.Get_Shortcut(e);
        switch (key) {
          case inputUtil.enter:
            this.RenderState = rs._default
            break;
        }
      }

      genUtil.Add(
        obj.OnSet_RenderState_Func_Dict,
        () => {
          if (obj.RenderState === rs._default) {
            this.Description = obj.Content;
          }
        },
        false
      )

    }
    return this.descriptionVM;
  }

  get DescriptionView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.DescriptionVM} />
  }

  get DescriptionWrapper_View() {
    return <div className='mgs__cell__wrapper'>
      {this.DescriptionView}
      {/* {this.Commands.includes(pct.edit)  */}
      {comUtil.Validate(pct.edit)
      && this.DD_UseType === mgsdd_ut.query ?
        (this.D_UseType === mgsd_ut.project ?
          this.AddTemplate.View : this.DeleteVM.View) : ''}
    </div>
  }

  // AddTemplate
  addTemplate
  get AddTemplate() {
    if (!this.addTemplate) {
      this.addTemplate = util.GetAddTemplate(this)
    } return this.addTemplate
  }

  // Active
  activeTool
  get ActiveTool() {
    if (!this.activeTool) {
      this.activeTool = util.GetActiveTool(this)
    } return this.activeTool
  }

  isActive = false
  get IsActive() {
    return this.isActive
  } set IsActive(v) {
    this.isActive = v;
  }

  get ActiveStyle() {
    return this.DD_UseType === mgsdd_ut.query &&
      this.IsActive ? 'active' : ''
  }

  Set_Active = () => {
    var q1 = pmDataVM().Mgs_Dict_Dict
    if (q1.Current_MGS) {
      q1.Current_MGS.IsActive = false
    }

    q1.Current_MGS = this;
    this.IsActive = true;
  }

  UnSet_Active = () => {
    pmDataVM().Mgs_Dict_Dict.Current_MGS = undefined;
    this.IsActive = false;
  }

  // Save Table
  get D_UseType() {
    return this.Dict.UseType;
  }

  dd_UseType = undefined
  get DD_UseType() {
    if (!this.dd_UseType) {
      this.dd_UseType = util.GetDD_UseType(this)
    }
    return this.dd_UseType;
  } set DD_UseType(v) {
    this.dd_UseType = v;
  }

  saveTable
  get SaveTable() {
    if (!this.saveTable) {
      this.saveTable = util.GetSaveTable(this)
    } return this.saveTable
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    switch (this.DD_UseType) {
      case mgsdd_ut.query:
        {
          this.ActiveTool.Show();

          switch (this.D_UseType) {
            case mgsd_ut.project:
              this.AddTemplate.Show();
              break;
            case mgsd_ut.template:
              this.DeleteVM.Show();
              break;
          }
        }
        break;
      case mgsdd_ut.save:
        this.SaveTable.Show();
        break;
    }
  }

  Hide_Tool() {
    switch (this.DD_UseType) {
      case mgsdd_ut.query:
        {
          this.ActiveTool.Hide();

          switch (this.D_UseType) {
            case mgsd_ut.project:
              this.AddTemplate.Hide();
              break;
            case mgsd_ut.template:
              this.DeleteVM.Hide();
              break;
          }
        }
        break;
      case mgsdd_ut.save:
        this.SaveTable.Hide();
        break;
    }
  }

  // Row
  get RowView() {
    return <tr className={`mgs__row ${this.ActiveStyle}`}
      onDoubleClick={this.Edit}
      onMouseEnter={this.OnMouseEnter}
      onMouseLeave={this.OnMouseLeave}>
      <td>{this.NameWrapper_View}</td>
      <td>{this.DescriptionWrapper_View}</td>
    </tr>
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle, false);
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  get Commands() {
    return pmDataVM().Commands;
  }
}