import { MEP_ItemVM } from "./MEP_ItemVM"
import * as genUtil from '../../../../../../util/generalUtil'

// Instance
export const get = (q, dict) => {
    var qI = new MEP_ItemVM();
  
    qI.ModelItem = q;
    qI.Dict = dict;
  
    return qI;
  }

  // Property
export const Handle_Value_StorageList = (q) => {
    if (q.IsGet_Value_StorageList) return undefined;
    q.IsGet_Value_StorageList = true;
  
    var project = q.Project.ModelItem
  
    var setObj = (massTypeDict) => {
      q.Value_StorageList = massTypeDict.Items.map(x => x.Name);
    }
  
    if (project.massTypeDict){
      setObj(project.massTypeDict)
    } else {
      genUtil.Add_2(
      project.OnSet_massTypeDict_Func_Dict,
      () => {
        setObj(project.massTypeDict)
      }, false);
    }
}