//import * as dataVMUtil from './TypicalLevelSettingDataVMUtil'
import * as dataVMUtil from './wrapper/TLS_Data_WrapperVMUtil'
import { TypicalLevelSettingVM } from './TypicalLevelSettingVM'
import * as fieldUtil from '../../mass/item/MassItemField'

export const get = (q, project) => {
  if (!q) return undefined;
  
  var qI = new TypicalLevelSettingVM();

  qI.ModelItem = q;
  qI.Project = project;

  // var level_Dict = project.Level_Dict;
  // const getLevelsFunc = () => { 
  //   qI.LevelStorageList = level_Dict.Items.map(x => x.Name);
  //   qI.GetLevelStorageList = true;
  // }
  // if (level_Dict.Items){
  //   getLevelsFunc();
  // } else{
  //   level_Dict.OnGetItemsDone_Funcs.push(getLevelsFunc);
  // }

  q.OnNameChanged = (v) => {
    if (q.M2VM){
      qI.Name = v;
    }
  }

  q.OnDescriptionChanged = (v) => {
    if (q.M2VM){
      qI.Description = v;
    }
  }

  q.OnDataChanged = (v) => {
    if (q.M2VM){
      qI.Data = v;
      qI.DataVM = qI.GetDataVM();
    }
  }

  qI.OnGetDataVM = () => {
    var data = q.Data;
    return dataVMUtil.get(data, qI);
  }    

  return qI;
}

export const GetFieldFilterStorageList = () => {
  return fieldUtil.GetListForTypicalLevelFilter();
}