import * as mif from '../item/MassItemField'
import { EntFilter } from '../../filter/EntFilter'
import * as fvt from '../../filter/FilterValueType'
import * as sft from '../../filter/StringFilterType'
import * as mt from '../MassType'
import MassGroupComponent from './rowgroup/MassGroupComponent'
import MassGroupItem from './rowgroup/MassGroupItem'
import * as mgiUtil from './rowgroup/MassGroupItemUtil'
import * as genUtil from '../../../../../util/generalUtil'
import * as commandDictUtil from './command/CommandDictUtil'

export const GetMainMGC = (q) => {
  var qI = new MassGroupComponent();

  var mgi1 = mgiUtil.get(mif.MassType);
  qI.Items = [mgi1];

  return qI;
}

export const GetMassGroupName = (q) => {
  return q.MassGroupSetting ? q.MassGroupSetting.Name
    : '';
}

export const GetAllRows = (q) => {
  var list = [];
  q.MainRows.forEach(x => {
    list.push.apply(list, x.AllItems)
  });
  return list;
}

export const Handle_MainMGI = (q) => {
  if (q.IsGet_MainMGI) return undefined
  q.IsGet_MainMGI = true;

  const setObj = (data) => {
    var qI = data
    qI.Table = q
    q.MainMGI = qI
  }

  var host = q.Mgs_Dict_Dict
  if (host.Used_MassGroupItem){
    setObj(host.Used_MassGroupItem)
  } else {
    genUtil.Add_2(
      host.OnSet_Used_MassGroupItem_Func,
      () => setObj(host.Used_MassGroupItem)
    )
  }
}

// backup
export const GetCommandDict = (q) => {
  return commandDictUtil.get(q)
}