import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import MassRow_ToolElement from "./MassRow_ToolElement";
import { Button } from "antd";
import { DownCircleOutlined, ReloadOutlined, UpCircleOutlined } from "@ant-design/icons";
import * as trs from '../../../../../text/TextRenderState';


class MassRow_ToolElementView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassRow_ToolElement();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.Editable_TitleVM_RenderState === trs._default ?
          <Button size='small' type='primary'
            className='btn-warning'
            onClick={() => data.Editable_TitleVM_RenderState = trs.edit}>
            e
          </Button> :
          ''}

        <Button size='small' type='primary'
          className='btn-success'
          onClick={data.Method('MoveUp')}>
          <UpCircleOutlined />
        </Button>
        <Button size='small' type='primary'
          className='btn-success'
          onClick={data.Method('MoveDown')}>
          <DownCircleOutlined />
        </Button>

        <Button type='primary' size='small'
          className='btn-warning'
          onClick={data.Method('OpenModal')}>...</Button>

        <Button size='small' type='primary'
          className='btn-success'
          onClick={data.Method('AddSub')}>+</Button>
        <Button type='primary' size='small' danger
          onClick={data.Method('Delete')}>x</Button>
      </>
    )
  }
}

export default observer(MassRow_ToolElementView)