import { PMDataVM } from "../model/main/single/form/PMDataVM"

const pmDataVM = () => PMDataVM.Instance

export const Validate = (commands) => {
  if (!commands) return false;
  var list = pmDataVM().Commands;
  if (ValidateInGroup(commands, list)) return true;
  return ValidateInGroup(commands, pmDataVM().AdminCommands)
}

export const ValidateInGroup = (commands, list) => {
  var data = list;
  if (!data) return false;
  if (typeof commands === 'string') return data.includes(commands);

  var isValid = false
  commands.forEach(x => {
    if (!isValid){
      isValid = data.includes(x)
    }
  })
  return isValid;
}