import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './ItemVMUtil'
import * as rs from '../../../../../text/TextRenderState';
import './Item.css'
import SelectView from '../../../../../select/SelectView';
import TextView from '../../../../../text/TextView';
import MultiSelectView from '../../../../../select/MultiSelectView';
import { Button, message } from 'antd';

export class ItemVM {
  constructor() {
    makeObservable(this, {
      renderState: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  renderState = rs.edit
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
  }

  Edit = () => {
    if (this.RenderState === rs._default) {
      this.RenderState = rs.edit
      this.Hide_Tool();
    }
  }

  // User
  get User() {
    return this.ModelItem.User;
  } set User(v) {
    this.ModelItem.User = v;
    this.User_Render.Content = v?.Username;
  }

  user_Render
  get User_Render() {
    if (!this.user_Render) {
      this.user_Render = util.GetUser_Render(this)
    } return this.user_Render
  }

  user_Select
  get User_Select() {
    if (!this.user_Select) {
      this.user_Select = util.GetUser_Select(this)
    } return this.user_Select
  }

  get User_View() {
    switch (this.RenderState) {
      case rs._default:
        return <TextView store={this.User_Render} />
      case rs.edit:
        return <SelectView store={this.User_Select} />
    }
  }

  // Commands
  get Commands() {
    return this.ModelItem.Commands
  } set Commands(v) {
    this.ModelItem.Commands = v;
  }

  command_Select
  get Command_Select() {
    if (!this.command_Select) {
      this.command_Select = util.GetCommand_Select(this)
    } return this.command_Select
  }

  get Command_View() {
    return <MultiSelectView store={this.Command_Select} />
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    this.DeleteVM.Show();
  }

  Hide_Tool() {
    this.DeleteVM.Hide();
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle, false);
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  get RowView() {
    return <tr>
      <td>
        <div className='f-ac admin__pgc__user'>
          {this.User_View}
          <Button type='primary' size='small'
            className='btn-danger'
            onClick={this.Delete} >x</Button>
        </div>
      </td>
      <td>
        <div className='admin__pgc__command'>
          {this.Command_View}
        </div>
      </td>
    </tr>
  }
}