import * as genUtil from '../../../../util/generalUtil'
import { PMData } from '../model/PMData';

import * as project_DictVMUtil from '../../entity/project/Project_DictVMUtil'
import * as massResultCompVMUtil from '../../entity/mass/result/MassResultCompVMUtil'

import * as mgsddUtil from '../../entity/setting/massGroup/dict/dict/MGS_Dict_DictVMUtil'
import * as mgsdUtil from '../../entity/setting/massGroup/dict/MassGroupSetting_DictVMUtil'
import * as mgsd_ut from '../../entity/setting/massGroup/dict/MGS_Dict_UseType'

import * as userDictUtil from '../../../user/dict/User_DictVMUtil'
import * as adminUtil from '../../entity/admin/main/AdminVMUtil'

export const pmData = () => PMData.Instance;

export const GetProject_DictVM = (data) => {
  return project_DictVMUtil.get
    (pmData().Project_Dict);
}

export const GetMassResultCompVM = (data) => {
  return massResultCompVMUtil.get
    (pmData().MassResultComp);
}

export const GetMgs_Dict_Dict = (data) => {
  return mgsddUtil.get(pmData().Mgs_Dict_Dict);
}

export const GetMassGroupSetting_Dict = (data) => {
  return genUtil.SingleFilter
    (data.Mgs_Dict_Dict.Items, (x) => x.UseType === mgsd_ut.project);
}

// UserDict
export const GetUserDict = (data) => {
  var qI = userDictUtil.get(pmData().UserDict, data);

  qI.ViewRender = () => <div className='ud__wrapper'>
    <div className='ud__dec f'>
      <div className='f1' />
      <div>
        <div className='ud__main__wrapper'>
          {qI.MainView}
        </div>
        {qI.ModalView}
      </div>
    </div>
  </div>

  return qI;
}

export const GetAdmin = (data) => {
  return adminUtil.get(pmData().Admin, data);
}

// Commands
export const Handle_Commands = (q) => {
  if (q.IsGet_Commands) return undefined;
  q.IsGet_Commands = true;

  const setObj = (data) => {
    q.Commands = data
    q.IsGet_Commands = false;
  }

  var host = pmData()
  if (host.Commands) {
    setObj(host.Commands)
  }

  genUtil.Add_2(
    host.OnSet_Commands_Func,
    () => setObj(host.Commands),
    false
  )
}

export const Handle_AdminCommands = (q) => {
  if (q.IsGet_AdminCommands) return undefined;
  q.IsGet_AdminCommands = true;

  const setObj = (data) => {
    q.AdminCommands = data
    q.IsGet_AdminCommands = false;
  }

  var host = pmData()
  if (host.AdminCommands) {
    setObj(host.AdminCommands)
  }

  genUtil.Add_2(
    host.OnSet_AdminCommands_Func,
    () => setObj(host.AdminCommands),
    false
  )
}