import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Popover, Switch } from 'antd';
import { TLS_FilterValueVM } from './TLS_FilterValueVM';
import NewInputView from '../../../../../input/NewInputView';
import './TLS_FilterValue.css'
import * as pct from '../../../command/ProjectCommand'

class TLS_FilterValueView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new TLS_FilterValueVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='tls_filter__value'>
          {/* Input */}
          {data.Commands.includes(pct.edit) ?
            <>
              <Popover
                trigger={data.PopoverState.Content}
                content={
                  <Switch unCheckedChildren='inp' checkedChildren='sel'
                    checked={data.SwitchChecked_Funcs()} onChange={data.ToggleInputType} />
                }>
                <span>
                  {data.InputElement}
                </span>
              </Popover>


              {/* Delete */}
              <Button type='primary' size='small' onClick={data.Delete}
                className='tls_filter__value__delete'>x</Button>
            </>
            : data.Value}
        </div>

      </>
    );
  }
}

export default observer(TLS_FilterValueView);
