import * as genUtil from '../../../../../../../util/generalUtil'
import { MGS_Dict_Dict } from './MGS_Dict_Dict'
import * as mgsdUtil from '../MassGroupSetting_DictUtil'
import * as mgsd_ut from '../MGS_Dict_UseType'
import * as mgcUtil from 
  '../../../../mass/table/rowgroup/MassGroupComponentUtil'

// Instance
export const get = () => {
  return new MGS_Dict_Dict();
}

// Property
export const GetItems = (q) => {
  return [
    mgsdUtil.get(mgsd_ut.project, q),
    mgsdUtil.get(mgsd_ut.template, q),
  ]
}

export const Handle_Used_MassGroupComponent = (q) => {
  if (q.IsGet_Used_MassGroupComponent) return undefined
  q.IsGet_Used_MassGroupComponent = true

  const setObj = (data) => {
    q.Used_MassGroupComponent = data ?
      mgcUtil.Clone(data.MainMassGroupComponent)
      : mgcUtil.GetDefault()
    q.IsGet_Used_MassGroupComponent = false;
  }

  var host = q.Current_MGS
  if (host) {
    if (host.Data) {
      setObj(host.Data)
    } else {
      genUtil.Add_2(
        host.OnSet_Data_Func,
        () => setObj(host.Data)
      )
    }
  } else {
    setObj()
  }
}

export const Handle_Used_MassGroupItem = (q) => {
  if (q.IsGet_Used_MassGroupItem) return undefined
  q.IsGet_Used_MassGroupItem = true

  const setObj = (data) => {
    q.Used_MassGroupItem = data.Items[0]
    q.IsGet_Used_MassGroupItem = false
  }

  var host = q;
  if (host.Used_MassGroupComponent) {
    setObj(host.Used_MassGroupComponent)
  } else {
    genUtil.Add_2(
      host.OnSet_Used_MassGroupComponent_Func,
      () => setObj(host.Used_MassGroupComponent)
    )
  }
}