import * as api from '../../../../../util/apiUtil'
import { url } from './ExportSet_Url'
import { url as versionUrl } from './ExportSetVersion_Url'
import { url as massUrl } from '../../mass/item/MassItemUrl'
import { ExportSet } from './ExportSet'
import { message } from 'antd'
import * as massItemUtil from '../../mass/item/MassItemUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = new ExportSet();

  qI._id = data._id
  qI.projectId = data.projectId
  qI.Name = data.name;
  qI.Description = data.description
  qI.MassType = data.massType
  qI.Dict = dict;

  return qI;
}

export const get = (dict) => {
  var qI = new ExportSet();

  qI.Dict = dict;

  return qI;
}

// Property
export const Handle_MassItems = (q) => {
  if (q.IsGet_MassItems) return undefined;
  q.IsGet_MassItems = true;

  const setObj = (data) => {
    q.MassItems = data ? 
      data.map(x => massItemUtil.get(x)) : []
    q.IsGet_MassItems = false
  }

  if (q._id) {
    var apiData = {
      otherData: {
        exportSetId: q._id
      }
    }

    massItemUtil.GetData(apiData, (data) => {
      setObj(data)
    });
  } else {
    setObj();
  }
}

// Method
export const NewVersion = (q, handle) => {
  var apiData = {
    exportSetId: q._id
  }

  const apiFunc = () => api.post(`${versionUrl}/add`, apiData);
  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}

export const SaveMassItems = (q) => {
  var data = q.MassItems;

  const addDataFunc = (versionId) => {
    var apiData = {
      projectId: q.Project._id,
      exportSetId: q._id,
      exportSetVersionId: versionId,
      items: data.map(x => x.ApiData_ForInput)
    }

    const apiFunc = () => api.post(`${massUrl}/addlist`, apiData);
    apiFunc().then(response => response.data)
      .then(data => {
        message.success('import data successfully!')
      })
  }

  q.NewVersion((v) => {
    addDataFunc(v._id);
  })
}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}