import { Button, Popover } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import CheckboxView from '../../../../../checkbox/CheckboxView';
import * as checkBoxUtil from '../../../../../checkbox/CheckboxVMUtil';
import * as multiSelectUtil from "../../../../../select/MultiSelectVMUtil";
import * as textUtil from "../../../../../text/TextVMUtil";
import { PMDataVM } from '../../../../single/form/PMDataVM';
import { text } from '../pageItem/TLS_PageItemStateType';
import * as util from './LevelGroupVMUtil'
import * as pct from '../../../command/ProjectCommand'

const pmDataVM = () => PMDataVM.Instance

export class LevelGroupVM {
  constructor() {
    makeObservable(this, {
      primary: observable,
      isSelectOpen: observable,
    })
  }

  get id() {
    return this.ModelItem.id;
  }

   // Level StorageList
   levelStorageList
   get LevelStorageList(){
     if (!this.levelStorageList){
       this.levelStorageList = util.GetLevelStorageList(this);
     }
     return this.levelStorageList;
   } set LevelStorageList(v){
     this.levelStorageList = v;

     var selectVM = this.LevelSelectVM;
     selectVM.Items = v;
     selectVM.InitialItems = this.Levels;
   }

  get Levels() {
    return this.ModelItem.Levels;
  } set Levels(v) {
    this.ModelItem.Levels = v;
  }

  get SettingVM() {
    return this.Dict.SettingVM;
  }

  levelSelectVM
  get LevelSelectVM() {
    if (!this.levelSelectVM) {
      var list = this.LevelStorageList;
      if (!list) {
        this.SettingVM.ModelItem.OnGetLevelStorageList_Funcs.push(() => {
          var obj = this.levelSelectVM;
          obj.Items = this.LevelStorageList;
          obj.InitialItems = this.Levels;
        })
      }

      list = list ? list : []
      var obj = this.levelSelectVM = multiSelectUtil.get(list, this.Levels);
      obj.Placeholder = 'chọn level ...';
      obj.GetDisabled_Func = () => !this.Commands.includes(pct.edit)

      obj.TitleRender = (item) => {
        const getTriggerContent = () => {
          return this.IsSelectOpen || this.Primary === item ? 'click' : 'hover';
        }
        var triggerVM = textUtil.get(getTriggerContent())

        const getButtonType = () => {
          return this.Primary === item ? 'primary' : 'dashed'
        }
        var buttonTypeVM = textUtil.get(getButtonType())

        const getAddContent = () => {
          return this.Primary === item ? '' : 'Set'
        }
        var addContentVM = textUtil.get(getAddContent())

        const getItemCss = () => {
          return this.Primary === item ? 'levelgroup__primary' : '';
        }

        return <Popover trigger={triggerVM.Content}
          content={
            <span>
              {this.Primary !== item ?
                <span className='levelgroup__addtext'>{addContentVM.Content}</span>
                : ''}
              <Button size='small'
                type={buttonTypeVM.Content}
                onClick={() => {
                  this.Primary = item;
                }}>Primary</Button>
            </span>
          }>
          <span className={getItemCss()}>{item}</span>
        </Popover>
      }

      obj.OnSetOpen = (v) => this.IsSelectOpen = v;
      obj.OnSelectedItemChanged = (v) => {
        this.Levels = v;
        if (this.Levels.indexOf(this.Primary) === -1) {
          this.Primary = util.GetDefaultPrimary(this);
        }
      }
    }
    return this.levelSelectVM;
  }

  get Index() {
    return this.Dict.LevelGroups.indexOf(this);
  }

  Delete = () => {
    this.ModelItem.Delete();
    var list = this.Dict.LevelGroups;
    list.splice(this.Index, 1);

    this.Primary = util.GetDefaultPrimary(this);
  }

  primary = undefined
  get Primary() {
    if (!this.primary) {
      this.primary = this.ModelItem.Primary;
    }
    return this.primary
  } set Primary(v) {
    this.primary = this.ModelItem.Primary = v;
  }

  isSelectOpen = undefined
  get IsSelectOpen() {
    return this.isSelectOpen
  } set IsSelectOpen(v) {
    this.isSelectOpen = v;
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }
}