import * as api from '../../../../../util/apiUtil'
import { PMData } from '../../../single/model/PMData';
import MassItem from './MassItem';
import * as elementUtil from '../../element/ElementUtil'
import * as mt from '../MassType'
import * as ut from '../../value/ValueUnit'
import * as mif from './MassItemField'
import * as genUtil from '../../../../../util/generalUtil'
import * as phaseItemUtil from './phaseValue/MIPh_ItemUtil'
import * as levelItemUtil from './levelValue/Level_ItemUtil'
import * as projectUtil from '../../project/ProjectUtil'
import * as phaseUtil from '../../phase/PhaseUtil'
import * as valueItemUtil from './value/MI_ValueItemUtil'
import * as siteUtil from '../site/SiteUtil'
import { url as resultUrl } from './MassItemResult_Url'
import { MI_ValueDict } from './value/MI_ValueDict';

export const pmData = () => {
  return PMData.Instance;
}

// Instance
export const get = (q, isDetailView, phase) => {
  var qI = new MassItem();

  qI._id = q._id;

  // ValueData
  if (q.valueData) {
    qI.ValueDict.Data = q.valueData.map
      (x => valueItemUtil.GetFromDb(x, qI))
  } else {
    qI.ValueDict.Data = [
      valueItemUtil.GetFromDb(q, qI)
    ]
  }

  if (q.massType) {
    qI.MassType = q.massType;
  } else {
    qI.MassType = q.ExportSets && q.ExportSets.length !== 0 ?
      q.ExportSets[0].massType : undefined
  }

  qI.Info = q.info;
  qI.projectId = q.projectId;
  qI.workpackageId = q.workpackageId;
  qI.exportSetId = q.exportSetId;
  qI.exportSetVersionId = q.exportSetVersionId;
  qI.Element = q.Elements && q.Elements.length !== 0 ? q.Elements[0] : undefined;
  qI.Site = q.SiteInfos.length !== 0 ?
    siteUtil.GetFromDb(q.SiteInfos[0], qI) : undefined

  var project
  var projectData = q.Projects && q.Projects.length !== 0 ? q.Projects[0] : undefined;
  if (projectData) {
    var dict = pmData().Project_Dict_ForMassDetail
    // Cập nhập hoặc lọc thông tin dự án
    project = qI.Project = dict.Get_Project(projectData);
  }

  if (isDetailView) {
    qI.IsDetailView = true;

    if (phase) {
      qI.IsHavePhase = true;
      var dict = pmData().Phase_Dict_ForMassDetail
      var phase = dict.Get_Phase(phase)

      phase.Project = project;
      qI.Phases = [phase]
    }
  }

  return qI;
}

export const GetDefaultRebarMass = (project) => {
  var qI = new MassItem();

  qI.Unit = ut.kg;
  qI.MassType = mt.Rebar;
  qI.Info = {}
  qI.projectId = project._id;

  const setExportSetData = (exportSet) => {
    qI.exportSetId = exportSet._id;

    var currentVersion = exportSet.CurrentVersion;
    if (!currentVersion) {
      exportSet.OnGetCurrentVersionDone_Func.push(() => {
        currentVersion = exportSet.CurrentVersion;
        qI.exportSetVersionId = currentVersion._id;
      })
    } else {
      qI.exportSetVersionId = currentVersion._id;
    }
  }

  var inputExportSet = project.InputExportSet;
  if (!inputExportSet) {
    project.OnGetInputExportSetDone_Funcs
      .push(() => {
        inputExportSet = project.InputExportSet;
        setExportSetData(inputExportSet);
      })
  } else {
    setExportSetData(inputExportSet);
  }

  return qI;
}

export const GetProject = (q) => {
  var list = pmData().Project_Dict.Items;
  var { projectId } = q;
  var index = list.findIndex(x => x._id === projectId);
  return index !== -1 ? list[index] : undefined;
}

export const getAll = (apiData, handle) => {
  var url = "https://api.bimdev.vn/api/hbmr/newlist";

  //var start = window.performance.now();

  api.post(url, apiData, undefined, 2000000)
    .then(response => response.data)
    .then(data => {
      //var end = window.performance.now();
      //console.log(`Execution time: ${(end - start)/1000} s`);

      if (!data) {
        console.log('mass result undefined');
        return;
      }

      var items = data.map(x => {
        return get(x)
      })
      if (handle) {
        handle(items);
      }
    });
}

export const getRebars = (projectId, handle) => {
  if (!projectId) return;
  var url = `https://api.bimdev.vn/api/hbmr/rebar?projectId=${projectId}`;
  api.get(url).then(response => response.data)
    .then(data => {
      var items = undefined;
      if (!data) {
        items = [];
      } else {
        var items = data.map(x => get(x))
      }
      if (handle) {
        handle(items);
      }
    });
}

export const getByViewDetail = (_id, handle) => {
  var url = `https://api.bimdev.vn/api/hbdv/massdetail?id=${_id}`;
  api.get(url).then(response => response.data)
    .then(data => {
      var { viewDetail, masses, phase } = data;
      var items = masses.map(x => {
        // Viết ra _id của mass đang xét, để test
        // console.log(x._id); 

        return get(x, true, phase)
      });
      if (handle) {
        handle(items);
      }
    });
}

// Property
export const GetWorkpackage = (q) => {
  var list = pmData().Workpackage_Dict.Workpackages;
  var { workpackageId } = q;
  var index = list.findIndex(x => x._id === workpackageId);
  return index !== -1 ? list[index] : undefined;
}

export const GetPhases = (q) => {
  var dict = pmData().Phase_Dict;
  var list = [];
  if (!dict) return list;

  var phases = dict.Items;
  var exportSetVersionId = q.exportSetVersionId;
  phases.forEach(x => {
    if (x.exportSetVersionIds.includes(exportSetVersionId)) {
      list.push(x);
    }
  })
  return list;
}

export const GetValidPhasesByInput = (q) => {
  // Nếu ViewDetail thì lấy tất cả các Phase sẵn có
  if (q.IsDetailView) {
    return q.Phases;
  } else {
    var inputPhases = pmData().MassResultComp.Phases;
    var phases = q.Phases;
    var list = [];
    phases.forEach(x => {
      if (inputPhases.includes(x)) {
        list.push(x);
      }
    })
    return list;
  }
}

export const GetElementAsync = (q) => {
  var elemId = q.elementId
  if (elemId) {
    elementUtil.GetById(elemId, (data) => {
      q.Element = data
      q.IsGetElement = true;
      q.GetElementDone = true;
    });
  } else {
    q.IsGetElement = true;
  }
}

export const Handle_TLS_LevelGroup = (q) => {
  if (q.IsGet_TLS_LevelGroup) return undefined;
  q.IsGet_TLS_LevelGroup = true;

  var project = q.Project;

  const setObj = (data) => {
    q.TLS_LevelGroup = data;
    q.IsGet_TLS_LevelGroup = false
  }

  const getLevelGroup_Func = (tls) => {
    // Thiết lập tầng điển hình mới nằm trong data.Typical
    var data = tls.Data.Typical

    var pi = GetValidTLS_PageItem(q, data);
    var obj = GetValidTLS_LevelGroup(q, pi);
    return obj;
  }

  var tls = project.TypicalLevelSetting;
  if (project.GetTypicalLevelSetting || tls) {
    setObj(getLevelGroup_Func(tls));
  } else {
    genUtil.Add_2(
      project.OnGetTypicalLevelSetting_Func_Dict,
      () => setObj(getLevelGroup_Func(project.TypicalLevelSetting))
    )
  }
}

export const GetValidTLS_PageItem = (q, tls) => {
  if (!tls) return undefined;
  var validPi = undefined
  tls.PageItems.forEach(x => {
    if (!validPi && IsValidTLS_PageItem(q, x)) {
      validPi = x;
    }
  })

  return validPi;
}

export const IsValidTLS_PageItem = (q, pageItem) => {
  var filters = pageItem.Filters;
  var isValid = true;
  filters.forEach(x => {
    if (isValid) {
      var field = x.Field;
      var qVal = q.GetValue_Mas(field);
      isValid = x.Values.includes(qVal);
    }
  })
  return isValid;
}

export const GetValidTLS_LevelGroup = (q, pageItem) => {
  if (!pageItem) return undefined;
  var validLg = undefined
  pageItem.LevelGroups.forEach(x => {
    // New Version
    var primary_Values = x.Primary_Values
    var qVal = q.GetValue_Mas(mif.level);
    if (!validLg && primary_Values.includes(qVal)) {
      validLg = x;
    }
  })
  return validLg;
}

// ValueDict
export const GetValueDict = (q) => {
  var qI = new MI_ValueDict();
  qI.Dict = q;
  return qI;
}

// PhaseItem
export const GetPhaseItems = (q) => {
  return q.ValidPhasesByInput.map(x => phaseItemUtil.get(x, q));
}

export const Get_Lastest_PhaseItem = (q) => {
  if (q.IsGet_Lastest_PhaseItem) return undefined;
  q.IsGet_Lastest_PhaseItem = true;

  var list = q.PhaseItems;
  if (!list || list.length === 0) return undefined;

  if (list.length === 1) return list[0];

  list.sort((a, b) =>
    b.Phase.CreatedDate - a.Phase.CreatedDate);

  return list[0];
}

// LevelItem
export const GetLevelItem = (q) => {
  if (!pmData().CurrentProject) {
    return q.OriginLevelItem
  }

  if (q.IsDetailView && !q.IsHavePhase) {
    return q.OriginLevelItem
  }
  var phaseItem = q.Lastest_PhaseItem;
  return phaseItem ? phaseItem.LevelItem : q.OriginLevelItem
}

export const GetOriginLevelItem = (q) => {
  return levelItemUtil.get(q);
}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${resultUrl}/newlist`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}