import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './DictUtil'
import * as iUtil from '../item/ItemUtil'
import { message } from 'antd'

export class Dict {
  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Item
  OnSet_Items_Func = []

  items
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
    
    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = (v) => {
    this.Items.push(v);
    if (this.OnAddItem){
      this.OnAddItem(v)
    }
  }

  // Save
  Save = () => {
    var items = this.Items;
    var count = items.length
    if (count === 0){
      message.success('update data successfully!');
    } else {
      var i = -1;
      const func = () => {
        i++;
        if (i === count-1){
          message.success('update data successfully!');
        }
      }
      
      items.forEach(x => {
        x.SaveDatabase(false, func)
      })
    }
  }
}