import { makeObservable, observable, action } from 'mobx';
import * as util from './LevelSettingDataVMUtil'
import { ControlOutlined } from '@ant-design/icons';
import { Button, message } from 'antd';
import * as itemUtil from './item/LS_ItemVMUtil'
import * as rs from '../../../../text/TextRenderState';
import { PMDataVM } from '../../../single/form/PMDataVM';

const pmDataVM = () => PMDataVM.Instance

export class LevelSettingDataVM {
  constructor() {
    makeObservable(this, {
      items: observable,
    })
  }

  items = undefined
  get Items() {
    if (!this.items) {
      this.items = util.GetItems(this);
    }
    return this.items;
  } set Items(v){
    this.items = v;
  }

  get Project(){
    return this.Dict.Project
  }

  AddItem = () => {
    var item = itemUtil.get(this.ModelItem.AddItem(), this);

    item.NameVM.RenderState = rs.edit

    this.Items.push(item);
    this.Table.AddItem(item);
    return item;
  }

  // Table
  table
  get Table(){
    if (!this.table){
      this.table = util.GetTable(this);
    }
    return this.table;
  }

  get Project(){
    return this.Dict.Project
  }

  Update_LevelData = () => {
    this.ModelItem.Update_LevelData();
    message.success('save successfully!');
  }

  // Commands
  get Commands(){
    return pmDataVM().Commands;
  }
}