import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Row } from 'antd';
import { withRouter } from "react-router-dom";
import { MassDetailVM } from './MassDetailVM';
import MassDetailTableView from './MassDetailTableView';
import TableView from '../../../../table/TableView';
import { PMDataVM } from '../../../single/form/PMDataVM';
import './MassDetail.css'
import { SaveFilled } from '@ant-design/icons';
import SelectView from '../../../../select/SelectView';

const pmDataVM = () => PMDataVM.Instance;

class MassDetailView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassDetailVM();
    } else {
      this.state = store;
    }
  }

  componentDidMount() {
    var data = this.state;
    const id = this.props.match.params.id;

    data.id = id;

    // Set up Main Width
    pmDataVM().Main_Classname = 'massdetail__comp'
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='massdetail__setting__wrapper'>
          <b>Bộ thiết lập:</b>
          <SelectView className='massdetail__setting__select' store={data.SettingSelectVM_ForInput} />
          <Button type='primary' size='small'
            onClick={data.SettingOK}>ok</Button>
        </div>
        <div className="massdetail__title__wrapper">
          <h4 className="massdetail__title">
            BẢNG CHI TIẾT KHỐI LƯỢNG
            </h4>
          <div>
            <Button type='primary' size='small' style={{ marginLeft: 15 }}
              disabled={data.IsOnSaveSetting}
              onClick={data.SaveSetting}>
              <SaveFilled />
            </Button>
          </div>
        </div>

        {data.IsOnSaveSetting ?
          <div className='massdetail__groupname__wrapper'>
            <div className='massdetail__groupname__wrapper_dec'>
              <div className='massdetail__groupname__input'>
                {data.SettingInput_Element}
              </div>

              <div className='massdetail__groupname__button_wrapper'>
                <Button type='primary' size='small'
                  className={data.SettingInput_Toggle_Classname}
                  onClick={data.Toggle_SettingInput_State}>
                  {data.SettingInput_Toggle_Content}
                </Button>
                <Button type='primary' size='small'
                  onClick={data.SettingInputOK}>ok</Button>
                <Button type='default' size='small'
                  onClick={data.SettingInputCancel}>cancel</Button>

                {data.Override_Setting_ModalElement}
              </div>
            </div>
          </div>
          : ''}

        <MassDetailTableView store={data.DetailTable} />
      </>
    );
  }
}

export default withRouter(observer(MassDetailView));
