import Setting from "../base/Setting";
import * as util from './TypicalLevelSettingDataUtil'
import { reaction } from "mobx";
import { PMData } from "../../../single/model/PMData";
import * as pageItemUtil from './pageItem/TLS_PageItemUtil'

export const pmData = () => {
  return PMData.Instance;
}

export class TypicalLevelSettingData {
  // Setting
  get Setting() {
    return this.Dict;
  }

  get LevelStorageList() {
    return this.Dict.LevelStorageList;
  }
  
  // Page Item
  pageItems
  get PageItems(){
    if (!this.pageItems){
      this.pageItems = util.GetPageItems(this);
    }
    return this.pageItems;
  } set PageItems(v){
    this.pageItems = v;
  }

  get FieldFilterStorageList() {
    return this.Dict.FieldFilterStorageList;
  }

  AddPageItem = () => {
    var item = pageItemUtil.GetDefault(this);
    this.PageItems.push(item);
    item.Name = `setting ${item.Index +1}`
    return item;
  }

  get ApiData() {
    return {
      pageItems : this.PageItems.map(x => x.ApiData)
    }
  }
}