import * as genUtil from '../../../../../../util/generalUtil'
import * as it from './TLS_FilterValue_InputType'

export class TLS_FilterValue {
  value
  get Value(){
    return this.value;
  } set Value(v) {
    this.value = v;
  }
  
  InputType = it.select

  get ApiData(){
    var obj = {
      value: this.Value,
      inputType : this.InputType
    }

    return obj;
  }
}