import { makeObservable, observable, action } from 'mobx';
import TableColumn from './TableColumn';
import * as colUtil from './TableColumnUtil'
import * as renderUtil from './RenderItemUtil'
import * as rs from './RenderState'
import * as util from './TableVMUtil'
import * as textUtil from '../text/TextVMUtil'
import InputView from '../input/InputView'
import { Button } from 'antd';
import * as genUtil from '../../util/generalUtil'
import * as rowUtil from './TableRowUtil'
import * as cit from './TableColumn_InputType'

export default class TableVM {
  constructor() {
    makeObservable(this, {
      columns: observable,
      dataSource: observable,
      rows: observable,

      AllColumnNames: observable,
      Column_InputType: observable,

      CanHandleItem: observable,
      CanAddItem: observable,
      CanDeleteItem: observable,
      CanEditItem: observable,

      CanHandleItem: observable,
      CanAddItem: observable,
      CanDeleteItem: observable,
      CanEditItem: observable,
      IsShowActionColumn: observable,
    })
  }

  // Có thể xử lý dữ liệu 1 dòng (bao gồm Xóa, Thêm, Sửa)
  CanHandleItem = true

  // Thêm 1 dòng
  CanAddItem = true

  // Xóa 1 dòng
  CanDeleteItem = true

  // Dùng để chỉnh sửa thông tin 1 Dòng
  CanEditItem = true

  // Có thể xử lý dữ liệu 1 cột (bao gồm Xóa, Thêm, Sửa)
  CanHandleColumn = true

  // Thêm 1 cột
  CanAddColumn = true

  // Xóa 1 cột
  CanDeleteColumn = true

  // Chỉnh sửa thông tin 1 cột
  CanEditColumn = true

  IsShowActionColumn = true

  columns = undefined
  get Columns() {
    if (!this.columns) {
      this.columns = [
        colUtil.get('Tên cấu kiện', 'ElementName', null, this),
        colUtil.get('Đường kính', 'Diameter', null, this),
      ];

      if (this.IsShowActionColumn) {
        this.columns.push(colUtil.GetActionColumn(this));
      }
    }
    return this.columns;
  }
  set Columns(v) {
    this.columns = v;
    this.Rows = undefined;
  }

  dataSource = undefined
  get DataSource() {
    if (!this.dataSource) {
      this.dataSource = []
    }
    return this.dataSource;
  }
  set DataSource(v) {
    this.dataSource = v;
    this.Rows = undefined;
  }

  rows = undefined
  get Rows() {
    if (!this.rows) {
      this.rows = util.GetRows(this);
    }
    return this.rows;
  }
  set Rows(v) {
    this.rows = v;
  }

  AddItem = (item, isEdit = false) => {
    if (this.CanHandleItem && this.CanAddItem) {
      if (item === undefined) {
        if (this.OnGetItemForAdd) {
          item = this.OnGetItemForAdd();
        } else {
          item = {
            Key: genUtil.randomId(),
            IsTypeNotDefined: true
          };
        }
      }
      this.DataSource.push(item);

      var row = rowUtil.get(item, this);
      this.Rows.push(row);

      if (isEdit) {
        row.Edit();
      }
    }
  }

  DeleteItem = (item, row) => {
    if (this.CanHandleItem && this.CanDeleteItem) {
      var list = this.DataSource;
      var index = list.indexOf(item);
      list.splice(index, 1);
      this.Rows.splice(index, 1);

      if (this.OnDeleteItem) {
        this.OnDeleteItem(item);
      }
    }
  }

  EditItem = (item) => {
    if (this.CanHandleItem && this.CanEditItem) {
      if (item.IsTypeNotDefined) {
        var index = this.DataSource.findIndex(x => x.Key === item.Key);
        item = this.DataSource[index] = { ...item };
      }

      if (this.OnEditItem) {
        this.OnEditItem(item);
      }
    }
  }

  AddColumn = () => {
    if (this.CanHandleColumn && this.CanDeleteColumn) {
      var col = colUtil.GetByTitle(null, this);

      var actionCol_Index = this.Columns.findIndex(x => x.IsAction);
      if (actionCol_Index !== -1) {
        this.Columns.splice(actionCol_Index, 0, col);
      } else {
        this.Columns.push(col);
      }

      col.Edit();
    }
  }

  Update = () => {
    this.Rows = null;
  }

  // Ghi đè cách truy xuất giá trị 1 cell theo Item, Cell, Row, Col
  Override_Get_CellValue

  // Ghi đè cách render 1 cell theo Item, Cell, Row, Col
  Override_Get_CellRender

  // Khi hiệu chỉnh đầu mục cột,
  // sử dụng 2 cách để hiểu chỉnh: select và input
  Column_InputType = cit.input
  AllColumnNames = []

  // Có thể di chuyển vị trí các cột hay không
  CanShow_MoveCommand = true

  // Thiết lập sẵn bề rỗng của 
  Column_Title_SelectWidth
}