import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { ExportSet_DictVM } from '../dict/ExportSet_DictVM';
import './ExportSetMain.css'
import { UploadOutlined } from '@ant-design/icons';

class ExportSetMain_View extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new ExportSet_DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='es__wrapper'>
          <div className='es__pick__wrapper'>
            <div className='f'>
              {data.PickView}
            </div>
            {data.ModalView}
          </div>
          <div className='es__render__wrapper'>
            {data.CurrentItem &&
              data.CurrentItem.MassItems_Table ?
              data.CurrentItem.MassItems_Table.WrapperView
              : ''}
          </div>
        </div>
      </>
    );
  }
}

export default observer(ExportSetMain_View);
