import { PMDataVM } from '../../../single/form/PMDataVM';
import { LevelSettingDataVM } from './LevelSettingDataVM';
import * as itemUtil from './item/LS_ItemVMUtil'
import TableVM from '../../../../table/TableVM';
import TableColumn from '../../../../table/TableColumn';
import * as genUtil from '../../../../../util/generalUtil';
import EditableTextView from '../../../../text/EditableTextView';
import MultiSelectView from '../../../../select/MultiSelectView';
import * as pct from '../../command/ProjectCommand'

export const pmDataVM = () => {
  return PMDataVM.Instance;
}

// Instance
export const get = (q, dict) => {
  var qI = new LevelSettingDataVM();
  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetItems = (q) => {
  return q.ModelItem.Items.map(x => itemUtil.get(x, q));
}

export const GetTable = (q) => {
  var qI = new TableVM();

  // name
  var nameCol = new TableColumn();
  nameCol.Title = 'Tên';
  nameCol.DataIndex = ['name']
  nameCol.Key = genUtil.randomId();
  nameCol.Table = qI;
  nameCol.ClassName = 'ls__title-column'

  // name
  var valCol = new TableColumn();
  valCol.Title = 'Tương đương với';
  valCol.DataIndex = ['value']
  valCol.Key = genUtil.randomId();
  valCol.Table = qI;
  valCol.ClassName = 'ls__value-column'

  qI.Columns = [nameCol, valCol];
  qI.CanShow_MoveCommand = false;
  qI.CanHandleColumn = false

  // Cell Value
  qI.Override_Get_CellRender = (value, item, dataIndex, cell, row, col) => {
    switch (col.DataIndex[0]) {
      case 'name':
        return q.Commands.includes(pct.edit) ?
          <EditableTextView store={item.NameVM} />
          : item.NameVM.Content
      case 'value':
        // return <MultiSelectView store={item.ValueVM} />
        return item.Value_Element
    }
  }

  qI.DataSource = [...q.Items];

  return qI;
}