import * as api from '../../../../../util/apiUtil'
//import * as dataUtil from './TypicalLevelSettingDataUtil'
import * as dataUtil from './wrapper/TLS_Data_WrapperUtil'
import { PMData } from '../../../single/model/PMData';
import { TypicalLevelSetting } from './TypicalLevelSetting';
import * as st from '../base/SettingType'
import * as fieldUtil from '../../mass/item/MassItemField'
import { message } from 'antd';

export const pmData = () => {
  return PMData.Instance;
}

// Instance
export const get = (data, project, phase) => {
  var qI = new TypicalLevelSetting();

  qI._id = data._id;
  qI.Name = data.name;
  qI.Description = data.description;
  qI.SettingType = data.settingType;

  qI.Data = dataUtil.GetFromData(data.data, qI);

  qI.Project = project;
  qI.Phase = phase;

  return qI;
}

export const GetTest = (project) => {
  var qI = new TypicalLevelSetting();

  qI.Name = 'thiết lập ...';
  qI.Description = 'Mô tả thiết lập ...';
  qI.SettingType = st.typicallevel
  qI.Data = dataUtil.GetTest(qI);
  qI.Project = project;

  return qI;
}

export const GetFromDb_Async = (project, phase, handle) => {
  var apiData = {
    projectId: project._id,
    phaseId: phase?._id
  }

  var prefix = 'tl';
  var url = `https://api.bimdev.vn/api/hbs/${prefix}`;

  const apiFunc = () => api.post(`${url}/get`, apiData)

  apiFunc().then(response => response.data)
    .then(data => {
      var resData = data ? get(data, project, phase) : undefined;
      var editData = resData ? resData : GetTest(project);
      handle(resData, editData);
    })
}

// Property
export const GetFieldFilterStorageList = () => {
  return fieldUtil.GetListForTypicalLevelFilter();
}

// Method
export const SaveDatabase = (q) => {
  var _id = q._id;
  var apiData = q.ApiData;

  var prefix = 'tl';
  var url = `https://api.bimdev.vn/api/hbs/${prefix}`;

  var isNew = _id ? true : false
  const apiFunc = () => isNew ?
    api.put(`${url}/edit`, apiData) : api.post(`${url}/add`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (isNew) q._id = data._id
      message.success('add setting successfully!');
    })
}

export const GetLevelStorageList = (q) => {
  var level_Dict = q.Project.Level_Dict;

  const getLevelsFunc = (items) => {
    return items.map(x => x.Name);
  }

  if (level_Dict.Items) {
    return getLevelsFunc(level_Dict.Items);
  } else {
    level_Dict.OnGetItemsDone_Funcs.push(() => {
      q.LevelStorageList = getLevelsFunc(level_Dict.Items);
    });
  }
}
