import { PMData } from "../../../single/model/PMData";
import * as genUtil from '../../../../../util/generalUtil'
import * as tableUtil from './MassDetailTableUtil'
import * as mt from '../MassType'
import * as settingUtil from '../../setting/massDetail/MassDetailSettingUtil'

export const pmData = () => PMData.Instance;

// Property
export const GetSettingName = (q) => {
  return q.Setting?.Name;
}

export const Handle_SettingStorageList = (q) => {
  const set_StorageList = (v) => q.SettingStorageList = v;
  
  var dict = pmData().MassDetailSetting_Dict;
  if (dict.Items){
    set_StorageList(dict.Items);
  } else {
    genUtil.Add(
      dict.OnGetItemsDone_Func_Dict,
      () => set_StorageList(dict.Items)
    )
  }
}

export const GetTable = (q) => {
  return tableUtil.get(q);
}

export const GetDefaultSetting = (q) => {
  var obj = settingUtil.GetDefault();
  return obj;
}

// Xảy ra khi cập nhập thành công Items
export const Handle_Setting = (q) => {
  var massType = q.MainMassType;

  const handle_SettingFunc = (settings) => {
    var index = settings.findIndex(x => x.Data.MainMassType === massType);
    if (index !== -1){
      var setting = settings[index];
      q.SettingSelectVM_ForInput.InitialItem = setting;
      q.Setting = settingUtil.Clone(setting);
    }
  }

  var list = q.SettingStorageList;
  if (list){
    handle_SettingFunc(list);
  } else {
    genUtil.Add(
      q.IsGet_SettingStorageList_Done_Func_Dict,
      () => handle_SettingFunc(q.SettingStorageList),
      true
    )
  }
}

export const GetMainMassType = (q) => {
  var list = q.Items;
  return !list || list.length === 0 ? mt.Concrete : list[0].MassType;
}