import { makeObservable, observable, action } from 'mobx';
import { message } from 'antd';
import * as genUtil from '../../../../../../../util/generalUtil'
import * as util from './ColumnUtil'
import * as rs from '../../../../../../text/TextRenderState';
import SelectView from '../../../../../../select/SelectView';
import * as cgt from './Column_GroupType'
import { PMDataVM } from '../../../../../single/form/PMDataVM';
import * as pct from '../../../../command/ProjectCommand'

const pmDataVM = () => PMDataVM.Instance

export class Column {
  constructor() {
    makeObservable(this, {
      renderState: observable,
      title: observable
    })
  }

  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
  }

  Edit = () => {
    if (this.Commands.includes(pct.edit)) {
      if (this.RenderState === rs._default) {
        this.RenderState = rs.edit
        this.Hide_Tool();
      }
    }
  }

  field
  get Field() {
    return this.field
  } set Field(v) {
    this.field = v;
    this.Title = util.GetTitle(this)
  }

  Group

  get Table() {
    return this.Dict.Table;
  }

  // Title
  title = undefined
  get Title() {
    if (!this.title) {
      this.title = util.GetTitle(this)
    } return this.title
  } set Title(v) {
    this.title = v;
  }

  titleSelect
  get TitleSelect() {
    if (!this.titleSelect) {
      this.titleSelect = util.GetTitleSelect(this)
    } return this.titleSelect
  }

  get TitleView() {
    switch (this.RenderState) {
      case rs._default:
        return <div className='f'>
          <div className='f1'>{this.Title}</div>
          {this.Commands.includes(pct.edit) ? this.DeleteVM.View : ''}
        </div>
      case rs.edit:
        return <SelectView store={this.TitleSelect} />
    }
  }

  get View() {
    return <th key={this.id} style={this.Table.Tr_Style}
      onDoubleClick={this.Edit}
      onMouseEnter={this.OnMouseEnter}
      onMouseLeave={this.OnMouseLeave}>
      {this.TitleView}
    </th>
  }

  // Index
  get CurrentList() {
    return this.Dict.Columns
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.Table.Refresh_ColumnCount()
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  // Hover
  OnMouseEnter = () => {
    if (this.RenderState === rs.edit) {
      return;
    }
    this.Show_Tool();
  }

  OnMouseLeave = () => {
    this.Hide_Tool();
  }

  Show_Tool() {
    this.DeleteVM.Show();
  }

  Hide_Tool() {
    this.DeleteVM.Hide();
  }

  // MassItem
  MassItem_Render = (v) => {
    var field = this.Field;
    const getVal = this.Group === cgt.info ?
      () => v.GetValue_Mas(field) :
      () => v.ValueDict.GetValue(field)

    return <td key={this.id}>{getVal()}</td>
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }
}