import { message } from 'antd';
import * as util from './Phase_DictUtil'
import * as api from '../../../../util/apiUtil'
import * as itemUtil from './PhaseUtil'
import * as settingUtil from '../setting/base/SettingUtil'

export class Phase_Dict {
  get Project() {
    return this.Dict;
  }

  Items

  OnGetItemsDone_Funcs = []

  set GetItemsDone (v){
    this.OnGetItemsDone_Funcs.forEach(x => x());
  }

  input
  get Input(){
    if (!this.input){
      this.input = util.GetInput(this);
    }
    return this.input;
  }

  Add = (item) => {
    this.Items.push(item);
    if (this.OnAdd){
      this.OnAdd(item);
    }
  }
  
  AddMassVersion = () => {
    var apiData = this.Input.ApiData;
    var url = `https://api.bimdev.vn/api/hbphase`;

    const apiFunc = () => api.put(`${url}/mass/add`, apiData);
    var project = this.Project;
    
    apiFunc().then(response => response.data)
      .then(data => {
        var item = itemUtil.get(data, this);
        this.Add(item);
        message.success("create version successfully!");

        settingUtil.AddPhase(project._id, data._id);
      })

    // Thêm version mới cho InputExportSet sau khi lưu version
    var addVersionFunc = (exportSet) => exportSet.AddVersion();

    var inputExportSet = project.InputExportSet;
    if (!inputExportSet){
      project.OnGetInputExportSetDone_Funcs.push(() => {
        inputExportSet = project.InputExportSet;
        addVersionFunc(inputExportSet);
      })
    }else{
      addVersionFunc(inputExportSet);
    }
  }

  Get_Phase = (data) => {
    var list = this.Items;
    var index = list.findIndex(x => x._id === data._id);
    if (index === -1){
      var item = itemUtil.get(data, this);
      list.push(item);
      return item;
    }
    return list[index];
  }
}