import MGI_Item from "./MGI_Item";
import * as ct from './MGI_I_CreateType'
import * as genUtil from '../../../../../../util/generalUtil'

// Instance
export const create = (mgi, createType) => {
  var qI = new MGI_Item();
  qI.MassGroupItem = mgi;

  if (!createType) {
    qI.CreateType = ct.byauto;
  } else {
    qI.CreateType = ct.byuser;
    qI.Value = genUtil.newGUID();
    qI.Title = 'Tên đầu mục';
  }

  return qI;
}

export const getFromDb = (data, mgi) => {
  var qI = new MGI_Item();

  qI.Value = data.value;
  qI.SupValue = data.supValue
  qI.Title = data.title;
  qI.newMGI_id = data.newMGI_id;
  qI.MassGroupItem = mgi;
  qI.CreateType = data.createType ? data.createType : ct.byauto

  return qI;
}

export const Clone = (q, mgi) => {
  var qI = new MGI_Item();

  qI.Value = q.Value;
  qI.SupValue = q.SupValue;
  qI.Title = q.Title;
  qI.newMGI_id = q.newMGI_id;
  qI.MassGroupItem = mgi;
  qI.CreateType = q.CreateType;

  return qI;
}

// Property
export const GetNewMGI = (q) => {
  var _id = q.newMGI_id;
  if (!_id) return undefined;
  var list = q.MassGroupItem.SupComponent.AllItems;

  var index = list.findIndex(x => x._id === _id);
  return index !== -1 ? list[index] : undefined;
}

export const GetSupItem = (q) => {
  if (q.IsGet_SupItem) return undefined
  q.IsGet_SupItem = true;

  var supValue = q.SupValue;
  if (!supValue) return undefined;
  
  var list = q.MassGroupItem.Items;
  var index = list.findIndex(x => x.Value === supValue);
  return index !== -1 ? list[index] : undefined
}