import * as FileSaver from 'file-saver';
import * as XLSX from 'xlsx';

export class CSVExport {
  data = []
  get Data() {
    if (this.Override_Get_Data) {
      return this.Override_Get_Data()
    }
    return this.data;
  } set Data(v) {
    this.data = v;
  }

  fileName
  get FileName() {
    if (this.Override_Get_FileName) {
      return this.Override_Get_FileName();
    }
    return this.fileName;
  } set FileName(v) {
    this.fileName = v;
  }

  Export = () => {
    const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
    const fileExtension = '.xlsx';

    const ws = XLSX.utils.json_to_sheet(this.Data, this.Headers);
    const wb = { Sheets: { 'data': ws }, SheetNames: ['data'] };
    const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
    const data = new Blob([excelBuffer], { type: fileType });
    FileSaver.saveAs(data, this.FileName + fileExtension);
  }
  
  ExportTest = () => {
    const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
    const fileExtension = '.xlsx';

    var ws1 = {}
    ws1.Name = 'Title';
    ws1['!ref'] = "A1:E5";
    ws1['A1'] = {
      t: 'n', 
      v: 100
    }

    var ws2 = {}
    ws2.Name = 'Raw Data'
    ws2['!ref'] = "A1:E5";
    ws2['B2'] = {
      t: 'n', 
      v: 200
    }
    ws2['B3'] = {
      v: 200, f : 'B2'
    }
    ws2['C1'] = {
      v: 300, 
      f : 'Title!A1'
    }

    ws2['C2'] = {
      v: 300, 
      f : 'sum(Title!A1, B2)'
    }

    // const ws = XLSX.utils.json_to_sheet(this.Data, this.Headers);
    // ws['!ref'] = "A1:E5";
    // ws['E5'] = {
    //   t : 's',
    //   v : '100'
    // };
    // debugger

    //const wb = { Sheets: { 'data': ws }, SheetNames: ['data'] };
    
    const wb = { 
      Sheets: { 
        
       }, 
      SheetNames: [ws1.Name, ws2.Name] 
    };
    wb.Sheets[ws1.Name] = ws1;
    wb.Sheets[ws2.Name] = ws2;

    const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
    const data = new Blob([excelBuffer], { type: fileType });
    FileSaver.saveAs(data, this.FileName + fileExtension);
  }
}