import { TLS_PageItem } from "./TLS_PageItem"
import * as levelGroupUtil from '../levelGroup/LevelGroupUtil'
import * as filterUtil from '../filter/TLS_FilterUtil'

// Instance
export const get = (data, dict) => {
  var qI = new TLS_PageItem();
  qI.Name = data.name;
  qI.Filters = data.filters.map(x => filterUtil.get(x, qI));
  qI.LevelGroups = data.levelGroups ? data.levelGroups.map(x => levelGroupUtil.get(x, qI))
     : data.levels.map(x => levelGroupUtil.get(x, qI))
  qI.Dict = dict;
  return qI;
}

export const GetDefault = (dict) => {
  var qI = new TLS_PageItem();
  qI.Name = 'setting 1';
  qI.Dict = dict;
  return qI;
}

// Property
export const GetLevelGroups = (q) => {
  return [ levelGroupUtil.GetDefault(q) ]
}

export const GetFilters = (q) => {
  return [ filterUtil.GetDefault(q) ]
}