import { makeObservable, observable, action, set } from 'mobx';
import * as selectUtil from '../../../../select/SelectVMUtil';
import * as massItemUtil from '../item/MassItemUtil'
import { MassDetailTable } from './MassDetailTable';
import * as mdi_st from './MassDetailInput_StateType'
import * as textUtil from '../../../../text/TextVMUtil';
import SelectView from '../../../../select/SelectView';
import InputView from '../../../../input/InputView';
import { message } from 'antd';
import { PMData } from '../../../single/model/PMData';
import * as util from './MassDetailVMUtil'
import Modal from 'antd/lib/modal/Modal';
import * as settingUtil from '../../setting/massDetail/MassDetailSettingUtil'

export const pmData = () => PMData.Instance;

export class MassDetailVM {
  constructor() {
    makeObservable(this, {
      IsOnSaveSetting: observable, 
      SettingInput_State : observable,
      Is_Override_Setting_Modal_Open : observable
    })
  }

  _id
  get id() {
    return this._id;
  }
  set id(v) {
    this._id = v;
    massItemUtil.getByViewDetail(v, (items) => {
      this.Items = items;
    })
  }

  items = undefined
  get Items() {
    return this.items;
  }
  set Items(v) {
    this.items = v;

    this.MainMassType = util.GetMainMassType(this);
    util.Handle_Setting(this);
    this.DetailTable.Data = v;
  }

  detailTable
  get DetailTable() {
    if (!this.detailTable) {
      this.detailTable = util.GetTable(this);
    }
    return this.detailTable;
  }

  // Setting for input
  IsGet_SettingStorageList_Done_Func_Dict = []

  settingStorageList
  get SettingStorageList() {
    if (!this.settingStorageList) {
      util.Handle_SettingStorageList(this);
    }
    return this.settingStorageList;
  } set SettingStorageList(v) {
    this.settingStorageList = v;
    this.SettingSelectVM_ForInput.Items = v;

    this.IsGet_SettingStorageList_Done_Func_Dict.forEach(x => x.Value());
  }

  settingSelectVM_ForInput
  get SettingSelectVM_ForInput() {
    if (!this.settingSelectVM_ForInput) {
      var list = this.SettingStorageList ?
        this.SettingStorageList : [];

      var obj = this.settingSelectVM_ForInput =
        selectUtil.get(list);

      obj.TitleRender = (item) => item.Name;
      obj.Placeholder = 'Chọn thiết lập';
      obj.OnSelectedItemChanged = (v) => {
        this.PreSetting = v;
      };
    }
    return this.settingSelectVM_ForInput;
  }

  SettingOK = () => {
    var preSetting = this.PreSetting;
    if (!preSetting){
      message.warning("Bạn chưa chọn bộ thiết lập");
      return;
    }

    this.Setting = settingUtil.Clone(preSetting);
  }
  
  setting
  get Setting(){
    if (!this.setting){
      this.setting = util.GetDefaultSetting(this);
    }
    return this.setting;
  } set Setting(v){
    this.setting = v;

    this.DetailTable.SettingData = v.Data;
  }

  // Save Settting
  IsOnSaveSetting = false;

  SaveSetting = () => {
    // Init MassGroup StorageList
    var refSelectVM = this.SettingSelectVM_ForInput;
    var obj = this.SettingSelectVM;
    obj.Items = [...refSelectVM.Items];
    obj.InitialItem = refSelectVM.SelectedItem;

    this.SettingInput_State = mdi_st.select
    this.IsOnSaveSetting = true;
  }

  SettingInput_State = undefined

  Toggle_SettingInput_State = () => {
    switch (this.SettingInput_State) {
      case mdi_st.select:
        this.SettingInput_State = mdi_st.input
        break;
      case mdi_st.input:
        this.SettingInput_State = mdi_st.select
        break;
    }
  }

  get SettingInput_Toggle_Classname() {
    switch (this.SettingInput_State) {
      case mdi_st.select:
        return 'btn-warning';
      case mdi_st.input:
        return 'btn-success';
    }
  }

  get SettingInput_Toggle_Content() {
    switch (this.SettingInput_State) {
      case mdi_st.select:
        return 'sel';
      case mdi_st.input:
        return 'inp';
    }
  }

  settingName = undefined
  get SettingName() {
    if (!this.settingName) {
      this.settingName = util.GetSettingName(this);
    }
    return this.settingName;
  }
  set SettingName(v) {
    this.settingName = v;
  }

  settingInputVM
  get SettingInputVM() {
    if (!this.settingInputVM) {
      var obj = this.settingInputVM =
        textUtil.get(this.SettingName, 'Tên thiết lập');
      obj.onSetContent = (v) => this.SettingName = v;
    }
    return this.settingInputVM;
  }

  settingSelectVM
  get SettingSelectVM() {
    if (!this.settingSelectVM) {
      var obj = this.settingSelectVM = selectUtil.get([], undefined);
      obj.Placeholder = 'Chọn thiết lập';
      obj.OnSelectedItemChanged = (v) => {
        this.SettingName = v?.Name
      };
    }
    return this.settingSelectVM;
  }

  get SettingInput_Element() {
    switch (this.SettingInput_State) {
      case mdi_st.select:
        var obj = this.SettingSelectVM;
        var list = obj.Items;
        var index = list.findIndex(x => x.Name === this.SettingName);
        obj.InitialItem = index !== -1 ?
          list[index] : undefined
        return <SelectView store={obj} />
      case mdi_st.input:
        var obj = this.SettingInputVM;
        obj.Content = this.SettingName;
        return <InputView store={obj} />
    }
  }

  SettingInputOK = () => {
    var name = this.SettingName;
    var input_State = this.SettingInput_State;

    if (!name || name.length === 0) {
      switch (input_State) {
        case mdi_st.select:
          message.warning('Vui lòng chọn bộ thiết lập!')
          return;
        case mdi_st.input:
          message.warning('Vui lòng nhập tên thiết lập!')
          return;
      }
    }

    var needValidateForOveride = false;
    var items = pmData().MassDetailSetting_Dict.Items;
    var index = items.findIndex(x => x.Name === name);

    var setting = this.Setting;
    setting.Name = name;
    
    if (index !== -1) {
      setting._id = items[index]._id;

      if (input_State === mdi_st.input) {
        needValidateForOveride = true;
      }
    }

    const handle_UpdateSetting = () => {
      // cập nhập MainMassType và Columns vào setting
      var settingData = setting.Data;
      settingData.MainMassType = this.MainMassType;
      settingData.Columns = this.DetailTable.ColumnNames;

      setting.SaveDatabase((data, isnew) => {
        var refVM = this.SettingSelectVM_ForInput
        var list = refVM.Items;
        var newItem = settingUtil.GetFromDb(data);
        console.log(newItem);

        if (isnew) {
          list.push(newItem);
          refVM.InitialItem = newItem;
        } else {
          list[index] = newItem;
          // Refresh lại selected item nếu trùng với đối tượng update
          if (refVM.SelectedIndex === index) {
            refVM.InitialItem = newItem;
          }
        }

        this.IsOnSaveSetting = false
        message.success(`${isnew ? 'add' : 'edit'} setting successfully`);
      })
    }

    if (!needValidateForOveride) {
      handle_UpdateSetting();
    } else {
      this.Override_Setting_Modal_OKFunc =
        () => handle_UpdateSetting();

      this.Is_Override_Setting_Modal_Open = true;
    }
  }

  Is_Override_Setting_Modal_Open = false

  get Override_Setting_ModalElement() {
    var title = 'Cảnh báo'
    var content = 'Thiết lập đã có sẵn, bạn có muốn ghi đè không?'

    const modalCancel = () =>
      this.Is_Override_Setting_Modal_Open = false

    const modalOK = () => {
      this.Is_Override_Setting_Modal_Open = false
      this.Override_Setting_Modal_OKFunc();
    }

    return <Modal title={title}
      visible={this.Is_Override_Setting_Modal_Open}
      onOk={modalOK} onCancel={modalCancel}>
      {content}
    </Modal>
  }

  SettingInputCancel = () => {
    this.IsOnSaveSetting = false;
    this.SettingInputVM.Content = util.GetSettingName(this);
  }

  // Setting Data
  MainMassType
}