import { Button, message, Space, Tag } from "antd";
import * as colUtil from '../../../../table/TableColumnUtil'
import TableVM from "../../../../table/TableVM";
import * as renderUtil from '../../../../table/RenderItemUtil'
import * as rs from '../../../../table/RenderState'
import * as genUtil from '../../../../../util/generalUtil'
import RebarMass_Dict from "./RebarMass_Dict";
import * as massItemUtil from '../item/MassItemUtil'

export const get = (project) => {
  var qI = new RebarMass_Dict();
  qI.Project = project;

  var projectId = qI.projectId;
  massItemUtil.getRebars(projectId, (items) => {
    qI.Items = items;
  });

  return qI;
}

export const GetTableVM = (q) => {
  var qI = new TableVM();

  qI.DataSource = q.Items;
  qI.Columns = [
    colUtil.get('Tên cấu kiện', ['Info', 'elementName'],
      [
        renderUtil.get(rs._default, (val, item) => item.Info.elementName)
      ], qI),

    colUtil.get('Hạng mục', ['Info', 'workItem'],
      [
        renderUtil.get(rs._default, (val, item) => item.Info.workItem)
      ], qI),

    colUtil.get('Đường kính', ['Info', 'diameter'],
      [
        renderUtil.get(rs._default, (val, item) => item.Info.diameter)
      ], qI),

    colUtil.get('Khối lượng', ['Value'], null, qI),

    colUtil.GetActionColumn(qI)
  ]

  qI.OnGetItemForAdd = () => {
    return massItemUtil.GetDefaultRebarMass(q.Project);
  }

  qI.OnEditItem = (item) => {
    item.AddDatabase();
  }

  qI.OnDeleteItem = (item) => {
    item.DeleteDatabase();
  }

  return qI;
}