import { makeObservable, observable, action } from 'mobx';
import * as util from './TypicalLevelSettingDataVMUtil'
import { ControlOutlined } from '@ant-design/icons';
import { Button } from 'antd';
import * as pageItemUtil from './pageItem/TLS_PageItemVMUtil';
import * as genUtil from "../../../../../util/generalUtil"
import { PMDataVM } from '../../../single/form/PMDataVM';

const pmDataVM = () => PMDataVM.Instance

export class TypicalLevelSettingDataVM {
  constructor() {
    makeObservable(this, {
      pageItems: observable,
      currentPageKey: observable,
      allTabs: observable
    })
  }

  ModelItem

  // Setting
  get SettingVM (){
    return this.Dict.SettingVM;
  }

  // Level StorageList
  OnSet_LevelStorageList_Func_Dict = []

  levelStorageList
  get LevelStorageList(){
    if (!this.levelStorageList){
      this.levelStorageList = util.GetLevelStorageList(this);
    }
    return this.levelStorageList;
  } set LevelStorageList(v){
    this.levelStorageList = v;

    genUtil.Handle(this.OnSet_LevelStorageList_Func_Dict,
      (v) => this.OnSet_LevelStorageList_Func_Dict = v)
  }

  get FieldFilterStorageList() {
    return this.Dict.FieldFilterStorageList;
  }

  // Page Items
  pageItems = undefined
  get PageItems() {
    if (!this.pageItems) {
      this.pageItems = util.GetPageItems(this);
    }
    return this.pageItems;
  }

  AddPageItem = () => {
    var item = pageItemUtil.get(this.ModelItem.AddPageItem(), this);
    this.PageItems.push(item);

    var i = this.IsHaveAddCommandTab ? 1 : 0;
    this.AllTabs.splice(this.AllTabs.length - i, 0, item);

    this.CurrentPageKey = item.id;

    return item;
  }

  addCommandTab
  get AddCommandTab() {
    if (!this.addCommandTab) {
      this.addCommandTab = {
        id: 'add',
        TitleElement: <Button className='tls__tabs__add'
          type='text' size='small'
          onClick={this.AddPageItem}>+</Button>
      }
    }
    return this.addCommandTab;
  }

  IsHaveAddCommandTab = false

  allTabs = undefined
  get AllTabs() {
    if (!this.allTabs) {
      var obj = [...this.PageItems];
      if (this.IsHaveAddCommandTab) {
        obj.push(this.AddCommandTab);
      }
      this.allTabs = obj;
    }
    return this.allTabs;
  }

  currentPageKey = undefined
  get CurrentPageKey() {
    if (!this.currentPageKey) {
      this.currentPageKey = this.PageItems[0].id;
    }
    return this.currentPageKey;
  } set CurrentPageKey(v) {
    this.currentPageKey = v;
  }

  IsModelChangePageKey = false

  ChangePageKey = (v) => {
    if (this.IsModelChangePageKey) {
      this.IsModelChangePageKey = false; return;
    }
    this.CurrentPageKey = v;
  }

  Save = () => {
    this.SettingVM.Save();
  }

  // Commands
  get Commands(){
    return pmDataVM().Commands;
  }
}