import TableVM from "../../../../table/TableVM";
import { WorkItemExportSetting_DictVM } from "./WorkItemExportSetting_DictVM";
import * as colUtil from '../../../../table/TableColumnUtil'
import * as renderUtil from '../../../../table/RenderItemUtil'
import * as rs from '../../../../table/RenderState'
import CheckboxView from '../../../../checkbox/CheckboxView'
import { WorkItemExportSettingVM } from "./WorkItemExportSettingVM";

export const get = (q) => {
  var qI = new WorkItemExportSetting_DictVM();
  qI.ModelItem = q;

  q.OnGetItemsDone_Funcs.push((v) => {
    var obj = qI.Items;
  });

  return qI;
}


export const GetTableVM = (q) => {
  var qI = new TableVM();

  qI.DataSource = q.Items;

  var nameCol = colUtil.get('Tên thiết lập', ['Name'], null, qI);
  var isCateCol = colUtil.get('Category', null, [
    renderUtil.get(rs._default, (val, item) => {
      debugger
      var checkBoxVM = item.CategoryCheckboxVM;
      return <CheckboxView store={item.CategoryCheckboxVM}/>
    })
  ], qI);
  var isSubWorkItemCol = colUtil.get('Hạng mục phụ', null, [
    renderUtil.get(rs._default, (val, item) => {
      return <CheckboxView />
    })
  ], qI);

  qI.Columns = [
    nameCol, isCateCol, isSubWorkItemCol,
    colUtil.GetActionColumn(qI)
  ]

  qI.OnGetItemForAdd = () => {
    return new WorkItemExportSettingVM();
  }
  
  return qI;
}