import InputView from "../input/InputView";
import * as rs from './TextRenderState'
import { TextRenderItem } from "./TextRenderItem";
import { Button } from "antd";

export const get = (state, render) => {
  var qI = new TextRenderItem();
  qI.State = state;
  qI.Render = render;
  return qI;
}

export const GetRender = (q) => {
  switch (q.State) {
    case rs._default:
      return (content, context) => {
        var className = context.ClassName;
        var style = context.Style;
        var onDoubleClick = context.OnDoubleClick
        var onClick = context.OnClick

        return <>
          <span className={className} style={style}
            onDoubleClick={onDoubleClick}
            onClick={onClick}>
            {content}
          </span>
          { context.Is_Tool_Visible && context.Is_Edit_Visible ?
            <Button type='primary' size='small'
              className='left10 btn-warning' onClick={context.Edit}
            >e</Button>
            : ''}
        </>
      }
    case rs.edit:
      return (content, context) => {
        var className = context.ClassName;
        var style = context.Style;
        var onFocus = context.OnFocus
        
        return <>
          <div className={`input__wrapper ${className}`}>
            <InputView ref={context.Ref}
              className='input__main'
              style={style}
              store={context}
              ref={context.Ref}
              onFocus={onFocus} />
            {context.Is_Tool_Visible && context.Is_OK_Visible ?
              <Button type='primary' size='small'
                onClick={context.EditOK}>ok</Button>
              : ''}
          </div>
        </>
      }
    default:
      return undefined;
  }
}