import * as ft from './Site_FieldType'
import * as genUtil from '../../../../../util/generalUtil'
import { message } from 'antd';
import { PMData } from '../../../single/model/PMData';
import * as util from './SiteUtil'

const pmData = () => PMData.Instance;

export class Site {
  _id

  Row

  MassItem

  get ApiData() {
    var obj = {
      _id : this._id
    }

    var info

    if (this.Row) {
      // projectId
      obj.projectId = pmData().CurrentProject._id

      const getFilter = (x) => {
        return {
          field: x.Field,
          value: x.RawValue
        }
      }

      var r = this.Row;
      // Filter
      var filter = obj.filter = [
        getFilter(r)
      ]
      
      r.Original_Full_SupRows.forEach(x => {
        filter.push(getFilter(x))
      })

      info = obj.input = {}
    } else {
      obj.massId = this.MassItem._id;
      info = obj;
    }

    ft.GetList().forEach(x => {
      info[ft.GetKey(x)] = this[x]
    })

    return obj;
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, this.Url,
      () => {
        if (this.Row){
          this._id = undefined;
        }

        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }

  // Run Input Row
  Run = () => {
    util.Run(this)
  }
}