import { SelectVM } from "../../../../../../select/SelectVM"
import { PMData } from "../../../../../single/model/PMData"
import * as mif from '../../../../mass/item/MassItemField'
import * as mut from '../../../../mass/unit/MassUnitType'
import * as cgt from './Column_GroupType'
import * as rs from '../../../../../../text/TextRenderState';
import * as genUtil from '../../../../../../../util/generalUtil'
import { Column } from "./Column"
import { EntDelete } from "../../../../../../delete/EntDelete"
import * as d_tt from '../../../../../../delete/EntDelete_ToggleType'

const pmData = () => PMData.Instance

// Instance
export const get = (field, group, dict) => {
  var qI = new Column();
  qI.Field = field;
  qI.Group = group;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetTitleSelect = (q) => {
  var qI = new SelectVM();

  switch (q.Group) {
    case cgt.info:
      qI.Items = pmData().MIF_Dict_ForInput
      break;
    case cgt.value:
      qI.Items = pmData().MUT_Dict
      break;
  }

  qI.TitleRender = (v) => v.Name;
  qI.InitialItem = genUtil.SingleFilter(qI.Items, 
    (x) => x.Name === q.Title);
  
  qI.OnSelectedItemChanged = (v) => {
    q.Field = v.Value
    q.RenderState = rs._default
  }

  return qI;
}

export const GetTitle = (q) => {
  var field = q.Field

  switch (q.Group) {
    case cgt.info:
      return mif.GetName(field)
    case cgt.value:
      return mut.GetName(field)
  }
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.mouseEvent
  qI.Title = 'Xác nhận xóa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}