import React, { Component } from 'react';
import { observer } from 'mobx-react';
import './Tab.css'
import * as util from './TabUtil'

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = util.GetTest()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.View}
      </>
    );
  }
}

export default observer(TableView);
