import InputView from "../input/InputView";
import RenderItem from "./RenderItem"
import * as rs from './RenderState'
import * as textUtil from '../text/TextVMUtil'
import TextView from "../text/TextView";
import * as cellUtil from './TableCellUtil'

export const get = (state, render) => {
  var qI = new RenderItem();
  qI.State = state;
  qI.Render = render;
  return qI;
}

export const GetRender = (q) => {
  switch (q.State) {
    case rs._default:
      return (val, item, dataIndex, cell, row, col) => {
        return cell.ValueVM.Content;
      };
    case rs.edit:
      return (value, item, dataIndex, cell, row, col) => {
        if (!dataIndex) return value;

        var inputVM = cell.ValueVM;

        var length = dataIndex.length;
        var obj = item;
        dataIndex.filter((x, i) => i < length - 1)
          .forEach((x) => obj = obj[x]);

        inputVM.onSetContent = (v) => {
          obj[dataIndex[length - 1]] = v;
        }
        return <InputView store={inputVM} />
      };
    default:
      return undefined;
  }
}