import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Row } from 'antd';
import { LevelGroupVM } from './LevelGroupVM';
import MultiSelectView from '../../../../../select/MultiSelectView';
import { DeleteFilled } from '@ant-design/icons';
import './LevelGroup.css'
import * as pct from '../../../command/ProjectCommand'

class LevelGroupView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new LevelGroupVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='levelgroup__container'>
          <MultiSelectView store={data.LevelSelectVM} />
          {data.Commands.includes(pct.edit) ?
            <Button size='small' type='text'
              className='levelgroup__deletebutton'
              onClick={data.Delete}>
              <DeleteFilled className='levelgroup__deleteicon' />
            </Button>
            : ''}
        </div>
      </>
    );
  }
}

export default observer(LevelGroupView);
