import { Category_Item } from "./Category_Item"

export const GetFromDb = (data,dict) =>{
    var qI = new Category_Item();

    qI.Name = data.name;
    qI.Values = data.values
    qI.Dict = dict;
  
    return qI;
}

export const GetDefault = (dict) => {
    var qI = new Category_Item();
  
    qI.Dict = dict;
  
    return qI;
  }