import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Row } from 'antd';
import './MassTypeSetting.css'
import { SaveFilled } from '@ant-design/icons';
import * as util from './MassTypeSettingVMUtil'
import MassTypeSettingDataView from './MassTypeSettingDataView';


class MassTypeSettingView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = util.GetDefault()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;


    return (
      <>
        <div>
          <div className ='tls__name'>
            <h3>{data.Name}</h3>
          </div>
          <div>
            {data.Description}
          </div>
            <MassTypeSettingDataView store={data.DataVM} />
        </div>

      </>
    );
  }
}



export default observer(MassTypeSettingView);
