import { makeObservable, observable, action } from 'mobx';
import TableView from './TableView';
import * as util from './TableUtil'
import { Column } from './column/Column';
import * as genUtil from '../../../../../../util/generalUtil'
import { url } from './TableUrl'
import { Button, message } from 'antd';
import { SaveFilled } from '@ant-design/icons';
import InputView from '../../../../../input/InputView';
import { TextVM } from '../../../../../text/TextVM';
import { PMDataVM } from '../../../../single/form/PMDataVM';
import * as pct from '../../../command/ProjectCommand'

const pmDataVM = () => PMDataVM.Instance

export class Table {
  constructor() {
    makeObservable(this, {
      items: observable,
      columnCount: observable
    })
  }

  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Dict

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
  }

  SetItems = (v) => {
    // this.items = v;
    // this.Dict.MassItems = v;
    this.Dict.ModelItem.MassItems = v;
  }

  Refresh_Info = () => {
    this.Infos = util.GetInfos(this)
    this.Values = util.GetValues(this)
  }

  infos = undefined
  get Infos() {
    if (!this.infos) {
      this.infos = util.GetDefaultInfos(this)
    } return this.infos
  } set Infos(v) {
    this.infos = v;
  }

  get InfoCount() {
    return this.Infos.length
  }

  values = undefined
  get Values() {
    if (!this.values) {
      this.values = util.GetDefaultValues(this)
    } return this.values
  } set Values(v) {
    this.values = v;
  }

  get ValueCount() {
    return this.Values.length
  }

  Refresh_ColumnCount = () => {
    this.ColumnCount = util.GetColumnCount(this)
  }

  columnCount = undefined
  get ColumnCount() {
    if (this.columnCount === undefined) {
      this.columnCount = util.GetColumnCount(this)
    } return this.columnCount
  } set ColumnCount(v) {
    this.columnCount = v;
  }

  get Tr_Style() {
    var val = ((1 / this.ColumnCount) * 100).toFixed(1);
    return {
      width: val + '%'
    }
  }

  columnGroups
  get ColumnGroups() {
    if (!this.columnGroups) {
      this.columnGroups = util.GetColumnGroups(this)
    } return this.columnGroups
  }

  GetColumnGroup = (group) => {
    return genUtil.SingleFilter(
      this.ColumnGroups,
      (x) => x.Group === group
    )
  }

  get ApiData() {
    this.Refresh_Info()

    var obj = {
      _id: this._id,
      dictId: this.Dict.ModelItem._id,
      infos: this.Infos,
      values: this.Values
    }

    // debugger
    return obj;
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }

  Save = () => {
    this.SaveDatabase(true);
  }

  // Paste
  pasteContent
  get PasteContent() {
    return this.pasteContent
  } set PasteContent(v) {
    this.pasteContent = v;
    if (v && v.length !== 0) {
      this.PasteVM.Content = ''
    }
  }

  pasteVM
  get PasteVM() {
    if (!this.pasteVM) {
      var qI = this.pasteVM = new TextVM();
      qI.Content = ''
      qI.Placeholder = 'paste dữ liệu vào đây ...'
      qI.onSetContent = (v) => this.PasteContent = v;
    } return this.pasteVM;
  }

  OnPaste = (v) => {
    var text = genUtil.GetPlainText(v);
    var q1 = text.split('\r\n')
    if (q1.length === 0 || (q1.length === 1 && q1[0].length === 0)) {
      return;
    }

    var q2 = q1.map(x => x.split('\t'));
    // remove item cuối cùng thừa
    var lastIndex = q2.length - 1;
    if (q2[lastIndex].length === 1 && q2[lastIndex][0].length === 0) {
      q2.splice(lastIndex, 1);
    }

    // Update Data
    this.SetItems(util.GetItems_FromPaste(this, q2));
    this.Dict.SaveMassItems();

    this.PasteVM.Content = ''
  }

  // View
  get View() {
    return <TableView store={this} />
  }

  get WrapperView() {
    return <div>
      {/* Tool */}
      {this.Commands.includes(pct.edit) ?
        <div className='es__table__tool__wrapper'>
          <div className='f-ac'>
            <Button type='primary' size='small'
              onClick={this.Save}>
              LƯU ĐẦU MỤC BẢNG
            </Button>
            <InputView store={this.PasteVM}
              className='es__table__paste'
              onPaste={this.OnPaste} />
          </div>
        </div>
        : ''}

      {/* Table */}
      <TableView store={this} />
    </div>
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }
}