import * as genUtil from '../../../../../../../util/generalUtil'
import * as util from './MGS_Dict_DictUtil'

export class MGS_Dict_Dict {
  items
  get Items(){
    if (!this.items){
      this.items = util.GetItems(this)
    } return this.items;
  }

  current_MGS
  get Current_MGS(){
    return this.current_MGS
  } set Current_MGS(v){
    this.current_MGS = v;

    this.Used_MassGroupComponent = undefined
    this.Used_MassGroupItem = undefined
  }

  // MassGroupComponent - Item
  OnSet_Used_MassGroupComponent_Func = []

  used_MassGroupComponent
  get Used_MassGroupComponent() {
    if (!this.used_MassGroupComponent) {
      util.Handle_Used_MassGroupComponent(this)
    }
    return this.used_MassGroupComponent
  } set Used_MassGroupComponent(v) {
    this.used_MassGroupComponent = v;
    genUtil.Handle(this.OnSet_Used_MassGroupComponent_Func,
      (v) => this.OnSet_Used_MassGroupComponent_Func = v)
  }

  OnSet_Used_MassGroupItem_Func = []

  used_MassGroupItem
  get Used_MassGroupItem() {
    if (!this.used_MassGroupItem) {
      util.Handle_Used_MassGroupItem(this)
    }
    return this.used_MassGroupItem
  } set Used_MassGroupItem(v) {
    this.used_MassGroupItem = v;

    genUtil.Handle(this.OnSet_Used_MassGroupItem_Func,
      (v) => this.OnSet_Used_MassGroupItem_Func = v)
  }
}