import React, { Component } from 'react';
import { observer } from 'mobx-react';
import './Save.css'
import { SaveVM } from './SaveVM';
import { Button } from 'antd';

class SaveView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new SaveVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.Is_Input_Visible ?
          <div className='mgs__save__wrapper'>
            <div className='mgs__save__dec'>
              <div className='mgs__save__input'>
                {data.InputView}
              </div>

              <div className='mgs__save__tool'>
                <Button type='primary' size='small'
                  onClick={data.ToggleState}>
                  {data.ToggleContent}
                </Button>
                <Button type='primary' size='small'
                  onClick={data.OK}>ok
                </Button>
                <Button type='primary' size='small'
                  onClick={data.Cancel}>cancel
                </Button>
              </div>
            </div>
          </div>
          : ''
        }
      </>
    );
  }
}

export default observer(SaveView);
