import { Level_Item } from "./Level_Item"
import * as mif from '../MassItemField'
import * as genUtil from '../../../../../../util/generalUtil'

// Instance
export const get = (mi, miPh_Item) => {
  var qI = new Level_Item();
  qI.MassItem = mi;
  qI.MassPhase_Item = miPh_Item;
  return qI;
}

// Property
export const GetOrigin_Value = (q) => {
  var massItem = q.MassItem;

  return massItem.GetValue_Mas(mif.level);
}

export const Handle_Replace_Value = (q) => {
  if (q.IsGet_Replace_Value) return undefined;
  q.IsGet_Replace_Value = true;

  // Truy xuất từ dự án hoặc phase
  //var project = q.Project

  var source = q.Phase ? q.Phase : q.Project
  var originVal = q.Origin_Value;

  const setObj = (tls) => {
    var list = tls.Data.Binding.Items;
    var isValid = false;
    var replaceVal

    list.forEach((x) => {
      if (!isValid && x.Values.includes(originVal)) {
        isValid = true;
        replaceVal = x.Name;
      }
    })

    q.Replace_Value = replaceVal ? replaceVal : originVal;
  }

  if (source.TypicalLevelSetting) {
    setObj(source.TypicalLevelSetting);
  } else {
    genUtil.Add_2(
      source.OnGetTypicalLevelSetting_Func_Dict,
      () => setObj(source.TypicalLevelSetting),
      false
    )
  }
}

export const Handle_Value = (q) => {
  if (q.IsGet_Value) return undefined;
  q.IsGet_Value = true;

  const setObj = (reVal) => {
    q.Value = reVal;
  }

  if (q.Replace_Value){
    setObj(q.Replace_Value);
  } else {
    // Thiết lập trước bằng Origin, không dùng thuộc tính để bỏ qua Func
    //setObj(q.Origin_Value);
    q.value = q.Origin_Value;

    // Sau khi truy xuất xong Replace => thay đổi thông tin
    genUtil.Add_2(
      q.OnSet_Replace_Value_Func_Dict,
      () => setObj(q.Replace_Value),
      false
    )
  }
}