import { makeObservable, observable, action } from 'mobx';
import * as selectUtil from "../../../../../select/SelectVMUtil";
import * as util from './TLS_FilterVMUtil'
import * as valueUtil from './TLS_FilterValueVMUtil'
import { PMDataVM } from '../../../../single/form/PMDataVM';
import * as pct from '../../../command/ProjectCommand'

const pmDataVM = () => PMDataVM.Instance

export class TLS_FilterVM {
  constructor() {
    makeObservable(this, {
      items : observable
    })
  }

  get id(){
    return this.ModelItem.id;
  }

  get Field(){
    return this.ModelItem.Field;
  } set Field(v){
    this.ModelItem.Field = v;
  }

  get FieldStorageList(){
    return this.Dict.FieldFilterStorageList;
  }

  fieldSelectVM
  get FieldSelectVM(){
    if (!this.fieldSelectVM){
      var obj = this.fieldSelectVM = 
        selectUtil.get(this.FieldStorageList, this.Field);
      obj.Placeholder = 'chọn bộ lọc ...';
      obj.OnSelectedItemChanged = (v) => this.Field = v;
      obj.GetDisabled_Func = () => !this.Commands.includes(pct.edit)
    }
    return this.fieldSelectVM;
  }

  get Index (){
    return this.Dict.Filters.indexOf(this);
  }

  Delete = () => {
    this.ModelItem.Delete();
    this.Dict.Filters.splice(this.Index, 1);
  }

  items = undefined
  get Items(){
    if (!this.items){
      this.items = util.GetItems(this);
    }
    return this.items;
  }

  AddItem = () => {
    var item = valueUtil.get(this.ModelItem.AddItem(),this);
    this.Items.push(item);
    return item;
  }

  // Commands
  get Commands() {
    return pmDataVM().Commands;
  }
}
