import * as util from './User_DictVMUtil'
import * as vt from './User_Dict_ViewType'
import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../util/generalUtil'

export class User_DictVM {
  constructor() {
    makeObservable(this, {
      viewType: observable
    })
  }

  get Url() {
    return this.ModelItem.Url
  } set Url(v) {
    this.ModelItem.Url = v;
  }

  // LoginUser
  OnSet_LoginUser_Func = []

  loginUser
  get LoginUser() {
    if (!this.loginUser) {
      util.Handle_LoginUser(this)
    } return this.loginUser;
  } set LoginUser(v) {
    this.loginUser = v;
    this.ViewType = util.GetViewType(this)

    if (this.IsSet_Model) {
      this.ModelItem.LoginUser = v?.ModelItem;
    }else {
      this.IsSet_Model = true;
    }

    genUtil.Handle(this.OnSet_LoginUser_Func,
      (v) => this.OnSet_LoginUser_Func = v)
  }

  // Login
  login
  get Login() {
    if (!this.login) {
      this.login = util.GetLogin(this)
    } return this.login;
  }

  // NewPassword
  newPassword
  get NewPassword() {
    if (!this.newPassword) {
      this.newPassword = util.GetNewPassword(this)
    } return this.newPassword;
  }

  Logout = () => {
    this.LoginUser = undefined
  }

  // View
  viewType = undefined;
  get ViewType() {
    if (!this.viewType) {
      util.Handle_ViewType(this)
    } return this.viewType
  } set ViewType(v) {
    this.viewType = v;
  }

  loginRender
  get LoginRender() {
    if (!this.loginRender) {
      this.loginRender = util.GetDefault_LoginRender(this)
    } return this.loginRender;
  } set LoginRender(v) {
    this.loginRender = v;
  }

  userRender
  get UserRender() {
    if (!this.userRender) {
      this.userRender = util.GetDefault_UserRender(this)
    } return this.userRender;
  } set UserRender(v) {
    this.userRender = v;
  }

  mainViewRender
  get MainViewRender() {
    if (!this.mainViewRender) {
      this.mainViewRender = util.GetDefault_MainViewRender(this)
    } return this.mainViewRender
  } set MainViewRender(v) {
    this.mainViewRender = v;
  }

  get MainView() {
    return this.MainViewRender();
  }

  modalViewRender
  get ModalViewRender() {
    if (!this.modalViewRender) {
      this.modalViewRender = util.GetDefault_ModalViewRender(this)
    } return this.modalViewRender
  } set ModalViewRender(v) {
    this.modalViewRender = v;
  }

  get ModalView() {
    return this.ModalViewRender();
  }

  viewRender
  get ViewRender() {
    if (!this.viewRender) {
      this.viewRender = util.GetDefault_ViewRender(this)
    } return this.viewRender
  } set ViewRender(v) {
    this.viewRender = v;
  }

  get View() {
    return this.ViewRender();
  }
}