import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../../util/generalUtil'
import MassRowView from '../row/MassRowView';

export class MassTBody {
  constructor() {
    makeObservable(this, {
      rows : observable
    })
  }

  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  rows = []
  get Rows() {
    return this.rows
  } set Rows(v){
    this.rows = v;
  }

  get View() {
    return <tbody>
      {this.Rows ? this.Rows.map((x, i) => {
        return <MassRowView key={x.id} store={x} />
      }) : <tr/>}
    </tbody>
  }
}