import { makeObservable, observable, action } from 'mobx';
import { PMDataVM } from '../../../single/form/PMDataVM';
import { pmData } from '../../mass/item/MassItemUtil';
import * as pvt from '../../project/ProjectViewType'
import * as util from './LeftNavVMUtil'

export const pmDataVM = () => PMDataVM.Instance

export class LeftNavVM {
  constructor() {
    makeObservable(this, {
      projectViewType : observable,
      items : observable,
      title : observable,
      preTitle : observable
    })
  }

  preTitle = undefined
  get PreTitle (){
    if (!this.preTitle){
      this.preTitle = util.GetPreTitle(this);
    }
    return this.preTitle;
  } set PreTitle(v){
    this.preTitle = v;
  }

  title = undefined
  get Title (){
    if (!this.title){
      this.title = util.GetTitle(this);
    }
    return this.title;
  } set Title(v){
    this.title = v;
  }

  items = undefined
  get Items (){
    if (!this.items){
      this.items = util.GetItems(this);
    }
    return this.items;
  } set Items(v){
    this.items = v;
  }

  currentProject
  get CurrentProject(){
    return this.currentProject;
  } set CurrentProject(v){
    this.currentProject = v;

    this.ModelItem.CurrentProject = v?.ModelItem;
    this.Items = util.GetItems(this);
    this.PreTitle = util.GetPreTitle(this);
    this.Title = util.GetTitle(this);
  }

  projectViewType = undefined
  get ProjectViewType(){
    if (!this.projectViewType){
      this.projectViewType = pmDataVM().ProjectViewType;
    }
    return this.projectViewType
  } set ProjectViewType(v){
    this.projectViewType = pmDataVM().ProjectViewType = v;
  }

  GetItemClass = (item) => {
    return `leftnav__item ${item.Value === this.ProjectViewType ? 'active' : ''}`;
  }

  SetView = (item) => {
    this.ProjectViewType = item.Value;
  }

  get UpdatedDate(){
    return pmData().UpdatedDate;
  }
}