import * as util from './MT_Data_RowUtil'

export class MT_Data_Row {
  Worksheet

  Row

  get Rank(){
    return this.Row.Rank;
  }

  subItems
  get SubItems(){
    if (!this.subItems){
      this.subItems = util.GetSubItems(this);
    }
    return this.subItems;
  }

  get FullItems(){
    return util.GetFullItems(this);
  }

  cells
  get Cells(){
    if (!this.cells){
      this.cells = util.GetCells(this);
    }
    return this.cells;
  }

  get FullIndex() {
    return this.Worksheet.FullRows.indexOf(this);
  }

  title
  get Title(){
    if (!this.title){
      this.title = util.GetTitle(this);
    }
    return this.title;
  }
}