import * as mif from '../item/MassItemField'
import * as tableUtil from '../../../../table/TableVMUtil'
import * as colUtil from '../../../../table/TableColumnUtil'
import * as cit from '../../../../table/TableColumn_InputType'
import TableVM from '../../../../table/TableVM'
import { MassDetailTable } from './MassDetailTable'
import { PMData } from '../../../single/model/PMData'

export const pmData = () => PMData.Instance

// Instance
export const get = (dict) => {
  var qI = new MassDetailTable();

  qI.Dict = dict;
  var obj = qI.Table;

  return qI;
}

// Property
export const GetInitialColumnNames = (q) => {
  return q.SettingData.Columns;
}

export const GetColumns = (q) => {
  //return q.InitialColumnNames.map(x => colUtil.GetByTitle(x, q.Table));

  var values = q.InitialColumnNames;
  var dict = pmData().MIF_Dict_ForDetail;
  var list = dict.filter(x => values.indexOf(x.Value) !== -1);

  return list.map(x => colUtil.GetByTitle(x.Name, q.Table, x.Value));
  //return q.InitialColumnNames.map(x => colUtil.GetByTitle(x, q.Table));
}

export const GetTable = (q) => {
  var qI = new TableVM();

  qI.CanHandleItem = false;

  //qI.AllColumnNames = mif.GetForMassGroup();
  qI.AllColumnNames = pmData().MIF_Dict_ForDetail;

  qI.Column_InputType = cit.select
  qI.Override_Get_CellValue = (item, cell, row, col) => {
    var field = col.DataIndex[0];
    var val = item.GetValue(field,
      (val) => {
        cell.ValueVM.Content = val;
      }).Value;
    return val;
  }
  qI.Column_Title_SelectWidth = 150
  return qI;
}