import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Select } from "antd";
import { MultiSelectVM } from "./MultiSelectVM";
import * as genUtil from '../../util/generalUtil'

const { Option } = Select;

class MultiSelectView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MultiSelectVM();
    } else {
      this.state = store;
    }
  }

  onValueChange = (v) => {
    var data = this.state;
    data.SelectedIndexs = v;
  }

  onDropdownVisibleChange = (v) => {
    var data = this.state;
    data.Open = v;
  }

  render() {
    var data = this.state;
    var style = this.props.style ? this.props.style : { width: '100%' }; 
    var { Placeholder, Items, SelectedIndexs, InitialIndexs } = data;

    if (SelectedIndexs.length === 0) {
      SelectedIndexs = InitialIndexs;
      data.InitialIndexs = [];
    }

    var elems = Items ? Items.map((item, index) => {
      var displayContent = data.TitleRender(item);

      var id = undefined;
      if (item === undefined){
        id = genUtil.randomId();
      }else if (typeof item === 'string' || !isNaN(item)) {
        id = item;
      } else {
        id = item._id ? item._id : item.id;
      }
      return <Option key={id} value={index}>{displayContent}</Option>
    }) : "";

    return (
      <>
        <Select style={style}
          mode="multiple"
          placeholder={Placeholder}
          optionFilterProp="children"
          onChange={this.onValueChange}
          value={SelectedIndexs}
          filterOption={(input, option) =>
            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
          open={data.CanOpen && data.Open}
          onDropdownVisibleChange={this.onDropdownVisibleChange}
          disabled={data.Disabled}
        >
          {elems}
        </Select>
      </>
    )
  }
}

export default observer(MultiSelectView)