import * as itemUtil from './MassDetailSettingUtil'
import * as genUtil from '../../../../../util/generalUtil'

export class MassDetailSetting_Dict {
  items
  get Items(){
    return this.items;
  } set Items(v){
    this.items = v;
    this.GetItemsDone = true;
  }

  OnGetItemsDone_Func_Dict = []

  set GetItemsDone (v){
    this.OnGetItemsDone_Func_Dict.forEach(x => x.Value());
  }
}