import * as util from './MT_Data_WorksheetUtil'

export class MT_Data_Worksheet {
  get Table(){
    return this.Workbook.Table;
  }

  Title

  mainRows
  get MainRows(){
    if (!this.mainRows){
      this.mainRows = util.GetMainRows(this)
    }
    return this.mainRows;
  }

  fullRows
  get FullRows(){
    if (!this.fullRows){
      this.fullRows = util.GetFullRows(this);
    }
    return this.fullRows;
  }

  valueCells
  get ValueCells(){
    if (!this.valueCells){
      this.valueCells = util.GetValueCells(this);
    }
    return this.valueCells;
  }

  titleCells
  get TitleCells(){
    if (!this.titleCells){
      this.titleCells = util.GetTitleCells(this);
    }
    return this.titleCells;
  }

  allCells
  get AllCells(){
    if (!this.allCells){
      this.allCells = this.TitleCells.concat(this.ValueCells);
    }
    return this.allCells;
  }

  get RangeName(){
    return util.GetRangeName(this); 
  }

  First_TitleColumn = 0
  First_TitleRow = 0

  First_ValueColumn = 0;
  First_ValueRow = 1;

  get ResultData(){
    var obj = {
      '!ref' : this.RangeName
    };

    this.AllCells.forEach(x => {
      obj[x.Address] = x.ResultData;
    })

    return obj;
  }
}