import { TLS_Data_WrapperVM } from "./TLS_Data_WrapperVM"
import * as bindingUtil from '../../level/LevelSettingDataVMUtil'
import * as typicalUtil from '../TypicalLevelSettingDataVMUtil'
import * as genUtil from '../../../../../../util/generalUtil'

// Instance
export const get = (q, dict) => {
  var qI = new TLS_Data_WrapperVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  // Gán sự kiện update Level cho Binding
  genUtil.Add_2(
    q.OnSet_LevelStorageList_Func_Dict,
    () => qI.LevelStorageList = GetLevelStorageList(qI),
    false
  );

  return qI;
}

// Property
export const GetBinding = (q) => {
  return bindingUtil.get(q.ModelItem.Binding, q);
}

export const GetTypical = (q) => {
  return typicalUtil.get(q.ModelItem.Typical, q);
}

export const GetLevelStorageList = (q) => {
  return q.ModelItem.LevelStorageList;
}