import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Input, Row, Select } from "antd";
import RebarMass_Dict from "./RebarMass_Dict";
import TableView from "../../../../table/TableView";

const { Option } = Select;

class RebarMass_DictView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new RebarMass_Dict();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <TableView store={data.TableVM}/>
      </>
    )
  }
}

export default observer(RebarMass_DictView)