export class MI_ValueItem {
  Unit

  Value = 0

  get ValueString() {
    var roundingFactor = 2;
    var unit = this.Unit;
    switch (unit) {
      case 'each':
        roundingFactor = 0;
        break;
      default:
        roundingFactor = 2;
        break;
    }

    var formatValue = this.Value.toLocaleString('en-US', { maximumFractionDigits: roundingFactor });
    return `${formatValue}`;
  }

  get ApiData(){
    return {
      unit : this.Unit,
      value : this.Value
    }
  }
}