import { SaveFilled } from '@ant-design/icons';
import { Button, message } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../../util/generalUtil'
import InputView from '../../../../../input/InputView';
import SelectView from '../../../../../select/SelectView';
import { PMDataVM } from '../../../../single/form/PMDataVM';
import SaveView from './SaveView';
import * as util from './SaveVMUtil'
import * as is from './Save_InputState'
import * as mgsdd_ut from '../dict/dict/MGS_Dict_Dict_UseType'

const pmDataVM = () => PMDataVM.Instance

export class SaveVM {
  constructor() {
    makeObservable(this, {
      state: observable,
      Is_Input_Visible : observable
    })
  }

  get Name(){
    return this.ModelItem.Name;
  } set Name(v){
    this.ModelItem.Name = v
  }

  state = undefined
  get State(){
    return this.state
  } set State(v){
    this.state = v;
    util.SetState(this, v)
  }

  ToggleState = () => {
    this.State = this.State === is.select ? is.input : is.select
  }

  get ToggleContent(){
    return this.State === is.select ? 'inp' : 'sel'
  }

  selectVM
  get SelectVM() {
    if (!this.selectVM) {
      this.selectVM = util.GetSelectVM(this)
    } return this.selectVM
  }

  inputVM
  get InputVM() {
    if (!this.inputVM) {
      this.inputVM = util.GetInputVM(this)
    } return this.inputVM
  }

  Is_Input_Visible = false

  Initial = () => {
    this.Is_Input_Visible = true;
    this.State = is.select;
  }

  OK = () => {
    var name = this.Name
    if (!name || name.length === 0){
      message.warning('Vui lòng chọn / điền bộ thiết lập!');
      return;
    }

    this.Is_Input_Visible = false
    this.Run();
  }

  Cancel = () => {
    this.Is_Input_Visible = false
  }

  Run = () => {
    this.ModelItem.Run();

    var q1 = pmDataVM().Mgs_Dict_Dict;
    if (q1.UseType === mgsdd_ut.save){
      q1.Is_Modal_Visible = false;
    }
  }

  get SaveIcon_View(){
    return <Button type='primary' size='small'
      disabled={this.Is_Input_Visible}
      onClick={this.Initial}>
      <SaveFilled/>
    </Button>
  }

  get InputView() {
    switch (this.State) {
      case is.select:
        return <SelectView store={this.SelectVM} />
      case is.input:
        return <InputView store={this.InputVM} />
    }
  }

  get MainView(){
    return <SaveView store={this} />
  }
}