import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Col, Row } from 'antd';
import { withRouter } from "react-router-dom";
import './ProjectComp.css'
import * as util from './ProjectCompVMUtil'
import TwoCol_SelectView from '../../../../select/twocol/TwoCol_SelectView';

class ProjectCompView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = util.get()
    } else {
      this.state = store;
    }
  }

  componentDidMount() {
    var data = this.state;
    const id = this.props.match.params.id;
    data.id = id;
  }

  render() {
    var data = this.state;
    var projectVM = data.ProjectVM;

    return (
      <>
        {projectVM ? data.ViewElement : ''}
      </>
    );
  }
}

export default withRouter(observer(ProjectCompView));
