import * as util from './SiteVMUtil'
import { makeObservable, observable, action } from 'mobx';
import { message } from 'antd';


export class SiteVM {
  constructor() {
    makeObservable(this, {
      Is_Modal_Visible: observable,
    })
  }

  GetValue = (field) => {
    return this.ModelItem[field]
  }
  SetValue = (field, v) => {
    this.ModelItem[field] = v;
  }

  inputVMs
  get InputVMs() {
    if (!this.inputVMs) {
      this.inputVMs = util.GetInputVMs(this)
    } return this.inputVMs;
  }

  Initial = () => {
    util.Initial(this)
  }

  Run_SetValue = () => {
    util.Run_SetValue(this)
  }

  OpenModal = () => {
    this.Initial();
    this.Is_Modal_Visible = true;
    this.Is_Run_Valid = false;
  }

  ModalCancel = () => {
    this.Is_Modal_Visible = false
  }

  ModalOK = () => {
    this.Run_SetValue();

    if (this.Is_OK_Valid) {
      this.Is_Modal_Visible = false
      this.Is_Run_Valid = true;

      this.Run();
    } else {
      message.warn('Bạn phải điền đủ các trường thông tin!');
    }
  }

  Run = () => {
    if (this.Is_Run_Valid) {
      this.ModelItem.Run();
    }
  }

  // Database
  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle);
  }

  // View
  get InputView() {
    return util.GetInputView(this)
  }

  Is_Modal_Visible = false

  get ModalView() {
    return util.GetModalView(this)
  }
}