import Setting from "../base/Setting";
import * as util from './TypicalLevelSettingUtil'

export class TypicalLevelSetting extends Setting {
  fieldFilterStorageList
  get FieldFilterStorageList(){
    if (!this.fieldFilterStorageList){
      this.fieldFilterStorageList = util.GetFieldFilterStorageList();
    }
    return this.fieldFilterStorageList;
  }

  Save = () => {
    util.SaveDatabase(this)
  }

  levelStorageList
  get LevelStorageList(){
    if (!this.levelStorageList){
      this.levelStorageList = util.GetLevelStorageList(this);
    }
    return this.levelStorageList;
  } set LevelStorageList(v){
    this.levelStorageList = v;
    this.GetLevelStorageList = true;
  }

  OnGetLevelStorageList_Funcs = []

  set GetLevelStorageList(v){
    this.OnGetLevelStorageList_Funcs.forEach(x => x());
  }
}