import { makeObservable, observable, action } from 'mobx';
import * as util from './MassDetailTableUtil'

export class MassDetailTable {
  constructor() {
    // makeObservable(this, {

    // })
  }

  data
  get Data(){
    return this.data;
  } set Data(v){
    this.data = v;
    this.Table.DataSource = v;
  }

  initialColumnNames
  get InitialColumnNames(){
    if (!this.initialColumnNames){
      this.initialColumnNames = util.GetInitialColumnNames(this);
    }
    return this.initialColumnNames;
  } set InitialColumnNames (v){
    this.initialColumnNames = v;

    this.Columns = util.GetColumns(this);
  }

  columns
  get Columns(){
    if (!this.columns){
      this.columns = util.GetColumns(this);
    }
    return this.columns
  } set Columns(v){
    this.columns = v;
    this.Table.Columns = v;
  }

  table
  get Table(){
    if (!this.table){
      var obj = this.table = util.GetTable(this);
      obj.Columns = this.Columns;
    }
    return this.table;
  }

  AddColumn = () => {
    this.Table.AddColumn();
  }

  get ColumnNames (){
    return this.Table.Columns.filter(x => !x.IsAction)
      .map(x => x.Title);
  }

  settingData
  get SettingData(){
    if (!this.settingData){
      this.settingData = this.Dict.Setting?.Data;
    }
    return this.settingData;
  } set SettingData(v){
    this.settingData = v;

    this.InitialColumnNames = util.GetInitialColumnNames(this);
  }
}