// Property
export const GetSubItems = (q) => {
  var column = q.Column;
  return q.Dict.SubItems.map(x => {
    var list = x.Cells;
    var index = list.findIndex(y => y.Column === column);
    return index !== -1 ? list[index] : undefined;
  }).filter(x => x !== undefined);
}

export const GetIsContinueSubRows = (q) => {
  var isValid = true;

  q.SubItems.forEach(x => {
    if (isValid && x.SubItems.length !== 0){
      isValid = false;
    }
  })

  return isValid;
}