import { makeObservable, observable, action } from 'mobx';
import MassTable from "../table/MassTable";
import * as textUtil from '../../../../text/TextVMUtil'
import * as util from './MassItem_DictUtil'
import MassTableView from "../table/MassTableView";

export default class MassItem_Dict {
  constructor() {
    makeObservable(this, {
      massTable: observable,
      IsDataPending: observable
    })
  }

  items
  get Items() {
    return this.items;
  }
  set Items(v) {
    this.items = v;

    this.IsDataPending = true;
    this.MassTable = util.GetMassTable(this)
  }

  // Table
  massTable = undefined
  get MassTable() {
    if (!this.massTable) {
      this.massTable = util.GetMassTable(this);
    } return this.massTable;
  } set MassTable(v) {
    this.massTable = v
  }

  get MassTableView() {
    return <MassTableView
      key={this.MassTable?.id}
      store={this.MassTable} />
  }

  // Loading
  IsDataPending = false;

  get LoadingSpinTip() {
    return `loading data ... (${(this.HandledRatio * 100).toFixed(1)}%)`;
  }

  loadingSpinTipVM
  get LoadingSpinTipVM() {
    if (!this.loadingSpinTipVM) {
      this.loadingSpinTipVM = textUtil.get(this.LoadingSpinTip);
    }
    return this.loadingSpinTipVM;
  }

  handledRatio = 0
  get HandledRatio() {
    return this.handledRatio;
  }
  set HandledRatio(v) {
    this.handledRatio = v;
    this.LoadingSpinTipVM.Content = this.LoadingSpinTip;
  }
}