import { LevelDict } from "./LevelDict"
import * as itemUtil from './LevelItemUtil'
import { baseurl } from './LevelDict_Url'
import * as api from '../../../../../util/apiUtil'

// Instance
export const GetFromData = (data, project) => {
  var qI = new LevelDict();

  qI.Project = project;

  var items = data.data.items;
  qI.Items = items ? items.map(x => itemUtil.GetFromData(x, qI)) : []

  return qI;
}

export const GetApi = (projectId, handle) => {
  var data = {
    projectId
  }

  var url = `${baseurl()}/get`;
  api.post(url, data).then(response => response.data)
  .then(data => {
    if (handle){
      handle(data);
    }
  });
}