import * as api from '../../../../../../util/apiUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import { url } from './Item_Url'
import { Item } from './Item'
import { message } from 'antd'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id
  qI.projectId = data.projectId
  qI.userId = data.userId
  qI.Commands = data.commands

  return qI;
}

export const get = (dict) => {
  var qI = new Item();

  qI.Dict = dict;

  return qI;
}

// Property
export const Handle_User = (q) => {
  if (q.IsGet_User) return undefined;
  q.IsGet_User = true;

  const setObj = (data) => {
    if (data) {
      q.User = genUtil.SingleFilter(data, x => x._id === q.userId)
    } else {
      q.User = null
    }
    q.IsGet_User = false;
  }

  if (!q.userId){
    setObj();
  } else {
    var host = q.Dict.Dict
    if (host.Users){
      setObj(host.Users)
    } else {
      genUtil.Add_2(
        host.OnSet_Users_Func,
        () => setObj(host.Users)
      )
    }
  }
}

export const Handle_Project = (q) => {
  if (q.IsGet_Project) return undefined;
  q.IsGet_Project = true;

  const setObj = (data) => {
    if (data) {
      q.Project = genUtil.SingleFilter(data, x => x._id === q.projectId)
    } else {
      q.Project = null
    }
    q.IsGet_Project = false;
  }

  if (!q.projectId){
    setObj();
  } else {
    var host = q.Dict.Dict
    if (host.Projects){
      setObj(host.Projects)
    } else {
      genUtil.Add_2(
        host.OnSet_Projects_Func,
        () => setObj(host.Projects)
      )
    }
  }
}

// Method


// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}