import * as selectUtil from "../../../../../select/SelectVMUtil"
import * as textUtil from "../../../../../text/TextVMUtil";
import { SaveVM } from "./SaveVM";
import * as is from './Save_InputState'

// Instance
export const get = (q, dict) => {
  var qI =new SaveVM();
  
  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetSelectVM = (q) => {
  var qI = selectUtil.get();

  qI.Placeholder = 'chọn thiết lập';
  qI.TitleRender = (v) => v.Name
  qI.OnSelectedItemChanged = (v) => q.Name = v?.Name

  return qI;
}

export const GetInputVM = (q) => {
  var qI = textUtil.get(q.Name, 'tên thiết lập');
  qI.onSetContent = (v) => q.Name = qI.Content
  return qI;
}

// Method
export const SetState = (q, v) => {
  var host = q.Dict;

  switch (v) {
    case is.select:
      var obj = q.SelectVM;
      obj.Items = host.Items;
      obj.InitialItem = host.Main_CurrentItem
      break;
    case is.input:
      var obj = q.InputVM
      obj.Content = q.Name
      break;
  }
}