import React, { Component } from 'react';
import { observer } from 'mobx-react';
import PMDataView from '../main/single/form/PMDataView';
import { Col, Row } from 'antd';
import MassResultCompView from '../main/entity/mass/result/MassResultCompView';
import { PMDataVM } from '../main/single/form/PMDataVM';
import * as pvt from '../main/entity/project/ProjectViewType'
import DO_CompView from '../main/entity/doc/comp/DO_CompView';
import '../main/entity/project/comp/ProjectComp.css'
import MassTypeSettingView from '../main/entity/setting/massType/MassTypeSettingView';

class TestView extends Component {
  render() {
    var data = PMDataVM.Instance;

    var elem = ''
    switch (data.ProjectViewType) {
      case pvt.mass:
        elem = <div className='massresult__comp__wrapper'>
          <MassResultCompView store={data.MassResultCompVM} />
        </div>
        break;
      case pvt.mepsetting:
        elem = 'MEP Setting'
        break;
      case pvt.document:
        elem = <DO_CompView />
        break;
      case pvt.masstype:
        elem = <MassTypeSettingView />
        break;
    }

    return (
      <>
        {elem}
      </>
    );
  }
}

export default observer(TestView);
