import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { MassTBody } from "./MassTBody";

class MassTBody_View extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassTBody();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.View}
      </>
    )
  }
}

export default observer(MassTBody_View)