import { MT_Cell } from "./MT_Cell";
import * as c_vt from './MT_Cell_ValueType'
import * as genUtil from '../../../../../../util/generalUtil'

// Property
export const GetDataRows = (q) => {
  return q.Workbook.Data_WS.FullRows
    .filter(x => x.SubItems.length === 0)
}

export const GetTableRows = (q) => {
  return q.DataRows.map(x => x.Row)
}

export const GetValueFields = (q) => {
  return q.Table.Columns.map(x => x.Name);
}

export const GetRecords = (q) => {
  return q.DataRows.map(x => {
    // Init Cells
    var cells = x.Cells;

    var obj = x.Row.ExportData;

    // Xử lý phần sắp xếp  Info Fields
    var infoFields = q.InfoFields;
    obj.Infos.map(x => x.Key).forEach(y => {
      if (!infoFields.includes(y)) {
        infoFields.push(y);
      }
    });

    return {
      Binding_DataRow: x,
      Value: obj
    }
  })
}

export const GetValueCells = (q) => {
  var fc = q.First_ValueColumn;
  var fr = q.First_ValueRow;

  // Init InfoFiels
  var records = q.Records;

  var list = [];
  var ifs = q.InfoFields;
  var ifl = ifs.length
  var vfs = q.ValueFields;

  records.forEach((x, i) => {
    // Truy xuất phần binding
    var vc_dict = x.Binding_DataRow.ValueCell_Dict

    // Xử lý phần Value Object
    var value = x.Value;
    var cell
    var row = i + fr;

    // Xử lý Info
    value.Infos.forEach(y => {
      cell = new MT_Cell();
      cell.Type = c_vt.string;
      cell.Value = y.Value;
      cell.Column = ifs.indexOf(y.Key) + fc;
      cell.Row = row

      list.push(cell);
    })

    // Xử lý Value
    value.Values.forEach(y => {
      var key = y.Key;
      var cell_val = y.Value;

      cell = new MT_Cell();
      cell.Type = c_vt.number;
      cell.Value = cell_val;
      cell.Format = '0.00'
      cell.Column = vfs.indexOf(key) + fc + ifl;
      cell.Row = row

      list.push(cell);

      // Xử lý phần Binding
      //debugger
      var index = vc_dict.findIndex(z => z.Key === key);
      var data_cell = vc_dict[index].Value;

      if (cell_val) {
        data_cell.Formula = `raw_data!${cell.Address}`;
      }
    })
  })

  return list;
}

export const GetTitleCells = (q) => {
  // Init InfoFiels
  var records = q.Records;

  var fc = q.First_TitleColumn;
  var fr = q.First_TitleRow;

  var fields = q.InfoFields.concat(q.ValueFields)

  var list = [];
  fields.forEach((x, i) => {
    var cell = new MT_Cell();
    cell.Type = c_vt.string;
    cell.Value = x;
    cell.Column = i + fc;
    cell.Row = fr
    list.push(cell);
  })

  return list;
}

export const GetRangeName = (q) => {
  var listCells = q.AllCells;

  var range = listCells.reduce(
    ({ minRow, minCol, maxRow, maxCol }, x) => ({
      minRow: minRow < x.Row ? minRow : x.Row,
      minCol: minCol < x.Column ? minCol : x.Column,
      maxRow: maxRow > x.Row ? maxRow : x.Row,
      maxCol: maxCol > x.Column ? maxCol : x.Column,
    }), {
    minRow: listCells[0].Row, minCol: listCells[0].Column,
    maxRow: listCells[0].Row, maxRow: listCells[0].Column
  });

  return genUtil.ExcelRangeName
    (range.minRow, range.minCol, range.maxRow, range.maxCol);
}