import { message } from 'antd';
import * as api from '../../../../../util/apiUtil'

export const AddPhase = (projectId, phaseId) => {
  var apiData = {
    projectId, phaseId
  }
  var url = `https://api.bimdev.vn/api/hbs`;

  const apiFunc = () => api.post(`${url}/addphase`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      message.success("attach phase to settings successfully!");
    })
}