import { PMData } from '../../../single/model/PMData';
import { TypicalLevelSettingData } from './TypicalLevelSettingData';
import * as pageItemUtil from './pageItem/TLS_PageItemUtil'

export const pmData = ()  =>{
  return PMData.Instance;
}

export const get = (data, dict) => {
  var qI = new TypicalLevelSettingData();
  qI.PageItems = data.pageItems.map(x => pageItemUtil.get(x, qI));
  qI.Dict = dict;
  return qI;
}

export const GetTest = (dict) => {
  var qI = new TypicalLevelSettingData();
  qI.Dict = dict;
  return qI;
}

export const GetPageItems = (q) => {
  return [ pageItemUtil.GetDefault(q) ]
}