import * as genUtil from '../../../../../util/generalUtil'
import { ExportSetVM } from './ExportSetVM'
import { EntDelete } from '../../../../delete/EntDelete';
import * as d_tt from '../../../../delete/EntDelete_ToggleType'
import { Button, message } from 'antd';
import { CheckCircleOutlined, CloseCircleOutlined, SaveFilled } from '@ant-design/icons';
import { Table } from './massitem_table/Table';
import * as tableUtil from './massitem_table/TableUtil';
import { SelectVM } from '../../../../select/SelectVM';
import { TextVM } from '../../../../text/TextVM';
import * as mt from '../../mass/MassType'

// Instance
export const get = (q, dict) => {
  var qI = new ExportSetVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetActiveTool = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.mouseEvent
  qI.Retrieve_WrapperView = () => !q.IsActive ?
    <Button type='primary' size='small'
      onClick={q.Set_Active}>
      <CheckCircleOutlined />
    </Button>
    :
    <Button type='primary' size='small'
      onClick={q.UnSet_Active}
      className='btn-warning'>
      <CloseCircleOutlined />
    </Button>

  return qI;
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = d_tt.mouseEvent
  qI.Title = 'Xác nhận xóa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

export const Handle_MassItems = (q) => {
  if (q.IsGet_MassItems) return undefined;
  q.IsGet_MassItems = true;

  const setObj = (data) => {
    q.MassItems = data
    q.IsGet_MassItems = false;
  }

  var host = q.ModelItem
  if (host.MassItems) {
    setObj(host.MassItems)
  }

  genUtil.Add_2(
    host.OnSet_MassItems_Func,
    () => setObj(host.MassItems),
    false
  )
}

export const Handle_MassItems_Table = (q) => {
  if (q.IsGet_MassItems_Table) return undefined;
  q.IsGet_MassItems_Table = true;

  const setObj = (data) => {
    q.MassItems_Table = data.length !== 0 ?
      tableUtil.GetFromDb(data[0], q) : tableUtil.get(q)
    q.IsGet_MassItems_Table = false;
  }

  var host = q.ModelItem
  var apiData = {
    dictId : host._id
  }

  tableUtil.GetData(apiData, (data) => {
    setObj(data)
  });
}

export const GetMassItems_Table = (q) => {
  var qI = new Table();
  qI.Dict = q;
  return qI;
}

export const GetMassType_Render = (q) => {
  var qI = new TextVM();
  qI.Content = q.MassType;
  return qI;
}

export const GetMassType_Select = (q) => {
  var qI = new SelectVM();
  qI.Items = mt.GetList();
  qI.InitialItem = q.MassType;
  qI.OnSelectedItemChanged = (v) => { 
    q.MassType = v;
  }

  return qI;
}