import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Divider, Input, Select, Spin } from "antd";
import { PMDataVM } from "../../../single/form/PMDataVM";
import { DO_CompVM } from "./DO_CompVM";
import * as util from './DO_CompVMUtil'
import './DO_Comp.css'
import DO_ElementView from "../DO_ElementView";

const { Option } = Select;

export const pmDataVM = () => PMDataVM.Instance;

class DO_CompView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = util.GetTest();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='doelement__dict'>
          {data.Items ?
            data.Items.map(x => {
              return <DO_ElementView key={x.Title}
                store={x} />
            })
            : ''}
        </div>
      </>
    )
  }
}

export default observer(DO_CompView)