import { PMData } from "../main/single/model/PMData";
import TableCell from "./TableCell"

export const pmData = () => PMData.Instance;

// Instance
export const get = (row, col) => {
  var qI = new TableCell();
  qI.Row = row;
  qI.Column = col;
  qI.Table = row.Table;
  return qI;
}

// Property
export const GetValue = (q) => {
  var dataIndex = q.DataIndex;
  var item = q.Item;

  if (q.Table.Override_Get_CellValue){
    var val = q.Table.Override_Get_CellValue(item, q, q.Row, q.Column);
    return val;
  }

  var value = undefined;

  if (dataIndex) {
    value = item;
    dataIndex.forEach(x => {
      value = value[x]
    })
  }

  return value;
}