import MIPh_Item from "./MIPh_Item"
import * as genUtil from '../../../../../../util/generalUtil'
import * as mif from '../MassItemField'
import * as levelItemUtil from '../levelValue/Level_ItemUtil'

// Instance
export const get = (phase, dict) => {
  var qI = new MIPh_Item();
  qI.Phase = phase;
  qI.Dict = dict;
  return qI;
}

// Property
export const Handle_TLS_LevelGroup = (q) => {
  if (q.IsGet_TLS_LevelGroup) return undefined;
  q.IsGet_TLS_LevelGroup = true;

  var phase = q.Phase;

  const getLevelGroup_Func = (tls) => {
    // var pi = GetValidTLS_PageItem(q, tls.Data);
    // return GetValidTLS_LevelGroup(q, pi);

    var data = tls.Data.Typical

    var pi = GetValidTLS_PageItem(q, data);
    var obj = GetValidTLS_LevelGroup(q, pi);
    return obj;
  }

  var tls = phase.TypicalLevelSetting;
  if (tls) {
    q.TLS_LevelGroup = getLevelGroup_Func(tls);
  } else {
    // phase.OnGetTypicalLevelSetting_Funcs.push(() => {
    //   q.TLS_LevelGroup = getLevelGroup_Func(phase.TypicalLevelSetting);
    // })

    genUtil.Add_2(
      phase.OnGetTypicalLevelSetting_Func_Dict,
      () => q.TLS_LevelGroup = getLevelGroup_Func(phase.TypicalLevelSetting)
    )
  }
}

export const GetValidTLS_PageItem = (q, tls) => {
  if (!tls) return undefined;
  var validPi = undefined
  tls.PageItems.forEach(x => {
    if (!validPi && IsValidTLS_PageItem(q, x)) {
      validPi = x;
    }
  })

  return validPi;
}

export const IsValidTLS_PageItem = (q, pageItem) => {
  var filters = pageItem.Filters;
  var isValid = true;
  filters.forEach(x => {
    if (isValid) {
      var field = x.Field;
      var qVal = q.MassItem.GetValue_Mas(field);
      isValid = x.Values.includes(qVal);
    }
  })
  return isValid;
}

export const GetValidTLS_LevelGroup = (q, pageItem) => {
  if (!pageItem) return undefined;
  var validLg = undefined
  pageItem.LevelGroups.forEach(x => {
    var primary_Values = x.Primary_Values
    var qVal = q.MassItem.GetValue_Mas(mif.level);
    if (!validLg && primary_Values.includes(qVal)) {
      validLg = x;
    }
  })
  return validLg;
}

export const Handle_FullValue = (q) => {
  if (q.IsGet_FullValue) return undefined;
  q.IsGet_FullValue = true;

  const getFullValue_Func = (lg) => {
    var count = lg ? lg.Count : 1
    var obj = q.MassItem.Value * count;
    return obj;
  }

  if (q.TLS_LevelGroup || q.Get_TLS_LevelGroup) {
    q.FullValue = getFullValue_Func(q.TLS_LevelGroup)
  } else {
    genUtil.Add(q.OnGet_TLS_LevelGroup_Func_Dict,
      () => {
        q.FullValue = getFullValue_Func(q.TLS_LevelGroup)
      });
  }
}

// LevelItem
export const GetLevelItem = (q) => {
  return levelItemUtil.get(q.Dict, q);
}