import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './LevelGroupUtil'

export class LevelGroup {
  _id
  get id() {
    if (!this._id) {
      this._id = genUtil.newGUID();
    }
    return this._id;
  }

  get Index() {
    return this.Dict.LevelGroups.indexOf(this);
  }

  Delete = () => {
    this.Dict.LevelGroups.splice(this.Index, 1);
  }

  get ApiData() {
    return {
      levels : this.Levels ? this.Levels : [],
      primary: this.Primary
    }
  }

  get Count(){
    return this.Levels.length
  }


  OnGetLevelStorageList_Func_Dict = []
  get GetLevelStorageList(){
    return this.getLevelStorageList;
  }
  set GetLevelStorageList(v){
    this.getLevelStorageList = v;
    this.OnGetLevelStorageList_Func_Dict.forEach(x => x.Value())
  }

  IsGet_LevelStorageList

  get LevelStorageList (){
    var obj = this.Dict.LevelStorageList;
    if (!obj && !this.IsGet_LevelStorageList){
      this.IsGet_LevelStorageList = true;
      this.Setting.OnGetLevelStorageList_Funcs.push(() => {
        this.GetLevelStorageList = true
      });
    }
    return obj;
  }

  get Setting (){
    return this.Dict.Setting;
  }

  get LevelIndexs(){
    return this.Levels.map(x => this.LevelStorageList.indexOf(x));
  }

  get MinLevel(){
    var list = this.LevelStorageList;
    return list[Math.min.apply(Math, this.LevelIndexs)]
  }

  get MaxLevel(){
    var list = this.LevelStorageList;
    return list[Math.max.apply(Math, this.LevelIndexs)]
  }

  get Description(){
    if (!this.LevelStorageList) return undefined;
    return `Typ: ${this.MinLevel} - ${this.MaxLevel} (${this.Count})`;
  }

  get FullDescription() {
    if (!this.Description) return undefined;
    return `${this.Dict.FilterDescription} (${this.Description})`;
  }

  primary
  get Primary(){
    if (!this.primary){
      this.primary = util.GetDefaultPrimary(this);
    }
    return this.primary
  } set Primary(v){
    this.primary = v;
  }

  get Primary_Values(){
    return util.GetPrimary_Values(this);
  }
}