import * as genUtil from '../../../../../../util/generalUtil'
import * as util from './MassGroupSetting_DictUtil'

export class MassGroupSetting_Dict {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Project
  project
  get Project() {
    return this.project
  } set Project(v) {
    if (this.project === v) return;
    this.project = v;
    util.ReHandle_Items(this);
  }

  UseType

  get Category(){
    return util.GetCategory(this)
  }

  get CategoryName(){
    return util.GetCategoryName(this)
  }

  // Item
  OnSet_Items_Func = []

  items
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
    
    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = (v) => {
    this.Items.push(v);
    if (this.OnAddItem){
      this.OnAddItem(v)
    }
  }

  main_CurrentItem
  get Main_CurrentItem() {
    return this.main_CurrentItem
  } set Main_CurrentItem(v) {
    this.main_CurrentItem = v;
    this.Used_MassGroupComponent = undefined
    this.Used_MassGroupItem = undefined
  }

  save
  get Save() {
    if (!this.save) {
      this.save = util.GetSave(this)
    } return this.save
  }
}