import * as util from './Level_ItemUtil'
import * as genUtil from '../../../../../../util/generalUtil'

export class Level_Item {
  originValue
  get Origin_Value(){
    if (!this.originValue){
      this.originValue = util.GetOrigin_Value(this);
    }
    return this.originValue;
  }

  OnSet_Replace_Value_Func_Dict = []

  replace_Value
  get Replace_Value(){
    if (!this.replace_Value){
      util.Handle_Replace_Value(this);
    }
    return this.replace_Value;
  } set Replace_Value(v){
    this.replace_Value = v;

    genUtil.Handle(
      this.OnSet_Replace_Value_Func_Dict,
      (v) => this.OnSet_Replace_Value_Func_Dict = v
    )
  }

  get Project(){
    return this.MassItem.Project
  }

  get Phase(){
    return this.MassPhase_Item?.Phase
  }

  OnSet_Value_Func_Dict = []

  value
  get Value(){
    if (!this.value){
      util.Handle_Value(this);
    }
    return this.value;
  } set Value(v){
    this.value = v;

    genUtil.Handle(
      this.OnSet_Value_Func_Dict,
      (v) => this.OnSet_Value_Func_Dict = v
    )
  }
}